import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { Group, SmallGroupTeeTime } from '../../../@intermediate/models/get-booking-details.model';
import { PlayerStatus, PriceLevel } from '../../../@intermediate/models/get-tee-sheet.model';
import { ManageTeeTimeTooltipConfigModel } from '../../../@intermediate/models/manage-tee-time.model';

@Component({
  selector: 'tei-pos-manage-small-group-tee-time',
  templateUrl: './manage-small-group-tee-time.component.html',
  styleUrls: ['./manage-small-group-tee-time.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ManageSmallGroupTeeTimesComponent implements OnInit, OnDestroy
{
  @Input() form: UntypedFormGroup;
  @Input() courseID: string;
  @Input() AttachmentGroups: Array<Group>;
  @Input() SmallGroupTeeTime: SmallGroupTeeTime;
  @Input() bookingID: number;
  @Input() ContactLimit: number;
  @Input() CartRequired: boolean;
  @Input() PowerCartRequired: boolean;
  @Input() priceLevels: PriceLevel[];
  @Input() HasAttachments: boolean;
  @Input() checkIn: boolean;
  @Input() noShow: boolean;
  @Input() contactIsRequired: boolean;
  @Input() teeTimeIndex: number;

  StartHoleNumber: string;
  screenSize: number;
  playerStatus = PlayerStatus;
  mediaBreakpointSubscription: Subscription;
  checkInTooltipConfig: ManageTeeTimeTooltipConfigModel = {
    position: 'above',
    text: this.translate.instant('POS_TEE_SHEET.GENERAL.CHECK_IN_ALL'),
    delay: 100
  };
  noShowTooltipConfig: ManageTeeTimeTooltipConfigModel = {
    position: 'above',
    text: this.translate.instant('POS_TEE_SHEET.GENERAL.NO_SHOW_ALL'),
    delay: 100
  };

  get formRawValue() { return this.form?.getRawValue(); }
  get playerListForm(): UntypedFormGroup
  {
    return this.form?.controls["playersList"] as UntypedFormGroup;
  }

  constructor(
    private translate: TranslateService,
    private changeDetectorRef: ChangeDetectorRef,
    private responsiveService: ResponsiveService
  )  {  }

  ngOnInit(): void
  {
    this.StartHoleNumber = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.HOLE') + (Number(this.teeTimeIndex) + 1);
    this.detectMobileScreen();
  }

  private detectMobileScreen(): void
  {
    this.mediaBreakpointSubscription = this.responsiveService.screenWidth.subscribe(screenSize =>
    {
      this.screenSize = screenSize;
      this.changeDetectorRef.detectChanges();
    });
  }

  expansionToggle(event: MouseEvent): void
  {
    event.stopPropagation();
    const parentNode = 'parentNode';
    if ((event.target[parentNode].className as string).includes('toggle'))
    {
      return;
    }
    this.form.get('panelOpenState').patchValue(!this.form.get('panelOpenState').value, { emitEvent: false });
  }

  ngOnDestroy(): void
  {
    this.mediaBreakpointSubscription?.unsubscribe();
  }
}



