import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { PrepaidDepositBreakdownDialogComponent } from './prepaid-deposit-breakdown-dialog.component';

const MATERIAL_MODULES = [
  MatButtonModule,
  MatIconModule,
  MatDividerModule
];
const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule
];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];
const COMPONENTS = [
  PrepaidDepositBreakdownDialogComponent
];


@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS
  ]
})
export class PrepaidDepositBreakdownDialogModule { }
