import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { BehaviorSubject, Subscription, firstValueFrom } from 'rxjs';
import { IManageTeeTimeService } from '../../../@intermediate/manage-tee-time.interface';
import { Player } from '../../../@intermediate/models/get-booking-details.model';
import { GetPlayerEmailsRequest, GetPlayerEmailsResponse } from '../../../@intermediate/models/get-player-email.model';
import { GetRainCheckSetupRequest } from '../../../@intermediate/models/get-rain-check-setup.model';
import { RainCheckWizardDataModel, RainCheckWizardTransitionDataModel } from '../../../@intermediate/models/rain-check-helper.model';
import { ITeeSheetService } from '../../../@intermediate/tee-sheet.interface';

@Component({
  selector: 'tei-pos-tee-sheet-rain-check-wizard',
  templateUrl: './rain-check-wizard.component.html',
  styleUrls: ['./rain-check-wizard.component.scss']
})
export class RainCheckWizardComponent implements OnInit, OnDestroy
{

  transitionData: RainCheckWizardTransitionDataModel = {
    totalPrice: 0,
    players: []
  };

  reloadSelectPlayers: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  subscriptions: Subscription = new Subscription();

  constructor(
    @Inject('ITeeSheetService') private teeSheetService: ITeeSheetService,
    @Inject('IManageTeeTimeService') private manageTeeTimeService: IManageTeeTimeService,
    @Inject(MAT_DIALOG_DATA) public data: RainCheckWizardDataModel
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.getPlayerEmails();
    await this.getRainCheckSetup();
  }

  private async getRainCheckSetup(): Promise<void>
  {
    const rainCheckSetupRequest = new GetRainCheckSetupRequest();
    rainCheckSetupRequest.CourseID = this.data?.Course?.CourseID;
    rainCheckSetupRequest.BookingID = this.data?.BookingID;

    const response = await firstValueFrom(this.teeSheetService.GetRainCheckSetup(rainCheckSetupRequest));
    this.data.RainCheckSetup = response;
  }

  private getPlayerEmails(): void
  {
    const request = new GetPlayerEmailsRequest();
    request.BookingID = this.data.BookingID;
    request.Players = this.data.Players?.reduce(
      (acc: string[], curr: Player) =>
      {
        if (curr?.Contact?.ContactID)
          acc.push(curr?.Contact?.ContactID);

        return acc;
      }, []
    );

    this.subscriptions.add(
      this.manageTeeTimeService.GetPlayerEmails(request).subscribe((response: GetPlayerEmailsResponse) =>
      {
        this.data.Players?.forEach((player, index) =>
        {
          if (player?.Contact)
            player.Contact.Email = response.PlayerEmails[index] || '';
        });
      })
    );
  }

  setTransitionData(transitionData: RainCheckWizardTransitionDataModel): void
  {
    this.transitionData = transitionData;
  }

  async removeRainCheckByPlayerNumber(playerNumber: number): Promise<void>
  {
    this.data.RainCheckSetup.PlayerRainchecks = this.data.RainCheckSetup.PlayerRainchecks.filter(x => x.PlayerNumber !== playerNumber)
    this.reloadSelectPlayers.next(true);
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
