import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { MultiChipModule, NotesModule } from '@tei/material';
import { ContactAvatarModule } from '@tei/office-contacts';
import { ConfirmTeeTimeBookingDialogComponent } from './confirm-tee-time-booking-dialog.component';



const MATERIAL_MODULES = [
  MatChipsModule,
  MatCheckboxModule,
  MatIconModule,
  MatSelectModule,
  MatButtonModule,
  MatDividerModule
];
const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule
];
const TEI_MATERIAL_MODULES = [
  NotesModule,
  ContactAvatarModule,
  MultiChipModule
];
const TEI_MODULES = [
  TeiDateTranslateModule,
  TeiTextTranslatePipeModule,
  TeiCurrencyTranslateModule
];
const COMPONENTS = [
  ConfirmTeeTimeBookingDialogComponent
];


@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
})
export class ConfirmTeeTimeBookingDialogModule { }
