import { Contact } from "@tei/office-contacts";
import { TeeTimeHoles } from "./get-booking-details.model";
import { Transportation } from "./get-tee-sheet.model";


export class GetBookingGroupRequest
{
  BookingID?: number;
  GroupUID?: string;
}

export class GetBookingGroupResponse
{
  GroupName: string;
  Managers: Array<BookingGroupManager>;
  TeeTimes: Array<BookingGroupTeeTime>;
  GroupUID?: string;
  CartType: Transportation;
  IsShotgun: boolean;
  NineHoles: TeeTimeHoles;
  PriceLevelID: string;
  ShotgunDisplayTime: string;
  ShotgunHoleNumber: string;
  ShotgunHoleNumberList: Array<string>;
  IsSelected: boolean;
  Indeterminate: boolean;
  PriceLevelDescription: string;
}

export class BookingGroupManager
{
  ContactID: string;
  IsPrimary: boolean;
  DisplayName?: string;
  FirstName?: string;
  ManagerUID?: string;
  LastName?: string;
}

export class BookingGroupTeeTime
{
  TeeTimeID: string;
  PlayerCount: number;
  Time: string;
  Contacts: Contact[];
  BookingID: number;
  DisplayTime?: string;
  HoleNumber?: string;
  IsSelected?: boolean;
}

export class BookingGroupContact
{
  PlayerName: string;
  ContactID: string;
}

