import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { Attachment, AttachmentGroup, AttachmentsDialogDataModel, GetTeeTimeAttachmentResponse } from '../../../@intermediate/models/get-tee-time-attachment.model';
import { PostTeeTimeAttachmentRequest, PostTeeTimeAttachmentResponse } from '../../../@intermediate/models/post-tee-time-attachment.model';
import { TeeTimeAttachmentService } from '../../../@services/tee-time-attachment.service';
import { AttachmentsItemComponent } from './attachments-item/attachments-item.component';

@Component({
  selector: 'tei-pos-tee-sheet-attachments-dialog',
  templateUrl: './attachments-dialog.component.html',
  styleUrls: ['./attachments-dialog.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(AttachmentsDialogComponent)],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AttachmentsDialogComponent implements OnInit, OnDestroy
{
  hiddenScrollBar = true;
  actionButtonTitle = 'POS_TEE_SHEET.ATTACHMENTS_DIALOG.SELECTIONS_REQUIRED';
  formIsValid = true;
  model: GetTeeTimeAttachmentResponse;
  attachments: Array<AttachmentGroup> = [];
  isToEdit = false;
  subscription = new Subscription();

  @ViewChild('attachmentsContainer') attachmentContainer: ElementRef;

  constructor(
    @Inject('ITeeTimeAttachmentService') private service: TeeTimeAttachmentService,
    @Inject(MAT_DIALOG_DATA) public attachmentsDialogData: AttachmentsDialogDataModel,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    private dialogRef: MatDialogRef<AttachmentsItemComponent>,
    private changeDetectorRef: ChangeDetectorRef,
    public manageLoadingService: ManageLoadingService,
  )
  { }

  ngOnInit(): void
  {
    this.subscription.add(this.dialogRef.backdropClick().subscribe(() => this.dialogRef.close({ IsChanged: false })));

    this.load();
  }

  load(): void
  {

    this.attachmentsDialogData.teeTimeAttachmentResponse?.AttachmentGroups.forEach(attachment =>
    {
      if (attachment.Attachments.length > 0)
      {

        this.attachments.push({
          ...attachment,
          Attachments: attachment.Attachments.sort((a, b) => (a.SortIndex > b.SortIndex) ? 1 : -1)
        });
      }
    });

    this.attachments.sort((a, b) => (a.GroupSortIndex > b.GroupSortIndex) ? 1 : -1);

    this.checkFormValidation();

    this.changeDetectorRef.detectChanges();
    this.hiddenScrollBar = this.attachmentContainer?.nativeElement?.clientWidth >= this.attachmentContainer?.nativeElement?.scrollWidth;

  }

  get extraPaid(): number
  {
    let extraPaid = 0;
    this.attachments.forEach(attachment =>
    {
      extraPaid += attachment.Attachments.filter(x => x.Selected)
        .reduce((sum: number, opt: Attachment) => sum + opt.AttachmentPrice, 0);
    });
    return extraPaid;
  }

  onClose(): void
  {
    this.dialogRef.close({ IsChanged: false });
  }

  onAddItemAndClose(): void
  {
    if (!this.formIsValid)
    {
      return;
    }
    const postTeeTimeAttachmentRequest: PostTeeTimeAttachmentRequest = {
      BookingID: this.attachmentsDialogData.BookingID,
      PlayerNumber: this.attachmentsDialogData.PlayerNumber,
      SelectedAttachments: []
    };

    this.attachments.forEach(attach =>
    {
      attach.Attachments.forEach((attachment: Attachment) =>
      {
        if (attachment.Selected)
        {
          postTeeTimeAttachmentRequest.SelectedAttachments.push(attachment.AttachmentUID);
        }
      });
    });
    this.manageLoadingService.startLoading();
    this.subscription.add(this.service.PostTeeTimeAttachment(postTeeTimeAttachmentRequest)
      .pipe(finalize(() => this.manageLoadingService.finishLoading())).subscribe({
        next: (response: PostTeeTimeAttachmentResponse) =>
        {
          if (response.Result)
          {
            const successMessage = this.translate.instant('POS_TEE_SHEET.ATTACHMENTS_DIALOG.POST_ATTACHMENTS_SUCCESS');
            this.snackBarService.showSuccess(successMessage, '', null, 'left');
            this.dialogRef.close({ IsChanged: true, CheckItemID: postTeeTimeAttachmentRequest });
          }
        },
        error: () =>
        {
          const successMessage = this.translate.instant('POS_TEE_SHEET.ATTACHMENTS_DIALOG.POST_ATTACHMENTS_ERROR');
          this.snackBarService.showError(successMessage, '', null, 'left');
        }
      }));
  }

  onClickAttachment(attachment: Attachment): void
  {
    attachment.Selected = !attachment.Selected;
    this.checkFormValidation();
  }

  onScrollLeft(): void
  {
    this.attachmentContainer.nativeElement.scrollLeft -= 250;
  }

  onScrollRight(): void
  {
    this.attachmentContainer.nativeElement.scrollLeft += 250;
  }

  checkFormValidation(): void
  {
    this.actionButtonTitle = this.getActionButtonTitle();
  }


  getActionButtonTitle(): string
  {
    if (this.formIsValid)
    {
      return this.isToEdit ? 'POS_TEE_SHEET.ATTACHMENTS_DIALOG.SAVE_CHANGES' : 'POS_TEE_SHEET.ATTACHMENTS_DIALOG.ADD_ITEM';
    }
    return 'POS_TEE_SHEET.ATTACHMENTS_DIALOG.SELECTIONS_REQUIRED';
  }

  onBackToParentAttachment(attachment: AttachmentGroup): void
  {
    //To Do: on Back To Parent Attachment handling
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
