import { Injectable } from "@angular/core";
import { StorageService } from "@tei/core";
import { teeSheetCacheName } from "@tei/pos-shared";
import { Subject } from "rxjs";
import { TeeSheet } from "../@intermediate/models/get-tee-sheet.model";
import { TeeSheetSelectedView } from "../@intermediate/models/tee-sheet-toolbar-model";
import { MultiTeeSheetTransfer } from "../@intermediate/models/tee-sheet-transfer-service.model";

@Injectable({ providedIn: 'root' })
export class TeeSheetMainService
{  
  constructor(private storageService: StorageService) { }

  teeSheetArray: TeeSheet[] = [];

  private multiTeeSheetTransfer = new Subject<MultiTeeSheetTransfer>();
  currentMultiTeeSheetTransfer = this.multiTeeSheetTransfer.asObservable();

  changeMultiTeeSheet(subject: MultiTeeSheetTransfer): void
  {
    this.multiTeeSheetTransfer.next(subject);
  }

  ReloadAllCurrentTeeSheet()
  {
    const teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));

    const pageIndex = (teeSheetSelectedView.CurrentTeeSheetPage * teeSheetSelectedView.ColumnNumber) - (teeSheetSelectedView.ColumnNumber);
    const lastIndex = teeSheetSelectedView.CurrentTeeSheetPage * teeSheetSelectedView.ColumnNumber;
    for (let index = pageIndex; index < lastIndex; index++)
    {
      const tSheet = this.teeSheetArray[index];
      this.changeMultiTeeSheet({
        ReloadTeeSheet: true,
        SelectedCourse: tSheet.SelectedCourse
      });
    }
  }

  setTeeSheetArray(teeSheetArray: TeeSheet[])
  {
    this.teeSheetArray = teeSheetArray;
  }
}
