import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { ManageStandAloneLoadingService, ResponsiveTableHeightModule, TeiTwoDigitDecimalModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { TeeSheetWaitListService } from '../../@services/tee-sheet-wait-list.service';
import { TeeSheetWaitListSearchHeaderComponent } from './tee-sheet-wait-list-search-header/tee-sheet-wait-list-search-header.component';
import { TeeSheetWaitListComponent } from './tee-sheet-wait-list.component';
import { PlayersWaitListComponent } from './players-wait-list/players-wait-list.component';






const MATERIAL_MODULES = [MatButtonModule, MatDividerModule, MatCheckboxModule, MatDialogModule, MatInputModule, MatIconModule, MatFormFieldModule, MatSlideToggleModule, MatTableModule, MatChipsModule, MatTabsModule, MatTooltipModule];
const BASE_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const TEI_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule, ResponsiveTableHeightModule];
const COMPONENTS = [TeeSheetWaitListComponent, TeeSheetWaitListSearchHeaderComponent];
const TEI_MATERIAL_MODULES = [FilterChipsModule, LoadingModule, QuickSearchFilterChipModule, ContactQuickSearchModule, LoadingModule];


@NgModule({
  declarations: [...COMPONENTS, PlayersWaitListComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [
    { provide: 'ITeeSheetWaitListService', useClass: TeeSheetWaitListService },
    { provide: ManageStandAloneLoadingService }
  ]
})
export class TeeSheetWaitListModule { }
