import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { TeeTime, GetDisplayDataResponse } from '../../../@intermediate/models/get-teesheet-display.model';
import { TeeSheetDisplayService } from '../../../@services/tee-sheet-display.service';
import { firstValueFrom, Subscription } from 'rxjs';
import { PostDisplayTeeSheetRequest, PostDisplayTeeSheetResponse, SaveDisplayFormData } from '../../../@intermediate/models/post-display-tee-sheet.model';
import moment from 'moment';
import { TeeSheetMainService } from '../../../@services/tee-sheet-main.service';
import { TeeSheetService } from '../../../@services/tee-sheet.service';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { ManageLoadingService } from '@tei/core';

@Component({
  selector: 'tei-pos-tee-sheet-display-grid',
  templateUrl: './display-grid.component.html',
  styleUrls: ['./display-grid.component.scss']
})
export class DisplayGridComponent implements OnInit {

  dataSource = new MatTableDataSource<TeeTime>();
  emptyTable = false;
  public isFormChanged = false;


  subscription = new Subscription();
  @Input() isMobile = false;
  @Output() cancelClick = new EventEmitter<void>();

  displayedColumns: string[] = ['time','holeNumber','displayTime'];

  constructor(
    public teeSheetDisplayService: TeeSheetDisplayService,
    private teeSheetMainService: TeeSheetMainService,
    private teeSheetService: TeeSheetService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService,
  ) {}

  ngOnInit(): void {
    this.loadData()
  }

  loadData() {
     this.manageLoadingService.startLoading();
    this.subscription.add(
      this.teeSheetDisplayService.load$.subscribe((value: GetDisplayDataResponse) => {

        if (value){
          this.fillGridData(value)
        }
         this.manageLoadingService.finishLoading();
      })
    );
  }

  fillGridData(value: GetDisplayDataResponse){
    const rows: TeeTime[] = (value?.TeeTimes ?? []).map((t, idx) => ({
      TeeTime: t.TeeTime,
      HoleNumber: t.HoleNumber,
      DisplayTime: t.DisplayTime,
      Index: idx
    }));
    this.dataSource.data = rows;
    this.emptyTable = !rows.length;
    this.isFormChanged = true;
  }

  getHoleNumber(element: TeeTime): string {
    return element.HoleNumber ?? '';
  }

  setHoleNumber(element: TeeTime, value: string): void {
    element.HoleNumber = value;
     this.isFormChanged = true;
  }

  getDisplayTime(element: TeeTime): string {
    return element.DisplayTime ?? '';
  }

  setDisplayTime(element: TeeTime, value: string): void {
    element.DisplayTime = value;
    this.isFormChanged = true;
  }

  async onSave() {

   const teeSheetDisplayConfig: SaveDisplayFormData = this.teeSheetDisplayService.saveDisplayFormData;

    this.teeSheetDisplayService.displayFormChange(false);
    try{
      this.manageLoadingService.startLoading();
      const request: PostDisplayTeeSheetRequest = {
        CourseID: teeSheetDisplayConfig.CourseID,
        StartDate: moment(teeSheetDisplayConfig.StartDate).format('YYYY-MM-DDTHH:mm:ss'),
        EndDate:teeSheetDisplayConfig.EndDate ? moment(teeSheetDisplayConfig.EndDate).format('YYYY-MM-DDTHH:mm:ss') : '',
        TeeSheetHoleData: this.dataSource.data.splice(teeSheetDisplayConfig.TimeRangeStartIndex, teeSheetDisplayConfig.TimeRangeEndIndex + 1)
      }
      const response: PostDisplayTeeSheetResponse = await firstValueFrom(this.teeSheetService.PostDisplayData(request));
      if (response) {
        this.snackBarService.showSuccess(this.translate.instant("POS_TEE_SHEET.TEESHEET_DISPLAY.SUCCESS_MESSAGE"),'', null, 'left');
        this.teeSheetMainService.ReloadAllCurrentTeeSheet();
      }
    }

      catch (error)
      {
        console.error(error);
      } finally
      {
        this.manageLoadingService.finishLoading();
      }
  }

  cancel() {
    this.cancelClick.emit();
  }
}
