import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { CourseCrossoversSheetTime, GetModifiedCourseCrossoversResponse, TeeSheetCourseCrossoversApply } from '../../../@intermediate/models/get-modified-course-cross-overs.model';
import { CrossoversItem, PostModifiedTeeSheetCourseCrossoversRequest } from '../../../@intermediate/models/post-modified-tee-sheet-course-cross-overs.model';
import { TeeSheetCourseCrossoversService } from '../../../@services/tee-sheet-course-crossovers.service';
import { TeeSheetFilterViewService } from '../../../@services/tee-sheet-filter-view.service';
@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-course-crossovers-tee-time',
  templateUrl: './tee-sheet-course-crossovers-tee-time.component.html',
  styleUrls: ['./tee-sheet-course-crossovers-tee-time.component.scss']
})
export class TeeSheetCourseCrossoversTeeTimeComponent implements OnInit, OnDestroy
{
  @Output() public onCancel = new EventEmitter();
  teeSheetCourseCrossoversData: GetModifiedCourseCrossoversResponse;
  subscription = new Subscription();
  public isCheckboxChanged = false;
  dataSource = new MatTableDataSource<CourseCrossoversSheetTime>();
  courseColumns: string[] = [];
  displayedColumns: string[] = [];
  @Input() public set teeSheetCourseCrossoversDataInput(value: GetModifiedCourseCrossoversResponse)
  {
    this.dataSource.data = value.SheetTimes;
    if (value.SheetTimes?.length && value.CourseInfo)
    {
      this.courseColumns = value.CourseInfo.map(group => group.CourseID);
      this.displayedColumns = ['teeTime', ...this.courseColumns];
    }
    this.teeSheetCourseCrossoversData = value;
    this.updateShowingCourses(this.teeSheetFilterViewService.getApplyIds());
  };

  constructor(
    public teeSheetFilterViewService: TeeSheetFilterViewService,
    public teeSheetCourseCrossoversService: TeeSheetCourseCrossoversService
  )
  {
  }

  ngOnInit(): void
  {
    this.loadData();
  }

  getCourseValue(element: CourseCrossoversSheetTime, courseId: string): boolean
  {
    const course = element.CourseCrossOvers.find(course => course.CourseID === courseId);
    return course.Value;
  }

  setCourseValue(element: CourseCrossoversSheetTime, courseId: string, value: boolean): void
  {
    element.CourseCrossOvers.forEach(item =>
    {
      item.Value = (item.CourseID === courseId) ? value : false;
    });
    this.isCheckboxChanged = true;
    this.teeSheetCourseCrossoversService.SheetTimeFormChanged = true;
  }

  isAllCourseValuesChecked(courseCol: string): boolean
  {
    return this.dataSource.data.every(element => this.getCourseValue(element, courseCol));
  }

  isSomeCourseValuesChecked(courseCol: string): boolean
  {
    const values = this.dataSource.data.map(element => this.getCourseValue(element, courseCol));
    return values.includes(true) && values.includes(false);
  }

  toggleAllCourseValues(courseCol: string, checked: boolean): void
  {
    this.dataSource.data.map(element =>
    {
      this.setCourseValue(element, courseCol, checked);
    });
  }

  public loadData(): void
  {
    this.subscription.add(this.teeSheetFilterViewService.applyIds$.subscribe(value =>
    {
      this.updateShowingCourses(value);
    }));

    this.subscription.add(this.teeSheetCourseCrossoversService.apply$.subscribe((value: TeeSheetCourseCrossoversApply) =>
    {
      this.updateTable(value);
    }));
  }

  updateShowingCourses(coursesIds: string[]): void
  {
    this.teeSheetCourseCrossoversData.CourseInfo.map(item =>
    {
      if (coursesIds.length === 0)
      {
        item.Show = true;
        return;
      }
      item.Show = !!coursesIds.find(courseId => courseId === item.CourseID.toLowerCase());
    });
  }

  updateTable(teeSheetCourseCrossoversApply: TeeSheetCourseCrossoversApply): void
  {
    const {
      TimeRangeStartIndex,
      TimeRangeEndIndex,
      Course
    } = teeSheetCourseCrossoversApply;

    if (TimeRangeStartIndex !== null && TimeRangeEndIndex !== null)
    {
      for (let i = TimeRangeStartIndex; i <= TimeRangeEndIndex; i++)
      {
        const sheetTime = this.dataSource.data[i];
        sheetTime.CourseCrossOvers.map((item) =>
        {
          item.Value = (item.CourseID === Course);
        });
      }
    }
    this.isCheckboxChanged = true;
  }

  cancel(): void
  {
    this.onCancel.emit();
  }


  onSave(): void
  {
    const crossoversItems: CrossoversItem[] = [];
    this.dataSource.data.map(item =>
    {
      crossoversItems.push({
        SheetTime: item.SheetTime,
        Courses: item.CourseCrossOvers
      });
    });

    const request: PostModifiedTeeSheetCourseCrossoversRequest = {
      CourseID: this.teeSheetCourseCrossoversData.CourseID,
      SheetDate: moment(this.teeSheetCourseCrossoversData?.TeeSheetTemplate?.SelectedTeeSheetDate).format('YYYY-MM-DD'),
      CrossoversItems: crossoversItems
    };

    this.teeSheetCourseCrossoversService.save(request);
    this.teeSheetCourseCrossoversService.setSheetTimeFormChange(false);
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
