import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { TeeSheetFilterView } from '../@intermediate/models/tee-sheet-filter-view.model';

@Injectable({
  providedIn: 'root'
})
export class TeeSheetFilterViewService
{
  private FilterSearch = new BehaviorSubject<string>('');
  filterSearch$ = this.FilterSearch.asObservable();

  private Load = new BehaviorSubject<TeeSheetFilterView[]>([]);
  load$ = this.Load.asObservable();

  private ApplyIds = new BehaviorSubject<string[]>([]);
  applyIds$ = this.ApplyIds.asObservable();

  constructor() { }

  setFilterSearch(value: string): void
  {
    this.FilterSearch.next(value);
  }

  load(value: TeeSheetFilterView[]): void
  {
    this.Load.next(value);
  }

  applyIds(value: string[]): void
  {
    this.ApplyIds.next(value);
  }

  getApplyIds(): string[]
  {
    return this.ApplyIds.getValue();
  }

  destroy(): void
  {
    this.FilterSearch.next('');
    this.ApplyIds.next([]);
  }
}
