import
{
  ChangeDetectionStrategy, ChangeDetectorRef,
  Component, ElementRef, EventEmitter,
  Inject, Input, OnDestroy, OnInit, Output,
  ViewChild
} from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MatButtonToggleChange } from '@angular/material/button-toggle';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { IFormBuilder } from '@rxweb/types';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { ContactNotificationType, NotificationPosition } from '@tei/office-contacts';
import { Contact as BaseContact, CheckNotificationDialogModel, ContactNotificationDialogComponent, GetNotificationsRequest, IContactNotificationsService, IOrderStoreService } from '@tei/pos-shared';
import { Subscription, firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { DeleteTeeTimeCheckInRequest, DeleteTeeTimeCheckInResponse } from '../../../../@intermediate/models/delete-tee-time-check-in.model';
import { Attachment, CartTypes, CartTypesID, GetBookingDetailsResponse, Group, NumPlayers, PastDayStatusBookingPanel, Player } from '../../../../@intermediate/models/get-booking-details.model';
import { GetContactHasNotificationResponse } from '../../../../@intermediate/models/get-contact-notification.model';
import
{
  Holes1or10, PlayerStatus, PriceLevel, StartTeeHole, TeeTimeConfig, TeeTimeHoles
} from '../../../../@intermediate/models/get-tee-sheet.model';
import { AttachmentsDialogDataModel, GetTeeTimeAttachmentRequest, GetTeeTimeAttachmentResponse } from '../../../../@intermediate/models/get-tee-time-attachment.model';
import { ManageTeeTimeScreenSizeEnum } from '../../../../@intermediate/models/manage-tee-time.model';
import { ContactChanged } from '../../../../@intermediate/models/player-row.model';
import { Contact, PlayerForForm } from '../../../../@intermediate/models/post-booking-details.model';
import { PostManageOrderTeeTimeRequest } from '../../../../@intermediate/models/post-manage-order-tee-time.model';
import { PostPlayerPriceRequest, PostPlayerPriceResponse } from '../../../../@intermediate/models/post-player-price.model';
import { PostReactivateNoShowPlayerRequest, PostReactivateNoShowPlayerResponse } from '../../../../@intermediate/models/post-reactivate-no-show-player.model';
import { PostRemoveBookingPlayerRequest, PostRemoveBookingPlayerResponse } from '../../../../@intermediate/models/post-remove-booking-player.model';
import { BookingRestrictionModeEnum, PostRestrictionsRequest } from '../../../../@intermediate/models/post-restrictions.model';
import { PriceBreakdownDialogDataModel, PriceForPriceBreakdown } from '../../../../@intermediate/models/price-breakdown.model';
import { BookingRestrictionPerPlayer, PlayerItem } from '../../../../@intermediate/models/restrictions-helper.model';
import { GroupBookingService } from '../../../../@services/group-booking.service';
import { ManageOrderTeeTimeService } from '../../../../@services/manage-order-tee-time.service';
import { ManageTeeTimeHelperService } from '../../../../@services/manage-tee-time.helper.service';
import { ManageTeeTimeService } from '../../../../@services/manage-tee-time.service';
import { TeeTimeAttachmentService } from '../../../../@services/tee-time-attachment.service';
import { AttachmentsDialogComponent } from '../../attachments-dialog/attachments-dialog.component';

@Component({
  selector: 'tei-pos-tee-sheet-player-row',
  templateUrl: './player-row.component.html',
  styleUrls: ['./player-row.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlayerRowComponent implements OnInit, OnDestroy
{
  @Input() GroupBookingIndex = 0;
  @Input() config: TeeTimeConfig;
  @Input() form: UntypedFormGroup;
  @Input() formArray: UntypedFormGroup;
  @Input() playerIndex: number;
  @Input() bookingDetailsPlayer: Player;
  @Input() contactIsRequired: boolean;
  @Input() bookingID: number;
  @Input() CartRequired: boolean;
  @Input() PowerCartRequired: boolean;
  @Input() courseID: string;
  @Input() HasAttachments: boolean;
  @Input() startTee: StartTeeHole;

  _checkIn = false;
  @Input() set checkIn(value: boolean)
  {
    this._checkIn = value;
    this.updateLastPlayerStatus();
  }
  get checkIn()
  {
    return this._checkIn;
  }

  _noShow = false;
  @Input() set noShow(value: boolean)
  {
    this._noShow = value;
    this.updateLastPlayerStatus();
  }
  get noShow()
  {
    return this._noShow;
  }

  @Input() attachmentGroups: Array<Group>;
  @Input() BookingDateTime: string;
  @Input() isNewBooking: boolean;
  @Input() is9HoleOnly: boolean;
  @Input() pastDayStatus: PastDayStatusBookingPanel;
  @Input() availablePlayers: number = 1;
  booking: GetBookingDetailsResponse;

  @Input() set playersFormSubmitted(value: boolean)
  {
    this.playersFormSubmittedValue = value;
  }
  @Output() updateGroupPriceFromEachPlayerItem = new EventEmitter();
  @Output() contactChanged: EventEmitter<ContactChanged> = new EventEmitter();
  @Output() removePlayer: EventEmitter<number> = new EventEmitter();
  @Output() cancelBooking = new EventEmitter();
  @Output() updatePlayersQuantityButtons: EventEmitter<number> = new EventEmitter();
  @Output() playerStatusChanged = new EventEmitter<{ playerIndex: number, newStatus: PlayerStatus; }>();

  get hidden(): boolean
  {
    return this.config?.FilterPlayer &&
      ((this.config?.FilterPlayer?.HideCheckInPlayers && (this.form?.get('Status')?.value === PlayerStatus.CheckIn || this.form?.get('Status')?.value === PlayerStatus.CheckInInprogress))
        ||
        (this.config?.FilterPlayer?.ContactID && this.formRawValue?.Contact?.ContactID !== this.config?.FilterPlayer?.ContactID));
  }

  get playersFormSubmitted(): boolean
  {
    return this.playersFormSubmittedValue;
  }
  get formRawValue() { return this.form?.getRawValue(); }

  playersFormSubmittedValue: boolean;
  isCheckIn: boolean;
  isNoShow: boolean;
  teeTimeAttachmentResponse: GetTeeTimeAttachmentResponse;
  unableToChangeAttachments: boolean;
  screenSize: number;
  formBuilder: IFormBuilder;
  playersForm: UntypedFormGroup;
  teeTimeHoles = TeeTimeHoles;
  NumPlayers = NumPlayers;
  StartTee = Holes1or10;
  playerStatus = PlayerStatus;
  lastPlayerState: PlayerStatus;
  playerStateValue: PlayerStatus[] = [];
  transportationTypes: Array<CartTypes>;
  cartTypesID = CartTypesID;
  holesInputId: string;
  screenSizeEnum = ManageTeeTimeScreenSizeEnum;
  mediaBreakpointSubscription: Subscription;
  isPlayerPriceBreakdownDialogOpen: { [key: number]: boolean; } = {};
  playerTotalPriceForBreakDown: PriceBreakdownDialogDataModel;
  sharedSubscription: Subscription = new Subscription();
  detectFormValueChangesSubscription: Subscription;
  position = NotificationPosition.TopLeft;
  notificationType = ContactNotificationType.Warn;
  subscription = new Subscription();
  @ViewChild('displayNameInput') displayNameInput: ElementRef;
  constructor(
    private manageTeeTimeHelper: ManageTeeTimeHelperService,
    @Inject('IManageTeeTimeService') private manageTeeTimeService: ManageTeeTimeService,
    @Inject('ITeeTimeAttachmentService') private teeTimeAttachmentService: TeeTimeAttachmentService,
    @Inject('IManageOrderTeeTimeService') private manageOrderTeeTimeService: ManageOrderTeeTimeService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    @Inject('IContactNotificationsService') protected contactNotificationsService: IContactNotificationsService,
    private fb: UntypedFormBuilder,
    private dialog: MatDialog,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    private changeDetectorRef: ChangeDetectorRef,
    private responsiveService: ResponsiveService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    public groupBookingService: GroupBookingService,
  )
  {
    this.formBuilder = fb;
  }

  ngOnInit(): void
  {
    this.booking = this.groupBookingService?.groupBookingDetails?.BookingDetails?.find(item => item.BookingID === this.bookingID);
    if (!this.config)
    {
      this.config = {
        GroupDefaultValues: {
          CartType: this.groupBookingService.bookingGroup?.CartType,
          NineHoles: this.groupBookingService.bookingGroup?.NineHoles,
          PriceLevelID: this.groupBookingService.bookingGroup?.PriceLevelID
        },
        ShowMoveButton: false,
        ShowDisplayContactIcon: true,
        ShowNotificationIcon: true,
        CallPlayerPriceApi: true
      } as TeeTimeConfig;
    }
    this.createTransportationTypes();
    this.detectMobileScreen();
    this.detectFormValueChanges();
    this.initialData();

    const player = this.form.getRawValue();
    if (this.config?.CallPlayerPriceApi || !player.TeeTimeID)
    {
      this.getPlayerPrice(player);
    }

    if (!this.config?.CallPlayerPriceApi && player.TeeTimeID)
    {
      this.updatePlayerPrice(this.booking?.Players?.[this.playerIndex]?.PlayerPrice);
    }

    const editDisplayName = !this.form.get('Contact')?.value && this.form.get('DisplayName')?.value;
    this.form.get('EditDisplayName').setValue(editDisplayName);
  }

  private initialData(): void
  {
    this.holesInputId = Math.random().toString();
    this.isCheckIn = this.isCheckInFn();
    this.isNoShow = this.isNoShowFn();
    this.updateLastPlayerStatus();
  }

  get disabledMoveButton(): boolean
  {
    return !this.bookingDetailsPlayer?.Contact?.ContactID || this.bookingDetailsPlayer?.Contact?.ContactID === '00000000-0000-0000-0000-000000000000' || this.bookingDetailsPlayer?.Contact?.ContactID === '88888888-8888-8888-8888-888888888888'
      || this.form.controls.Status?.value == this.playerStatus.NoShow || this.form.controls.Status?.value == this.playerStatus.NoShowFromAPI || this.form.controls.Status?.value == this.playerStatus.CheckIn || this.form.controls.Status?.value == this.playerStatus.CheckInInprogress;
  }
  get editDisplayName(): boolean
  {
    return this.form.get('EditDisplayName').value;
  }
  changeEditDisplayName(): void
  {
    const editDisplayName = !this.editDisplayName;
    this.form.get('EditDisplayName').patchValue(editDisplayName, { emitEvent: false });
  }

  movePlayer()
  {
    if (!this.bookingDetailsPlayer?.Contact?.ContactID || this.bookingDetailsPlayer?.Contact?.ContactID === '00000000-0000-0000-0000-000000000000' || this.bookingDetailsPlayer?.Contact?.ContactID === '88888888-8888-8888-8888-888888888888')
    {
      return;
    }

    this.groupBookingService.setMoveSwapPlayer(this.bookingDetailsPlayer);
  }

  onDisplayNameClicked(): void
  {
    this.form.get('EditDisplayName').patchValue(true, { emitEvent: false });
    setTimeout(() =>
    {
      if (this.displayNameInput)
      {
        this.displayNameInput.nativeElement.focus();
      }
    }, 0);
  }

  detectFormValueChanges(): void
  {
    let lastFormState = { ...this.form.value };
    this.detectFormValueChangesSubscription = this.form.valueChanges.subscribe(async (newValues) =>
    {
      const oldValues = { ...lastFormState };
      lastFormState = { ...newValues };
      this.updateLastPlayerStatus();
      const key = this.manageTeeTimeHelper.findChangedRootKeys(newValues, oldValues);
      if (!key.length) return;

      if (key.includes('Cart') || key.includes('Holes') || key.includes('Attachments') || key.includes('PriceLevelID'))
      {
        this.getPlayerPrice(this.form.getRawValue());
      } else if (key.includes('DisplayName'))
      {
        if (!this.form.get('Contact')?.value)
        {
          this.form.get('EditDisplayName').patchValue(!this.form.get('Contact')?.value && this.form.get('DisplayName')?.value);
        }
      }
    });
  }

  async deleteContact(baseContact: BaseContact): Promise<void>
  {
    const contact: Contact = { ...baseContact } as Contact;
    const hasDenyRestriction = true;

    const contactChanged: ContactChanged = {
      contact: null,
      playerIndex: this.playerIndex,
      hasRestrictionAuthorize: hasDenyRestriction
    };
    this.snackBarService.showSuccess(this.translate.instant('OFFICE_CONTACTS.CONTACT_QUICK_SEARCH.REMOVED_MESSAGE', { DisplayName: contact ? contact.DisplayName : '' }), '', null, 'left');

    this.form.get('Contact').patchValue(null, { emitEvent: false });
    this.form.patchValue({ DisplayName: '' });
    this.form.markAllAsTouched();
    this.contactChanged.emit(contactChanged);
    this.changeDetectorRef.detectChanges();

  }

  async updateContact(baseContact: BaseContact): Promise<void>
  {
    const contact: Contact = { ...baseContact } as Contact;
    contact.HasRestriction = false;
    const playerContactDetail = await this.checkPlayerNotification(contact?.ContactID);
    contact.HasNotification = playerContactDetail?.HasNotification;
    const result = await this.checkPlayerRestrictions([{ PlayerID: this.contactTransformer(contact?.ContactID), PlayerNumber: this.playerIndex }]);

    if (!result)
    {
      this.form.get('Contact').patchValue(null, { emitEvent: false });
      this.form.patchValue({ DisplayName: null });
      this.form.markAllAsTouched();
      this.changeDetectorRef.detectChanges();
      return;
    }

    const hasDenyRestriction = result.some(restriction => restriction.errors.some(error => error.restrictionMode === BookingRestrictionModeEnum.Deny));

    contact.HasRestriction = hasDenyRestriction;
    this.form.patchValue({ DisplayName: playerContactDetail?.DisplayName });
    this.form.get('Contact').patchValue(contact, { emitEvent: false });

    const contactChanged: ContactChanged = {
      contact: this.form.value.Contact,
      playerIndex: this.playerIndex,
      hasRestrictionAuthorize: hasDenyRestriction
    };

    this.contactChanged.emit(contactChanged);
    this.form.markAllAsTouched();
    if (this.config.ShowMoveButton)
    {
      this.bookingDetailsPlayer.Contact = this.form.get('Contact').value;
      this.groupBookingService.groupBookingDetails.BookingDetails.find(item => item.BookingID === this.bookingID).Players.find(player => player.TeeTimeID === this.bookingDetailsPlayer.TeeTimeID).Contact = this.form.get('Contact').value;
    }
  }

  async checkPlayerRestrictions(PlayerIDList: Array<PlayerItem>): Promise<BookingRestrictionPerPlayer[] | null>
  {
    this.manageStandAloneLoadingService.startLoading();
    this.changeDetectorRef.detectChanges();
    const request: PostRestrictionsRequest = {
      BookingID: this.bookingID,
      CourseID: this.courseID,
      TeeTime: this.BookingDateTime,
      CustomerID: this.formArray.getRawValue().player[0].Contact?.ContactID,
      Is9Holes: this.is9Holes(this.form.getRawValue()?.Holes),
      PlayerList: PlayerIDList.map(playerItem => playerItem.PlayerID)
    };

    const response = await firstValueFrom(this.manageTeeTimeService.PostRestrictions(request)
      .pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())));
    if (!response.BookingRestrictions?.length)
    {
      return [];
    } else
    {
      for (let restriction of response.BookingRestrictions)
      {
        restriction.PlayerNumber = PlayerIDList.find(player => player.PlayerID == restriction.PlayerID)?.PlayerNumber;
      }
      return await firstValueFrom(this.manageTeeTimeHelper.openBookingRestrictionsDialog(response.BookingRestrictions)
        .pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())));
    }
  }

  async checkPlayerNotification(contactID: string): Promise<GetContactHasNotificationResponse>
  {
    this.manageStandAloneLoadingService.startLoading();
    this.changeDetectorRef.detectChanges();

    const response = await firstValueFrom(this.manageTeeTimeService.GetContactHasNotification({ ContactID: contactID })
      .pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())));
    return response;
  }

  contactTransformer(contactId: any): string
  {
    if (!contactId) return "00000000-0000-0000-0000-000000000000";
    return contactId;
  }

  detectMobileScreen(): void
  {
    this.mediaBreakpointSubscription = this.responsiveService.screenWidth.subscribe(screenSize =>
    {
      this.screenSize = screenSize;
      this.changeDetectorRef.detectChanges();
    });
  }

  onStatusChange($event: MatButtonToggleChange)
  {
    if (this.isCheckIn)
    {
      this.updateLastPlayerStatus();
      return;
    }
    this.onStatusToggle(null, $event.value).then();
  }

  // In PlayerRowComponent

  async onStatusToggle(event: MouseEvent, selectedValue: PlayerStatus[]): Promise<void>
  {
    let newValue: PlayerStatus = PlayerStatus.None;
    if (selectedValue?.length)
    {
      newValue = selectedValue[selectedValue?.length - 1];
    }

    if (this.bookingDetailsPlayer.Status === PlayerStatus.NoShowFromAPI
      || this.formRawValue?.Status === PlayerStatus.NoShowFromAPI
      || this.lastPlayerState === PlayerStatus.NoShowFromAPI)
    {
      this.playerStateValue.push(PlayerStatus.NoShow);
      const result: boolean = await firstValueFrom(this.manageTeeTimeHelper.openReactivatePlayerDialog());

      if (result)
      {
        const request: PostReactivateNoShowPlayerRequest = {
          BookingID: this.bookingID,
          PlayerNumber: this.bookingDetailsPlayer.PlayerNumber,
          NoShow: false
        };

        try
        {
          const response: PostReactivateNoShowPlayerResponse = await firstValueFrom(this.manageTeeTimeService.ReactivateNoShowPlayer(request));
          if (response.Succeed)
          {
            this.handleReactivateNoShowPlayerResponse(request, newValue === PlayerStatus.CheckIn ? newValue : PlayerStatus.None);
            this.form.markAsTouched();

            this.playerStatusChanged.emit({
              playerIndex: this.playerIndex,
              newStatus: newValue === PlayerStatus.CheckIn ? newValue : PlayerStatus.None
            });
          }
          if (response.InsufficientRoom)
          {
            this.form.get('Status').patchValue(PlayerStatus.NoShowFromAPI, { emitEvent: false });
            this.snackBarService.showError(
              this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.REACTIVATE_PLAYER_INSUFFICIENT_ROOM'), '', null, 'left'
            );
          }
        } catch (error)
        {
          this.form.get('Status').patchValue(PlayerStatus.NoShowFromAPI, { emitEvent: false });
        } finally
        {
          this.updateLastPlayerStatus();
        }
      }
      else
      {
        this.form.get('Status').patchValue(PlayerStatus.NoShowFromAPI, { emitEvent: false });
      }
    }
    else
    {
      const theLastPlayerState = this.lastPlayerState;
      const newPlayerState = this.lastPlayerState === newValue ? PlayerStatus.None : newValue;

      this.form.get('Status').setValue(newPlayerState, { emitEvent: true });
      this.form.markAsTouched();

      this.playerStatusChanged.emit({
        playerIndex: this.playerIndex,
        newStatus: newPlayerState
      });
    }

    this.updateLastPlayerStatus();
  }

  private updateLastPlayerStatus()
  {
    const state = this.form.get('Status').value;

    if (state === PlayerStatus.CheckIn || state === PlayerStatus.CheckInInprogress)
    {
      this.lastPlayerState = PlayerStatus.CheckIn;
      this.playerStateValue = [...[this.lastPlayerState]];
    } else if (state === PlayerStatus.NoShow || state === PlayerStatus.NoShowFromAPI)
    {
      this.lastPlayerState = PlayerStatus.NoShow;
      this.playerStateValue = [...[state]];
    } else
    {
      this.lastPlayerState = PlayerStatus.None;
      this.playerStateValue = [...[]];
    }
    this.changeDetectorRef.detectChanges();
  }

  private handleReactivateNoShowPlayerResponse(request: PostReactivateNoShowPlayerRequest, playerStatus: PlayerStatus): void
  {
    this.form.get('Status').patchValue(playerStatus, { emitEvent: false });
    this.bookingDetailsPlayer.Status = playerStatus;
    this.updatePlayersQuantityButtons.emit(request.PlayerNumber - 1);
    this.snackBarService.showSuccess(
      this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.REACTIVATE_PLAYER_SUCCESS', {
        playerNum: request.PlayerNumber
      }), '', null, 'left'
    );
    this.isNoShow = false;
    this.changeDetectorRef.detectChanges();
  }

  createTransportationTypes(): void
  {
    this.transportationTypes = [
      {
        CartID: this.cartTypesID.Power,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.POWER')
      },
      {
        CartID: this.cartTypesID.Pull,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.PULL')
      },
      {
        CartID: this.cartTypesID.Walk,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.WALK')
      }
    ];
  }

  getPriceLevelByID(PriceLevelID: string): PriceLevel
  {
    return this.form.controls.PriceLevels?.value.find(priceLevel => priceLevel.PriceLevelID === PriceLevelID);
  }

  getPriceLevels(): PriceLevel[]
  {
    const priceLevelIDs = new Set<string>();
    return this.form.controls.PriceLevels?.value
      .filter(x =>
      {
        if (priceLevelIDs.has(x.PriceLevelID))
        {
          return false;
        }
        if ((x.IsDefault && (x.PlayerNumber !== this.playerIndex && x.IsMultiplePrice))
          || (!x.IsDefault
            && ((x.PlayerNumber === null || x.PlayerNumber === undefined || x.IsMultiplePrice)
              || (x.PlayerNumber === this.playerIndex))))
        {
          priceLevelIDs.add(x.PriceLevelID);
          return true;
        }
        return false;
      });
  }

  getContactPriceLevels(): PriceLevel[]
  {
    const priceLevelIDs = new Set<string>();
    return this.form.controls.PriceLevels?.value.filter(x =>
    {
      if (priceLevelIDs.has(x.PriceLevelID))
      {
        return false;
      }
      if (x.IsDefault && x.PlayerNumber === this.playerIndex)
      {
        priceLevelIDs.add(x.PriceLevelID);
        return true;
      }
      return false;
    });
  }

  getPlayerPrice(newPlayer: PlayerForForm): void
  {
    this.manageStandAloneLoadingService.startLoading();
    const request = new PostPlayerPriceRequest();
    request.BookingID = this.bookingID;
    request.PlayerNumber = newPlayer.PlayerNumber;
    request.StartTee = this.startTee;
    request.Holes = newPlayer.Holes;
    request.PriceLevelID = this.form.controls.PriceLevelID?.value;
    request.Cart = this.form.controls.Cart?.value;
    request.Attachments = this.form.controls.Attachments?.value;

    this.sharedSubscription.add(this.manageTeeTimeService.PostPlayerPrice(request)
      .pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe(
        {
          next: (response: PostPlayerPriceResponse) =>
          {
            this.updatePlayerPrice(response);
          }
        }));
  }

  public updatePlayerPrice(response: PostPlayerPriceResponse)
  {
    if (!response) { return; }

    this.form.patchValue(
      {
        GolfPrice: response.PlayerTotal,
        PlayerPrice: this.manageTeeTimeHelper.transformPlayerPriceForPriceBreakdown(response)
      },
      { emitEvent: false }
    );
    this.updateGroupPriceFromEachPlayerItem.emit();
  }

  expansionToggle(event: MouseEvent): void
  {
    event.stopPropagation();
    const parentNode = 'parentNode';
    if ((event.target[parentNode].className as string).includes('toggle'))
    {
      return;
    }
    this.form.get('PanelOpenState').patchValue(!this.form.get('PanelOpenState').value, { emitEvent: false });
  }

  openPlayerPriceBreakdownDialog(playerPrice: PriceForPriceBreakdown, index: number): void
  {
    this.isPlayerPriceBreakdownDialogOpen = {};
    const title = this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.PLAYER_PRICE_BREAKDOWN', { playerNum: index + 1 });
    this.playerTotalPriceForBreakDown = this.manageTeeTimeHelper.convertPriceToPriceBreakdownDialogDataModel(playerPrice, title);
    this.isPlayerPriceBreakdownDialogOpen[index] = true;
  }

  patchAttachments(attachmentsUIDs: Array<string>): Array<Attachment>
  {
    const AttachmentGroups = this.attachmentGroups;
    let attachments = [];
    if (AttachmentGroups && AttachmentGroups.length)
    {
      AttachmentGroups.map(group =>
      {
        attachments = [...group.Attachments.filter(attachment => attachmentsUIDs.some(uID => uID === attachment.AttachmentUID)),
        ...attachments];
      });
      return attachments;
    }
    return [];
  }

  closePlayerPriceBreakdownDialog(index: number): void
  {
    this.isPlayerPriceBreakdownDialogOpen[index] = false;
  }

  onOpenAttachmentsDialog(item: UntypedFormGroup): void
  {
    this.manageStandAloneLoadingService.startLoading();
    const errorMessage = this.translate.instant('POS_TEE_SHEET.ATTACHMENTS_DIALOG.POST_ATTACHMENTS_ERROR');
    if (this.unableToChangeAttachments === true)
    {
      this.snackBarService.showError(errorMessage, '', null, 'left');
      return;
    }
    const request: GetTeeTimeAttachmentRequest =
    {
      CourseID: this.courseID,
      BookingID: this.bookingID,
      PlayerNumber: item.value.PlayerNumber,
      Is9Holes: this.is9Holes(this.form.getRawValue()?.Holes)
    };
    this.sharedSubscription.add(this.teeTimeAttachmentService.GetTeeTimeAttachment(request)
      .pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
        next: (response: GetTeeTimeAttachmentResponse) =>
        {
          this.teeTimeAttachmentResponse = response;
          if (this.teeTimeAttachmentResponse.AttachmentGroups.length)
          {
            this.openAttachmentsDialog(request);
          } else
          {
            this.unableToChangeAttachments = true;
            this.snackBarService.showError(errorMessage, '', null, 'left');
          }
        }
      }));
  }

  is9Holes(hole: TeeTimeHoles | boolean): boolean
  {
    if (typeof hole !== 'boolean')
    {
      return hole === TeeTimeHoles.Holes9 ? true : false;
    }

    return hole;
  }

  private openAttachmentsDialog(request: GetTeeTimeAttachmentRequest): void
  {
    const attachmentsDialogData: AttachmentsDialogDataModel = {
      ...request,
      teeTimeAttachmentResponse: this.teeTimeAttachmentResponse
    };
    const attachmentsDialog = this.dialog.open(AttachmentsDialogComponent, {
      maxWidth: '80vw',
      maxHeight: '80vh',
      panelClass: 'menu-item-attachment-modal',
      data: attachmentsDialogData
    });

    attachmentsDialog.afterClosed().subscribe(dialogResponse =>
    {
      if (dialogResponse.IsChanged)
      {
        (this.form.get('Attachments') as UntypedFormArray).clear({ emitEvent: false });
        let attachmentItems = this.patchAttachments(dialogResponse.CheckItemID.SelectedAttachments);

        attachmentItems.forEach((attachment) =>
        {
          (this.form.get('Attachments') as UntypedFormArray).push(this.fb.group(attachment), { emitEvent: false });
        });
        (this.form.get('Attachments') as UntypedFormArray).updateValueAndValidity();
        this.changeDetectorRef.detectChanges();
      }
    });
  }

  togglePlayerStatusClass(): string
  {
    return !this.isCheckIn
      && this.lastPlayerState === this.playerStatus.CheckIn
      ? 'a-check-in-toggle' : 'a-checked-in-toggle';

  }

  handleNoShowClass(): string
  {
    return this.bookingDetailsPlayer?.Status === this.playerStatus.NoShow
      ? 'a-noshow-toggle' : 'a-noshow-toggle a-noshow-toggle-deactive';
  }

  deleteTeeTimeCheckIn(): void
  {
    const request: DeleteTeeTimeCheckInRequest = {
      BookingID: this.bookingID,
      PlayerNumber: this.playerIndex + 1
    };

    this.sharedSubscription.add(this.manageTeeTimeService.DeleteTeeTimeCheckIn(request).subscribe(
      {
        next: (response: DeleteTeeTimeCheckInResponse) =>
        {
          if (response.Result)
          {
            this.enableForm();
            this.deleteTeeTimeCheckInFromOrder();
            this.updatePlayersQuantityButtons.emit(this.playerIndex);
          }

        },
        error: () => { },
        complete: () =>
        {
        }
      }));
  }

  enableForm(): void
  {
    this.form.get('Status').patchValue(this.playerStatus.None, { emitEvent: true });
    this.form.get('Status').enable({ emitEvent: false });
    this.form.get('PriceLevelID').enable({ emitEvent: false });
    this.form.get('Cart').enable({ emitEvent: false });
    this.form.get('Holes').enable({ emitEvent: false });
    this.form.get('IsCheckedIn').patchValue(false, { emitEvent: false });
    this.form.get('DisplayName').enable({ emitEvent: false });
    this.form.updateValueAndValidity();
  }

  deleteTeeTimeCheckInFromOrder(): void
  {
    const postManageOrderTeeTimeRequest: PostManageOrderTeeTimeRequest = {
      BookingID: this.bookingID,
      PlayerNumbers: [],
      CustomerID: this.formArray.getRawValue().player[0].Contact.ContactID
    };

    const productOrder = this.orderStoreService.Get();
    if (productOrder)
    {
      postManageOrderTeeTimeRequest.RefID = productOrder.RefID;
    }

    this.sharedSubscription.add(this.manageOrderTeeTimeService.TeeTime(postManageOrderTeeTimeRequest).subscribe(orderData =>
    {
      this.orderStoreService.changeProductOrderValue(orderData.Order);
    }));
  }

  confirmDeleteTeeTimeCheckIn(): void
  {
    const confirmDialog = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog-delete-check-in',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_TEE_SHEET.MANAGE_TEE_TIME.CANCEL_CHECK_IN',
          content: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.CANCEL_CHECK_IN_DIALOG', { playerNum: this.playerIndex + 1 }),
          submitButtonLabel: 'POS_TEE_SHEET.MANAGE_TEE_TIME.CANCEL_CHECK_IN',
          submitButtonClass: 'a-confirm-submit-button'
        }
      },
      width: '350px',
      autoFocus: false
    });
    this.sharedSubscription.add(confirmDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.deleteTeeTimeCheckIn();
      }
    }));
  }

  isCheckInFn(): boolean
  {
    return this.bookingDetailsPlayer?.Status === this.playerStatus.CheckIn;
  }

  isCheckInInProgress(): boolean
  {
    return this.form.get('Status').value == this.playerStatus.CheckInInprogress;
  }

  isNoShowFn(): boolean
  {
    return this.bookingDetailsPlayer?.Status === PlayerStatus.NoShow;
  }

  confirmRemoveBookingPlayer(index: number, event?: MouseEvent): void
  {
    if (this.isCheckIn || this.isCheckInInProgress() || this.isNoShow)
    {
      return;
    }
    if (event)
    {
      event?.stopPropagation();
    }
    if (index === 1)
    {
      return;
    }
    if (this.formArray.getRawValue().player?.length === 1)
    {
      this.cancelBooking.emit();
      return;
    }

    const confirmDialog = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_TEE_SHEET.MANAGE_TEE_TIME.REMOVE_PLAYER',
          content: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.REMOVE_PLAYER_DESCRIPTION', { playerNum: index }),
          submitButtonLabel: 'POS_TEE_SHEET.MANAGE_TEE_TIME.REMOVE_PLAYER',
          cancelButtonLabel: 'POS_TEE_SHEET.GENERAL.CANCEL'
        }
      },
      width: '350px',
      autoFocus: false
    });
    this.sharedSubscription.add(confirmDialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.removeBookingPlayer(index);
      }
    }));
  }

  async removeBookingPlayer(index: number): Promise<void>
  {
    const request: PostRemoveBookingPlayerRequest = {
      BookingID: this.bookingID,
      PlayerPosition: index
    };

    this.sharedSubscription.add(this.manageTeeTimeService.RemoveBookingPlayer(request).subscribe(
      {
        next: (response: PostRemoveBookingPlayerResponse) =>
        {
          if (response.Result)
          {
            this.removePlayer.emit(index - 1);
          }
        },
        error: () => { },
        complete: () => { }
      }));
  }


  async notify(contact: Contact): Promise<void>
  {
    if (contact)
    {
      const request: GetNotificationsRequest = {
        RecordID: contact.ContactID
      };
      this.manageStandAloneLoadingService.startLoading();

      try
      {
        const response = await firstValueFrom(
          this.contactNotificationsService.GetNotifications(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading()))
        );

        if (response)
        {
          const dialogRef = this.dialog.open(ContactNotificationDialogComponent, {
            panelClass: ['check-notification-dialog', 'o-dialog-sm-fullscreen'],
            width: '400px',
            data: { Contact: contact, DataModel: response } as CheckNotificationDialogModel
          });
          const result = await firstValueFrom(dialogRef.afterClosed());

          if (result === 'success')
          {
            const contact = this.form.get('Contact').value;
            const playerHasNotification = await this.checkPlayerNotification(contact?.ContactID);
            contact.HasNotification = playerHasNotification.HasNotification;
            this.form.get('Contact').patchValue(contact, { emitEvent: false });
            this.changeDetectorRef.detectChanges();
          }
        }
      } catch (error)
      {
      }
    }
  }
  get contactValue()
  {
    return this.form.controls.Contact?.value;
  }

  disableRemoveBookingPlayer(): boolean
  {
    return this.playerIndex === 0 || this.isCheckIn || this.isCheckInInProgress() || this.isNoShow || this.pastDayStatus.IsCheckedInAndPaidAll || this.pastDayStatus.IsPrePaidNotCheckIn;
  }

  handleNoShowFromApiClass()
  {
    return this.form.controls.Status?.value === PlayerStatus.NoShowFromAPI && !this.playerStateValue.includes(PlayerStatus.NoShow);
  }

  ngOnDestroy(): void
  {
    this.detectFormValueChangesSubscription?.unsubscribe();
    this.mediaBreakpointSubscription?.unsubscribe();
    this.sharedSubscription?.unsubscribe();
    this.subscription?.unsubscribe();
  }
}
