import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, FormTimePickerModule, LoadingModule, SnackBarModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { GroupTeeTimeService } from '../../../@services/group-tee-time.service';
import { MoveSwapPlayersComponent } from './move-swap-players.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const COMPONENT = [MoveSwapPlayersComponent];
const MATERIAL_MODULES = [MatDialogModule, MatButtonModule, MatFormFieldModule, MatIconModule, MatInputModule, MatRadioModule, MatExpansionModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];
const TEI_MODULES = [TeiTextTranslatePipeModule, TeiTextTranslatePipeModule, TranslateModule,
  FormTimePickerModule, LoadingModule, MatDividerModule, ContactQuickSearchModule];
const TEI_CONTACT_MODULES = [ContactAvatarModule, AvatarModule];

@NgModule({
  declarations: [
    COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_CONTACT_MODULES
  ],
  exports: [
    ...TEI_MODULES,
    ...MATERIAL_MODULES,
    ...COMPONENT,
    ...TEI_CONTACT_MODULES
  ],
  providers: [
    {
      provide: 'IGroupTeeTimeService',
      useClass: GroupTeeTimeService
    }
  ]
})
export class MoveSwapPlayersModule { }
