import { ScrollingModule } from '@angular/cdk/scrolling';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
// Material Modules
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
// TEI Modules
import { LoadingModule } from '@tei/material';
import { HeaderService } from '../../@services/header.service';
// Services
import { MainService } from '../../@services/main.service';
import { TeeSheetTeeTimesModule } from '../../components/tee-sheet-tee-times/tee-sheet-tee-times.module';
import { TeeSheetToolbarModule } from '../../components/tee-sheet-toolbar/tee-sheet-toolbar.module';
// Components
import { ManageStandAloneLoadingService } from '@tei/core';
import { TeeSheetHeaderModule } from '../../components/tee-sheet-header/tee-sheet-header.module';
import { TeeSheetMainComponent } from './tee-sheet-main.component';


const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatRippleModule, ScrollingModule, MatIconModule, MatButtonModule];
const BASE_MODULES = [CommonModule, RouterModule];
const MODULES = [TeeSheetTeeTimesModule, TeeSheetToolbarModule, TeeSheetHeaderModule];
const TEI_MATERIAL_MODULES = [TeiTextTranslatePipeModule, LoadingModule];
const COMPONENTS = [TeeSheetMainComponent];
const PIPES = [];
@NgModule({
  declarations: [...COMPONENTS, ...PIPES],
  imports: [...MATERIAL_MODULES, ...BASE_MODULES, ...TEI_MATERIAL_MODULES, ...MODULES],
  providers: [
    { provide: 'IMainService', useClass: MainService },
    HeaderService,
    ManageStandAloneLoadingService
  ]
})
export class TeeSheetMainModule { }
