import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'streamline'
})
export class StreamlinePipe implements PipeTransform
{

  transform(value: string)
  {
    if (value.trim().length !== 0)
    {
      const streamline = value.replace(/\*|\[\(\$\)\]/g, '');
      const newStr = streamline.replace(/\\/g, '  ');
      if (newStr.trim().length === 0)
      {
        return 'Guest';
      } else
      {
        return newStr;
      }
    } else
    {
      return 'Guest';
    }
  }

}
