import { Component, Inject } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ResponsiveService } from '@tei/core';
import { PrepaidDepositBreakdownData } from '../../../@intermediate/models/get-booking-details.model';
@Component({
  selector: 'tei-pos-tee-sheet-prepaid-deposit-breakdown',
  templateUrl: 'prepaid-deposit-breakdown-dialog.component.html',
  styleUrls: ['prepaid-deposit-breakdown-dialog.component.scss']
})
export class PrepaidDepositBreakdownDialogComponent
{
  constructor(
    public dialogRef: MatDialogRef<PrepaidDepositBreakdownDialogComponent>,
    public responsiveService: ResponsiveService,
    @Inject(MAT_DIALOG_DATA) public data: PrepaidDepositBreakdownData,
  )
  {

  }
  onClose(): void
  {
    this.dialogRef.close();
  }
  onProcessReturn(): void
  {
    this.dialogRef.close(true);
  }
}
