import { CartTypesID } from './get-booking-details.model';
import { PlayerStatus } from './get-tee-sheet.model';
import { PlayerPriceLevelForForm } from './manage-tee-time.model';
import { PriceForPriceBreakdown } from './price-breakdown.model';

export class BasePostBookingDetailsRequest
{
  BookingID: number;
  BookingDateTime: string;
  CourseID: string;
  NumPlayers: NumPlayers;
  StartTee: Holes1or10;
  Notes = '';
}

export class PlayerModel
{
  IsBooker: boolean;
  ContactID: string;
  BookingID: number;
  Email?: string;
}

export class PostBookingDetailsRequest extends BasePostBookingDetailsRequest
{
  Players: Player[];
  NotificationEmails: string[];
  CCEmails: string[];
  SendNotificationEmailsToAllPlayers: boolean;
  HasManagerAuthorizeRestriction: boolean;
}

export class PostBookingDetailsRequestForForm extends BasePostBookingDetailsRequest
{
  playersList?: PlayersList;
  HasManagerAuthorizeRestriction: boolean;
}

export class PlayersList
{
  player: Array<PlayerForForm>;
}
export class PostBookingDetailsResponse
{
  Result: boolean;
  BookingID: number;
  Message: string;
  EmailErrorMessage: string;
}

export class BasePlayer
{
  PlayerNumber: NumPlayers;
  DisplayName?: string;
  Contact: Contact;
  Status: PlayerStatus;
  Holes: TeeTimeHoles;
  PriceLevelID: string;
  Cart: CartTypesID;
  NoShow: boolean;
  CheckIn?: boolean;
  CartDiscount = 0;
  CartPrice = 0;
  GolfDiscount = 0;
  GolfPrice = 0;
}

export class Player extends BasePlayer
{
  Attachments: Array<string> = [];
}
export class PlayerForForm extends BasePlayer
{
  Attachments: Array<Attachment> = [];
  PriceLevels: PlayerPriceLevelForForm[];
  PlayerPrice: PriceForPriceBreakdown;
  IsCheckedIn: boolean;
  IsExceed: boolean;
  PanelOpenState: boolean;
}

export class Contact
{
  ContactID: string;
  DisplayName: string;
  ProfilePicture: string;
  HasRestriction?: boolean;
  HasNotification?: boolean;
}

export class Attachment
{
  AttachmentUID: string;
  ItemID: string;
  ItemDescription: string;
  Price: number;
  UofM: string;
  Abbreviation: string;
  GroupUID: string;
  GroupName: string;
  groupSortIndex: number;
  SortIndex: number;
  Selected: boolean;
  autoApply: boolean;
}

export enum NumPlayers
{
  NonePlayer,
  OnePlayer,
  TwoPlayer,
  ThreePlayer,
  FourPlayer,
  fivePlayer
}

export enum Holes1or10
{
  Hole1,
  Hole10,
}

export enum TeeTimeHoles
{
  Holes18,
  Holes9
}
