import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { LoadingModule, OverlayDialogModule } from '@tei/material';
import { PipesModule } from '../../@pipes/pipes.module';
import { ManageTeeTimeService } from '../../@services/manage-tee-time.service';
import { TeeSheetService } from '../../@services/tee-sheet.service';
import { TeeSheetHeaderModule } from '../tee-sheet-header/tee-sheet-header.module';

import { AutoFocusDirectiveModule } from "@tei/core";
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ManagePlayersDialogModule } from '../manage-players-dialog/manage-players-dialog.module';
import { ManageTeeTimeModule } from '../manage-tee-time/manage-tee-time.module';
import { TeeSheetPriceOverlayModule } from '../tee-sheet-price-overlay/tee-sheet-price-overlay.module';
import { TeeSheetSlotModule } from '../tee-sheet-slot/tee-sheet-slot.module';
import { TeeSheetTeeTimesComponent } from './tee-sheet-tee-times.component';

const MATERIAL_MODULES = [MatButtonModule, MatCardModule, MatDividerModule, OverlayDialogModule, MatMenuModule, MatCheckboxModule, MatTooltipModule];
const BASE_MODULES = [CommonModule, PipesModule, TeeSheetSlotModule];
const MODULES = [TeeSheetHeaderModule, TeeSheetPriceOverlayModule];
const TEI_MATERIAL_MODULES = [LoadingModule];
const COMPONENTS = [TeeSheetTeeTimesComponent];
const TEI_CORE_MODULES = [TeiDateTranslateModule, TeiCurrencyTranslateModule, TeiTextTranslatePipeModule];
const TEI_TEE_SHEET_MODULE = [ManageTeeTimeModule, ManagePlayersDialogModule];
const PIPES = [];
const TEI_DIRECTIVES = [AutoFocusDirectiveModule];

@NgModule({
  declarations: [...COMPONENTS, ...PIPES],
  imports: [...MATERIAL_MODULES, ...BASE_MODULES, ...TEI_CORE_MODULES, ...TEI_MATERIAL_MODULES, ...MODULES, ...TEI_TEE_SHEET_MODULE, ...TEI_DIRECTIVES],
  exports: [...COMPONENTS, ...TEI_DIRECTIVES],
  providers: [
    { provide: 'ITeeSheetService', useClass: TeeSheetService },
    ManageTeeTimeService
  ]
})
export class TeeSheetTeeTimesModule { }
