import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { SnackBarService } from '@tei/material';
import moment from 'moment';
import { Subscription, finalize } from 'rxjs';
import { TeeSheetDialogData } from '../../@intermediate/models/get-tee-sheet.model';
import { PostTeeSheetBlockForm } from '../../@intermediate/models/post-tee-sheet-block-form';
import { BlockingType, GetTeeSheetBlockPriceLevelResponse, GetTeeSheetBlockTypeResponse, PostTeeTimeBlockRequest, PostTeeTimeBlockResponse, PriceLevel } from '../../@intermediate/models/tee-sheet-block.model';
import { TeeSheetBlockService } from '../../@services/tee-sheet-block.service';
import { DateTimeUtilService } from '../../@services/utilities/date-time-util.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-block',
  templateUrl: './tee-sheet-block.component.html',
  styleUrls: ['./tee-sheet-block.component.scss']
})
export class TeeSheetBlockComponent implements OnInit, OnDestroy
{
  isMobile = false;
  slotPerRowMin = 1;
  slotPerRowMax = this.data.FivePlayerTemplate ? 5 : 4;
  form = new PostTeeSheetBlockForm(this.slotPerRowMax);
  teeTimeList: string[] = this.data.TemplateTimes || [];
  teeTimes: string[];
  blockTypeList: BlockingType[] = [];
  priceLevelList: PriceLevel[] = [];
  subscribe: Subscription = new Subscription();
  selectedBlockTypeDescription: string;
  selectedColor: string;
  selectedBlockTypeAllowBookingStatus: boolean = false;
  outOfRangeMessage: string;

  currency: string;
  list: { TeeTime: string; }[] = [];

  constructor(
    private dialogRef: MatDialogRef<TeeSheetBlockComponent>,
    @Inject(MAT_DIALOG_DATA) public data: TeeSheetDialogData,
    private responsiveService: ResponsiveService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private teeSheetBlockService: TeeSheetBlockService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    private currencyService: TeiCurrencyTranslateService,
    private dateTimeUtilService: DateTimeUtilService
  ) { }

  ngOnInit(): void
  {
    if (this.teeTimeList.length)
    {
      this.initForm();
      this.setTeeTimes();
    }
    this.getMobileSize();
    this.getCurrency();
    this.getBlockTypeList();
    this.getPriceLevelList();
  }

  getCurrency(): void
  {
    this.currency = this.currencyService.getCurrencySymbol();
  }

  initForm(): void
  {
    this.setDefaultTeeTimes();
    this.setDefaultSlotsPerRow();
    this.onTeeTimeChange();
  }

  setTeeTimes(): void
  {
    const startTimeIndex = this.data.TemplateTimes.findIndex(t => t == this.form.value.startTeeTime);
    this.teeTimes = this.data.TemplateTimes.slice(startTimeIndex, this.data.TemplateTimes.length);
  }

  onStartTeeTimeChange(): void
  {
    if (this.changeFormatOfTime(this.form.value.endTeeTime) !== this.changeFormatOfTime(this.form.value.startTeeTime))
    {
      this.form.patchValue({
        endTeeTime: this.form.value.startTeeTime
      });
    }
    this.setTeeTimes();
    this.onTeeTimeChange();
  }

  changeFormatOfTime(time: string): string
  {
    return moment(time, 'h:mm A').format('HH:mm');
  }

  setDefaultTeeTimes(): void
  {
    const defaultTeeTime = this.teeTimeList[this.data.RowIndex] || '';
    this.form.patchValue({
      startTeeTime: defaultTeeTime,
      endTeeTime: defaultTeeTime
    });
    this.list = [{ TeeTime: defaultTeeTime }];
  }

  changeBlockCost(): void
  {
    const blockCostControl = this.form.get('blockCost');
    blockCostControl.patchValue(Number(blockCostControl!.value).toFixed(2));
  }

  setDefaultSlotsPerRow(): void
  {
    this.form.get('slotsPerRow')?.setValue((this.data.PlayerIndex ?? -1) + 1);
  }

  onTeeTimeChange(): void
  {
    const { startTeeTime, endTeeTime } = this.form.value;
    const startIndex = this.teeTimeList.indexOf(startTeeTime);
    const endIndex = this.teeTimeList.indexOf(endTeeTime);

    if (startIndex >= 0 && endIndex >= startIndex)
    {

      this.form.get('numberOfRows')?.setValue(endIndex - startIndex + 1);
      this.list = [];
      for (let i = startIndex; i <= endIndex; i++)
      {
        this.list.push({ TeeTime: this.teeTimeList[i] });
      }

    }
  }

  changeSlotsPerRow(): void
  {
    this.outOfRangeMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_BLOCK_DIALOG.SLOTS_PER_ROW_RANGE', { slotPerRowMin: this.slotPerRowMin });
  }

  getBlockTypeList(): void
  {
    this.manageStandAloneLoadingService.startLoading();
    this.teeSheetBlockService.BlockTypes().pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: GetTeeSheetBlockTypeResponse) =>
      {
        this.blockTypeList = response.BlockingTypes;
      }
    });
  }

  getPriceLevelList(): void
  {
    this.manageStandAloneLoadingService.startLoading();
    this.subscribe = this.teeSheetBlockService.PriceLevels().pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: GetTeeSheetBlockPriceLevelResponse) =>
      {
        this.priceLevelList = response.PriceLevels;
      }
    });
  }

  selectBlockTypeList(item: BlockingType): void
  {
    this.selectedBlockTypeDescription = item.Description;
    this.selectedColor = item?.BlockColor;
    this.selectedBlockTypeAllowBookingStatus = item?.AllowBookings;
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  onApply(): void
  {
    if (this.form.valid)
    {
      this.manageStandAloneLoadingService.startLoading();
      const formValues = this.form.value;
      const request: PostTeeTimeBlockRequest = {
        CourseID: this.data.Course.CourseID,
        StartTee: this.data.Course.StartTeeHole,
        DisplayName: formValues.displayName,
        Players: formValues.slotsPerRow.toString(),
        Notes: formValues.note,
        BlockedTypeID: formValues.blockType,
        BlockedCost: formValues.blockCost,
        PriceLevel: formValues.priceLevel,
        TeeTimes: this.dateTimeUtilService.buildTeeTimesArray(
          this.data.SelectedTeeSheetDate,
          this.list
        )
      };

      this.subscribe = this.teeSheetBlockService.PostTeeTimeBlocks(request)
        .pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading()))
        .subscribe({
          next: (response: PostTeeTimeBlockResponse) =>
          {
            if (response.IsSuccess)
            {
              this.snackBarService.showSuccess(
                this.translate.instant("POS_TEE_SHEET.MANAGE_BLOCK_DIALOG.SAVE_SUCCESS"),
                '', null, 'left'
              );

            }
            if (response)
            {
              this.dialogRef.close(response.IsSuccess);
            }
          },

        });

    }
  }


  ngOnDestroy(): void
  {
    this.subscribe?.unsubscribe();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  };
}
