import { COMMA, ENTER, TAB, SPACE } from '@angular/cdk/keycodes';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { Component, HostListener, Inject, OnInit } from '@angular/core';

import { FormArray, FormControl, FormGroup, UntypedFormBuilder, Validators } from '@angular/forms';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService , ResponsiveService} from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, ConfirmDialogService, DialogDataModel, SnackBarService } from '@tei/material';
import moment from 'moment';
import { finalize, firstValueFrom, Subscription } from 'rxjs';
import { IMainService } from '../../../@intermediate/main.interface';
import { GetTeeSheetMainCoursesRequest } from '../../../@intermediate/models/get-tee-sheet-main-courses.model';
import { Course } from '../../../@intermediate/models/get-tee-sheet-main.model';
import { GetTeeSheetMessageTemplateRequest, TeeSheetMessageTemplate } from '../../../@intermediate/models/get-tee-sheet-message-template.model';
import { GetTeeSheetPlayerEmailInfoRequest, TeeSheetPlayerEmailInfo } from '../../../@intermediate/models/get-tee-sheet-player-email-info.model';
import { GetTeeSheetRequest } from '../../../@intermediate/models/get-tee-sheet.model';
import { TeeSheetMessageStaff } from '../../../@intermediate/models/post-tee-sheet-message.model';
import { TeeSheetMessageDialogData, TeeSheetMessageEmailsForm, TeeSheetMessageForm, TeeSheetMessageSteps } from '../../../@intermediate/models/tee-sheet-message.model';
import { ITeeSheetService } from '../../../@intermediate/tee-sheet.interface';
import { EmailPreviewDialogComponent } from '../email-preview-dialog/email-preview-dialog.component';
@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-message',
  templateUrl: './tee-sheet-message.component.html',
  styleUrls: ['./tee-sheet-message.component.scss']
})

export class TeeSheetMessageComponent implements OnInit
{

  @HostListener('keydown.enter', ['$event'])
  handleEnterKey(event: KeyboardEvent): void
  {
    event.stopPropagation();
  }
  teeSheetMessageForm = new FormGroup<TeeSheetMessageForm>({
    Course: new FormControl(this.messageDialogData.CourseID, Validators.required),
    Template: new FormControl(null, Validators.required),
    StartTime: new FormControl('', Validators.required),
    EndTime: new FormControl('', Validators.required),
    Subject: new FormControl('', Validators.required),
    Message: new FormControl('', Validators.required)
  });

  teeSheetMessageEmailsForm = new FormGroup<TeeSheetMessageEmailsForm>({
    Staff: new FormControl(''),
    StaffEmails: new FormControl(''),
    Emails: new FormArray([], [Validators.required])
  });
  teeSheetMessageSteps = TeeSheetMessageSteps;
  step = TeeSheetMessageSteps.StepOne;
  courses: Course[];
  templates: TeeSheetMessageTemplate[];
  timesTemplate: string[];
  selectable = true;
  removable = true;
  invalidStaffEmails: string[] = [];
  emails: Array<string> = [];
  nameLength: number = 30;
  readonly separatorKeysCodes = [ENTER, COMMA, TAB, SPACE] as const;
  playerEmails: TeeSheetPlayerEmailInfo[];
  subscription: Subscription;
  defaultProfilePicture = '/assets/images/ProfilePicture.png';
  get startTime(): string
  {
    return this.teeSheetMessageForm.get('StartTime').value;
  }
  get endTime(): string
  {
    return this.teeSheetMessageForm.get('EndTime').value;
  }
  get teeSheetMessageEmailsFormControls()
  {
    return this.teeSheetMessageEmailsForm.controls;
  }
  get invalidStaffEmailsString(): string
  {
    return this.invalidStaffEmails.join(', ');
  }
  get EmailsFormArray()
  {
    return this.teeSheetMessageEmailsForm.get('Emails') as FormArray;
  }
  constructor(
    public dialogRef: MatDialogRef<TeeSheetMessageComponent>,
    @Inject(MAT_DIALOG_DATA) public messageDialogData: TeeSheetMessageDialogData,
    @Inject('IMainService') private mainService: IMainService,
    @Inject('ITeeSheetService') private teeSheetService: ITeeSheetService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private translate: TranslateService,
    private snackBarService: SnackBarService,
    private formBuilder: UntypedFormBuilder,
    private confirmDialogService: ConfirmDialogService,
    private dialog: MatDialog,
    public responsiveService: ResponsiveService,
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.setNameLength();
    this.getCourses();
    this.getTeeSheetMessageTemplate();

  }

  async getCourses(): Promise<void>
  {
    const request: GetTeeSheetMainCoursesRequest = {};

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(this.mainService.Courses(request).pipe(
        finalize(() => this.manageStandAloneLoadingService.finishLoading())
      ));
      this.courses = response.Courses;
    } catch (error)
    {
      this.snackBarService.showError(error, '', null, 'left');
    }
  }

  async getTeeSheetMessageTemplate(): Promise<void>
  {
    const request: GetTeeSheetMessageTemplateRequest = {
      CourseID: this.teeSheetMessageForm.get('Course').value
    };

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(this.teeSheetService.GetTeeSheetMessageTemplate(request).pipe(
        finalize(() => this.manageStandAloneLoadingService.finishLoading())
      ));
      this.templates = response.Templates;

      this.fillTimesAndTemplate();
    } catch (error)
    {
      this.snackBarService.showError(error, '', null, 'left');
    }
  }

  fillTimesAndTemplate(): void
  {
    const teeSheetDate = this.messageDialogData.teeSheetArray[0].SelectedTeeSheetDate;
    const courseID = this.teeSheetMessageForm.get('Course').value;

    const teeSheet = this.messageDialogData.teeSheetArray.find(
      teeSheet => teeSheet.SelectedCourse.CourseID === courseID
    );

    if (teeSheet)
    {
      this.timesTemplate = teeSheet.GetTeeSheet.TemplateTimes;
      this.setFormValues();
    } else
    {
      this.loadTeeSheet(teeSheetDate);
    }
  }

  async loadTeeSheet(teeSheetDate: Date): Promise<void>
  {
    const courseID = this.teeSheetMessageForm.get('Course').value;
    const course = this.courses.find(course => course.CourseID === courseID);
    const request = new GetTeeSheetRequest();

    if (courseID)
    {
      request.CourseID = courseID;
    }

    if (course?.StartTeeHole)
    {
      request.StartTeeHole = course.StartTeeHole;
    }

    if (teeSheetDate)
    {
      request.TeeSheetDate = moment(teeSheetDate).format('YYYY-MM-DD');
    }

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(this.teeSheetService.GetTeeSheet(request).pipe(
        finalize(() => this.manageStandAloneLoadingService.finishLoading())
      ));
      this.timesTemplate = response.TemplateTimes;
      this.setFormValues();
    } catch (error)
    {
      this.snackBarService.showError(error, '', null, 'left');
    }
  }

  setFormValues(): void
  {
    const defaultTemplate = this.templates.find(template => template.IsDefault);
    const template = defaultTemplate ? defaultTemplate : this.templates?.[0];

    if (!template)
    {
      this.snackBarService.showError(
        this.translate.instant('POS_TEE_SHEET.SEND_MESSAGE.NO_DEFAULT_TEMPLATE'),
        '', null, 'left'
      );
    }

    this.teeSheetMessageForm.patchValue({
      StartTime: this.timesTemplate[0],
      EndTime: this.timesTemplate[this.timesTemplate.length - 1],
      Template: template
    });
  }

  changeFormatOfTime(time: string): string
  {
    return moment(time, 'h:mm A').format('HH:mm');
  }

  async GetTeeSheetPlayerEmailInfo(): Promise<void>
  {
    const courseID = this.teeSheetMessageForm.get('Course').value;
    const teeSheetDate = this.messageDialogData.teeSheetArray[0].SelectedTeeSheetDate;
    const request: GetTeeSheetPlayerEmailInfoRequest = {
      CourseID: courseID,
      StartTime: this.changeFormatOfTime(this.teeSheetMessageForm.get('StartTime').value),
      EndTime: this.changeFormatOfTime(this.teeSheetMessageForm.get('EndTime').value),
      TeeSheetDate: moment(teeSheetDate).format('YYYY-MM-DD')
    };

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(this.teeSheetService.GetTeeSheetPlayerEmailInfo(request).pipe(
        finalize(() => this.manageStandAloneLoadingService.finishLoading())
      ));
      if (response)
      {
        this.playerEmails = response.TeeSheetPlayerEmailInfos;
        this.playerEmails.map(playerEmail =>
        {
          this.EmailsFormArray.push(this.formBuilder.group(
            {
              CourseID: playerEmail.CourseID,
              CourseName: playerEmail.CourseName,
              TeeTimeID: playerEmail.TeeTimeID,
              TeeTimeDisplay: playerEmail.TeeTimeDisplay,
              TeeTime: playerEmail.TeeTime,
              StartTee: playerEmail.StartTee,
              DisplayName: playerEmail.DisplayName,
              FirstName: playerEmail.FirstName,
              LastName: playerEmail.LastName,
              Email: playerEmail.Email,
              ContactID: playerEmail.ContactID,
              ProfilePicture: playerEmail.ProfilePicture,
              checked: [true]
            }
          ));
        });
        this.step = this.teeSheetMessageSteps.StepTwo;
      }

    } catch (error)
    {
      this.snackBarService.showError(error, '', null, 'left');
    }
  }

  duplicateEmailValidator = (control: FormControl) =>
  {
    const emails = (control.parent as FormArray).controls;
    const emailValue = control.value;

    for (const emailControl of emails)
    {
      if (emailControl !== control && emailControl.value === emailValue)
      {
        return { duplicateEmail: true };
      }
    }

    return null;
  };
 
  addEmailChip(value: Array<string>): void
  {
    this.emails = value;
  }

  checkAll(): void
  {
    if (this.allChecked())
    {
      this.EmailsFormArray.controls.forEach((control) =>
      {
        control.patchValue({ checked: false });
      });
    } else
    {
      this.EmailsFormArray.controls.forEach((control) =>
      {
        control.patchValue({ checked: true });
      });
    }
  }

  allChecked(): boolean
  {
    return this.EmailsFormArray.controls.every((control) => control.get('checked').value);
  }

  numberOfChecks(): number
  {
    return this.EmailsFormArray.controls.filter((control) => control.get('checked').value)?.length;
  }

  back(): void
  {
    this.step = this.teeSheetMessageSteps.StepOne;
    (this.teeSheetMessageEmailsForm.get('Emails') as FormArray).clear();
    this.playerEmails = [];
  }

  onConfirmSend(): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_TEE_SHEET.SEND_MESSAGE.CONFIRMATION',
          content: this.translate.instant('POS_TEE_SHEET.SEND_MESSAGE.CONFIRMATION_CONTENT', { number: this.numberOfChecks() }),
          submitButtonLabel: 'POS_TEE_SHEET.SEND_MESSAGE.SEND',
          submitButtonColor: 'accent',
          cancelButtonLabel: 'POS_TEE_SHEET.GENERAL.CANCEL'
        } as DialogDataModel
      },
      width: '320px',
    });
    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        try
        {
          this.manageStandAloneLoadingService.startLoading();
          const response = firstValueFrom(this.teeSheetService.PostTeeSheetMessage({
            AutomationID: +this.teeSheetMessageForm.value.Template.AutomationID,
            Message: this.teeSheetMessageForm.value.Message,
            CourseName: this.getCourseName(this.teeSheetMessageForm.value.Course),
            Players: this.EmailsFormArray.controls
              .filter((control) => control.get('checked').value)
              .map((control) => ({
                ContactID: control.value.ContactID,
                FirstName: control.value.FirstName,
                Email: control.value.Email,
                LastName: control.value.LastName,
              })),
            Staffs: this.emails.map(p => {
              const staff : TeeSheetMessageStaff = {
                Email : p
                };
              return staff;
            }),
            SubjectLine: this.teeSheetMessageForm.value.Subject,
            CourseID: this.teeSheetMessageForm.value.Course,
          }).pipe(
            finalize(() =>
            {
              this.manageStandAloneLoadingService.finishLoading();

            })
          ));
          if (response)
          {
            this.onClose();
            this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.EMAIL_SENT_SUCCESSFULLY'), '', null, 'left');
          }
        } catch (error)
        {
          this.snackBarService.showError(this.translate.instant('POS_TEE_SHEET.SEND_MESSAGE.SEND_EMAIL_ERROR'), '', null, 'left');
        }
      }

    });
  }

  onEmailPreview(): void
  {
    this.dialog.open(EmailPreviewDialogComponent, {
      scrollStrategy: new NoopScrollStrategy(),
      data: {
        AutomationCategoryTemplateID: this.teeSheetMessageForm.value.Template.AutomationCategoryTemplateID,
        Message: this.teeSheetMessageForm.value.Message,
        SubjectLine: this.teeSheetMessageForm.value.Subject,
        TeeSheetDate: moment(new Date()).format("YYYY-MM-DD"),
        TeeTimeDisplay: moment(this.teeSheetMessageForm.value.StartTime).format('LT'),
        Course: this.getCourseName(this.teeSheetMessageForm.value.Course),
      },
      panelClass: ['email-preview-dialog', 'o-dialog-sm-fullscreen'],
      width: '800px',
    });
  }

  getCourseName(courseID: string): string
  {
    return this.courses.find(course => course.CourseID === courseID).CourseName;
  }
  setNameLength(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    if(screenSize === 'xs')
      {
        this.nameLength = 15;
      }else if(screenSize === 'sm')
      {
        this.nameLength = 22;
      };
  }
  
  onClose(): void
  {
    this.dialogRef.close();
  }
}
