import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService } from '@tei/core';
import { ChipItemModel, ChipOptionModel, ChipType } from '@tei/material';
import { Contact } from '@tei/office-contacts';
import moment from "moment";
import { catchError, concat, debounceTime, Observable, of, Subject, switchMap } from 'rxjs';
import { Course } from '../../../@intermediate/models/get-tee-sheet-main.model';
import { CreatorEnum, PostTeeSheetWaitListRequest, WaitListDialogData } from '../../../@intermediate/models/post-tee-sheet-wait-list.model';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-wait-list-search-header',
  templateUrl: './tee-sheet-wait-list-search-header.component.html',
  styleUrls: ['./tee-sheet-wait-list-search-header.component.scss']
})
export class TeeSheetWaitListSearchHeaderComponent implements OnInit
{
  @Input() headerData: WaitListDialogData;
  @Input() request: PostTeeSheetWaitListRequest;
  @Input() isMobile: boolean;
  @Input() selectedDateAndTime: string;
  @Input() selectedCourse: Course;
  ChipType = ChipType;
  selectedContact: Contact;
  selectedBooker: Contact;
  coursesWithDisplayName: ChipItemModel[] = [
    { chipName: 'CourseName', displayName: this.translate.instant('POS_TEE_SHEET.TEESHEET_WAITLIST.COURSE'), bindValue: 'UniqueID' } as ChipItemModel,
  ];
  coursesSubjectArray: Observable<Course[]> = of([]);
  coursesTypeAheadInput$ = [];
  numPlayersSelectedChip = new ChipOptionModel();
  createdBySelectedChip = new ChipOptionModel();
  availableOnly = false;
  creatorEnum = CreatorEnum;
  defaultDate = new ChipOptionModel();
  numOfPlayer = [
    { NumPlayer: '1', Value: '1' },
    { NumPlayer: '2', Value: '2' },
    { NumPlayer: '3', Value: '3' },
    { NumPlayer: '4', Value: '4' },
    { NumPlayer: '5', Value: '5' },
  ];
  numberOfPlayerItemsWithDisplayName = [
    {
      chipName: 'NumPlayer',
      displayName: this.translate.instant(
        'POS_TEE_SHEET.TEESHEET_WAITLIST.NUM_PLAYERS'
      ),
      bindValue: 'Value'
    }
  ];
  @Output() enterFilterValueEvent = new EventEmitter<PostTeeSheetWaitListRequest>();

  constructor(public manageLoadingService: ManageStandAloneLoadingService,
    private translate: TranslateService,
  ) { }

  ngOnInit(): void
  {
    this.defaultDate.firstValue = this.selectedDateAndTime;
    this.defaultDate.secondValue = this.selectedDateAndTime;
    this.defaultDate.hasValue = true;
    this.filterCoursesByChipItem();
  }

  filterCoursesByChipItem(): void
  {
    this.coursesTypeAheadInput$.push(new Subject<string>());
    this.coursesSubjectArray[0] = concat(
      of(this.headerData.Courses),
      this.coursesTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterCourses(searchTerm as string).pipe(
            catchError(() => of(this.headerData.Courses)),
          ))
      )
    );
  }

  onAvailableOnly(): void
  {
    this.availableOnly = !this.availableOnly;
    this.request.AvailableOnly = !!this.availableOnly;
    this.load();
  }

  filterCourses(searchTerm: string): Observable<Array<Course>>
  {
    return of(this.headerData.Courses.filter(p => p.CourseName.toLowerCase().includes(searchTerm?.toLowerCase())));
  }

  dateSearchChange(event: ChipOptionModel): void
  {
    if (event.firstValue && event.secondValue)
    {
      this.request.StartDateTime = moment(event.firstValue).format('YYYY-MM-DD') + ' ' + '00:00:00';
      this.request.EndDateTime = moment(event.secondValue).format('YYYY-MM-DD') + ' ' + '23:59:59';
    } else
    {
      this.request.StartDateTime = '';
      this.request.EndDateTime = '';
    }
    this.load();
  }

  createdDateSearchChange(event: ChipOptionModel): void
  {
    if (event.firstValue && event.secondValue)
    {
      this.request.CreatedDateStart = moment(event.firstValue).format('YYYY-MM-DD');
      this.request.CreatedDateEnd = moment(event.secondValue).format('YYYY-MM-DD');
    } else
    {
      this.request.CreatedDateStart = '';
      this.request.CreatedDateEnd = '';
    }
    this.load();
  }

  onContactItemClickEvent(contact: Contact, creatorEnum: CreatorEnum): void
  {
    if (creatorEnum === CreatorEnum.Booker)
    {
      this.selectedBooker = contact;
      this.request.BookerID = contact.ContactID;
    } else
    {
      this.selectedContact = contact;
      this.request.ContactID = contact.ContactID;
    }
    this.load();
  }

  removeContactItemClickEvent(contact: Contact, creatorEnum: CreatorEnum): void
  {
    if (creatorEnum === CreatorEnum.Booker)
    {
      this.selectedBooker = null;
      this.request.BookerID = '';
    } else
    {
      this.selectedContact = null;
      this.request.ContactID = '';
    }
    this.load();
  }

  timeSearchChange(event: ChipOptionModel): void
  {

  }

  onFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    filterObjects?.forEach((chipItem: any) =>
    {
      if (chipItem.chipName === 'CourseName')
      {
        this.request.Courses = chipItem.selectedItem.length > 0
          ? Array.isArray(chipItem.value) ? chipItem.value : [chipItem.value]
          : null;
      }
      this.load();
    });
  }

  createdByChange(event: ChipOptionModel): void
  {
    this.request.CreatedBy = event.hasValue ? event.firstValue : '';
    this.load();
  }

  numPlayersChange(chipOptionModel: ChipOptionModel): void
  {
    if (chipOptionModel[0].value)
    {
      this.request.Players = chipOptionModel[0].value;
    } else
    {
      this.request.Players = null;
    }
    this.load();
  }

  removeCoursesItem(filterObject: any): void
  {
    if (filterObject.chipName === 'CourseName')
    {
      this.request.Courses = filterObject.selectedItem.length > 0
        ? Array.isArray(filterObject.value) ? filterObject.value : [filterObject.value]
        : null;
    }
    this.load();
  }

  load(): void
  {
    this.enterFilterValueEvent.emit(this.request);
  }
}
