import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ManageLoadingService } from '@tei/core';
import { distinctUntilChanged, Subscription } from 'rxjs';
import { GetModifiedTeeSheetResponse, TeeSheetTemplate } from '../../../@intermediate/models/get-modified-tee-sheet.model';
import { TeeSheetTemplateApply } from '../../../@intermediate/models/get-teesheet-template.model';
import { TeeSheetTemplateService } from '../../../@services/tee-sheet-template.service';


@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-template-tee-time',
  templateUrl: './tee-sheet-template-tee-time.component.html',
  styleUrls: ['./tee-sheet-template-tee-time.component.scss']
})
export class TeeSheetTemplateTeeTimeComponent implements OnInit, OnDestroy
{
  @Output() public onCancel = new EventEmitter();
  @Input() isMobile = false;
  public teeSheetModifiedData: GetModifiedTeeSheetResponse;
  public checkboxesState: { indeterminate: boolean, checked: boolean; }[] = [];
  public isCheckboxChanged = false;
  teeSheetTemplateApply: TeeSheetTemplateApply;
  @Input() public set teeSheetModified(value: GetModifiedTeeSheetResponse)
  {
    this.dataSource = new MatTableDataSource(value.TeeSheetTemplates);
    if (value.TeeSheetTemplates.length > 0)
    {
      this.flagAndTimezoneColumns = value.TimeZoneAndFlagTemplates.map(item => item.Name);
    }
    this.displayedColumns = ['teeTime', ...this.flagAndTimezoneColumns];
    this.teeSheetModifiedData = value;
    this.isCheckboxChanged = false;
  }
  public subscription = new Subscription();
  dataSource: MatTableDataSource<TeeSheetTemplate>;
  flagAndTimezoneColumns: string[] = [];
  displayedColumns: string[] = [];

  constructor(
    public fb: FormBuilder,
    public teeSheetTemplateService: TeeSheetTemplateService,
    public manageLoadingService: ManageLoadingService
  ) { }

  ngOnInit(): void
  {
    this.getTeeSheetTemplateApply();
  }

  toggleAllFlagAndTimezoneValues(flagAndTimezoneCol: string, checked: boolean, isTimeZone: boolean): void
  {
    this.dataSource.data.forEach(item => this.setFlagAndTimezoneValue(item, flagAndTimezoneCol, checked, isTimeZone));
    this.isCheckboxChanged = true;
  }

  isAllFlagAndTimezoneValuesChecked(flagAndTimezoneCol: string): boolean
  {
    return this.dataSource.data.every(item => !!item[flagAndTimezoneCol]);
  }

  isSomeFlagAndTimezoneValuesChecked(flagAndTimezoneCol: string): boolean
  {
    const checkedCount = this.dataSource.data.filter(item => item[flagAndTimezoneCol]).length;
    return checkedCount > 0 && checkedCount < this.dataSource.data.length;
  }

  getFlagAndTimezoneValue(element: TeeSheetTemplate, flagAndTimezoneCol: string): boolean
  {
    return !!element[flagAndTimezoneCol];
  }

  setFlagAndTimezoneValue(element: TeeSheetTemplate, flagAndTimezoneCol: string, value: boolean, isTimeZone: boolean): void
  {
    if (isTimeZone)
    {
      const timezones = this.teeSheetModifiedData.TimeZoneAndFlagTemplates.filter(timezone => timezone.IsTimeZone);
      timezones.forEach(timezone =>
      {
        element[timezone.Name] = (timezone.Name === flagAndTimezoneCol) ? value : false;
      });
    } else
    {
      element[flagAndTimezoneCol] = value;
    }
    this.isCheckboxChanged = true;
  }

  public getTeeSheetTemplateApply(): void
  {
    this.subscription.add(
      this.teeSheetTemplateService.apply$.pipe(
        distinctUntilChanged()
      ).subscribe((teeSheetTemplateApply: TeeSheetTemplateApply) =>
      {
        if (teeSheetTemplateApply)
          this.updateForm(teeSheetTemplateApply);
      })
    );
  }

  public updateForm(teeSheetTemplateApply: TeeSheetTemplateApply): void
  {
    const {
      TimeRangeStartIndex,
      TimeRangeEndIndex,
      Timezone,
      FlagType,
    } = teeSheetTemplateApply;

    if (TimeRangeStartIndex !== null && TimeRangeEndIndex !== null)
    {
      const timezones = this.teeSheetModifiedData.TimeZoneAndFlagTemplates.filter(tz => tz.IsTimeZone);
      for (let i = teeSheetTemplateApply.TimeRangeStartIndex; i <= teeSheetTemplateApply.TimeRangeEndIndex; i++)
      {
        timezones.map(timezone =>
        {
          if (Timezone === 'NONE' || timezone.Name !== Timezone)
          {
            this.dataSource.data[i][timezone.Name] = false;
          } else
          {
            this.dataSource.data[i][timezone.Name] = true;
          }
        });

        FlagType.map(flag =>
        {
          this.dataSource.data[i][flag] = true;
        });
      }
    }
    this.isCheckboxChanged = true;
    this.teeSheetModifiedData.FivePlayerTemplate = teeSheetTemplateApply.FivePlayer;
  }

  public async onSave(): Promise<void>
  {
    this.teeSheetTemplateService.saveTemplate({ ...this.teeSheetModifiedData, ModifiedTSItems: this.dataSource.data });
    this.teeSheetTemplateService.teeTimeFormChange(false);
    this.isCheckboxChanged = false;
  }

  public cancel(): void
  {
    this.onCancel.emit();
  };

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}