import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { GetModifiedCourseCrossoversResponse, TeeSheetCourseCrossoversApply } from '../@intermediate/models/get-modified-course-cross-overs.model';
import { PostModifiedTeeSheetCourseCrossoversRequest } from '../@intermediate/models/post-modified-tee-sheet-course-cross-overs.model';

@Injectable()
export class TeeSheetCourseCrossoversService
{
  constructor() { }
  private Load = new BehaviorSubject<GetModifiedCourseCrossoversResponse>(new GetModifiedCourseCrossoversResponse());
  load$ = this.Load.asObservable();

  private Apply = new Subject<TeeSheetCourseCrossoversApply>();
  apply$ = this.Apply.asObservable();

  private Reset = new Subject<boolean>();
  reset$ = this.Reset.asObservable();

  public SheetTimeFormChanged = false;

  load(value: GetModifiedCourseCrossoversResponse): void
  {
    this.Load.next(value);
  }

  private Save = new Subject<PostModifiedTeeSheetCourseCrossoversRequest>();
  save$ = this.Save.asObservable();

  apply(value: TeeSheetCourseCrossoversApply): void
  {
    this.Apply.next(value);
    this.setSheetTimeFormChange(true);
  }

  reset(): void
  {
    this.Reset.next(true);
    this.setSheetTimeFormChange(false);
  }

  setSheetTimeFormChange(value: boolean): void
  {
    this.SheetTimeFormChanged = value;
  }

  save(value: PostModifiedTeeSheetCourseCrossoversRequest): void
  {
    this.Save.next(value);
  }
}
