import { Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from "@tei/core";
import { finalize, Subscription } from "rxjs";
import { TeeSheetService } from "../../../@services/tee-sheet.service";
import { GetEmailPreviewTeeSheetResponse } from "../../../@intermediate/models/get-email-preview-tee-sheet.model";
@Component({
  selector: 'tei-pos-tee-sheet-email-preview-dialog',
  templateUrl: './email-preview-dialog.component.html',
  styleUrls: ['./email-preview-dialog.component.scss'],

})
export class EmailPreviewDialogComponent implements OnInit, OnDestroy
{
  subscriptions = new Subscription();
  emailBody: GetEmailPreviewTeeSheetResponse;
  constructor(
    public dialog: MatDialogRef<EmailPreviewDialogComponent>,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject(MAT_DIALOG_DATA) public data,
    @Inject('ITeeSheetService') private teeSheetService: TeeSheetService
  ) { }

  async ngOnInit():Promise<void>
  {
   await this.loadData();
  }

   async loadData(): Promise<void>
  {

    this.manageStandAloneLoadingService.startLoading();
    this.subscriptions.add(this.teeSheetService.GetEmailPreviewTeeSheet(this.data).pipe(finalize(() =>
    {
      this.manageStandAloneLoadingService.finishLoading();
    })).subscribe((response) =>
    {
      this.emailBody = response;
    }));
  }

  onCloseClick(): void
  {
    this.dialog.close();
  }

  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}
