import { Component, Input, OnDestroy, OnInit, Output, EventEmitter } from '@angular/core';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import moment from 'moment';
import { firstValueFrom, Subscription } from 'rxjs';
import { CourseTypeEnum } from '../../../@intermediate/models/get-tee-sheet-template.model';
import { TeeSheetDisplayForm, TeeTime as DisplayTeeTime, DateSelectionEnum, HoleNumberTypeEnum, TeeSheetDisplayApply, GetDisplayDataRequest, DisplayTeeSheetDialogData, GetDisplayDataResponse } from '../../../@intermediate/models/get-teesheet-display.model';
import { Course } from '../../../@intermediate/models/get-tee-sheet-main.model';
import { TeeSheetDisplayService } from '../../../@services/tee-sheet-display.service';
import { GetGeneratingHoleNumbersRequest, GetGeneratingHoleNumbersResponse, SaveDisplayFormData } from '../../../@intermediate/models/post-display-tee-sheet.model';
import { TeeSheetService } from '../../../@services/tee-sheet.service';
import { ManageLoadingService } from '@tei/core';

export const CUSTOM_DATE_FORMATS = {
  parse: {
    dateInput: 'MMM DD, YYYY',
  },
  display: {
    dateInput: 'MMM DD, YYYY',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};

@Component({
  selector: 'tei-pos-tee-sheet-display-form',
  templateUrl: './display-form.component.html',
  styleUrls: ['./display-form.component.scss'],
  providers: [
    { provide: MAT_DATE_FORMATS, useValue: CUSTOM_DATE_FORMATS },
  ],
})
export class DisplayFormComponent implements OnInit, OnDestroy
{
  private teeTimesArray: DisplayTeeTime[] = [];

  coursesList: Course[] = [];
  selectedCourse: Course;
  sheetDate: string;
  dialogData = new DisplayTeeSheetDialogData();



  @Output() onGenerateClicked = new EventEmitter<TeeSheetDisplayApply>();
  @Output() onClearClicked = new EventEmitter<boolean>();

  public form: FormGroup<TeeSheetDisplayForm>;
  subscription = new Subscription();
  courseTypeTitle = '';
  courseType = CourseTypeEnum;
  DateSelectionEnum = DateSelectionEnum;
  HoleNumberTypeEnum = HoleNumberTypeEnum;
  // Time options computed from API TeeTimes

  teeSheetTemplates: Array<{ Index: number; TeeTime: string }> = [];
  constructor(public teeSheetDisplayService: TeeSheetDisplayService,
    public dialog: MatDialog,
    public teeSheetService: TeeSheetService,
    public manageLoadingService: ManageLoadingService
  ) { }

  ngOnInit(): void
  {


    this.dialogData = this.teeSheetDisplayService.getDisplayDialogData()
    if (this.dialogData.selectedDate) {

      this.selectedCourse = this.dialogData.selectedCourse;
      this.sheetDate = this.dialogData.selectedDate;
      this.coursesList = this.dialogData.courses;
      this.createTeeSheetConfigForm();
      this.handleCustomInput();
      this.loadData();

      const saveDisplayFormData: SaveDisplayFormData = {
        CourseID: this.selectedCourse.CourseID,
        StartDate: this.sheetDate,
        EndDate: '',
        TimeRangeStartIndex: 0,
        TimeRangeEndIndex: null
      }

      this.teeSheetDisplayService.setSaveDisplayFormData(saveDisplayFormData);
    }

  }

  loadData() {
    this.manageLoadingService.startLoading();
    this.subscription.add(
      this.teeSheetDisplayService.load$.subscribe((value: GetDisplayDataResponse) => {

        if (value){
          this.teeTimesArray = value.TeeTimes || [];
         if (this.teeTimesArray.length) {
           this.convertDateTimeToTemplateTime();
          }
        }
         this.manageLoadingService.finishLoading();
      })
    );
  }

  handleCustomInput() {
   this.subscription.add( this.form.get('HoleNumberType')?.valueChanges.subscribe(value => {
    const customControl = this.form.get('CustomHoleNumbers');

    if (value === this.HoleNumberTypeEnum.Custom) {
      customControl?.enable();
    } else {
      customControl?.disable();
      customControl?.reset(); // optional: clear input when not custom
    }
  }))

    // Initialize state based on current value
    const currentValue = this.form.get('HoleNumberType')?.value;
    if (currentValue !== this.HoleNumberTypeEnum.Custom) {
      this.form.get('CustomHoleNumbers')?.disable();
    }
  }


  fillForm(): void
  {

    this.form.patchValue({
      Course: this.selectedCourse.CourseID,
      DateSelection: this.DateSelectionEnum.Date,
      TimeRangeStart: '',
      TimeRangeEnd: '',
      TimeRangeStartIndex: 0,
      TimeRangeEndIndex: null,
      DisplayTime: '',
      HoleNumberType: this.HoleNumberTypeEnum.OneTwoThree,
      CustomHoleNumbers: ''
    });

    // Initialize dates using centralized logic

    this.initializeDates(this.sheetDate);

  }

  convertDateTimeToTemplateTime()
  {

      this.teeSheetTemplates = this.teeTimesArray
        .map((t, originalIndex) => ({
          Index: originalIndex, // Use the original index from teeTimes array
          TeeTime: t.TeeTime ? moment(t.TeeTime).format('LT') : ''
        }))
        .filter(t => !!t.TeeTime); // Only include valid times
  }


  onSelectTimeRange(): void
  {
    // Reset the end time index when start time changes
    this.form.get('TimeRangeEndIndex').setValue(null);

    // Also reset the string-based time range fields if they exist
    this.form.get('TimeRangeEnd').setValue(null);
  }

  createTeeSheetConfigForm(): void
  {
    this.form = new FormGroup<TeeSheetDisplayForm>({
      Course: new FormControl<string>(''),
      DateSelection: new FormControl<DateSelectionEnum>(this.DateSelectionEnum.Date),
      SelectedDate: new FormControl<string>(this.sheetDate),
      StartDate: new FormControl<string>(this.sheetDate),
      EndDate: new FormControl<string>(null),
      TimeRangeStart: new FormControl<string>(''),
      TimeRangeEnd: new FormControl<string>(''),
      TimeRangeStartIndex: new FormControl<number>(0),
      TimeRangeEndIndex: new FormControl<number>(null),
      DisplayTime: new FormControl<string>(''),
      HoleNumberType: new FormControl<HoleNumberTypeEnum>(this.HoleNumberTypeEnum.OneTwoThree),
      CustomHoleNumbers: new FormControl<string>('')
    });

    this.fillForm();



  }

  onClear(): void
  {
    // Reset form to default values
    this.resetFormToDefaults();

    // Reset the service
    this.teeSheetDisplayService.reset();
    const initialDisplayDialogData = this.teeSheetDisplayService.getDisplayDialogData();
    const request:GetDisplayDataRequest = {
      CourseID: initialDisplayDialogData.selectedCourse.CourseID,
      SheetDate: initialDisplayDialogData.selectedDate
    }
    this.teeSheetDisplayService.load(request);

  }

  private resetFormToDefaults(): void
  {
    // Reset form to initial state and mark as pristine/untouched
    this.form.reset();
    this.form.markAsPristine();
    this.form.markAsUntouched();

    // Re-apply default values
    this.fillForm();

    this.initializeDates(this.sheetDate);


    // Re-apply custom input handling
    this.handleCustomInput();
  }

  async courseChange() {
    const request: GetDisplayDataRequest = {
      CourseID: this.form.get('Course').value,
      SheetDate: moment(this.form.get('SelectedDate').value).format('YYYY-MM-DD'),
    };
    this.manageLoadingService.startLoading();
    await this.teeSheetDisplayService.load(request);

    this.manageLoadingService.finishLoading();
    this.onSelectTimeRange();
  }

  async onDateChange(event) {
    this.manageLoadingService.startLoading();
    // Use centralized date initialization logic
    this.initializeDates(event.value);
    const request: GetDisplayDataRequest = {
      CourseID: this.form.get('Course').value,
      SheetDate:  moment(this.form.get('SelectedDate').value).format('YYYY-MM-DD'),
    };
    await this.teeSheetDisplayService.load(request);
    this.manageLoadingService.finishLoading();
  }


  onGenerate(): void
  {
    this.teeSheetDisplayService.apply(this.form.getRawValue());
    const request : GetGeneratingHoleNumbersRequest = {
      HoleNumberTemplate : this.form.get('HoleNumberType').value === HoleNumberTypeEnum.Custom ? this.form.get('CustomHoleNumbers').value : this.form.get('HoleNumberType').value,
      TeeTimesCount : (this.form.get('TimeRangeEndIndex').value - this.form.get('TimeRangeStartIndex').value) + 1,
    }
    this.generateHoleNumber(request);



  }



  async generateHoleNumber(request: GetGeneratingHoleNumbersRequest): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const response: GetGeneratingHoleNumbersResponse = await firstValueFrom(this.teeSheetService.GetGeneratingHoleNumbers(request));
      this.manipulateGeneratedData(response);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

  manipulateGeneratedData(response: GetGeneratingHoleNumbersResponse) {

   const displayData = this.teeSheetDisplayService.getLoadData()
    // Create a deep copy of the original display data]

    const GeneratedDisplayData = {
      ...displayData,
      TeeTimes: displayData.TeeTimes.map((item) => ({ ...item }))
    };

    const start = this.form.get('TimeRangeStartIndex').value ?? 0;
    const end = this.form.get('TimeRangeEndIndex').value ?? 0;

    let holeIndex = 0;
    for (let i = start; i <= end; i++) {
      if (i < GeneratedDisplayData.TeeTimes.length) {
        GeneratedDisplayData.TeeTimes[i].HoleNumber = response.HoleNumbers[holeIndex] ?? '';
        GeneratedDisplayData.TeeTimes[i].DisplayTime = this.form.get('DisplayTime').value ?? '';
        holeIndex++;
      }
    }

    this.teeSheetDisplayService.setLoadData(GeneratedDisplayData);

    const saveDisplayFormData: SaveDisplayFormData = {
      CourseID: this.form.get('Course').value,
      StartDate: this.form.get('StartDate').value,
      EndDate:this.form.get('DateSelection').value === DateSelectionEnum.DateRange ? this.form.get('EndDate').value : '',
      TimeRangeStartIndex: this.form.get('TimeRangeStartIndex').value,
      TimeRangeEndIndex: this.form.get('TimeRangeEndIndex').value
    }

    this.teeSheetDisplayService.setSaveDisplayFormData(saveDisplayFormData);
  }

  public initializeDates(selectedDate: string): void {
    const dateToUse = selectedDate;
    const startDate = dateToUse;
    const endDate = moment(dateToUse).add(1, 'day').format('YYYY-MM-DD');

    this.form.get('SelectedDate').setValue(startDate);
    this.form.get('StartDate').setValue(startDate);
    this.form.get('EndDate').setValue(endDate);
  }

  hasFormChanges(): boolean {
    return this.form.dirty || this.form.touched;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
