import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTeeTimeAttachmentRequest, GetTeeTimeAttachmentResponse } from '../@intermediate/models/get-tee-time-attachment.model';
import { PostTeeTimeAttachmentRequest, PostTeeTimeAttachmentResponse } from '../@intermediate/models/post-tee-time-attachment.model';
import { ITeeTimeAttachmentService } from '../@intermediate/tee-time-attachment.interface';

@Injectable()
export class TeeTimeAttachmentService implements ITeeTimeAttachmentService
{
  private readonly serviceUrl: string;


  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/';
  }

  GetTeeTimeAttachment(request: GetTeeTimeAttachmentRequest): Observable<GetTeeTimeAttachmentResponse>
  {
    const apiUrl = this.serviceUrl + 'TeeTimeAttachment';
    return this.http.get<GetTeeTimeAttachmentResponse>(apiUrl, { params: request as any });
  }

  PostTeeTimeAttachment(request: PostTeeTimeAttachmentRequest): Observable<PostTeeTimeAttachmentResponse>
  {
    const apiUrl = this.serviceUrl + 'TeeTimeAttachment';
    return this.http.post<PostTeeTimeAttachmentResponse>(apiUrl, request);
  }
}
