  @Input() blocking: Blocking;
  @Input() hasBlocking: boolean;
  @Input() isFivePlayer: boolean;
  @Input() PlayerIndex: number;
  @Input() holes1or10: Holes1or10;
  @Input() time: string;
  @Input() blockingFirstContactDisplayName: string;
  @Input() priceLevels: PriceLevel[];
  @Input() isReadOnly: boolean;
  @Input() teeTimeContacts: Contact[];
  @Input() teeTimeCurrentContact: Contact;
  @Input() startTeeHole: StartTeeHole;
  @Input() selectType: SelectType;
