import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { PostModifiedTeeSheetRestrictionsRequest } from '../@intermediate/models/post-modified-tee-sheet-restrictions.model';
import { PostTeeSheetClassRestrictionsResponse, TeeSheetRestrictionsApply } from '../@intermediate/models/post-tee-sheet-class-restrictions.mode';

@Injectable({
  providedIn: 'root'
})
export class TeeSheetRestrictionsService
{

  constructor() { }
  private Load = new BehaviorSubject<PostTeeSheetClassRestrictionsResponse>(new PostTeeSheetClassRestrictionsResponse());
  load$ = this.Load.asObservable();

  private Apply = new Subject<TeeSheetRestrictionsApply>();
  apply$ = this.Apply.asObservable();

  private Reset = new Subject<boolean>();
  reset$ = this.Reset.asObservable();

  private Save = new Subject<PostModifiedTeeSheetRestrictionsRequest>();
  save$ = this.Save.asObservable();

  public SheetTimeFormChanged = false;

  load(value: PostTeeSheetClassRestrictionsResponse): void
  {
    this.Load.next(value);
  }

  apply(value: TeeSheetRestrictionsApply): void
  {
    this.Apply.next(value);
    this.setSheetTimeFormChange(true);
  }

  setSheetTimeFormChange(value: boolean): void
  {
    this.SheetTimeFormChanged = value;
  }

  reset(): void
  {
    this.Reset.next(true);
    this.setSheetTimeFormChange(false);
  }

  saveRestrictions(value): void
  {
    this.Save.next(value);
  }

}
