import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { SmallGroupBookingHelperService } from '../../../@services/small-group-booking-helper.service';
import { SmallGroupBookingService } from '../../../@services/small-group-booking.service';
import { ManageSmallGroupBookingComponent } from './manage-small-group-booking.component';


const COMPONENTS = [ManageSmallGroupBookingComponent];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatDividerModule, MatButtonModule, MatIconModule, MatFormFieldModule, MatRadioModule, MatCheckboxModule];
const TEI_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [ContactQuickSearchModule];



@NgModule({
  declarations: [...COMPONENTS],
  imports: [...BASE_MODULES, ...MATERIAL_MODULES, ...TEI_MODULES, ...TEI_MATERIAL_MODULES],
  providers: [
    { provide: 'ISmallGroupBookingService', useClass: SmallGroupBookingService },
    SmallGroupBookingHelperService
  ]
})
export class ManageSmallGroupBookingModule { }
