import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DisplayGridComponent } from './display-grid.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { TeeSheetService } from '../../../@services/tee-sheet.service';
import { ManageLoadingService } from '@tei/core';

const MATERIAL_MODULES = [MatDividerModule, MatInputModule, MatFormFieldModule, MatTableModule, MatTooltipModule, ScrollingModule, MatButtonModule];
const COMPONENTS = [DisplayGridComponent]
const BASE_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const TEI_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [LoadingModule];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENTS
  ],
  providers: [
    {provide: TeeSheetService},
    {provide: ManageLoadingService}
  ]
})
export class DisplayGridModule { }
