import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { PostTeeSheetClassRestrictionsResponse } from '../../@intermediate/models/post-tee-sheet-class-restrictions.mode';
import { TeeSheetRestrictionsService } from '../../@services/tee-sheet-restrictions.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-restrictions',
  templateUrl: './tee-sheet-restrictions.component.html',
  styleUrls: ['./tee-sheet-restrictions.component.scss']
})
export class TeeSheetRestrictionsComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  isMobile = false;
  data: PostTeeSheetClassRestrictionsResponse;
  constructor(
    private dialogRef: MatDialogRef<TeeSheetRestrictionsComponent>,
    public responsiveService: ResponsiveService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private teeSheetRestrictionsService: TeeSheetRestrictionsService
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.loadData();
  }

  loadData(): void
  {
    this.subscription.add(this.teeSheetRestrictionsService.load$.subscribe((value: PostTeeSheetClassRestrictionsResponse) =>
    {
      this.manipulateData(value);
    }));
  }

  manipulateData(data: PostTeeSheetClassRestrictionsResponse): void
  {
    data.SheetTimes.map((item, index) =>
    {
      item['Index'] = index;
      item['SheetTimeTitle'] = moment(item.SheetTime).format('LT');
    });

    data.Groups.map(item =>
    {
      item.Show = false;
    });
    this.data = data;
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}