export class PostRestrictionsRequest
{
  BookingID: number;
  POSID?: string;
  UserID?: string;
  CourseID?: string;
  TeeTime: string;
  CustomerID: string;
  Is9Holes: boolean;
  PlayerList: Array<string>;
}

export class PostRestrictionsResponse
{
  AllowBooking: boolean;
  BookingRestrictions: BookingRestriction[];
}

export class BookingRestriction
{
  ErrorCode: BookingRestrictionErrorCodeEnum;
  ErrorMsg: string;
  FirstName: string;
  LastName: string;
  PlayerID: string;
  PlayerNumber: number;
  RestrictionMode: BookingRestrictionModeEnum;
}

export enum BookingRestrictionErrorCodeEnum
{
  ContactClassRestricted,
  BookingDaysInAdvanceRestricted,
  SameDayBookingRestricted,
  MinimumTimeBetweenBookingsRestricted,
  MemberStatusRestricted,
  TeeSheetCourseRestricted,
  CheckPlayerBookingConflict
}

export enum BookingRestrictionModeEnum
{
  Warn,
  Deny,
  ManagerAuthRequired,
  NoRestriction
}
