import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ChipItemModel, ChipOptionModel, ChipType, QuickSearchFilterChipItem } from '@tei/material';
import { Observable, ReplaySubject, Subscription, concat, of } from 'rxjs';
import { catchError, debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Course } from '../../../../@intermediate/models/get-tee-sheet-main.model';
import { DateCategoryTypeEnum, ISearchOptionButton, PostTeeTimesRequest, SearchFieldEnum, SearchOptionButtonType, TeeSheetAdvancedSearchType, UpcomingOnlyQuickFilterChipModel } from '../../../../@intermediate/models/post-tee-time-advance-search.model';

@Component({
  selector: 'tei-pos-tee-sheet-tee-time-booking-advanced-search-header',
  templateUrl: './tee-time-booking-advanced-search-header.component.html',
  styleUrls: ['./tee-time-booking-advanced-search-header.component.scss']
})
export class TeeTimeBookingAdvancedSearchHeaderComponent implements OnInit, OnDestroy
{
  ChipType = ChipType;
  @Input() courses = new Array<Course>(0);
  @Output() loadItems = new EventEmitter();
  @Output() enterFilterValueEvent = new EventEmitter<PostTeeTimesRequest>();

  dateCategoryItems: Array<UpcomingOnlyQuickFilterChipModel> =
    [
      { UpcomingOnly: 'Upcoming Only' },
      { UpcomingOnly: 'Any' },
    ];
  SearchOptionButtonType = SearchOptionButtonType;
  SearchField = SearchFieldEnum;
  minTermLength = 0;
  courseItemsWithDisplayName: ChipItemModel[] = [
    { chipName: 'CourseID', displayName: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.COURSE') } as ChipItemModel,
  ];

  upcomingOnlyItemsWithDisplayName: ChipItemModel[] = [
    { chipName: 'UpcomingOnly', displayName: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.UPCOMING_ONLY') } as ChipItemModel,
  ];

  courseSubjectArray: Observable<Course[]> = of([]);
  upComingSubjectArray: Observable<UpcomingOnlyQuickFilterChipModel[]> = of([]);
  courseTypeAheadInput$ = new ReplaySubject<string>();
  upComingTypeAheadInput$ = new Array<ReplaySubject<string>>();
  subscription = new Subscription();
  request: PostTeeTimesRequest = new PostTeeTimesRequest();

  form = new UntypedFormGroup({
    filter: new UntypedFormControl()
  });

  searchOptions: Array<ISearchOptionButton> = [
    {
      name: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.CONFIRMATION'),
      value: SearchFieldEnum.Confirmation, type: SearchOptionButtonType.GenericFilterChip
    },
    {
      name: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.BOOKER'),
      value: SearchFieldEnum.Booker, type: SearchOptionButtonType.GenericFilterChip
    },
    {
      name: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.PLAYER_NAME'),
      value: SearchFieldEnum.PlayerName, type: SearchOptionButtonType.GenericFilterChip
    },
    {
      name: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.COURSE'),
      value: SearchFieldEnum.Course, type: SearchOptionButtonType.QuickSearchFilterChip
    },
    {
      name: this.translate.instant('POS_TEE_SHEET.TEE_SHEET_ADVANCED_SEARCH.UPCOMING_ONLY'),
      value: SearchFieldEnum.UpcomingOnly, type: SearchOptionButtonType.QuickSearchFilterChip
    },
  ];

  constructor(private translate: TranslateService)
  { }

  ngOnInit(): void
  {
    this.loadTypeAheadValues();
  }

  loadTypeAheadValues(): void
  {
    this.courseTypeAheadInput$ = new ReplaySubject<string>();
    this.filterCourseByChipItem();
    this.upComingTypeAheadInput$.push(new ReplaySubject<string>());
  }

  enterFilterValue($event: Event): void
  {
    $event?.stopPropagation();
    $event?.preventDefault();
    setTimeout(() => { this.load(); }, 300);
  }

  public load(): void
  {
    const request = this.request;

    this.enterFilterValueEvent.emit(request);
  }

  searchOptionChange(event: ChipOptionModel, option: ISearchOptionButton): void
  {
    if ((this.request.SearchText?.length ?? 0) === 0)
    {
      this.request.SearchText = new Array<TeeSheetAdvancedSearchType>();
    } else
    {
      this.request.SearchText = this.request.SearchText.filter(
        (a) => a.SearchType !== option.value
      );
    }
    if (event.firstValue)
    {
      this.request.SearchText.push({ SearchText: event.firstValue, SearchType: option.value } as TeeSheetAdvancedSearchType);
    }
    this.load();
  }

  onFilterChange(filterObjects: Array<ChipItemModel>): void
  {
    filterObjects.forEach((chipItem: ChipItemModel) =>
    {
      if (chipItem.chipName === 'UpcomingOnly')
      {
        this.request.DateCategory = this.calculateUpcomingOnlyValue(chipItem.value as string);
      }
      else if (chipItem.chipName === 'CourseID')
      {
        this.request.Courses = (chipItem.selectedItem as Array<string>);
      }
      else if (chipItem.value)
      {
        this.request[chipItem.chipName] = chipItem.value;
      }
      else
      {
        delete this.request[chipItem.chipName];
      }
    });
    this.load();
  }

  calculateUpcomingOnlyValue(value: string | null): DateCategoryTypeEnum | null
  {
    switch (value)
    {
      case 'Upcoming Only': return DateCategoryTypeEnum.UpcomingOnly;
      case 'Any': return DateCategoryTypeEnum.Any;
    }
    return null;
  }

  filterByChipItem(item: QuickSearchFilterChipItem): void
  {
    this.upComingSubjectArray[item.index] = concat(
      of(this.dateCategoryItems), // default items
      this.upComingTypeAheadInput$[item.index].pipe(
        debounceTime(300), distinctUntilChanged(),
        switchMap((searchTerm) =>
          this.filterUpcomingOnly(searchTerm as string).pipe(
            catchError(() => of(this.dateCategoryItems)),
          ))
      )
    );
  }

  filterCourseByChipItem(): void
  {
    this.courseSubjectArray[0] = concat(
      of(this.courses), // default items
      this.courseTypeAheadInput$.pipe(
        debounceTime(300),
        switchMap((searchTerm = "") =>
          this.filterCourse(searchTerm as string).pipe(
            catchError(() => of(this.courses)),
          ))
      )
    );
  }

  filterUpcomingOnly(searchTerm: string): Observable<Array<UpcomingOnlyQuickFilterChipModel>>
  {
    return of(this.dateCategoryItems.filter(p => p.UpcomingOnly.toLocaleLowerCase().indexOf(searchTerm) > -1));
  }

  filterCourse(searchTerm: string): Observable<Array<Course>>
  {
    return of(this.courses.filter(p => p.CourseName.toLocaleLowerCase().indexOf(searchTerm) > -1));
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
