import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Observable, finalize, firstValueFrom, tap } from 'rxjs';
import { FlagType } from '../../@intermediate/models/post-flag-tee-time.model';
import { TeeSheetFlagType } from '../../@intermediate/models/tee-sheet-un-flag.model';
import { ITeeSheetFlagService } from '../../@intermediate/tee-sheet-flag-service.interface';
@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-un-flag',
  templateUrl: './tee-sheet-un-flag.component.html',
  styleUrls: ['./tee-sheet-un-flag.component.scss']
})
export class TeeSheetUnFlagComponent implements OnInit
{
  isMobile: boolean;
  flagType = FlagType;
  selectedFlag: FlagType = this.data.FlagType;
  teeSheetFlag: TeeSheetFlagType;
  constructor(
    private dialogRef: MatDialogRef<TeeSheetUnFlagComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private responsiveService: ResponsiveService,
    public changeDetector: ChangeDetectorRef,
    private snackBarService: SnackBarService, private translate: TranslateService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject('ITeeSheetFlagService') private teeFlagService: ITeeSheetFlagService,
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.getFlaggedTeeTimes();
  }
  async getFlaggedTeeTimes(): Promise<void>
  {
    if (this.selectedFlag === this.flagType.Reserved)
    {
      this.GetReservedTeeTimes();
    }
    if (this.selectedFlag === this.flagType.NineHole)
    {
      this.GetNineHoleTeeTimes();
    }
    if (this.selectedFlag === this.flagType.CrossOver)
    {
      this.GetFlagCrossoverTeeTimes();
    }
  }

  async GetReservedTeeTimes(): Promise<void>
  {
    this.manageStandAloneLoadingService.startLoading();
    await firstValueFrom(this.teeFlagService.ReservedTeeTimes({
      CourseID: this.data.CourseID,
      SheetDate: this.data.SheetDate,
    }).pipe(finalize(() =>
    {
      this.manageStandAloneLoadingService.finishLoading();
    }),
      tap(response =>
      {
        this.teeSheetFlag = {
          FlagType: FlagType.Reserved,
          Indeterminate: response.ReserveTeeTimes?.length > 1,
          teeSheetFlags: response.ReserveTeeTimes.map(teeTime => ({
            SheetTime: teeTime.SheetTime,
            Selected: teeTime.SheetTime == this.data?.slotTime
          })),
          Selected: response.ReserveTeeTimes?.length == 1
        };
      }
      )));
  }

  async GetFlagCrossoverTeeTimes(): Promise<void>
  {
    this.manageStandAloneLoadingService.startLoading();
    await firstValueFrom(this.teeFlagService.GetCrossoverTeeTimes({
      CourseID: this.data.CourseID,
      SheetDate: this.data.SheetDate,
    }).pipe(finalize(() =>
    {
      this.manageStandAloneLoadingService.finishLoading();
    }),
      tap(response =>
      {
        this.teeSheetFlag = {
          FlagType: FlagType.CrossOver,
          Indeterminate: response.TeeSheetCrossovers?.length > 1,
          teeSheetFlags: response.TeeSheetCrossovers.map(teeTime => ({
            SheetTime: teeTime.SheetTime,
            Selected: teeTime.SheetTime == this.data?.slotTime
          })),
          Selected: response.TeeSheetCrossovers?.length == 1,

        };
      }
      )));
  }

  async GetNineHoleTeeTimes(): Promise<void>
  {
    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response = await firstValueFrom(
        this.teeFlagService.GetTeeTimeNineHoles({
          CourseID: this.data.CourseID,
          SheetDate: this.data.SheetDate,
        }).pipe(finalize(() =>
        {
          this.manageStandAloneLoadingService.finishLoading();
        }))
      );

      if (response)
      {
        this.teeSheetFlag = {
          FlagType: FlagType.NineHole,
          Indeterminate: response.TeeTimeNineHoles?.length > 1,
          teeSheetFlags: response.TeeTimeNineHoles.map(teeTime => ({
            SheetTime: teeTime.SheetTime,
            Selected: teeTime.SheetTime == this.data?.slotTime
          })),
          Selected: response.TeeTimeNineHoles?.length == 1,
        };
      }
    } catch (error)
    {
    }
  }

  async UnFlag(): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      await firstValueFrom(this.unflagByFlagType(this.selectedFlag).pipe(finalize(() =>
      {
        this.manageStandAloneLoadingService.finishLoading();
      }),
        tap(() =>
        {
          this.dialogRef.close(true);
          this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.PLAYER.UNFLAGGED_SUCCESS', {
            FlagType: this.selectedFlag
          }), '', null, 'left');
        }),
      ));
    }
    catch (error)
    {
      this.snackBarService.showError(error, '', null, 'left');
    }
  }

  unflagByFlagType(selectedFlag: string): Observable<any>
  {
    const request = {
      CourseID: this.data?.CourseID,
      SheetTimes: this.teeSheetFlag.teeSheetFlags.filter(x => x.Selected).map(teeTime => teeTime.SheetTime),
      TeeSheetDate: this.data?.SheetDate,
    };

    if (selectedFlag === this.flagType.Reserved)
    {
      return this.teeFlagService.UnreserveTeeTimes(request);
    }
    if (selectedFlag === this.flagType.NineHole)
    {
      return this.teeFlagService.PostUnflagNineHole(request);
    }
    if (selectedFlag === this.flagType.CrossOver)
    {
      return this.teeFlagService.PostCrossoverTeeTimes(request);
    }

  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  };

  toggleAll(checked: boolean)
  {
    this.teeSheetFlag.teeSheetFlags.forEach((teeTimes) =>
    {
      teeTimes.Selected = checked;
    });
    this.updateFlagStates();
  }

  toggleSingle(selected: boolean, index?: number): void
  {
    this.teeSheetFlag.teeSheetFlags[index].Selected = selected;
    this.updateFlagStates();

  }
  private updateFlagStates(): void
  {
    const allSelected = this.teeSheetFlag.teeSheetFlags.every(teeTime => teeTime.Selected);
    const someSelected = this.teeSheetFlag.teeSheetFlags.some(teeTime => teeTime.Selected);

    this.teeSheetFlag.Selected = allSelected;
    this.teeSheetFlag.Indeterminate = !allSelected && someSelected;
  }
  onClose(): void
  {
    this.dialogRef.close();
  }
}

