import { Injectable } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { CardDialogComponent, ConfirmationDialogComponent } from '@tei/material';
import { Observable } from 'rxjs';
import { ConfirmRestrictionsDialog } from '../@intermediate/models/confirm-restrictions-dialog.model';
import { GetBookingDetailsResponse, Player } from '../@intermediate/models/get-booking-details.model';
import { PriceLevel } from '../@intermediate/models/get-tee-sheet.model';
import { PlayerPriceLevelForForm } from '../@intermediate/models/manage-tee-time.model';
import { PlayerForForm } from '../@intermediate/models/post-booking-details.model';
import { PostPlayerPriceResponse } from '../@intermediate/models/post-player-price.model';
import { BookingRestriction } from '../@intermediate/models/post-restrictions.model';
import { PriceForPriceBreakdown } from '../@intermediate/models/price-breakdown.model';
import { BookingRestrictionPerPlayer } from '../@intermediate/models/restrictions-helper.model';
import { BookingRestrictionsDialogComponent } from '../components/manage-tee-time/booking-restrictions-dialog/booking-restrictions-dialog.component';
import { ConfirmRestrictionsDialogComponent } from '../components/manage-tee-time/confirm-restrictions-dialog/confirm-restrictions-dialog.component';


@Injectable()
export class ManageTeeTimeHelperService
{

  constructor(
    private dialog: MatDialog,
    private translate: TranslateService,
  )
  {
  }

  transformBookingDetailsGroupForPriceBreakdown(bookingDetails: GetBookingDetailsResponse): PriceForPriceBreakdown
  {
    return {
      GolfPrice: bookingDetails.GolfPriceTotal,
      GolfDiscount: bookingDetails.GolfDiscountTotal,
      GolfTax: bookingDetails.GolfTaxTotal,
      CartPrice: bookingDetails.CartPriceTotal,
      CartDiscount: bookingDetails.CartDiscountTotal,
      CartTax: bookingDetails.CartTaxTotal,
      AttachmentPrice: bookingDetails.AttachmentPriceTotal,
      AttachmentTax: bookingDetails.AttachmentTaxTotal,
      Total: bookingDetails.GroupTotal,
      TotalDeposit: bookingDetails.TotalDeposit ?? 0,
      TotalPrepaid: bookingDetails.TotalPrepaid ?? 0,
      RemainTotal: bookingDetails.RemainTotal ?? 0,
    };
  }

  transformBookingDetailsPlayerForPriceBreakdown(model: Player): PriceForPriceBreakdown
  {
    return {
      GolfPrice: model.GolfPrice,
      GolfDiscount: model.GolfDiscount,
      GolfTax: model.GolfTax,
      CartPrice: model.CartPrice,
      CartDiscount: model.CartDiscount,
      CartTax: model.CartTax,
      AttachmentPrice: model.AttachmentPrice,
      AttachmentTax: model.AttachmentTax,
      Total: model.PlayerTotal,
      TotalDeposit: model.DepositAmount,
      TotalPrepaid: model.PrepaidAmount,
      RemainTotal: model.RemainTotal
    };
  }

  transformPlayerPriceForPriceBreakdown(model: PostPlayerPriceResponse): PriceForPriceBreakdown
  {
    return {
      GolfPrice: model.GolfPriceTotal,
      GolfDiscount: model.GolfDiscountTotal,
      GolfTax: model.GolfTaxTotal,
      CartPrice: model.CartPriceTotal,
      CartDiscount: model.CartDiscountTotal,
      CartTax: model.CartTaxTotal,
      AttachmentPrice: model.AttachmentPriceTotal,
      AttachmentTax: model.AttachmentTaxTotal,
      Total: model.PlayerTotal,
      TotalDeposit: model.DepositAmount,
      TotalPrepaid: model.PrepaidAmount,
      RemainTotal: model.RemainTotal
    };
  }

  convertPriceToPriceBreakdownDialogDataModel(priceForBreakDown: PriceForPriceBreakdown, title: string)
  {
    return {
      title: this.translate.instant(title),
      priceSections: 
      [
        {
          description: this.translate.instant('POS_TEE_SHEET.GENERAL.GOLF'),
          priceItems: [
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.SUBTOTAL'),
              price: priceForBreakDown.GolfPrice
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.DISCOUNT'),
              price: priceForBreakDown.GolfDiscount,
              isDiscount: true
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.TAX'),
              price: priceForBreakDown.GolfTax
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.GENERAL.TOTAL'),
              price: (
                priceForBreakDown.GolfPrice
                - priceForBreakDown.GolfDiscount
                + priceForBreakDown.GolfTax)
            },
          ]
        },
        {
          description: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.CART'),
          priceItems: [
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.SUBTOTAL'),
              price: priceForBreakDown.CartPrice
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.DISCOUNT'),
              price: priceForBreakDown.CartDiscount,
              isDiscount: true
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.TAX'),
              price: priceForBreakDown.CartTax
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.GENERAL.TOTAL'),
              price: (
                priceForBreakDown.CartPrice
                - priceForBreakDown.CartDiscount
                + priceForBreakDown.CartTax)
            },
          ]
        },
        {
          description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.ATTACHMENTS'),
          priceItems: [
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.SUBTOTAL'),
              price: priceForBreakDown.AttachmentPrice
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.TAX'),
              price: priceForBreakDown.AttachmentTax
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.GENERAL.TOTAL'),
              price: (
                priceForBreakDown.AttachmentPrice
                + priceForBreakDown.AttachmentTax)
            },
          ]
        },
      ],
      priceFooter:
      [
        {
          description: '',
          priceItems:
          [
            {
              description: this.translate.instant('POS_TEE_SHEET.GENERAL.TOTAL'),
              price: priceForBreakDown.Total,
              isPrimaryClass : true
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.PREPAID'),
              price: priceForBreakDown.TotalPrepaid ?? 0,
              isPrimaryClass : false
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.DEPOSIT'),
              price: priceForBreakDown.TotalDeposit ?? 0,
              isPrimaryClass : false
            },
            {
              description: this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.Remaining'),
              price: priceForBreakDown.RemainTotal ?? 0,
              isPrimaryClass : true
            },
          ]
        }
      ]
    };
  }

  getNewGroupPriceFromEachPlayer(players: PlayerForForm[]): PriceForPriceBreakdown
  {
    const initialPrice = 0;

    return players.reduce((accumulation: PriceForPriceBreakdown, current: PlayerForForm) =>
    {
      if (!current.IsExceed)
      {
        accumulation.AttachmentPrice = (accumulation.AttachmentPrice ?? initialPrice) + (current.PlayerPrice.AttachmentPrice ?? initialPrice);
        accumulation.AttachmentTax = (accumulation.AttachmentTax ?? initialPrice) + (current.PlayerPrice.AttachmentTax ?? initialPrice);
        accumulation.CartTax = (accumulation.CartTax ?? initialPrice) + (current.PlayerPrice.CartTax ?? initialPrice);
        accumulation.CartPrice = (accumulation.CartPrice ?? initialPrice) + (current.PlayerPrice.CartPrice ?? initialPrice);
        accumulation.CartDiscount = (accumulation.CartDiscount ?? initialPrice) + (current.PlayerPrice.CartDiscount ?? initialPrice);
        accumulation.GolfDiscount = (accumulation.GolfDiscount ?? initialPrice) + (current.PlayerPrice.GolfDiscount ?? initialPrice);
        accumulation.GolfPrice = (accumulation.GolfPrice ?? initialPrice) + (current.PlayerPrice.GolfPrice ?? initialPrice);
        accumulation.GolfTax = (accumulation.GolfTax ?? initialPrice) + (current.PlayerPrice.GolfTax ?? initialPrice);
        accumulation.Total = (accumulation.Total ?? initialPrice) + (current.PlayerPrice.Total ?? initialPrice);
        accumulation.TotalDeposit = (accumulation.TotalDeposit ?? initialPrice) + (current.PlayerPrice.TotalDeposit ?? initialPrice);
        accumulation.TotalPrepaid = (accumulation.TotalPrepaid ?? initialPrice) + (current.PlayerPrice.TotalPrepaid ?? initialPrice);
        accumulation.RemainTotal = (accumulation.RemainTotal ?? initialPrice) + (current.PlayerPrice.RemainTotal ?? initialPrice);
      }

      return accumulation;
    }, new PriceForPriceBreakdown());
  }

  mapToPlayerPriceLevel(priceLevels: PriceLevel[]): PlayerPriceLevelForForm[]
  {
    return priceLevels.map(pl =>
    {
      return {
        IsDefault: false,
        PriceLevelID: pl.PriceLevelID,
        Description: pl.Description,
        IsMultiplePrice: false,
        PlayerNumber:pl.PlayerNumber,
        ExpiryDate: pl.ExpiryDate,
      };
    });
  }

  getPriceLevelIDFromBookingDetailsPlayer(bookingDetailsPlayer: Player, generalPriceLevels: PriceLevel[]): string
  {
    if (!bookingDetailsPlayer.PriceLevelID || !generalPriceLevels.some(priceLevel => priceLevel.PriceLevelID === bookingDetailsPlayer.PriceLevelID))
    {
      return '';
    }

    return bookingDetailsPlayer.PriceLevelID;
  }

  openReactivatePlayerDialog(): Observable<boolean>
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: 'POS_TEE_SHEET.MANAGE_TEE_TIME.REACTIVATE_PLAYER',
          content: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.REACTIVATE_PLAYER_QUESTION'),
          submitButtonLabel: 'POS_TEE_SHEET.MANAGE_TEE_TIME.REACTIVATE_PLAYER',
          closeButtonLabel: 'ADMIN_CENTER.CANCEL'
        }
      },
      width: '350px',
    });

    return dialogRef?.afterClosed();
  }

  openRestrictionsDialog(dialogData: ConfirmRestrictionsDialog): Observable<boolean>
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmRestrictionsDialogComponent,
        dialogData: dialogData
      },
      width: '350px'
    });

    return dialogRef?.afterClosed();
  }

  openBookingRestrictionsDialog(dialogData: BookingRestriction[]): Observable<BookingRestrictionPerPlayer[] | null>
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: BookingRestrictionsDialogComponent,
        dialogData: dialogData
      },
      width: '350px'
    });

    return dialogRef?.afterClosed();
  }

  DownloadFile(url: string, orderName: string): void
  {
    const link = document.createElement('a');
    link.href = url;
    link.download = orderName + '.pdf';
    link.target = '_blank';
    link.click();
    window.URL.revokeObjectURL(url);
    link.remove();
  }

  findChangedRootKeys(obj1: any, obj2: any): string[]
  {
    const changedRootKeys = new Set<string>();
    if (typeof obj1 !== 'object' || obj1 === null || typeof obj2 !== 'object' || obj2 === null)
    {
      if (obj1 !== obj2)
      {
        return ['root'];
      }
      return [];
    }
    const allRootKeys = new Set([...Object.keys(obj1), ...Object.keys(obj2)]);
    allRootKeys.forEach(key =>
    {
      const value1 = obj1[key];
      const value2 = obj2[key];
      if (!(key in obj1) || !(key in obj2))
      {
        changedRootKeys.add(key);
        return;
      }
      const compare = (v1: any, v2: any, rootKey: string) =>
      {
        if (v1 === null || v2 === null || v1 === undefined || v2 === undefined)
        {
          if (v1 !== v2)
          {
            changedRootKeys.add(rootKey);
          }
          return;
        }
        if (typeof v1 !== typeof v2)
        {
          changedRootKeys.add(rootKey);
          return;
        }
        if (Array.isArray(v1) || Array.isArray(v2))
        {
          if (!Array.isArray(v1) || !Array.isArray(v2))
          {
            changedRootKeys.add(rootKey);
            return;
          }
          if (v1.length !== v2.length)
          {
            changedRootKeys.add(rootKey);
            return;
          }
          for (let i = 0; i < v1.length; i++)
          {
            compare(v1[i], v2[i], rootKey);
          }
          return;
        }

        if (typeof v1 === 'object' && typeof v2 === 'object')
        {
          const v1Keys = Object.keys(v1);
          const v2Keys = Object.keys(v2);
          if (v1Keys.length !== v2Keys.length)
          {
            changedRootKeys.add(rootKey);
            return;
          }
          for (const k of v1Keys)
          {
            if (!(k in v2))
            {
              changedRootKeys.add(rootKey);
              return;
            }
            compare(v1[k], v2[k], rootKey);
          }
          return;
        }
        if (v1 !== v2)
        {
          changedRootKeys.add(rootKey);
        }
      };
      compare(value1, value2, key);
    });

    return Array.from(changedRootKeys);
  }
}
