import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { CreateTeeTimeGroupForm, TeeTimesSettingsDialogModel } from '../../../@intermediate/models/post-create-tee-time-group';

@Component({
  selector: 'tei-pos-tee-sheet-tee-times-settings-dialog',
  templateUrl: './tee-times-settings-dialog.component.html',
  styleUrls: ['./tee-times-settings-dialog.component.scss']
})
export class TeeTimesSettingsDialogComponent implements OnInit
{
  teeTimeForm: CreateTeeTimeGroupForm = new CreateTeeTimeGroupForm();

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: TeeTimesSettingsDialogModel,
    private dialogRef: MatDialogRef<TeeTimesSettingsDialogComponent>,) { }

  ngOnInit(): void
  {
  }
  onClose(): void
  {
    this.dialogRef.close({ IsChanged: false });
  }

  onApply(): void
  {
    this.dialogRef.close({ IsChanged: true, result: this.teeTimeForm });
  }
}
