import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DisplayFormComponent } from './display-form.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { ManageLoadingService, TeiTwoDigitDecimalModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DatepickerModule, ExpansionPanelModule, LoadingModule } from '@tei/material';
import { TeeSheetCourseCrossoversModule } from '../../tee-sheet-course-crossovers/tee-sheet-course-crossovers.module';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { DisplayFormMobileComponent } from './display-form-mobile/display-form-mobile.component';
import { TeeSheetDisplayService } from '../../../@services/tee-sheet-display.service';
import { TeeSheetService } from '../../../@services/tee-sheet.service';


const MATERIAL_MODULES = [MatButtonModule, MatDividerModule,MatRadioModule, MatCheckboxModule, MatDialogModule, MatInputModule, MatIconModule, MatFormFieldModule, MatSlideToggleModule, MatTableModule, ExpansionPanelModule, MatChipsModule];
const BASE_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const TEI_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule];
const TEI_MATERIAL_MODULES = [DatepickerModule, LoadingModule];
const COMPONENTS = [DisplayFormComponent, DisplayFormMobileComponent];
const COURSE_CROSS_OVER_MODULES = [TeeSheetCourseCrossoversModule];

@NgModule({
 declarations: [...COMPONENTS],
   imports: [
     ...BASE_MODULES,
     ...MATERIAL_MODULES,
     ...TEI_MODULES,
     ...TEI_MATERIAL_MODULES,
     ...COURSE_CROSS_OVER_MODULES
   ],
   exports: [
     ...COMPONENTS
   ],
   providers: [
     {provide: TeeSheetDisplayService},
     {provide: TeeSheetService},
     {provide: ManageLoadingService}
   ]
})
export class DisplayFormModule { }
