import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Attachment, AttachmentGroup } from '../../../../@intermediate/models/get-tee-time-attachment.model';

@Component({
  selector: 'tei-pos-tee-sheet-attachments-item',
  templateUrl: './attachments-item.component.html',
  styleUrls: ['./attachments-item.component.scss']
})
export class AttachmentsItemComponent
{
  @Output() attachmentEmitter = new EventEmitter<Attachment>();
  @Output() backToParentAttachmentEmitter = new EventEmitter<AttachmentGroup>();

  @Input() attachment: AttachmentGroup;

  onClickAttachment(attachment: Attachment): void
  {
    this.attachmentEmitter.emit(attachment);
  }
}
