import { Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from "@ngx-translate/core";
import { ManageStandAloneLoadingService, ResponsiveService } from "@tei/core";
import { SnackBarService } from "@tei/material";
import moment from "moment";
import { Observable, Subscription, finalize, firstValueFrom, tap } from "rxjs";
import { FlagType, PostFlagTeeTimesResponse, PostFlagNineHoleTeeTimesResponse } from "../../@intermediate/models/post-flag-reserved-tee-time.model";
import { PostTeeSheetFlagForm } from "../../@intermediate/models/post-tee-sheet-flag-form";
import { ITeeSheetFlagService } from "../../@intermediate/tee-sheet-flag-service.interface";

@Component({
  selector: 'tei-pos-tee-sheet-flag',
  templateUrl: './tee-sheet-flag.component.html',
  styleUrls: ['./tee-sheet-flag.component.scss'],

})
export class TeeSheetFlagComponent implements OnDestroy, OnInit
{
  selectedFlag: FlagType = this.data.FlagType;
  flagType = FlagType;
  templateTimes: string[];
  teeTimes: string[];
  isMobile = false;
  form = new FormGroup<PostTeeSheetFlagForm>({
    startTeeTime: new FormControl(this.data.slotTime, Validators.required),
    endTeeTime: new FormControl(this.data.slotTime, Validators.required)
  });
  subscriptions = new Subscription();
  constructor(
    @Inject('ITeeSheetFlagService') private teeFlagService: ITeeSheetFlagService,
    public dialog: MatDialogRef<TeeSheetFlagComponent>,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject(MAT_DIALOG_DATA) public data, private translate: TranslateService,
    private snackBarService: SnackBarService, private responsiveService: ResponsiveService,
  )
  {

  }
  ngOnInit(): void
  {
    this.templateTimes = this.data?.templateTimes || [];
    this.setTeeTimes();
    this.getMobileSize();
  }

  setTeeTimes(): void
  {
    const startTimeIndex = this.templateTimes.findIndex(t => t == this.form.value.startTeeTime);
    this.teeTimes = this.templateTimes.slice(startTimeIndex, this.templateTimes.length);
  }

  onStartTeeTimeChange(): void
  {
    if (this.changeFormatOfTime(this.form.value.endTeeTime) < this.changeFormatOfTime(this.form.value.startTeeTime))
    {
      this.form.patchValue({
        endTeeTime: this.form.value.startTeeTime
      });
    }
    this.setTeeTimes();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs';
  }

  changeFormatOfTime(time: string): string
  {
    return moment(time, 'h:mm A').format('HH:mm');
  }

  getFormControlByName(controlName: string)
  {
    return this.form.get(controlName) as FormControl;
  }

  async Flag(): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      const response = await firstValueFrom(this.flagTeeTimes(this.selectedFlag));
        
      this.dialog.close(true);
      const warningMessage = (response as PostFlagNineHoleTeeTimesResponse)?.WarningMessage;
      if (warningMessage) {
        this.snackBarService.showError(warningMessage, '', null, 'left');
      }
      else
      {
        this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.PLAYER.SAVE_SUCCESS', {
          FlagType: this.selectedFlag
        }), '', null, 'left');
      }
    }
    catch (error)
    {
    }
    finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  flagTeeTimes(selectedFlag: string): Observable<PostFlagTeeTimesResponse> 
  {
    const request = {
      CourseID: this.data?.CourseID,
      EndTime: this.form.value.endTeeTime,
      StartTime: this.form.value.startTeeTime,
      TeeSheetDate: this.data?.SheetDate,
    };

    if (selectedFlag === this.flagType.Reserved)
    {
      return this.teeFlagService.PostFlagReserved(request);
    }
    if (selectedFlag === this.flagType.CrossOver)
    {
      return this.teeFlagService.PostFlagCrossover(request);
    }
    if (selectedFlag === this.flagType.NineHole)
    {
      return this.teeFlagService.PostFlagNineHole(request);
    }
  }

  onCloseClick(): void
  {
    this.dialog.close();
  }

  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}
