import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { IFormArray } from '@rxweb/types';
import { Attachment, CartTypesID, Group, PastDayStatusBookingPanel, Player, TeeTimeHoles } from '../../../../@intermediate/models/get-booking-details.model';
import { PlayerStatus, PriceLevel, StartTeeHole, TeeTimeConfig } from '../../../../@intermediate/models/get-tee-sheet.model';
import { ContactChanged, PlayersFormArray } from '../../../../@intermediate/models/player-row.model';
import { ManageTeeTimeHelperService } from '../../../../@services/manage-tee-time.helper.service';
@Component({
  selector: 'tei-pos-tee-sheet-players-list',
  templateUrl: './players-list.component.html',
  styleUrls: ['./players-list.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PlayersListComponent implements OnInit, OnChanges
{
  @Input() form: UntypedFormGroup;
  @Input() ContactLimit = 4;
  @Input() availablePlayers = 4;
  @Input() playersQuantity = 1;
  @Input() courseID: string;
  @Input() GroupBookingIndex = 0;
  @Input() attachmentGroups: Array<Group>;
  @Input() Players: Player[];
  @Input() bookingID: number;
  @Input() CartRequired: boolean;
  @Input() PowerCartRequired: boolean;
  @Input() priceLevels: PriceLevel[];
  @Input() startTee: StartTeeHole;
  @Input() HasAttachments: boolean;
  @Input() checkIn: boolean;
  @Input() noShow: boolean;
  @Input() contactIsRequired: boolean;
  @Input() BookingDateTime: string;
  @Input() isNewBooking: boolean;
  @Input() is9HoleOnly: boolean;
  @Input() pastDayStatus: PastDayStatusBookingPanel;
  @Input() config: TeeTimeConfig;
  @Output() playerStatusChanged = new EventEmitter<{ playerIndex: number, newStatus: PlayerStatus; }>();
  private playersFormSubmittedValue: boolean;
  get playersFormSubmitted(): boolean
  {
    return this.playersFormSubmittedValue;
  }

  get playerControls(): UntypedFormGroup[]
  {
    return (this.form.get('player') as UntypedFormArray).controls as UntypedFormGroup[];
  }

  @Input() set playersFormSubmitted(value: boolean)
  {
    this.playersFormSubmittedValue = value;
  }

  @Output() addOnePlayer: EventEmitter<number> = new EventEmitter();
  @Output() playersQuantityChanged: EventEmitter<UntypedFormGroup> = new EventEmitter();
  @Output() updateGroupPriceFromEachPlayerItem = new EventEmitter();
  @Output() contactChanged: EventEmitter<ContactChanged> = new EventEmitter();
  @Output() removePlayer: EventEmitter<number> = new EventEmitter();
  @Output() cancelBooking = new EventEmitter();
  @Output() updatePlayersQuantityButtons: EventEmitter<number> = new EventEmitter();
  @Output() updataManageGroupPlayer = new EventEmitter();

  players: IFormArray<PlayersFormArray> = this.fb.array([]);
  teeTimeHoles = TeeTimeHoles;
  cartTypesID = CartTypesID;

  constructor(
    private manageTeeTimeHelper: ManageTeeTimeHelperService,
    private changeDetectorRef: ChangeDetectorRef,
    private fb: UntypedFormBuilder)
  {

  }

  ngOnChanges(changes: SimpleChanges)
  {
    if ('playersQuantity' in changes)
    {
      if (!changes.playersQuantity.firstChange)
      {
        this.handlePlayersQuantity();
      }
    }
  }

  ngOnInit(): void
  {
    this.createForm();

    this.emitUpdateContact();
  }

  private emitUpdateContact()
  {
    const control = this.form.controls.player as UntypedFormArray;

    const contact = control.at(0).get('Contact')?.value;
    if (contact && contact.ContactID && contact.ContactID !== '00000000-0000-0000-0000-000000000000')
    {
      const contactChanged: ContactChanged = {
        contact: contact,
        playerIndex: 0,
        hasRestrictionAuthorize: false,
        InitState: true
      };

      this.contactChanged.emit(contactChanged);
    }
  }

  createForm(): void
  {
    for (let playerIndex = 0; playerIndex < this.playersQuantity; playerIndex++)
    {
      this.addPlayer(playerIndex);
    }
  }

  addPlayer(index?: number, playersQuantityChanged?: boolean): void
  {
    const control = this.form.controls.player as UntypedFormArray;
    let bookingDetailsPlayer: Player | null = null;
    let controlDisabled: boolean;

    if (index + 1 <= this.Players.length)
    {
      bookingDetailsPlayer = { ...this.Players[index] };

      if (this.playersQuantity > index && bookingDetailsPlayer)
      {
        if (bookingDetailsPlayer?.Contact &&
          bookingDetailsPlayer.Contact?.ContactID)
        {
          if (bookingDetailsPlayer.Contact?.ContactID === '00000000-0000-0000-0000-000000000000' || bookingDetailsPlayer.Contact?.ContactID === '88888888-8888-8888-8888-888888888888'
            || bookingDetailsPlayer.Contact?.DisplayName === 'In progress'
            || bookingDetailsPlayer.Contact?.DisplayName?.trim().length === 0)
          {
            if (bookingDetailsPlayer.Contact?.BookingDisplayName && bookingDetailsPlayer.Contact?.BookingDisplayName != 'In progress')
              bookingDetailsPlayer.PlayerDisplayName = bookingDetailsPlayer.Contact?.BookingDisplayName;
            bookingDetailsPlayer.Contact = null;
          }
        }
      }
    }
    controlDisabled = (bookingDetailsPlayer?.Status === PlayerStatus.CheckIn) || (bookingDetailsPlayer?.Status === PlayerStatus.CheckInInprogress);

    const form: UntypedFormGroup = this.createPlayerForm(bookingDetailsPlayer, controlDisabled, index);
    if (controlDisabled)
    {
      form.get('PriceLevelID').disable({ emitEvent: true, onlySelf: false });
    }

    if (playersQuantityChanged)
    {
      this.playersQuantityChanged.emit(form);
    } else
    {
      control.push(form);
    }
  }

  private createPlayerForm(bookingDetailsPlayer: Player, controlDisabled: boolean, index: number): UntypedFormGroup
  {
    return this.fb.group({
      Contact: [{
        value: bookingDetailsPlayer ? bookingDetailsPlayer.Contact : null,
        disabled: controlDisabled
      }, index === 0 && Validators.required],
      DisplayName: {
        value: bookingDetailsPlayer?.Contact?.BookingDisplayName ?? bookingDetailsPlayer?.PlayerDisplayName ?? '',
        disabled: controlDisabled
      },
      Holes: {
        value: this.config?.GroupDefaultValues && this.config.GroupDefaultValues.NineHoles != null && !bookingDetailsPlayer?.TeeTimeID
          ? this.config.GroupDefaultValues.NineHoles
          : (bookingDetailsPlayer ? bookingDetailsPlayer.Holes : (this.is9HoleOnly ? this.teeTimeHoles.Holes9 : this.teeTimeHoles.Holes18)),
        disabled: controlDisabled || this.is9HoleOnly
      },
      Cart: {
        value: this.prepareCardValue(bookingDetailsPlayer),
        disabled: controlDisabled || this.PowerCartRequired
      },
      Status: {
        value: this.getPlayerStatus(bookingDetailsPlayer),
        disabled: controlDisabled
      },
      PriceLevelID: {
        value: this.config?.GroupDefaultValues?.PriceLevelID && !bookingDetailsPlayer?.TeeTimeID
          ? this.config?.GroupDefaultValues.PriceLevelID
          : (bookingDetailsPlayer?.PriceLevelID ?? ''),
        disabled: controlDisabled
      },
      PriceLevels: this.fb.array(this.manageTeeTimeHelper.mapToPlayerPriceLevel(this.priceLevels)),
      GolfPrice: bookingDetailsPlayer ? bookingDetailsPlayer?.PlayerTotal : 0,
      PlayerPrice: bookingDetailsPlayer ? this.manageTeeTimeHelper.transformBookingDetailsPlayerForPriceBreakdown(bookingDetailsPlayer) : {},
      Attachments: this.fillAttachmentsFormArray(bookingDetailsPlayer),
      PlayerNumber: bookingDetailsPlayer?.PlayerNumber,
      IsExceed: index > this.playersQuantity - 1,
      IsCheckedIn: controlDisabled,
      PanelOpenState: false,
      TeeTimeID: bookingDetailsPlayer?.TeeTimeID,
      EditDisplayName: false
    });
  }

  add(): void
  {
    this.addOnePlayer.emit();
  }

  handlePlayersQuantity(): void
  {
    const control = this.form.controls.player as UntypedFormArray;

    if (control.length < this.playersQuantity)
    {
      this.addPlayer(this.playersQuantity - 1, true);
    } else
    {
      this.playersQuantityChanged.emit(null);
    }

    this.changeDetectorRef.detectChanges();
  }

  prepareCardValue(bookingDetailsPlayer): CartTypesID
  {
    if (this.config?.GroupDefaultValues && this.config?.GroupDefaultValues?.CartType != null && !bookingDetailsPlayer?.TeeTimeID)
    {
      return this.config?.GroupDefaultValues?.CartType;
    }

    if ((this.config?.GroupDefaultValues?.CartType == null || this.config?.GroupDefaultValues?.CartType == undefined)
      && (this.CartRequired || this.PowerCartRequired))
    {
      return this.cartTypesID.Power;
    }
    return bookingDetailsPlayer ? bookingDetailsPlayer.Cart : this.cartTypesID.Walk;
  }

  getPlayerStatus(bookingDetailsPlayer: Player): PlayerStatus
  {
    if (bookingDetailsPlayer)
    {
      if (bookingDetailsPlayer.Status === PlayerStatus.NoShow)
      {
        bookingDetailsPlayer.Status = PlayerStatus.NoShowFromAPI;
        return PlayerStatus.NoShowFromAPI;
      }
      else
      {
        return bookingDetailsPlayer.Status;
      }
    }

    return PlayerStatus.None;
  }

  fillAttachmentsFormArray(bookingDetailsPlayer: Player): UntypedFormArray
  {
    if (bookingDetailsPlayer && bookingDetailsPlayer?.Attachments?.length)
    {
      return this.fb.array(this.patchAttachments(bookingDetailsPlayer?.Attachments));
    } else
    {
      return this.fb.array([]);
    }
  }

  patchAttachments(attachmentsUIDs: Array<string>): Array<Attachment>
  {
    const attachmentGroups = this.attachmentGroups;
    let attachments = [];
    if (attachmentGroups && attachmentGroups.length)
    {
      attachmentGroups.map(group =>
      {
        group.Attachments.map(attachment =>
        {
          if (attachmentsUIDs.includes(attachment.AttachmentUID))
          {
            attachments.push(attachment);
          }
        });
      });
      return attachments;
    }
    return [];
  }

}


