import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSmallGroupBookingDetailsRequest, GetSmallGroupBookingDetailsResponse } from '../@intermediate/models/get-booking-details.model';
import { PostSmallGroupTeeTimesInitializeRequest, PostSmallGroupTeeTimesInitializeResponse } from '../@intermediate/models/post-small-group-tee-time.model';
import { PutSmallGroupTeeTimesRequest, PutSmallGroupTeeTimesResponse } from '../@intermediate/models/put-small-group-tee-time.model';
import { ISmallGroupBookingService } from '../@intermediate/small-group-booking.interface';

@Injectable({
  providedIn: 'root'
})
export class SmallGroupBookingService implements ISmallGroupBookingService
{

  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient,)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/SmallGroupTeeTimes/';
  }
  GetSmallGroupBookingDetails(request: GetSmallGroupBookingDetailsRequest): Observable<GetSmallGroupBookingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingDetails';
    return this.http.get<GetSmallGroupBookingDetailsResponse>(apiUrl, { params: request as any });

  }

  PostSmallGroupBookingInitialize(request: PostSmallGroupTeeTimesInitializeRequest): Observable<PostSmallGroupTeeTimesInitializeResponse>
  {
    const apiUrl = this.serviceUrl + 'Initialize';
    return this.http.post<PostSmallGroupTeeTimesInitializeResponse>(apiUrl, request);
  }

  PutSmallGroupBooking(request: PutSmallGroupTeeTimesRequest): Observable<PutSmallGroupTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.put<PutSmallGroupTeeTimesResponse>(apiUrl, request);
  }
}
