import { FormControl } from "@angular/forms";
import { GetBookingDetailsResponse, Player } from "./get-booking-details.model";


export class PostSwapMoveGroupPlayersRequest
{
    FromTeeTimeID: string;
    Action: MoveSwapActionEnum;
    ToTeeTimeID: string;
}

export class PostSwapMoveGroupPlayersResponse
{
    Success: boolean;
}
export class MoveSwapGroupPlayersForm
{
    FromTeeTimeID: FormControl<string>;
    Action: FormControl<MoveSwapActionEnum>;
    ToTeeTimeID: FormControl<string>;
}

export enum MoveSwapActionEnum
{
    Move,
    Swap
}

export class MoveSwapDialogData
{
    SelectedBooking: GetBookingDetailsResponse;
    SelectedPlayer: Player;
    BookingDetails: MoveSwapBookingDetail[];
}

export class MoveSwapBookingDetail
{
    Players: Player[];
    DisplayTime: string;
    BookingID: number;
    HoleNumber: string;
}