import { CourseType, Holes1or10, TeeTime } from './get-tee-sheet.model';

export class GetTeeSheetMainRequest
{
  Date: string;
}
export class GetTeeSheetMainResponse
{
  IsEmptyTeeSheet: boolean;
  Courses = new Array<Course>(0);
  DefaultCourseID: string;
  TeeTimes: TeeTime[] = [];
  EnableMultiViewGroups: boolean;
  MultiViewGroups: MultiViewGroup[] = [];
  IsReadOnlyWebPOSTeeSheet: boolean;
  TeeSheetViewType: TeeSheetViewType;
  TerminalSettings: TerminalSettingsType;
  IsFutureDate: boolean;
  IsPastDate: boolean;
}

export class TerminalSettingsType
{
  QuickCheckinContactPrompt: boolean;
  QuickCheckinContactRequired: boolean;
}

export class Course
{
  CourseID: string;
  CourseName: string;
  ShowInMultiView: boolean;
  CourseType?: CourseType;
  StartTeeHole?: Holes1or10;
  HasAttachments?: boolean;
  SortOrder: number;
  AllowFutureCheckins: boolean;
  AllowPastCheckins: boolean;
}

export class SelectedBlockCourse
{
  CourseID: string;
  StartTeeHole: string;
}

export class SelectedDisableOptionsCourse
{
  IsFutureDate: boolean;
  IsPastDate: boolean;
  AllowFutureCheckins: boolean;
  AllowPastCheckins: boolean;
}

export class MultiViewGroup
{
  GroupID: string;
  GroupName: string;
  SelectedGroupID: string;
  Courses = new Array<Course>(0);
}

export class TeeSheetWidthAndHeight
{
  height: number;
  width: number;
}

export enum TeeSheetViewType
{
  Editable = 1,
  PosReadonly = 2,
  Readonly = 3,
}
