import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { ManageStandAloneLoadingService, TeiTwoDigitDecimalModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule, TimePickerModule } from '@tei/material';
import { TeeSheetBlockComponent } from './tee-sheet-block.component';

const MATERIAL_MODULES = [MatButtonModule, MatDividerModule, MatCheckboxModule, MatDialogModule, MatInputModule, MatIconModule, MatFormFieldModule, MatSelectModule];
const BASE_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const TEI_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule];
const TEI_MATERIAL_MODULES = [TimePickerModule, LoadingModule];
const COMPONENTS = [TeeSheetBlockComponent];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiTwoDigitDecimalModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [
    ManageStandAloneLoadingService
  ]
})
export class TeeSheetBlockModule { }
