import
{
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostListener,
  Inject,
  OnDestroy,
  OnInit,
  ViewChild
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ManageStandAloneLoadingService, StorageService } from '@tei/core';
import { teeSheetCacheName } from '@tei/pos-shared';
import { Observable, Subscription, fromEvent } from 'rxjs';
import { debounceTime, delay, distinctUntilChanged, map, take } from 'rxjs/operators';
import
{
  Course,
  GetTeeSheetMainRequest,
  GetTeeSheetMainResponse,
  MultiViewGroup,
  SelectedDisableOptionsCourse,
  TeeSheetViewType,
  TeeSheetWidthAndHeight
} from '../../@intermediate/models/get-tee-sheet-main.model';
import { GetTeeSheetResponse, TeeSheet } from '../../@intermediate/models/get-tee-sheet.model';
import { AdvancedSearchTeeTime, TeeSheetAdvancedSearchResult } from '../../@intermediate/models/post-tee-time-advance-search.model';
import { ColumnType, Layers, LayersType, SelectType, TeeSheetSelectedView } from '../../@intermediate/models/tee-sheet-toolbar-model';
import { MainService } from '../../@services/main.service';
import { TeeSheetActionService } from '../../@services/tee-sheet-action.service';
import { TeeSheetTeeTimesComponent } from '../../components/tee-sheet-tee-times/tee-sheet-tee-times.component';
import { TeeSheetMainService } from '../../@services/tee-sheet-main.service';

@Component({
  selector: 'tei-pos-tee-sheet-pages-default',
  templateUrl: './tee-sheet-main.component.html',
  styleUrls: ['./tee-sheet-main.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TeeSheetMainComponent implements OnInit, OnDestroy, AfterViewInit
{
  @ViewChild('carouselContent', { static: true, read: ElementRef }) public carouselContent: ElementRef;
  @ViewChild('printTeeTimes') printTeeTimesSection: TeeSheetTeeTimesComponent;
  onPrintTeeTimes()
  {
    this.printTeeTimesSection.printTeeTimes();
  }

  subscription = new Subscription();
  teeSheetWidth: number;
  teeSheetHeight: number;
  teeSheetHeader: GetTeeSheetMainResponse;
  isCollapse = false;
  teeSheetSelectedView = new TeeSheetSelectedView();
  columnType = ColumnType;
  get teeSheetArray(): TeeSheet[]
  {
    return this.teeSheetMainService.teeSheetArray;
  }
  courses = new Array<Course>(0);
  isGroup = false;
  multiViewGroups: MultiViewGroup[] = [];
  groupCourses = new Array<Course>(0);
  multiViewCourses = new Array<Course>(0);
  enableMultiViewGroups?: boolean;
  disableLeftNavigator: boolean;
  disableRightNavigator: boolean;
  selectType: SelectType = SelectType.Single;
  pagesCount = 1;
  multiTeeSheetItemsCount: number;
  searchedTeeTime: AdvancedSearchTeeTime;
  showContactClass: boolean;
  toolbarLayers: Layers;
  teeSheetViewType: TeeSheetViewType = TeeSheetViewType.Editable;
  teeSheetViewTypeEnum = TeeSheetViewType;
  isReadOnly: boolean;
  loadTeeSheet = true;
  selectedDisableOptionsCourse = new SelectedDisableOptionsCourse();

  @HostListener('window:resize', ['$event'])
  onResize(event)
  {
    this.doTeeSheetSizeChange(this.getHeightAndWidthTeeSheet());
  }

  constructor(private changeDetector: ChangeDetectorRef,
    private storageService: StorageService,
    private route: ActivatedRoute,
    @Inject('IMainService') private service: MainService,
    @Inject('ITeeSheetActionService') private teeSheetActionService: TeeSheetActionService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private teeSheetMainService: TeeSheetMainService
  )
  {
  }

  ngOnInit(): void
  {
    this.loadData();
  }

  loadData(): void
  {
    this.getTeeSheetActionStatus();
    this.handleViewType();
    this.initialData();
    this.loadTeeSheetHeader();
    this.createToolbarLayers();
    this.disableWindowContextmenu();
    this.handleLoading();
  }

  private handleLoading(): void
  {
    this.subscription.add(this.teeSheetActionService.getUpdateTeeSheetConfigsSubject().subscribe((date) =>
    {
      this.updateHeaderConfigs(date).subscribe();
    }));
  }

  private initialData(): void
  {
    this.teeSheetSelectedView.ColumnType = this.columnType.Single;
    this.teeSheetSelectedView.ColumnNumber = 1;
  }

  private handleViewType()
  {
    this.subscription.add(this.route.data.subscribe((data) =>
    {
      if (data?.readOnly)
      {
        this.teeSheetViewType = this.teeSheetViewTypeEnum.Readonly;
      }
    }));
  }

  private getTeeSheetActionStatus(): void
  {
    this.subscription.add(this.teeSheetActionService.getTeeSheetActionStatus().subscribe(status =>
    {
      if (status)
      {
        this.checkMultipleTeeSheetStatus();
      }
    }));
  }

  createToolbarLayers(): void
  {
    this.revertToCurrentDate();

    const contactClass = Boolean(this.teeSheetSelectedView?.ContactClass);

    this.toolbarLayers = {
      type: LayersType.CheckInStatus,
      contactClass,
    } as Layers;

    this.showContactClass = contactClass;
  }

  revertToCurrentDate(): void
  {
    if (this.storageService.HasKey(teeSheetCacheName))
    {
      const cachedView = Object.assign(
        new TeeSheetSelectedView(),
        this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName)
      );

      const today = new Date().toDateString();
      cachedView.TeeSheetViewSettings.forEach(setting =>
      {
        if (setting?.LastDate && new Date(setting.LastDate).toDateString() !== today)
        {
          setting.LastDate = new Date();
        }
      });

      this.teeSheetSelectedView = cachedView;
      this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
    }
  }


  disableWindowContextmenu(): void
  {
    this.subscription.add(fromEvent(window, 'contextmenu').subscribe((e) =>
    {
      e.preventDefault();
      return false;
    }));
  }

  updateHeaderConfigs(date?: string): Observable<GetTeeSheetMainResponse>
  {
    if (!this.teeSheetHeader)
    {
      this.teeSheetHeader = new GetTeeSheetMainResponse();
    }

    const request = new GetTeeSheetMainRequest();
    if (date)
    {
      request.Date = date;
    }

    return this.service.Get(request).pipe(map((response: GetTeeSheetMainResponse) =>
    {
      this.teeSheetHeader = response;
      this.teeSheetViewType = response.TeeSheetViewType;
      this.isReadOnly = (this.teeSheetViewType === this.teeSheetViewTypeEnum.Readonly || this.teeSheetViewType === this.teeSheetViewTypeEnum.PosReadonly);
      this.courses = this.teeSheetHeader.Courses;
      this.multiViewGroups = this.teeSheetHeader.MultiViewGroups;
      this.enableMultiViewGroups = this.teeSheetHeader.EnableMultiViewGroups;
      this.multiViewCourses = JSON.parse(JSON.stringify(this.teeSheetHeader.Courses)).filter(item => item.ShowInMultiView === true);
      this.storageService.Set('terminal-settings', response.TerminalSettings);
      this.selectedDisableOptionsCourse.IsFutureDate = response.IsFutureDate;
      this.selectedDisableOptionsCourse.IsPastDate = response.IsPastDate;

      if (this.storageService.HasKey(teeSheetCacheName))
      {
        this.teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));
        if (this.teeSheetSelectedView.ColumnType == null || this.teeSheetSelectedView.ColumnType == undefined)
        {
          this.teeSheetSelectedView = new TeeSheetSelectedView();
        }
      } else
      {
        this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
      }
      this.changeDetector.detectChanges();
      return response;

    }));
  }

  private loadTeeSheetHeader(): void
  {
    this.updateHeaderConfigs().pipe(take(1)).subscribe(() =>
    {
      this.teeSheetMainService.setTeeSheetArray([new TeeSheet()]);
      this.teeSheetArray[0].SelectedTeeSheetDate = new Date();
      this.teeSheetArray[0].SelectedCourse = this.teeSheetHeader.Courses.find(x => x.CourseID === this.teeSheetHeader.DefaultCourseID);
      if (!this.teeSheetArray[0].SelectedCourse)
      {
        this.teeSheetArray[0].SelectedCourse = this.teeSheetHeader.Courses[0];
      }
      this.teeSheetArray[0].IsEmptyTeeSheet = false;
      this.teeSheetArray[0].IsLoading = true;

      if (this.storageService.HasKey(teeSheetCacheName))
      {
        this.onSelectedViewType(this.teeSheetSelectedView);
      }
      this.changeDetector.detectChanges();

    });
  }

  onSelectedViewType(teeSheetSelected?: TeeSheetSelectedView, selectedDateTime?: Date, courseId?: string): void
  {
    if (this.teeSheetSelectedView.ColumnNumber !== teeSheetSelected.ColumnNumber && !this.teeSheetSelectedView.CurrentTeeSheetPage)
    {
      this.carouselContent.nativeElement.scrollTo({ left: 0, behavior: 'smooth' });
    }
    if (teeSheetSelected)
    {
      this.teeSheetSelectedView = teeSheetSelected;
      if (this.teeSheetSelectedView.SelectedGroupID)
      {
        if (this.teeSheetSelectedView.SelectedGroupID === 'All_Courses')
        {
          this.groupCourses = this.multiViewCourses;
        } else
        {
          this.groupCourses = this.multiViewGroups.find(i => i.GroupID === this.teeSheetSelectedView.SelectedGroupID).Courses;
        }
      }
    }

    if (this.teeSheetSelectedView.ColumnType == this.columnType.Single)
    {
      this.teeSheetSelectedView.ColumnNumber = 1;
      this.loadSingleTeeSheet(selectedDateTime, courseId);
      this.isGroup = false;
    }
    this.checkMultipleTeeSheetStatus();
  }

  checkMultipleTeeSheetStatus()
  {
    if (this.teeSheetSelectedView.ColumnType == this.columnType.Multi)
    {
      if (this.groupCourses.length == 0)
      {
        this.loadMultipleTeeSheets(this.multiViewCourses);
        this.isGroup = false;
      } else if (this.groupCourses.length !== 0 && this.enableMultiViewGroups)
      {
        this.loadMultipleTeeSheets(this.groupCourses);
        this.isGroup = true;
      } else if (!this.enableMultiViewGroups)
      {
        this.isGroup = false;
        this.loadMultipleTeeSheets(this.multiViewCourses);
      }
    } else
    {
      this.teeSheetArray.splice(1);
      this.isGroup = false;
    }
  }

  loadSingleTeeSheet(selectedTeeSheetDate?: Date, courseId?: string): void
  {
    this.teeSheetMainService.setTeeSheetArray([new TeeSheet()]);
    this.teeSheetArray[0].IsEmptyTeeSheet = false;
    this.teeSheetArray[0].IsLoading = true;

    if (selectedTeeSheetDate)
    {
      this.teeSheetArray[0].SelectedTeeSheetDate = selectedTeeSheetDate;
    } else if (this.teeSheetSelectedView.firstSetting())
    {
      this.teeSheetArray[0].SelectedTeeSheetDate = this.teeSheetSelectedView.firstSetting().LastDate;
    }
    else
    {
      this.teeSheetArray[0].SelectedTeeSheetDate = new Date();
    }

    if (courseId)
    {
      this.teeSheetArray[0].SelectedCourse = this.courses.find(a => a.CourseID === courseId);
    }
    else if (this.teeSheetSelectedView.firstSetting())
    {
      this.teeSheetArray[0].SelectedCourse = this.teeSheetHeader.Courses.find(x => x.CourseName === this.teeSheetSelectedView.firstSetting().CourseName);
      if (!this.teeSheetArray[0].SelectedCourse)
      {
        this.teeSheetArray[0].SelectedCourse = this.courses[0];
      }
    } else
    {
      this.teeSheetArray[0].SelectedCourse = this.courses[0];
    }

    this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
  }

  loadMultipleTeeSheets(groupCourses: Course[]): void
  {
    this.loadTeeSheet = false;
    this.teeSheetMainService.setTeeSheetArray([]);
    groupCourses.forEach((course: Course, index: number) =>
    {
      this.teeSheetArray[index] = {
        GetTeeSheet: new GetTeeSheetResponse(),
      } as TeeSheet;

      if (!this.teeSheetSelectedView.TeeSheetViewSettings) { this.teeSheetSelectedView.TeeSheetViewSettings = []; }
      if (this.teeSheetSelectedView.ColumnType == this.columnType.Multi && this.teeSheetSelectedView.TeeSheetViewSettings.length > index)
      {
        this.teeSheetArray[index].SelectedCourse = this.teeSheetHeader.Courses.find(x => x.CourseName === this.teeSheetSelectedView.TeeSheetViewSettings[index]?.CourseName);
        if (!this.teeSheetArray[index].SelectedCourse)
        {
          this.teeSheetArray[index].SelectedCourse = course;
        }
      } else
      {
        this.teeSheetArray[index].SelectedCourse = course;
      }
      this.teeSheetArray[index].SelectedTeeSheetDate = this.teeSheetSelectedView.TeeSheetViewSettings.length > index ? this.teeSheetSelectedView.TeeSheetViewSettings[index]?.LastDate ?? new Date() : new Date();
    });
    this.disableRightNavigator = false;
    this.disableLeftNavigator = false;
    this.pagesCount = Math.ceil(groupCourses.length / this.teeSheetSelectedView.ColumnNumber);
    this.navigatorsDisabling(groupCourses.length);
    this.multiTeeSheetItemsCount = groupCourses.length;
    this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
    this.changeDetector.detectChanges();
    this.loadTeeSheet = true;
  }

  onSelectChecks(type: SelectType): void
  {
    this.selectType = type;
  }

  onSelectCollapseButton(isCollapse: boolean): void
  {
    this.isCollapse = isCollapse;

    if (this.storageService.HasKey(teeSheetCacheName))
    {
      this.onSelectedViewType(this.teeSheetSelectedView);
    }
  }

  onLayersChanged(form: Layers): void
  {
    this.showContactClass = form.contactClass;
    this.teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));
    this.teeSheetSelectedView.ContactClass = form.contactClass;
    this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
  }

  public scrollRight(): void
  {
    this.carouselContent.nativeElement.scrollTo({ left: (this.carouselContent.nativeElement.scrollLeft + this.teeSheetWidth) });
    this.teeSheetSelectedView.CurrentTeeSheetPage = ++this.teeSheetSelectedView.CurrentTeeSheetPage;
    this.navigatorsDisabling(this.multiTeeSheetItemsCount);
  }

  public scrollLeft(): void
  {
    this.carouselContent.nativeElement.scrollTo({ left: (this.carouselContent.nativeElement.scrollLeft - this.teeSheetWidth) });
    this.teeSheetSelectedView.CurrentTeeSheetPage = --this.teeSheetSelectedView.CurrentTeeSheetPage;
    this.navigatorsDisabling(this.multiTeeSheetItemsCount);
  }

  navigatorsDisabling(itemsCount?: number): void
  {
    if (this.teeSheetSelectedView.CurrentTeeSheetPage === 1 && itemsCount <= this.teeSheetSelectedView.ColumnNumber)
    {
      this.disableLeftNavigator = true;
      this.disableRightNavigator = true;
    }
    if (this.teeSheetSelectedView.CurrentTeeSheetPage === 1 && itemsCount > this.teeSheetSelectedView.ColumnNumber)
    {
      this.disableLeftNavigator = true;
      this.disableRightNavigator = false;
    }
    if (this.teeSheetSelectedView.CurrentTeeSheetPage > 1 && this.teeSheetSelectedView.CurrentTeeSheetPage !== this.pagesCount)
    {
      this.disableLeftNavigator = false;
      this.disableRightNavigator = false;
    }
    if (this.teeSheetSelectedView.CurrentTeeSheetPage === this.pagesCount && this.teeSheetSelectedView.CurrentTeeSheetPage !== 1)
    {
      this.disableLeftNavigator = false;
      this.disableRightNavigator = true;
    }

    const teeSheetSelectedView: TeeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));
    teeSheetSelectedView.CurrentTeeSheetPage = this.teeSheetSelectedView.CurrentTeeSheetPage;
    this.storageService.Set(teeSheetCacheName, teeSheetSelectedView);
  }

  getHeightAndWidthTeeSheet(): TeeSheetWidthAndHeight
  {
    const newValues = new TeeSheetWidthAndHeight();
    newValues.height = this.carouselContent.nativeElement.offsetHeight;
    newValues.width = this.carouselContent.nativeElement.offsetWidth;
    return newValues;
  }

  setupTeeSheetHeightMutationObserver(): void
  {
    const teeSheetObservable = new Observable<TeeSheetWidthAndHeight>(observer =>
    {
      const callback = () =>
      {
        observer.next(this.getHeightAndWidthTeeSheet());
      };
      const elementObserver = new MutationObserver(callback);

      const config = { attributes: true, childList: true, subtree: true };
      elementObserver.observe(this.carouselContent.nativeElement, config);
    });

    this.subscription.add(teeSheetObservable
      .pipe(
        debounceTime(50),
        distinctUntilChanged()
      )
      .subscribe((newValues =>
      {
        this.doTeeSheetSizeChange(newValues);
      })));

    this.subscription.add(teeSheetObservable.pipe(
      take(1),
      delay(500),
    ).subscribe(() =>
    {
      if (this.teeSheetSelectedView.CurrentTeeSheetPage === 1)
      {
        this.carouselContent.nativeElement.scrollTo({ left: 0 });
        this.navigatorsDisabling(this.multiTeeSheetItemsCount);
      } else
      {
        this.carouselContent.nativeElement.scrollTo({ left: this.teeSheetWidth * (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) });
        this.navigatorsDisabling(this.multiTeeSheetItemsCount);
      }
    }));
  }

  doTeeSheetSizeChange(newValues: TeeSheetWidthAndHeight): void
  {
    this.teeSheetHeight = newValues.height;
    this.teeSheetWidth = newValues.width;
  }

  transferTeeSheet(advancedTeeTimeSearchResult: TeeSheetAdvancedSearchResult): void
  {
    this.searchedTeeTime = advancedTeeTimeSearchResult.teeTime;
    this.onSelectedViewType(advancedTeeTimeSearchResult.teeSheetSelectedView, new Date(advancedTeeTimeSearchResult.teeTime.BookingDateTime), advancedTeeTimeSearchResult.teeTime.CourseID);
  }
  getColumnClasses(columnNumber: number): string
  {
    switch (columnNumber)
    {
      case 1:
        return 'col-12';
      case 2:
        return 'col-6 t-teesheet-col-6';
      case 3:
        return 'col-4 t-teesheet-col-4';
      case 4:
        return 'col-3 t-teesheet-col-3';
      default:
        return '';
    }
  }

  ngAfterViewInit(): void
  {
    this.setupTeeSheetHeightMutationObserver();
    this.doTeeSheetSizeChange(this.getHeightAndWidthTeeSheet());
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
