export class GetModifiedTeeSheetRequest
{
    CourseID: string;
    SheetDate: string;
}

export class GetModifiedTeeSheetResponse
{
    TemplateID: string;
    CourseType: number;
    CourseTypes: Array<CourseType>;
    StartTime: Date;
    EndTime: Date;
    Intervals: string;
    FivePlayerTemplate: boolean;
    TSTemplateUniqueID: string;
    TimeZoneAndFlagTemplates: Array<TimeZoneAndFlagTemplate>;
    TeeSheetTemplates: Array<TeeSheetTemplate>;
    // Use in FE
    SelectedCourseName: string;
    SelectedCourseID: string;
    SelectedTeeSheetDate: Date;
    ModifiedTSItems: Array<TeeSheetTemplate>;
}

export class CourseType
{
    ID: number;
    Title: string;
}

export class TimeZoneAndFlagTemplate
{
    Title: string;
    Name: string;
    Color: string;
    IsTimeZone: boolean;
}

export class TeeSheetTemplate
{
    SheetTime: string;
    [key: string]: boolean | string | number;
}