export class GetPlayerPriceLevelsRequest
{
  PlayerNumber: number;
  ContactID: string;
  BookerID: string;
  TeeTime: string;
  PlayerList: string[];
  CourseID: string;
}



export class GetPlayerPriceLevelsResponse
{
  CoursePriceLevels: PlayerCoursePriceLevel[];
  PriceLevels: PlayerPriceLevel[];
}

export class PlayerPriceLevel
{
  PriceLevelID: string;
  Description: string;
  IsPublic: boolean;
  PlayerNumber: number;
  IsDefault: boolean;
}
export class PlayerCoursePriceLevel
{
  Description: string;
  Expire: boolean;
  ExpiryDate: string;
  IsPublic: boolean;
  PriceLevelID: string;
}

