import
{
  ChangeDetectionStrategy, ChangeDetectorRef,
  Component, EventEmitter, Input,
  OnDestroy, OnInit, Output
} from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { IFormBuilder } from '@rxweb/types';
import { ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { CartTypesID, ManageTeeTimePlayerBookingInput, NumPlayers } from '../../../@intermediate/models/get-booking-details.model';
import { BlockingType, BookingType, CourseType, Holes1or10, PlayerStatus, PriceLevel, StartTeeHole, TeeTimeHoles } from '../../../@intermediate/models/get-tee-sheet.model';
import { ManageTeeTimeScreenSizeEnum } from '../../../@intermediate/models/manage-tee-time.model';
import { SmallGroupForm } from '../../../@intermediate/models/player-row.model';
import { PriceBreakdownDialogDataModel, PriceForPriceBreakdown } from '../../../@intermediate/models/price-breakdown.model';
import { ManageTeeTimeHelperService } from '../../../@services/manage-tee-time.helper.service';

@Component({
  selector: 'tei-pos-tee-sheet-manage-small-group-players',
  templateUrl: './manage-small-group-players.component.html',
  styleUrls: ['./manage-small-group-players.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ManageSmallGroupPlayersComponent implements OnInit, OnDestroy
{
  playersFormSubmittedValue: boolean;
  @Input() Booking: ManageTeeTimePlayerBookingInput;
  @Input() priceLevels: PriceLevel[];
  @Input() courseID: string;
  @Input() CourseType: CourseType;
  @Input() startTeeHole: StartTeeHole | undefined;
  @Input() CartRequired: boolean;
  @Input() PowerCartRequired: boolean;
  @Input() HasAttachments: boolean;
  @Input() set playersFormSubmitted(value: boolean)
  {
    this.playersFormSubmittedValue = value;
  }


  @Output() PlayersForm: EventEmitter<UntypedFormGroup> = new EventEmitter();
  @Output() playersFormChanged: EventEmitter<boolean> = new EventEmitter();
  @Output() onSaveClick: EventEmitter<boolean> = new EventEmitter();

  get playersFormSubmitted(): boolean
  {
    return this.playersFormSubmittedValue;
  }

  get teeTime(): UntypedFormArray
  {
    return (this.playersForm.controls["TeeTimesList"] as UntypedFormGroup).controls["teeTime"] as UntypedFormArray;
  }

  isLoadingGetPlayerPrice: boolean;
  isLoading: boolean;
  screenSize: number;
  MinimumOfPlayers = 1;
  BlockingTypeEnum = BlockingType;
  formBuilder: IFormBuilder;
  playersForm: UntypedFormGroup;
  groupTotalPriceForBreakDown: PriceBreakdownDialogDataModel;
  isPlayerPriceBreakdownDialogOpen: { [key: number]: boolean; } = {};
  teeTimeHoles = TeeTimeHoles;
  NumPlayers = NumPlayers;
  StartTee = Holes1or10;
  playerStatus = PlayerStatus;
  cartTypesID = CartTypesID;
  bookingTypeEnum = BookingType;
  availablePlayers: number;
  courseType = CourseType;
  allPlayersStatus: PlayerStatus = PlayerStatus.None;
  ContactLimit = 4;
  screenSizeEnum = ManageTeeTimeScreenSizeEnum;
  isGroupTotalPriceBreakdownDialogOpen = false;
  playerTotalPriceForBreakDown: PriceBreakdownDialogDataModel;
  playersFormValueChangesSubscription: Subscription;
  mediaBreakpointSubscription: Subscription;

  constructor(
    private manageTeeTimeHelper: ManageTeeTimeHelperService,
    private fb: UntypedFormBuilder,
    private translate: TranslateService,
    private changeDetectorRef: ChangeDetectorRef,
    private responsiveService: ResponsiveService
  )
  {
    this.formBuilder = fb;
  }

  ngOnInit(): void
  {
    this.createPlayerForm();
    this.createForm();
    this.ContactLimit = this.Booking?.isFivePlayer ? 5 : 4;
    this.detectMobileScreen();
  }

  private createPlayerForm(): void
  {
    this.playersForm = this.formBuilder.group<SmallGroupForm>({
      Players: this.Booking.smallGroupBookingDetails.NumPlayers,
      SmallGroupName: this.Booking.smallGroupBookingDetails.SmallGroupName,
      BookingID: this.Booking.smallGroupBookingDetails.BookingID,
      GroupPrice: this.manageTeeTimeHelper.transformBookingDetailsGroupForPriceBreakdown(this.Booking.smallGroupBookingDetails),
      Notes: this.Booking.smallGroupBookingDetails.OnlineNotes,
      TeeTimesList: this.fb.group({
        teeTime: this.fb.array([]),
      })
    });
  }

  createForm(): void
  {
    this.Booking.smallGroupBookingDetails.SmallGroupTeeTimes.forEach(() =>
    {
      this.addTeeTime();
    });
  }

  addTeeTime(): void
  {
    const control = (this.playersForm.controls["TeeTimesList"] as UntypedFormGroup).controls["teeTime"] as UntypedFormArray;
    const form: UntypedFormGroup = this.fb.group({
      StartHoleNumber: '',
      panelOpenState: false,
      playersList: this.fb.group({
        player: this.fb.array([])
      })
    });
    control.push(form);
  }

  private detectMobileScreen(): void
  {
    this.mediaBreakpointSubscription = this.responsiveService.screenWidth.subscribe(screenSize =>
    {
      this.screenSize = screenSize;
      this.changeDetectorRef.detectChanges();
    });
  }

  onlyNumber(event):boolean
  {
    return (event.charCode === 8 || event.charCode === 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  getPriceLevelByID(PriceLevelID: string): PriceLevel
  {
    return this.priceLevels.find(priceLevel => priceLevel.PriceLevelID === PriceLevelID);
  }

  openGroupTotalPriceBreakdownDialog(): void
  {
    const groupPlayerPrice: PriceForPriceBreakdown = this.playersForm.controls.GroupPrice.value;

    const title = this.translate.instant('POS_TEE_SHEET.PRICE_BREAKDOWN.GROUP_PRICE_BREAKDOWN');

    this.groupTotalPriceForBreakDown = this.manageTeeTimeHelper.convertPriceToPriceBreakdownDialogDataModel(groupPlayerPrice, title);

    this.isGroupTotalPriceBreakdownDialogOpen = true;
  }

  closeGroupTotalPriceBreakdownDialog(): void
  {
    this.isGroupTotalPriceBreakdownDialogOpen = false;
  }

  ngOnDestroy(): void
  {
    this.mediaBreakpointSubscription?.unsubscribe();
  }

  getTeeTimeFrom(teeTimeIndex: number): UntypedFormGroup
  {
    return this.teeTime?.controls[teeTimeIndex] as UntypedFormGroup;
  }
}



