import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { AppIdentifierService, ManageLoadingService, ManageStandAloneLoadingService, ModelDrivenFormGroupControlValueAccessor, ResponsiveService, StorageService, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { UserPosPreferenceKey, UserPosPreferenceOptions, UserPosPreferenceService, teeSheetCacheName } from '@tei/pos-shared';
import moment from 'moment';
import { Subscription, firstValueFrom } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { IMainService } from '../../@intermediate/main.interface';
import { GetModifiedTeeSheetRequest, GetModifiedTeeSheetResponse } from '../../@intermediate/models/get-modified-tee-sheet.model';
import { GetClassesAndGroupsRequest, GetClassesAndGroupsResponse, RestrictionType } from '../../@intermediate/models/get-tee-sheet-classes-groups.model';
import { GetTeeSheetMainCoursesRequest } from '../../@intermediate/models/get-tee-sheet-main-courses.model';
import { Course, MultiViewGroup, TeeSheetViewType } from '../../@intermediate/models/get-tee-sheet-main.model';
import { GroupBookingSelectedTeeTime, TeeSheet } from '../../@intermediate/models/get-tee-sheet.model';
import { PostModifiedTeeSheetCourseCrossoversRequest, PostModifiedTeeSheetCourseCrossoversResponse } from '../../@intermediate/models/post-modified-tee-sheet-course-cross-overs.model';
import { PostModifiedTeeSheetRestrictionsRequest, PostModifiedTeeSheetRestrictionsResponse } from '../../@intermediate/models/post-modified-tee-sheet-restrictions.model';
import { PostModifiedTeeSheetRequest, PostModifiedTeeSheetResponse } from '../../@intermediate/models/post-modified-tee-sheet.model';
import { PostTeeSheetClassRestrictionsRequest, PostTeeSheetClassRestrictionsResponse } from '../../@intermediate/models/post-tee-sheet-class-restrictions.mode';
import { PostTeeSheetRebuiltRequest } from '../../@intermediate/models/post-tee-sheet-rebuild.models';
import { AdvancedSearchTeeTime, TeeSheetAdvancedSearchResult } from '../../@intermediate/models/post-tee-time-advance-search.model';
import { TeeSheetFilterView } from '../../@intermediate/models/tee-sheet-filter-view.model';
import { TeeSheetMessageDialogData } from '../../@intermediate/models/tee-sheet-message.model';
import { ColumnType, ColumnViewType, Layers, LayersType, SelectType, TeeSheetSelectedView } from '../../@intermediate/models/tee-sheet-toolbar-model';
import { ITeeSheetService } from '../../@intermediate/tee-sheet.interface';
import { SmallGroupBookingHelperService } from '../../@services/small-group-booking-helper.service';
import { TeeSheetCourseCrossoversService } from '../../@services/tee-sheet-course-crossovers.service';
import { TeeSheetFilterViewService } from '../../@services/tee-sheet-filter-view.service';
import { TeeSheetMainService } from '../../@services/tee-sheet-main.service';
import { TeeSheetRestrictionsService } from '../../@services/tee-sheet-restrictions.service';
import { TeeSheetTemplateService } from '../../@services/tee-sheet-template.service';
import { TeeSheetDisplayComponent } from '../tee-sheet-display/tee-sheet-display.component';
import { TeeSheetRestrictionsComponent } from '../tee-sheet-restrictions/tee-sheet-restrictions.component';
import { TeeSheetTemplateComponent } from '../tee-sheet-template/tee-sheet-template.component';
import { TeeSheetWaitListComponent } from '../tee-sheet-wait-list/tee-sheet-wait-list.component';
import { RebuildTeeSheetDialogComponent } from './rebuild-tee-sheet-dialog/rebuild-tee-sheet-dialog.component';
import { TeeSheetMessageComponent } from './tee-sheet-message/tee-sheet-message.component';
import { TeeTimeBookingAdvancedSearchComponent } from './tee-time-booking-advanced-search/tee-time-booking-advanced-search.component';
import { DisplayTeeSheetDialogData, GetDisplayDataRequest, GetDisplayDataResponse } from '../../@intermediate/models/get-teesheet-display.model';
import { TeeSheetDisplayService } from '../../@services/tee-sheet-display.service';



@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-toolbar',
  templateUrl: './tee-sheet-toolbar.component.html',
  styleUrls: ['./tee-sheet-toolbar.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(TeeSheetToolbarComponent)
  ]
})
export class TeeSheetToolbarComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit, OnDestroy
{
  @Input() isCollapse = false;
  @Input() multiViewGroups: MultiViewGroup[] = [];
  @Input() enableMultiViewGroups: boolean;
  @Input() courses = new Array<Course>(0);
  @Input() teeSheetViewType: TeeSheetViewType;
  @Input() layers: Layers;

  get teeSheetArray(): TeeSheet[]
  {
    return this.teeSheetMainService.teeSheetArray;
  }

  @Input() set selectedCheckTypeInput(value: SelectType)
  {
    this.selectedCheckType = value;
  }
  @Output() onSelectCollapseButton = new EventEmitter();
  @Output() onSelectChecks = new EventEmitter();
  @Output() selectedViewType = new EventEmitter();

  @Output() transferTeeSheet = new EventEmitter<TeeSheetAdvancedSearchResult>();
  @Output() onLayersChanged = new EventEmitter<Layers>();
  @Output() print = new EventEmitter<void>();

  mediaBreakpointSubscription: Subscription;
  currentTeeSheetSubscription: Subscription = new Subscription();
  columns: ColumnViewType[] = [];
  columnType = ColumnType;
  columnNumber = 1;
  form: UntypedFormGroup;
  layersForm: UntypedFormGroup;
  teeSheetSelectedView: TeeSheetSelectedView = new TeeSheetSelectedView();
  SelectType = SelectType;
  selectedCheckType: SelectType;
  layersType = LayersType;
  teeSheetViewTypeEnum = TeeSheetViewType;
  teeSheetTemplateSubscription: Subscription;
  teeSheetFilterViewSubscription: Subscription;
  teeSheetRestrictionsSubscription: Subscription;
  teeSheetCourseCrossoversSubscription: Subscription;
  isMobileMode = false;

  constructor(
    private storageService: StorageService,
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    @Inject('IMainService') private mainService: IMainService,
    @Inject('ITeeSheetService') private teeSheetService: ITeeSheetService,
    public manageLoadingService: ManageLoadingService,
    private responsiveService: ResponsiveService,
    private formBuilder: UntypedFormBuilder,
    private dialog: MatDialog,
    private teeSheetTemplateService: TeeSheetTemplateService,
    public snackBarService: SnackBarService,
    private teeSheetRestrictionsService: TeeSheetRestrictionsService,
    private teeSheetFilterViewService: TeeSheetFilterViewService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private teeSheetCourseCrossoversService: TeeSheetCourseCrossoversService,
    private translate: TranslateService,
    private userPosPreferenceService: UserPosPreferenceService,
    private appIdentifierService: AppIdentifierService,
    private teeSheetMainService: TeeSheetMainService,
    private smallGroupBookingHelperService: SmallGroupBookingHelperService,
    public teeSheetDisplayService: TeeSheetDisplayService,
  )
  {
    super(errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.createForms();
    this.layersFormChangeDetection();
    this.detectMobileScreen();

    if (this.storageService.HasKey(teeSheetCacheName))
    {
      this.setCache();
    }
  }

  private loadCollapsePreference(): void
  {
    const currentAppSession = this.appIdentifierService.getAppSession();
    const collapsePreference = currentAppSession?.PosPreferences?.find(p => p.Identifier === UserPosPreferenceKey.TeeSheetToolbarCollapseState);

    if (collapsePreference)
    {
      this.isCollapse = collapsePreference.Value === UserPosPreferenceOptions.Collapsed;
      this.onSelectCollapseButton.emit(this.isCollapse);
    }
  }

  private async saveCollapsePreference(isCollapse: boolean): Promise<void>
  {

    if (!this.isMobileMode) return;
    const preferenceValue = isCollapse ? UserPosPreferenceOptions.Collapsed : UserPosPreferenceOptions.Expanded;
    try
    {
      await this.userPosPreferenceService.UpdatePreferenceAndSaveSession(UserPosPreferenceKey.TeeSheetToolbarCollapseState, preferenceValue);
    } catch (error)
    {
      console.log('Error saving collapse preference:', error);
    }
  }

  private createForms(): void
  {
    this.form = this.formBuilder.group({
      selectedType: ['0'],
      multiViewGroup: ['None']
    });
    this.layersForm = this.formBuilder.group({
      type: [this.layers?.type],
      contactClass: [this.layers?.contactClass]
    });
  }

  printTeeTimes()
  {
    this.print.emit();
  }

  layersFormChangeDetection(): void
  {
    this.currentTeeSheetSubscription.add(this.layersForm.valueChanges.subscribe(changes =>
    {
      this.onLayersChanged.emit(changes);
    }));
  }

  setCache(): void
  {
    this.teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));

    if (this.teeSheetSelectedView?.ColumnType?.toString())
    {
      this.form?.controls.selectedType?.setValue(this.teeSheetSelectedView?.ColumnType?.toString());
    } else
    {
      this.form?.controls.selectedType?.setValue('0');
    }

    if (this.teeSheetSelectedView.ColumnType == this.columnType.Single)
    {
      this.teeSheetSelectedView.ColumnNumber = 1;
      this.enableDisableColumn(this.teeSheetSelectedView.ColumnNumber);
    } else
    {
      this.enableDisableColumn(this.teeSheetSelectedView.ColumnNumber);
    }
    if (this.teeSheetSelectedView.SelectedGroupID)
    {
      this.form?.controls.multiViewGroup?.setValue(this.teeSheetSelectedView.SelectedGroupID.toString());
    }
  }

  private detectMobileScreen(): void
  {
    this.mediaBreakpointSubscription = this.responsiveService.screenWidth.subscribe(screenSize =>
    {
      this.isMobileMode = screenSize < 992;
      switch (true)
      {
        case (screenSize > 1900):
          this.columns = [{ ColumnNumber: 1, Disabled: false },
          { ColumnNumber: 2, Disabled: true },
          { ColumnNumber: 3, Disabled: true }
          ];
          break;
        case (screenSize > 992 && screenSize < 1900):
          this.columns = [{ ColumnNumber: 1, Disabled: false },
          { ColumnNumber: 2, Disabled: true },
          ];
          break;
        case (screenSize < 992):
          this.columns = [{ ColumnNumber: 1, Disabled: false },
          ];
          break;
      }
      if (this.isMobileMode)
      {
        this.loadCollapsePreference();
      }
    });
  }

  onCollapseButtonClick(): void
  {


    this.isCollapse = !this.isCollapse;
    if (this.isMobileMode)
    {
      this.saveCollapsePreference(this.isCollapse);
    }
    this.onSelectCollapseButton.emit(this.isCollapse);
  }

  enableDisableColumn(columnNumber: number): void
  {
    this.columnNumber = columnNumber;
    const item = this.columns.find(x => x.ColumnNumber === columnNumber);
    if (!item?.Disabled)
    {
      return;
    } else
    {
      item.Disabled = !item.Disabled;
    }
    this.columns.forEach(column =>
    {
      if (column.ColumnNumber !== item.ColumnNumber)
      {
        column.Disabled = true;
      }
    });
  }

  selectMultiViewGroups(groupID: string): void
  {
    if (groupID === 'All_Courses')
    {
      this.teeSheetSelectedView.SelectedGroupID = 'All_Courses';
    } else
    {
      this.teeSheetSelectedView.SelectedGroupID = groupID;
    }
    if (this.teeSheetSelectedView.SelectedGroupID !== 'All_Courses')
    {
      this.teeSheetSelectedView.TeeSheetViewSettings = [];
    }
    this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
  }

  enableSelection(type: SelectType)
  {
    this.selectedCheckType = type;
    this.onSelectChecks.emit(type);
    this.resetGroupMultiSelection();
  }

  resetGroupMultiSelection(): void
  {
    this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimesList([]);
    this.smallGroupBookingHelperService.SmallGroupBookingSelectedTimes = [];
    this.smallGroupBookingHelperService.SelectedExistingGroupsUIDs = [];
    this.smallGroupBookingHelperService.checkedTeeSheetSlot(new GroupBookingSelectedTeeTime());
  }

  Submit(): void
  {
    this.updateSelectedView();
    this.selectedViewType.emit(this.teeSheetSelectedView);
  }

  private updateSelectedView(): void
  {
    if (this.storageService.HasKey(teeSheetCacheName))
    {
      this.teeSheetSelectedView.ColumnNumber = this.columnNumber;
      this.teeSheetSelectedView.ColumnType = +this.form.get('selectedType').value;
      this.storageService.Set(teeSheetCacheName, this.teeSheetSelectedView);
    }
    this.teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));
    this.teeSheetSelectedView.ColumnNumber = this.columnNumber;
    this.teeSheetSelectedView.ColumnType = this.form.controls.selectedType.value.toString();
    this.teeSheetSelectedView.CurrentTeeSheetPage = 1;
  }

  closeDropDown(): void
  {
    this.setCache();
  }

  onRefreshClick(): void
  {
    if (this.storageService.HasKey(teeSheetCacheName))
    {
      this.teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));
      this.selectedViewType.emit(this.teeSheetSelectedView);
    }

  }

  ngOnDestroy(): void
  {
    this.currentTeeSheetSubscription?.unsubscribe();
    this.mediaBreakpointSubscription?.unsubscribe();
    this.dialog.closeAll();
  }

  openSendMessageDialog(): void
  {
    if (this.storageService.HasKey(teeSheetCacheName))
    {
      const selectedCourseName = this.storageService.Get(teeSheetCacheName)['TeeSheetViewSettings'][0].CourseName;
      const CourseID = this.courses.find(course => course.CourseName === selectedCourseName).CourseID;
      const dialogData: TeeSheetMessageDialogData = {
        CourseID: CourseID,
        teeSheetArray: this.teeSheetArray
      };
      const dialog = this.dialog.open(TeeSheetMessageComponent, {
        panelClass: ['tee-sheet-message-dialog', 'o-dialog-sm-fullscreen'],
        width: '500px',
        data: dialogData
      });
    }
  }

  async openSearchDialog(): Promise<void>
  {
    const request: GetTeeSheetMainCoursesRequest = {
    };

    this.manageLoadingService.startLoading();
    const response = await firstValueFrom(this.mainService.Courses(request).pipe(finalize(() => this.manageLoadingService.finishLoading())));

    if (response?.Courses)
    {
      const dialog = this.dialog.open(TeeTimeBookingAdvancedSearchComponent, {
        panelClass: ['tee-time-booking-advanced-search-dialog', 'o-dialog-sm-fullscreen'],
        width: '1000px',
        data: {
          courses: response.Courses
        }
      });
      dialog.afterClosed().subscribe((result: AdvancedSearchTeeTime) =>
      {
        if (result)
        {
          this.form.controls.selectedType.setValue(0);
          this.updateSelectedView();
          this.transferTeeSheet.emit(
            { teeSheetSelectedView: this.teeSheetSelectedView, teeTime: result } as TeeSheetAdvancedSearchResult);
        }

      });
    }

  }

  async openTemplateTeeSheet(): Promise<void>
  {
    await this.getModifiedTeeSheet().then(res =>
    {
      this.openTemplateDialog();
    });
  }

  async getModifiedTeeSheet(): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      this.teeSheetSelectedView = this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName);
      const startIndex = this.teeSheetSelectedView.CurrentTeeSheetPage ?
        (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) * this.teeSheetSelectedView.ColumnNumber : 0;

      const teeSheet = this.teeSheetArray[startIndex];
      const request: GetModifiedTeeSheetRequest = {
        CourseID: teeSheet.SelectedCourse.CourseID,
        SheetDate: moment(teeSheet.SelectedTeeSheetDate).format('YYYY-MM-DD'),
      };
      const response: GetModifiedTeeSheetResponse = await firstValueFrom(this.teeSheetService.ModifiedTeeSheet(request));
      const data = {
        ...response,
        SelectedCourseName: teeSheet.SelectedCourse.CourseName,
        SelectedCourseID: teeSheet.SelectedCourse.CourseID,
        SelectedTeeSheetDate: teeSheet.SelectedTeeSheetDate
      };
      this.teeSheetTemplateService.load(data);
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  openTemplateDialog(): void
  {
    const dialog = this.dialog.open(TeeSheetTemplateComponent, {
      width: '85vw',
      panelClass: ['a-teesheet-template-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
    });

    this.teeSheetTemplateSubscription = new Subscription();
    this.teeSheetCourseCrossoversSubscription = new Subscription();

    this.teeSheetTemplateSubscription.add(this.teeSheetTemplateService.reset$.subscribe(async value =>
    {
      if (value)
        await this.getModifiedTeeSheet();
    }));

    this.teeSheetTemplateSubscription.add(this.teeSheetTemplateService.save$.subscribe(async request =>
    {
      if (request)
      {
        await this.saveModifiedTeeSheet(request);
      }
    }));

    this.teeSheetCourseCrossoversSubscription.add(this.teeSheetCourseCrossoversService.save$.subscribe(async (request: PostModifiedTeeSheetCourseCrossoversRequest) =>
    {
      this.saveModifiedCourseCrossovers(request);
    }));

    dialog.afterClosed().subscribe(value =>
    {
      this.teeSheetTemplateSubscription.unsubscribe();
      this.teeSheetCourseCrossoversSubscription.unsubscribe();
    });
  }

  async openDisplayTeeSheet(): Promise<void>
  {
    // await this.getModifiedTeeSheet().then(res =>
    // {
    this.openDisplayDialog();
    // });
  }

  async openDisplayDialog(): Promise<void> {
    this.teeSheetSelectedView = this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName);

    const startIndex = this.teeSheetSelectedView.CurrentTeeSheetPage
      ? (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) * this.teeSheetSelectedView.ColumnNumber
      : 0;

    const teeSheet = this.teeSheetArray[startIndex];
    const selectedCourse = teeSheet.SelectedCourse;
    const selectedDate = moment(teeSheet.SelectedTeeSheetDate).format('YYYY-MM-DD');

    const request: GetDisplayDataRequest = {
      CourseID: selectedCourse?.CourseID,
      SheetDate:selectedDate,
    };
    const data: DisplayTeeSheetDialogData = { courses: this.courses, selectedCourse:selectedCourse, selectedDate: selectedDate }
    await this.teeSheetDisplayService.load(request);
    this.teeSheetDisplayService.setDisplayDialogData(data);
    this.dialog.open(TeeSheetDisplayComponent, {
      width: '85vw',
      panelClass: ['a-teesheet-display-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen']

    });

  }

  async saveModifiedTeeSheet(requestValue): Promise<void>
  {
    try
    {
      const request: PostModifiedTeeSheetRequest = {
        CourseID: requestValue.SelectedCourseID,
        SheetDate: moment(requestValue.SelectedTeeSheetDate).format('YYYY-MM-DD'),
        TemplateID: requestValue.TemplateID,
        FivePlayerTemplate: requestValue.FivePlayerTemplate,
        TsTemplateUniqueID: requestValue.TSTemplateUniqueID,
        ModifiedTSItems: requestValue.ModifiedTSItems
      };
      this.manageStandAloneLoadingService.startLoading();
      const response: PostModifiedTeeSheetResponse = await firstValueFrom(this.teeSheetService.PostModifiedTeeSheet(request));
      this.snackBarService.showSuccess(response.Message, '', null, 'left');

      this.teeSheetMainService.ReloadAllCurrentTeeSheet();
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  openTeeSheetRestrictions(): void
  {
    this.getTeeSheetRestrictions().then(() =>
    {
      this.openTeeSheetRestrictionsDialog();
    });
  }

  async openRebuildTeeSheet(): Promise<void>
  {


    const dialogRef = this.dialog.open(RebuildTeeSheetDialogComponent, {
      panelClass: 'o-confirm-dialog',
      width: '334px'
    });


    const result = await firstValueFrom(dialogRef.afterClosed());

    if (result)
    {
      try
      {

        this.teeSheetSelectedView = this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName);
        const request: PostTeeSheetRebuiltRequest = new PostTeeSheetRebuiltRequest();
        request.TeeSheetRebuiltRequest = [];

        const startIndex = this.teeSheetSelectedView.CurrentTeeSheetPage ?
          (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) * this.teeSheetSelectedView.ColumnNumber : 0;


        const endIndex = startIndex + this.teeSheetSelectedView.ColumnNumber > this.teeSheetArray.length - 1
          ? (this.teeSheetArray.length == 1 ? 1 : - 1)
          : startIndex + this.teeSheetSelectedView.ColumnNumber;

        for (let i = startIndex; i < endIndex; i++)
        {
          request.TeeSheetRebuiltRequest.push(
            {
              CourseID: this.teeSheetArray[i].SelectedCourse.CourseID,
              TeeSheetDate: moment(this.teeSheetArray[i].SelectedTeeSheetDate).format('YYYY-MM-DD'),
              StartTeeHole: this.teeSheetArray[i].StartTeeHole
            }
          );
        }
        this.manageStandAloneLoadingService.startLoading();
        await firstValueFrom(this.teeSheetService.PostRebuildTeeSheet(request));
        this.onRefreshClick();
        this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.REBUILD_TEE_SHEET.REBUILD_SUCCESS'), '', null, 'left');
      }
      catch (error)
      {
        console.log(error);
      }
      finally
      {
        this.manageStandAloneLoadingService.finishLoading();
      }
    }
  }


  async getTeeSheetRestrictions(classAndGroupUIDs: string[] = []): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      this.teeSheetSelectedView = this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName);
      const startIndex = this.teeSheetSelectedView.CurrentTeeSheetPage ?
        (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) * this.teeSheetSelectedView.ColumnNumber : 0;

      const teeSheet = this.teeSheetArray[startIndex];
      const request: PostTeeSheetClassRestrictionsRequest = {
        CourseID: teeSheet.SelectedCourse.CourseID,
        SheetDate: moment(teeSheet.SelectedTeeSheetDate).format('YYYY-MM-DD'),
        ClassAndGroupUIDs: classAndGroupUIDs
      };
      const response: PostTeeSheetClassRestrictionsResponse = await firstValueFrom(this.teeSheetService.GetTeeSheetClassRestrictions(request));
      const data = {
        ...response,
        SelectedCourseName: teeSheet.SelectedCourse.CourseName,
        SelectedCourseID: teeSheet.SelectedCourse.CourseID,
        SelectedTeeSheetDate: teeSheet.SelectedTeeSheetDate
      };
      this.teeSheetRestrictionsService.load(data);
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  async restrictionsFilterViewSearch(searchText: string): Promise<void>
  {
    try
    {
      this.teeSheetSelectedView = this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName);
      const startIndex = this.teeSheetSelectedView.CurrentTeeSheetPage ?
        (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) * this.teeSheetSelectedView.ColumnNumber : 0;
      const teeSheet = this.teeSheetArray[startIndex];
      const request: GetClassesAndGroupsRequest = {
        CourseID: teeSheet.SelectedCourse.CourseID,
        SearchText: searchText
      };
      const response: GetClassesAndGroupsResponse = await firstValueFrom(this.teeSheetService.GetClassesAndGroups(request));
      this.teeSheetFilterViewService.load(this.convertToFilterView(response));
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  convertToFilterView(data: GetClassesAndGroupsResponse): TeeSheetFilterView[]
  {
    const convertedData: TeeSheetFilterView[] = [];
    data.Items.map((restriction, index) =>
    {
      convertedData.push({
        Title: RestrictionType[restriction.RestrictionType],
        ID: null,
        Items: [],
        ShowChildren: restriction.RestrictionType !== RestrictionType.Public && restriction.RestrictionType !== RestrictionType.Web
      });
      restriction.Items.map(item =>
      {
        convertedData[index].Items.push({
          Title: item.Description,
          ID: item.ClassUID,
          Selected: restriction.RestrictionType === RestrictionType.Public || restriction.RestrictionType === RestrictionType.Web
        });
      });
    });
    return convertedData;
  }

  openTeeSheetRestrictionsDialog(): void
  {
    const dialog = this.dialog.open(TeeSheetRestrictionsComponent, {
      width: '85vw',
      panelClass: ['a-teesheet-restrictions-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
    });

    this.teeSheetFilterViewSubscription = new Subscription();
    this.teeSheetRestrictionsSubscription = new Subscription();

    this.teeSheetFilterViewSubscription.add(this.teeSheetFilterViewService.filterSearch$.subscribe(async value =>
    {
      await this.restrictionsFilterViewSearch(value);
    }));

    this.teeSheetRestrictionsSubscription.add(this.teeSheetRestrictionsService.save$.subscribe(async value =>
    {
      await this.saveModifiedTeeSheetRestrictions(value);
    }));

    this.teeSheetRestrictionsSubscription.add(this.teeSheetRestrictionsService.reset$.pipe().subscribe(async reset =>
    {
      if (reset)
      {
        await this.getTeeSheetRestrictions([]);
      }
    }));

    dialog.afterClosed().subscribe(() =>
    {
      this.teeSheetFilterViewSubscription.unsubscribe();
      this.teeSheetRestrictionsSubscription.unsubscribe();
      this.teeSheetFilterViewService.destroy();
    });
  }

  async saveModifiedTeeSheetRestrictions(requestValue): Promise<void>
  {
    try
    {
      const request: PostModifiedTeeSheetRestrictionsRequest = {
        CourseID: requestValue.SelectedCourseID,
        SheetDate: moment(requestValue.SelectedTeeSheetDate).format('YYYY-MM-DD'),
        IgnoreClassRestrictions: requestValue.IgnoreClassRestrictions,
        TSModifiedClassItems: requestValue.ModifiedTSItems
      };
      this.manageStandAloneLoadingService.startLoading();
      const response: PostModifiedTeeSheetRestrictionsResponse = await firstValueFrom(this.teeSheetService.PostModifiedTeeSheetRestrictions(request));
      this.snackBarService.showSuccess(response.Message, '', null, 'left');

      this.teeSheetMainService.ReloadAllCurrentTeeSheet();

    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  };

  async saveModifiedCourseCrossovers(requestValue: PostModifiedTeeSheetCourseCrossoversRequest): Promise<void>
  {
    try
    {
      const request: PostModifiedTeeSheetCourseCrossoversRequest = {
        CourseID: requestValue.CourseID,
        SheetDate: requestValue.SheetDate,
        CrossoversItems: requestValue.CrossoversItems
      };
      this.manageStandAloneLoadingService.startLoading();
      const response: PostModifiedTeeSheetCourseCrossoversResponse = await firstValueFrom(this.teeSheetService.PostModifiedTeeSheetCourseCrossovers(request));
      this.snackBarService.showSuccess(response.Message, '', null, 'left');

      this.teeSheetMainService.ReloadAllCurrentTeeSheet();

    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  openWaitListDialog(): void
  {
    const startIndex = this.teeSheetSelectedView.CurrentTeeSheetPage ?
      (this.teeSheetSelectedView.CurrentTeeSheetPage - 1) * this.teeSheetSelectedView.ColumnNumber : 0;
    const teeSheet = this.teeSheetArray[startIndex];
    this.courses.map(item =>
    {
      item['UniqueID'] = {
        CourseID: item.CourseID,
        StartTeeHole: item.StartTeeHole ? item.StartTeeHole : 0
      };
    });
    const dialog = this.dialog.open(TeeSheetWaitListComponent, {
      width: '100%',
      height: '100vh',
      maxWidth: 'unset',
      panelClass: ['a-teesheet-waitlist-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
      data: {
        Courses: this.courses,
        SelectedDateAndTime: this.teeSheetArray[0].SelectedTeeSheetDate,
        SelectedCourse: teeSheet.SelectedCourse
      }
    });
  }
}
