import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatRippleModule } from '@angular/material/core';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyProgressSpinnerModule as MatProgressSpinnerModule } from '@angular/material/legacy-progress-spinner';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { ManageTeeTimeService } from '../../../@services/manage-tee-time.service';
import { TeeTimeAttachmentService } from '../../../@services/tee-time-attachment.service';
import { AttachmentsDialogComponent } from './attachments-dialog.component';
import { AttachmentsItemComponent } from './attachments-item/attachments-item.component';

const BASE_MODULES = [CommonModule];
const TEI_CORE_MODULES = [TeiCurrencyTranslateModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatDialogModule, MatListModule,
  MatButtonModule, MatIconModule, MatRippleModule, MatProgressSpinnerModule];
const TEI_MATERIAL_MODULES = [LoadingModule];
const COMPONENT = [AttachmentsDialogComponent, AttachmentsItemComponent];
const CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENT],
  imports: [
    ...BASE_MODULES,
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...TEI_CORE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENT
  ],
  providers: [
    { provide: 'IManageTeeTimeService', useClass: ManageTeeTimeService },
    { provide: 'ITeeTimeAttachmentService', useClass: TeeTimeAttachmentService }
  ]
})
export class AttachmentsDialogModule { }
