import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DatepickerModule, ExpansionPanelModule } from '@tei/material';
import { TeeSheetStatsModule } from '../tee-sheet-stats/tee-sheet-stats.module';
import { TeeSheetHeaderComponent } from './tee-sheet-header.component';



const MATERIAL_MODULES = [MatDividerModule, MatSelectModule, MatFormFieldModule, MatRippleModule, MatMenuModule, MatButtonModule, MatTooltipModule, ExpansionPanelModule];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MODULES = [];
const TEI_MATERIAL_MODULES = [DatepickerModule, TeiTextTranslatePipeModule];
const COMPONENTS = [TeeSheetHeaderComponent];
const PIPES = [];
const TEESHEET_MODULES = [TeeSheetStatsModule];
@NgModule({
  declarations: [...COMPONENTS, ...PIPES],
  imports: [...MATERIAL_MODULES, ...BASE_MODULES, ...TEI_MATERIAL_MODULES, ...MODULES, ...TEESHEET_MODULES],
  exports: [...COMPONENTS],
  providers: [],
})
export class TeeSheetHeaderModule { }


