export class GetRainCheckSetupRequest
{
  CourseID: string;
  BookingID: number;
}

export class GetRainCheckSetupResponse
{
  CashEQName: string;
  HolePricings: HolePricing[];
  IssueAsCashEQ: boolean;
  RainCheckType: RainCheckTypeEnum;
  ReIssueBalance: boolean;
  RequireManagerPassword: boolean;
  ShowStatusOnRainChecks: boolean;
  PlayerRainchecks: PlayerRainchecks[];
}

export class PlayerRainchecks
{
  RainCheckID: string;
  Issued: boolean;
  BarcodeID: string;
  Redeemed: boolean;
  CourseID: string;
  POSID: string;
  CourseName: string;
  PlayerNumber: number;
}

export class HolePricing
{
  HoleNumber: number;
  GolfValue: number;
  PowerCartValue: number;
  PullCartValue: number;
}

export enum RainCheckTypeEnum
{
  TemplatePrice = 1,
  TemplatePriceWithOverride,
  HoleSetup
}
