import { Course } from './get-tee-sheet-main.model';
import { Booking, CourseType } from './get-tee-sheet.model';

export class TeeSheetTransfer
{
  SelectedCourse?: Course;
  SelectedTeeSheetDate?: Date;
  CourseType?: CourseType;
  RowTeeTime?: string;
  ReloadTeeSheet?: boolean;
  HasAttachments?: boolean;
  moving?: boolean;
  Book?: Booking;
}
export class MultiTeeSheetTransfer
{
  SelectedCourse?: Course;
  SelectedTeeSheetDate?: Date;
  CourseType?: CourseType;
  RowTeeTime?: string;
  ReloadTeeSheet?: boolean;
  HasAttachments?: boolean;
  moving?: boolean;
  Book?: Booking;
  StartTeeHole?: StartTeeHole;
  isMoving?: boolean;
}

export class BookingAction
{
  status: BookingActionEntity;
  Book: Booking;
  currentCourse?: Course;
  postponed?: BookingActionPostPond;
}

export class BookingActionPostPond
{
  status: boolean;
  message: string;
  messageFromAPI?: boolean;
}

export enum BookingActionEntity
{
  NONE,
  COPY,
  MOVE,
  NOTES
}

export enum StartTeeHole
{
  Hole1,
  Hole10
}
