import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PosLoginModule, PosLogoutModule } from '@tei/pos-shared';
import { DefaultLayoutModule } from '@tei/theme';
import { TeeSheetMainModule } from './pages/tee-sheet-main/tee-sheet-main.module';
import { TeeSheetRoutingModule } from './tee-sheet-routing.module';
import { TeeSheetComponent } from './tee-sheet.component';
import { PagesModule } from './pages/pages.module';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MODULES = [DefaultLayoutModule, TeeSheetMainModule,PagesModule];
const TEI_POS_SHARED_MODULES = [PosLoginModule, PosLogoutModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [TeeSheetComponent];
const PIPES = [];

@NgModule({
  imports: [TeeSheetRoutingModule, ...BASE_MODULES, ...MODULES, ...TEI_POS_SHARED_MODULES, ...TEI_MATERIAL_MODULES],
  exports: [...BASE_MODULES, ...PIPES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []
})
export class TeeSheetModule { }
