import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from '@tei/core';
import moment from 'moment';
import { firstValueFrom, Subscription } from 'rxjs';
import { GetModifiedCourseCrossoversRequest, GetModifiedCourseCrossoversResponse } from '../../../@intermediate/models/get-modified-course-cross-overs.model';
import { GetModifiedTeeSheetResponse, TimeZoneAndFlagTemplate } from '../../../@intermediate/models/get-modified-tee-sheet.model';
import { CourseTypeEnum } from '../../../@intermediate/models/get-tee-sheet-template.model';
import { TeeSheetTemplateForm } from '../../../@intermediate/models/get-teesheet-template.model';
import { ITeeSheetService } from '../../../@intermediate/tee-sheet.interface';
import { TeeSheetCourseCrossoversService } from '../../../@services/tee-sheet-course-crossovers.service';
import { TeeSheetTemplateService } from '../../../@services/tee-sheet-template.service';
import { TeeSheetCourseCrossoversComponent } from '../../tee-sheet-course-crossovers/tee-sheet-course-crossovers.component';


@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-template-apply',
  templateUrl: './tee-sheet-template-apply.component.html',
  styleUrls: ['./tee-sheet-template-apply.component.scss']
})
export class TeeSheetTemplateApplyComponent implements OnInit, OnDestroy
{
  @Input() public teeSheetModifiedData: GetModifiedTeeSheetResponse;
  public form: FormGroup<TeeSheetTemplateForm>;
  subscription = new Subscription();
  courseCrossoversSubscription: Subscription;
  public flagTypes: TimeZoneAndFlagTemplate[];
  public timezones: TimeZoneAndFlagTemplate[];
  public courseTypeTitle = '';
  public flagTypeNames: { [key: string]: string; } = {};
  public courseType = CourseTypeEnum;
  constructor(public teeSheetTemplateService: TeeSheetTemplateService,
    public dialog: MatDialog,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject('ITeeSheetService') public teeSheetService: ITeeSheetService,
    public courseCrossoversService: TeeSheetCourseCrossoversService
  ) { }

  ngOnInit(): void
  {
    this.createTeeSheetConfigForm();
  }

  public fillForm(teeSheetModifiedData: GetModifiedTeeSheetResponse): void
  {
    this.form.patchValue({
      TemplateID: teeSheetModifiedData?.TemplateID,
      CourseType: this.teeSheetModifiedData.CourseTypes.find(item => item.ID === teeSheetModifiedData.CourseType).ID,
      StartTime: moment(teeSheetModifiedData?.StartTime).format('LT'),
      Intervals: teeSheetModifiedData?.Intervals,
      EndTime: moment(teeSheetModifiedData?.EndTime).format('LT'),
      FivePlayer: teeSheetModifiedData?.FivePlayerTemplate
    });
    this.courseTypeTitle = this.teeSheetModifiedData.CourseTypes.find(item => item.ID === teeSheetModifiedData.CourseType).Title;
    this.teeSheetModifiedData.TimeZoneAndFlagTemplates.map(item =>
    {
      this.flagTypeNames[item.Name] = item.Title;
    });
    this.subscription.add(this.form.get('FivePlayer').valueChanges.subscribe(value =>
    {
      this.teeSheetTemplateService.apply(this.form.getRawValue());
    }));
    this.form.get('TimeRangeEndIndex').disable();
  }

  public onselectStartTime(): void
  {
    this.form.get('TimeRangeEndIndex').setValue(null);
    this.form.get('TimeRangeEndIndex').enable();
  }

  public createTeeSheetConfigForm(): void
  {
    this.form = new FormGroup<TeeSheetTemplateForm>({
      TemplateID: new FormControl<string>({ value: '', disabled: true }),
      CourseType: new FormControl<number>({ value: 0, disabled: true }),
      StartTime: new FormControl<string>({ value: '', disabled: true }),
      Intervals: new FormControl<string>({ value: '', disabled: true }),
      EndTime: new FormControl<string>({ value: '', disabled: true }),
      FivePlayer: new FormControl<boolean>(false),
      FlagType: new FormControl<string[]>([]),
      Timezone: new FormControl<string>('NONE'),
      TimeRangeStartIndex: new FormControl<number>(null, Validators.required),
      TimeRangeEndIndex: new FormControl<number>(null, Validators.required)
    });
    this.fillForm(this.teeSheetModifiedData);
  }

  public onApply(): void
  {
    this.teeSheetTemplateService.apply(this.form.getRawValue());
  }

  public onReset(): void
  {
    this.teeSheetTemplateService.reset();
  }

  public removeFlagType(flagType: string)
  {
    const selected = this.form.get('FlagType').value as string[];
    this.form.get('FlagType').setValue(selected.filter(item => item !== flagType));
    if (this.form.get('FlagType').value.length === 0 && !this.form.get('Timezone').value)
    {
      this.form.get('TimeRangeStartIndex').disable();
    }
  }

  public get disableApply(): boolean
  {
    return this.form.get('TimeRangeStartIndex').value === null || this.form.get('TimeRangeEndIndex').value === null;
  }

  public openCourseCrossoversDialog(): void
  {
    const dialog = this.dialog.open(TeeSheetCourseCrossoversComponent, {
      width: '85vw',
      panelClass: ['a-course-crossovers-dialog-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
    });

    this.courseCrossoversSubscription = new Subscription();

    this.courseCrossoversSubscription.add(this.courseCrossoversService.reset$.subscribe(async reset =>
    {
      if (reset)
      {
        this.getCourseCrossovers([]);
      }
    }));

    dialog.afterClosed().subscribe(() =>
    {
      this.courseCrossoversSubscription.unsubscribe();
    });
  }

  public openCourseCrossovers(): void
  {
    this.getCourseCrossovers([]).then(() =>
    {
      this.openCourseCrossoversDialog();
    });
  }

  public async getCourseCrossovers(courseFilter: []): Promise<void> 
  {
    try
    {
      const request: GetModifiedCourseCrossoversRequest = {
        CourseID: this.teeSheetModifiedData.SelectedCourseID,
        SheetDate: moment(this.teeSheetModifiedData.SelectedTeeSheetDate).format('YYYY-MM-DD'),
        CourseFilter: courseFilter
      };
      this.manageStandAloneLoadingService.startLoading();
      const response: GetModifiedCourseCrossoversResponse = await firstValueFrom(this.teeSheetService.GetModifiedCourseCrossovers(request));
      this.courseCrossoversService.load({ ...response, TeeSheetTemplate: this.teeSheetModifiedData });
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
