import { Component, Inject, OnDestroy, OnInit } from "@angular/core";
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from "@tei/core";
import { TeeSheetService } from "../../../@services/tee-sheet.service";

@Component({
  selector: 'tei-pos-tee-sheet-rebuild-tee-sheet-dialog',
  templateUrl: './rebuild-tee-sheet-dialog.component.html',
  styleUrls: ['./rebuild-tee-sheet-dialog.component.scss'],

})
export class RebuildTeeSheetDialogComponent implements OnInit, OnDestroy
{
   constructor(
    public dialog: MatDialogRef<RebuildTeeSheetDialogComponent>,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject(MAT_DIALOG_DATA) public data,
    @Inject('ITeeSheetService') private teeSheetService: TeeSheetService
  ) { }

  async ngOnInit():Promise<void>
  {
  }

  onRebuildClick(): void
  {
    this.dialog.close(true);
  }

  onCloseClick(): void
  {
    this.dialog.close();
  }

  ngOnDestroy(): void
  {
  }
}