import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';

import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import moment from 'moment';
import { firstValueFrom, Subscription } from 'rxjs';
import { TeeSheetBookedWaitListMetadata, TeeSheetWaitListMetadata } from '../../@intermediate/models/get-tee-sheet-wait-list.model';
import { PostTeeSheetWaitListRequest, PostTeeSheetWaitListResponse, WaitList, WaitListDialogData, WaitListStatus } from '../../@intermediate/models/post-tee-sheet-wait-list.model';
import { ITeeSheetWaitListService } from '../../@intermediate/tee-sheet-wait-list.interface';
import { PlayersWaitListComponent } from './players-wait-list/players-wait-list.component';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-wait-list',
  templateUrl: './tee-sheet-wait-list.component.html',
  styleUrls: ['./tee-sheet-wait-list.component.scss']
})
export class TeeSheetWaitListComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  isMobile = false;
  emptyFilterMessageState = false;
  totalCount = 0;
  pendingCount = 0;
  bookedCount = 0;
  cancelledCount = 0;
  expiredCount = 0;
  dataSource = [];
  metadata;
  pageNumber = 0;
  pageSize = 10;
  request = new PostTeeSheetWaitListRequest();
  waitListStatus = WaitListStatus;
  noDateSelected = false;

  constructor(private responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<TeeSheetWaitListComponent>,
    @Inject(MAT_LEGACY_DIALOG_DATA) public data: WaitListDialogData,
    public manageLoadingService: ManageStandAloneLoadingService,
    @Inject('ITeeSheetWaitListService') private teeSheetWaitListService: ITeeSheetWaitListService,
    private dialog: MatDialog,
  )
  {
  }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.initialData();
  }

  initialData(): void
  {
    this.request.Status = WaitListStatus.Pending;
    this.metadata = new TeeSheetWaitListMetadata();
    this.request.StartDateTime = moment(this.data.SelectedDateAndTime).format('YYYY-MM-DD') + ' ' + '00:00:00';
    this.request.EndDateTime = moment(this.data.SelectedDateAndTime).format('YYYY-MM-DD') + ' ' + '23:59:59';
    this.request.Courses = [{ CourseID: this.data.SelectedCourse.CourseID, StartTeeHole: this.data.SelectedCourse.StartTeeHole ? this.data.SelectedCourse.StartTeeHole : 0 }];
    this.loadData();
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  changeTab(event: number): void
  {
    this.dataSource = [];
    this.metadata = new TeeSheetWaitListMetadata();
    this.pageNumber = 0;
    switch (event)
    {
      case 0:
        this.request.Status = WaitListStatus.Pending;
        break;
      case 1:
        this.request.Status = WaitListStatus.Booked;
        this.metadata = new TeeSheetBookedWaitListMetadata();
        break;
      case 2:
        this.request.Status = WaitListStatus.Cancelled;
        break;
      case 3:
        this.request.Status = WaitListStatus.Expired;
        break;
    }
    this.loadData();
  }

  loadHandler(data: PostTeeSheetWaitListRequest): void
  {
    if (!data.StartDateTime && !data.EndDateTime)
    {
      this.noDateSelected = true;
      this.totalCount = 0;
      this.pendingCount = 0;
      this.cancelledCount = 0;
      this.bookedCount = 0;
      this.expiredCount = 0;
      return;
    }
    this.request = data;
    this.loadData();
  }

  handlePaginationEvent(event): void
  {
    this.pageNumber = event.pageIndex;
    this.pageSize = event.pageSize;
    this.loadData();
  }

  async loadData(): Promise<void>
  {
    this.noDateSelected = false;
    this.request.PageNumber = this.pageNumber + 1;
    this.request.PageSize = this.pageSize;
    try
    {
      this.manageLoadingService.startLoading();
      const response: PostTeeSheetWaitListResponse = await firstValueFrom(this.teeSheetWaitListService.PostWaitListSearch(this.request));
      this.dataSource = response.WaitLists;
      this.totalCount = response.TotalCount;
      this.pendingCount = response.PendingCount;
      this.cancelledCount = response.CancelledCount;
      this.bookedCount = response.BookedCount;
      this.expiredCount = response.ExpiredCount;
    } catch (error) 
    {

    } finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  openPlayersDialog(waitList: WaitList): void
  {
    const dialog = this.dialog.open(PlayersWaitListComponent, {
      width: '350px',
      panelClass: ['a-teesheet-waitlist-players-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
      data: {
        Players: waitList.PlayerDetails,
        PlayersCount: waitList.Players,
        IsMobile: this.isMobile
      }
    });
  }

  openNoteDialog(note: string): void
  {
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
