import
{
  ChangeDetectorRef, Component,
  EventEmitter, Inject, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, SkipSelf
} from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ModelDrivenFormGroupControlValueAccessor, ResponsiveService, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import moment from 'moment';
import { Subscription, firstValueFrom } from 'rxjs';
import { Course } from '../../@intermediate/models/get-tee-sheet-main.model';
import { BookingType, GroupBookingSelectedTeeTime, GroupTeeSheet, StatisticIndicator } from '../../@intermediate/models/get-tee-sheet.model';
import { NewGroupDialogResult } from '../../@intermediate/models/post-update-tee-time-group.model';
import { PostValidateAndLinkTeeTimeGroupRequest } from '../../@intermediate/models/post-validate-and-link-tee-time-to-group.model';
import { ColumnType, SelectType, TeeSheetSelectedView } from '../../@intermediate/models/tee-sheet-toolbar-model';
import { GroupTeeTimeService } from '../../@services/group-tee-time.service';
import { SmallGroupBookingHelperService } from '../../@services/small-group-booking-helper.service';
import { TeeSheetTransferService } from '../../@services/tee-sheet-transfer.service';
import { NewGroupDialogComponent } from '../tee-sheet-slot/new-group-dialog/new-group-dialog.component';
import { TeeSheetStatsComponent } from '../tee-sheet-stats/tee-sheet-stats.component';


@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-header',
  templateUrl: './tee-sheet-header.component.html',
  styleUrls: ['./tee-sheet-header.component.scss'],
  providers: [ngFormGroupControlValueAccessorProviders(TeeSheetHeaderComponent)],
})
export class TeeSheetHeaderComponent extends ModelDrivenFormGroupControlValueAccessor implements OnChanges, OnInit, OnDestroy
{
  @Input() selectedCourse: Course;
  @Input() courses = new Array<Course>(0);
  @Input() selectType: SelectType;
  @Input() selectedDateTime: Date;
  @Input() statisticIndicator: StatisticIndicator;
  @Input() teeSheetGroups: GroupTeeSheet[];
  @Input() teeSheetSelectedView: TeeSheetSelectedView;
  @Input() FivePlayerTemplate: boolean;
  @Output() selectedDate = new EventEmitter<Date>();
  @Output() changedSelectedCourse = new EventEmitter<Course>();
  @Output() clickSaveChangeAdjustTime = new EventEmitter();
  columnType = ColumnType;
  form: UntypedFormGroup;
  SelectType = SelectType;
  isSmallScreen: boolean = false;
  smallGroupBookingSelectedTimesList: GroupBookingSelectedTeeTime[] = [];
  subscription = new Subscription();
  disabledNewGroup = false;

  constructor(private formBuilder: UntypedFormBuilder,
    private responsiveService: ResponsiveService,
    private changeDetector: ChangeDetectorRef,
    public smallGroupBookingHelperService: SmallGroupBookingHelperService,
    @SkipSelf() protected errorStateMatcher: ErrorStateMatcher,
    private dialog: MatDialog,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject('ITeeSheetTransferService') private teeSheetTransferService: TeeSheetTransferService,
    @Inject('IGroupTeeTimeService') private groupTeeTimeService: GroupTeeTimeService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
  )
  {
    super(errorStateMatcher);
    this.form = this.formBuilder.group({
      selectedCourse: ['']
    });
  }

  ngOnInit(): void
  {
    this.handleScreenSizeChange();
  }

  private handleScreenSizeChange(): void
  {
    this.subscription.add(
      this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
      {
        this.isSmallScreen = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
        this.changeDetector.detectChanges();
      })
    );
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (changes.selectedCourse && changes.selectedCourse.currentValue)
    {
      this.form?.controls.selectedCourse?.setValue(changes.selectedCourse.currentValue.CourseName);
      this.smallGroupBookingHelperService.CourseChanged.next(true);
    }
    if (changes.courses && changes.courses.currentValue)
    {
      this.courses = changes.courses.currentValue;
    }
    this.changeDetector.detectChanges();
  }

  getSelectedDate(date: Date): void
  {
    this.selectedDate.emit(date);
    this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimesList([]);
  }

  changeSelectedCourse(): void
  {
    const selectedCourse = this.courses.find(course => course.CourseName === this.form.controls['selectedCourse'].value);
    this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimesList([]);
    this.changedSelectedCourse.emit(selectedCourse);
  }

  onSaveAdjustTime(): void
  {
    this.clickSaveChangeAdjustTime.emit();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

  openStats(): void
  {
    const dialog = this.dialog.open(TeeSheetStatsComponent, {
      panelClass: ['a-teesheet-stats-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
      maxWidth: "unset",
      data: {
        CourseID: this.selectedCourse.CourseID,
        SheetDate: this.selectedDateTime,
        CourseName: this.selectedCourse.CourseName
      }
    });
  }

  onNewGroup(): void
  {
    const dialog = this.dialog.open(NewGroupDialogComponent, {
      panelClass: ['o-new-group-dialog', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
      width: '400px'
    });

    this.subscription.add(dialog.afterClosed().subscribe((result: NewGroupDialogResult) =>
    {
      if (result)
      {
        this.createNewGroupByDialog(result);
      }
    }));
  }

  async createNewGroupByDialog(data: NewGroupDialogResult): Promise<void>
  {
    const invalidGroup = this.smallGroupBookingHelperService.getSmallGroupBookingSelectedTimesList().find(item =>
      item.Players.length < (this.FivePlayerTemplate ? 5 : 4)
    );

    if (invalidGroup)
    {
      this.snackBarService.showError(this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.CANNOT_GROUP_MSG'), '', null, 'left');
      return;
    }

    const request: PostValidateAndLinkTeeTimeGroupRequest = {
      GroupName: data.groupName,
      PrimaryGroupManagerID: data.contactID,
      CourseID: this.selectedCourse.CourseID,
    };
    request.TeeTimes = [];
    request.TeeTimesUIDs = '';
    const teeTimeIDs: string[] = [];

    this.smallGroupBookingHelperService.getSmallGroupBookingSelectedTimesList().map(item =>
    {
      const hasBlocked = item?.Bookings?.find(b => b.BookingType === BookingType.Blocked);
      if (hasBlocked)
      {
        return;
      }
      let bookingContactCount = 0;
      let playerCount = 0;
      if (item.Course.CourseID === this.selectedCourse.CourseID)
      {
        item.Bookings?.map(booking =>
        {
          booking?.Contacts.map(contact =>
          {
            teeTimeIDs.push(contact.TeeTimeID);
          });
          bookingContactCount += booking.Contacts.length;
        });

        request.TeeTimesUIDs = teeTimeIDs.join(',');

        playerCount = item?.Bookings[0]?.BookingID
          ? item.Players.length - bookingContactCount
          : item.Players.length;

        request.StartTeeHole = item.Course.StartTeeHole;

        if (playerCount > 0)
        {
          request.TeeTimes.push({
            SheetTime: `${ moment(this.selectedDateTime).format('YYYY-MM-DD') }T${ moment(item.Time, 'h:mm A').format('HH:mm') }`,
            Players: playerCount.toString(),
          });
        }
      }
    });

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      if (!request?.TeeTimes?.length && !request?.TeeTimesUIDs?.length)
      {
        this.snackBarService.showError(this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.FAILED_TO_GROUP'), '', null, 'left');
        return;
      }
      const response = await firstValueFrom(
        this.groupTeeTimeService.PostValidateAndLinkTeeTimeToGroup(request)
      );
      const successMessage = this.translate.instant(
        'POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.GROUP_CREATED',
        { groupName: data.groupName }
      );
      this.snackBarService.showSuccess(successMessage, '', null, 'left');
      this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimesList([]);
      this.smallGroupBookingHelperService.SmallGroupBookingSelectedTimes = [];
      this.smallGroupBookingHelperService.SelectedExistingGroupsUIDs = [];
      this.smallGroupBookingHelperService.checkedTeeSheetSlot(new GroupBookingSelectedTeeTime());
      this.reloadTeeSheet();
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  async addToExistingGroup(group: GroupTeeSheet): Promise<void>
  {
    const request = new PostValidateAndLinkTeeTimeGroupRequest();
    request.GroupUID = group.GroupUID;
    request.CourseID = this.selectedCourse.CourseID;
    request.TeeTimes = [];
    request.TeeTimesUIDs = '';
    const teeTimeIDs: string[] = [];

    this.smallGroupBookingHelperService.getSmallGroupBookingSelectedTimesList().map(item =>
    {
      const hasBlocked = item?.Bookings?.find(b => b.BookingType === BookingType.Blocked);
      if (hasBlocked)
      {
        return;
      }

      let bookingContactCount = 0;
      let playerCount = 0;
      if (item.Course.CourseID === this.selectedCourse.CourseID)
      {
        item?.Bookings?.map(booking =>
        {
          booking?.Contacts.map(contact =>
          {
            teeTimeIDs.push(contact.TeeTimeID);
          });
          bookingContactCount += booking.Contacts.length;
        });
        request.TeeTimesUIDs = teeTimeIDs.join(',');
        playerCount = item?.Bookings?.[0]?.BookingID
          ? item.Players.filter(player => !item?.Bookings?.[0]?.Contacts.find(contact => (contact.PlayerNumber - 1) === player))?.length
          : item.Players.length;

        request.StartTeeHole = item.Course.StartTeeHole;

        if (playerCount > 0)
        {
          request.TeeTimes.push({
            SheetTime: `${ moment(this.selectedDateTime).format('YYYY-MM-DD') }T${ moment(item.Time, 'h:mm A').format('HH:mm') }`,
            Players: playerCount.toString(),
          });
        }
      }
    });

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      if (!request?.TeeTimes?.length && !request?.TeeTimesUIDs?.length)
      {
        this.snackBarService.showError(this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.FAILED_TO_GROUP'), '', null, 'left');
        return;
      }
      const response = await firstValueFrom(this.groupTeeTimeService.PostValidateAndLinkTeeTimeToGroup(request));
      const successMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.ADDED_TO_GROUP', {
        groupName: group.GroupName
      });
      this.snackBarService.showSuccess(successMessage, '', null, 'left');
      this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimesList([]);
      this.smallGroupBookingHelperService.SmallGroupBookingSelectedTimes = [];
      this.smallGroupBookingHelperService.SelectedExistingGroupsUIDs = [];
      this.smallGroupBookingHelperService.checkedTeeSheetSlot(new GroupBookingSelectedTeeTime());
      this.reloadTeeSheet();
    }
    catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  reloadTeeSheet(): void
  {
    this.teeSheetTransferService.changeTeeSheet({
      ReloadTeeSheet: true
    });
  }
}
