import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { BookingAction } from '../@intermediate/models/tee-sheet-transfer-service.model';
import { ITeeSheetActionService } from '../@intermediate/tee-sheet-action-service.interface';

export class TeeSheetActionService implements ITeeSheetActionService
{
  private BookingActionTransfer = new BehaviorSubject<BookingAction>(new BookingAction());
  private TeeSheetActionTrigger = new BehaviorSubject<boolean>(false);
  private BookingSelectedTeeTime = new Subject<number>();
  private UpdateTeeSheetConfigsSubject = new Subject<string>();
  setBookingAction(bookingAction: BookingAction): void
  {
    this.BookingActionTransfer.next(bookingAction);
  }

  getBookingAction(): BehaviorSubject<BookingAction>
  {
    return this.BookingActionTransfer;
  }

  getCurrentBookingAction(): BookingAction
  {
    return this.BookingActionTransfer.closed ? null : this.BookingActionTransfer.value;
  }

  setTeeSheetActionStatus(status: boolean): void
  {
    this.TeeSheetActionTrigger.next(status);
  }

  getTeeSheetActionStatus(): BehaviorSubject<boolean>
  {
    return this.TeeSheetActionTrigger;
  }

  getBookingSelectedTeeTime(): Observable<number>
  {
    return this.BookingSelectedTeeTime.asObservable();
  }

  setBookingSelectedTeeTime(bookingId: number): void
  {
    this.BookingSelectedTeeTime.next(bookingId);
  }

  getUpdateTeeSheetConfigsSubject(): Observable<string>
  {
    return this.UpdateTeeSheetConfigsSubject.asObservable();
  }

  setUpdateTeeSheetConfigsSubject(value: string): void
  {
    this.UpdateTeeSheetConfigsSubject.next(value);
  }
}
