import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from '@tei/core';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FormTimePickerModule, LoadingModule } from '@tei/material';
import { TeeSheetFlagComponent } from './tee-sheet-flag.component';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { TeeSheetFlagService } from '../../@services/tee-sheet-flag.service';

const MATERIAL_MODULES = [SafeHtmlPipeModule, MatDividerModule, MatIconModule, MatButtonModule, MatDialogModule, MatSelectModule,MatFormFieldModule];
const TEI_MATERIAL_MODULES = [LoadingModule, FormTimePickerModule];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [TeeSheetFlagComponent];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [...COMMON_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...TEI_CORE_MODULES],
  exports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS,
    ...TEI_MATERIAL_MODULES,
  ],
  providers: [
    ManageStandAloneLoadingService,
    { provide: 'ITeeSheetFlagService', useClass: TeeSheetFlagService },
  ]
})
export class TeeSheetFlagModule { }
