import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PosAuthGuard } from '@tei/pos-shared';
import { PagesComponent } from './pages/pages.component';
import { TeeSheetMainComponent } from './pages/tee-sheet-main/tee-sheet-main.component';
import { TeeSheetComponent } from './tee-sheet.component';

const routes: Routes = [{
  path: '',
  component: TeeSheetComponent,
  children: [
    {
      path: '',
      component: PagesComponent,
      children: [
        {
          path: '',
          component: TeeSheetMainComponent,
          canActivate: [PosAuthGuard],
        },
        {
          path: 'read-only',
          component: TeeSheetMainComponent,
          data: {
            readOnly: true,
          }
        },
      ],
    },
    { path: '**', redirectTo: '' },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TeeSheetRoutingModule
{
}
