import { FormControl } from "@angular/forms";

export class DeleteGroupTeeTimesAndRemoveOrderRequest
{
  RefID?: string;
  HeaderUID: string;
  Action: TeeTimeActionEnum;
  BookingIDs?: Array<number>;
  NotificationEmails?: Array<string>;
  SendNotificationEmailsToAllPlayers?: boolean;
  CCEmails?: Array<string>;
  Reason?: string;
}

export class DeleteGroupTeeTimesAndRemoveOrderResponse
{
  Result: boolean;
}

export enum TeeTimeActionEnum
{
  RemoveGroup,
  RemoveGroupAndCancelBookings,
  CancelBookingsOnly
}

export class DeleteGroupTeeTimesForm
{
  DeleteTypes: FormControl<TeeTimeActionEnum>;
}
