import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { GetModifiedTeeSheetResponse } from '../../@intermediate/models/get-modified-tee-sheet.model';
import { TeeSheetTemplateService } from '../../@services/tee-sheet-template.service';

@Component({
  selector: 'tei-pos-shared-teesheet-template',
  templateUrl: './tee-sheet-template.component.html',
  styleUrls: ['./tee-sheet-template.component.scss']
})
export class TeeSheetTemplateComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  isMobile = false;
  data: GetModifiedTeeSheetResponse;
  constructor(
    private dialogRef: MatDialogRef<TeeSheetTemplateComponent>,
    public responsiveService: ResponsiveService,
    private teeSheetTemplateService: TeeSheetTemplateService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.loadData();
  }

  loadData(): void
  {
    this.subscription.add(this.teeSheetTemplateService.load$.subscribe((value: GetModifiedTeeSheetResponse) =>
    {
      this.manipulateData(value);
    }));
  }

  manipulateData(data: GetModifiedTeeSheetResponse): void
  {
    data.TeeSheetTemplates.map((item, index) =>
    {
      item['Index'] = index;
    });
    this.data = data;
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
