import { Component, EventEmitter, Inject, OnDestroy, Output } from '@angular/core';
import { UntypedFormBuilder } from '@angular/forms';
import { MatLegacyCheckboxChange as MatCheckboxChange } from '@angular/material/legacy-checkbox';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { Subscription } from 'rxjs';
import { ConfirmTeeTimeBookingDialogDataModel, ConfirmTeeTimeBookingDialogModel, ManageTeeTimeConfirmMode } from '../../../@intermediate/models/manage-tee-time.model';

@Component({
  selector: 'tei-pos-tee-sheet-confirm-booking-dialog',
  templateUrl: 'confirm-tee-time-booking-dialog.component.html',
  styleUrls: ['confirm-tee-time-booking-dialog.component.scss']
})
export class ConfirmTeeTimeBookingDialogComponent implements OnDestroy
{
  @Output() dialogSubmitted = new EventEmitter();

  confirmModeEnum = ManageTeeTimeConfirmMode;
  dialogData: ConfirmTeeTimeBookingDialogDataModel;
  form = this.fb.group({
    to: [],
    cc: [],
    emailTemplate: [''],
    cancelationReason: [''],
    allPlayersChecked: [false]
  });
  subscriptions: Subscription = new Subscription();

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: ConfirmTeeTimeBookingDialogModel,
    public dialogRef: MatDialogRef<ConfirmTeeTimeBookingDialogComponent>,
    private fb: UntypedFormBuilder
  )
  {
    this.dialogData = data.dialogData;
    this.form.get('cc').setValue([]);
    const bookerEmailArray = this.dialogData.BookerEmails;
    this.form.get('to').setValue(bookerEmailArray);
  }

  ngOnInit(): void
  {
    this.subscriptions.add(
      this.form.controls.to.valueChanges.subscribe((newListOfTo) =>
      {
        this.form.get('allPlayersChecked')?.setValue(!!this.dialogData.PlayerEmails.every(email => newListOfTo?.includes(email)));
      })
    );
  }

  changeCancellationReason(reason: string): void
  {
    this.form.controls.cancelationReason.patchValue(reason);
  }

  saveAndConfirm(): void
  {
    this.dialogRef.close(this.form.value);
  }

  onCloseClick(): void
  {
    this.dialogRef.close();
  }

  submitDialog(): void
  {
    if (this.dialogData.ConfirmMode === this.confirmModeEnum.Save)
    {
      this.saveAndConfirm();
    } else if (this.dialogData.ConfirmMode === this.confirmModeEnum.Cancel)
    {
      this.dialogSubmitted.emit(this.form.value);
    }
  }

  changeAllPlayers(event: MatCheckboxChange): void
  {    
    if (event.checked)
    {
      const currentEmails = this.form.get('to').value;
      const newEmails = [...this.dialogData.PlayerEmails || []];
      currentEmails.forEach(email =>
      {
        if (!newEmails.includes(email))
        {
          newEmails.push(email);
        }
      });
      this.form.get('to').setValue(newEmails);
    } else
    {
      this.form.get('to').reset();
      this.form.get('to').setValue([]);
    }
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
