import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { GetModifiedCourseCrossoversResponse } from '../../@intermediate/models/get-modified-course-cross-overs.model';
import { TeeSheetCourseCrossoversService } from '../../@services/tee-sheet-course-crossovers.service';


@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-course-crossovers',
  templateUrl: './tee-sheet-course-crossovers.component.html',
  styleUrls: ['./tee-sheet-course-crossovers.component.scss']
})
export class TeeSheetCourseCrossoversComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  isMobile = false;
  data: GetModifiedCourseCrossoversResponse;
  constructor(private dialogRef: MatDialogRef<TeeSheetCourseCrossoversComponent>,
    public responsiveService: ResponsiveService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    public teeSheetCourseCrossoversService: TeeSheetCourseCrossoversService
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    this.GetDataSubscription();
  }

  public GetDataSubscription(): void
  {
    this.subscription.add(this.teeSheetCourseCrossoversService.load$.subscribe((value: GetModifiedCourseCrossoversResponse) =>
    {
      this.manipulateData(value);
    }));
  }

  manipulateData(data: GetModifiedCourseCrossoversResponse): void
  {
    data.SheetTimes.map((item, index) =>
    {
      item['Index'] = index;
      item['SheetTimeTitle'] = moment(item.SheetTime).format('LT');
    });

    data.CourseInfo.map(course =>
    {
      course['Show'] = true;
    });
    this.data = data;
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
