import { Course } from './get-tee-sheet-main.model';
import { TeeSheetSelectedView } from './tee-sheet-toolbar-model';

export class PostTeeTimesRequest
{
  PageNumber: number;
  PageCount: number;
  SortOrder: any;
  SortType: any;

  Courses: Array<string>;
  DateCategory: DateCategoryTypeEnum;
  IncludeSmallGroups: boolean;
  SearchText: Array<TeeSheetAdvancedSearchType>;

}

export class TeeSheetAdvancedSearchType
{
  SearchType: SearchFieldEnum;
  SearchText: String;
}

export class PostTeeTimesResponse
{
  TeeTimes: AdvancedSearchTeeTime[];
  TotalCount: number;
}

export class AdvancedSearchDialogModel
{
  courses: Array<Course>;
}

export class TeeSheetAdvancedSearchResult
{
  teeTime: AdvancedSearchTeeTime;
  teeSheetSelectedView: TeeSheetSelectedView;
}

export class AdvancedSearchTeeTime
{
  BookingDateTime: string;
  BookingID: number;
  CourseName: string;
  CourseID: string;
  NumPlayers: number;
  Players: Array<string>;
  StartTee: number;
}

export enum SortType
{
  Ascending = 0,
  Descending = 1
}
export class TeeSheetSearchMetadata
{
  readonly Columns: string[] = ['Select', 'Date', 'Time', 'Course', 'Players', 'Player1', 'Player2', 'Player3', 'Player4', 'Player5', 'MobileMode'];
}

export enum SearchFieldEnum
{
  PlayerName = 0,
  Phone = 1,
  MemberID = 2,
  Confirmation = 3,
  Booker = 4,
  Course = 5,
  UpcomingOnly = 6,
}

export enum DateCategoryTypeEnum
{
  UpcomingOnly,
  Any
}

export class UpcomingOnlyQuickFilterChipModel
{
  UpcomingOnly: string;
}

export interface ISearchOptionButton
{
  name: string;
  value: SearchFieldEnum;
  type: SearchOptionButtonType;
}

export enum SearchOptionButtonType
{
  GenericFilterChip,
  QuickSearchFilterChip
}

