import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MatMenuTrigger } from '@angular/material/menu';
import { ResponsiveService } from '@tei/core';
import { debounceTime, distinctUntilChanged, Subscription } from 'rxjs';
import { RestrictionType } from '../../@intermediate/models/get-tee-sheet-classes-groups.model';
import { FilterViewCheckboxData, TeeSheetFilterView } from '../../@intermediate/models/tee-sheet-filter-view.model';
import { TeeSheetFilterViewService } from '../../@services/tee-sheet-filter-view.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-filter-view',
  templateUrl: './tee-sheet-filter-view.component.html',
  styleUrls: ['./tee-sheet-filter-view.component.scss']
})

export class TeeSheetFilterViewComponent implements OnInit, OnDestroy
{
  @Input() isStandalone = false;
  subscription = new Subscription();
  filterViewData: TeeSheetFilterView[];
  showFilterViewPanel = false;
  form: FormGroup;
  restrictionType = RestrictionType;
  filterViewInfo: { [key: string]: FilterViewCheckboxData; } = {};
  selectedIDs: string[] = [];
  searchForm = new FormControl('');
  isLoading = false;
  isMobile = false;
  initialize = true;
  @Output() selectedIDsEmitter = new EventEmitter<string[]>;
  @ViewChild(MatMenuTrigger) filterViewTrigger: MatMenuTrigger;
  public checkboxesState: { indeterminate: boolean, checked: boolean; }[] = [];
  constructor(private teeSheetFilterViewService: TeeSheetFilterViewService,
    private fb: FormBuilder,
    private responsiveService: ResponsiveService,
  ) { }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  ngOnInit(): void
  {
    this.loadData();
    this.initialSearchForm();
    this.getMobileSize();
  }

  resetForm(): void
  {
    this.searchForm.setValue('');
  }

  initialSearchForm(): void
  {
    this.subscription.add(this.searchForm.valueChanges.pipe(debounceTime(500)).subscribe(searchText =>
    {
      this.isLoading = true;
      this.teeSheetFilterViewService.setFilterSearch(searchText.toLowerCase());
    }));
  }

  loadData(): void
  {
    this.subscription = new Subscription();
    this.subscription.add(this.teeSheetFilterViewService.load$.pipe(distinctUntilChanged()).subscribe((value: TeeSheetFilterView[]) =>
    {
      this.isLoading = false;
      this.manipulateData(value);
    }));
  }

  manipulateData(value: TeeSheetFilterView[]): void
  {
    this.checkboxesState = [];
    value.map(() =>
    {
      this.checkboxesState.push({
        checked: false,
        indeterminate: false
      });
    });
    this.filterViewData = value;
    this.createForm(value);
  }

  createForm(filterViewData: TeeSheetFilterView[]): void
  {
    this.form = this.fb.group({
      FilterView: this.fb.array([])
    });
    this.fillForm(filterViewData);
  }

  get filterViewArray(): FormArray
  {
    return this.form.get('FilterView') as FormArray;
  }

  fillForm(filterViewData: TeeSheetFilterView[]): void
  {
    filterViewData?.map((parent, parentIndex) =>
    {
      const restriction = this.fb.group({
        Title: parent.Title,
        ID: parent.ID,
        Items: this.fb.array([])
      });

      parent.Items?.map((child, childIndex) =>
      {
        const isSelected = this.selectedIDs.includes(child.ID.toLowerCase()) || (child.Selected && !this.isStandalone && this.initialize);
        (restriction.get('Items') as FormArray).push(
          this.fb.group({
            Title: child.Title,
            ID: child.ID,
            Selected: isSelected,
          })
        );
        this.filterViewInfo[child.ID.toLowerCase()] = { Title: child.Title, ID: child.ID.toLowerCase(), ParentIndex: parentIndex, ChildIndex: childIndex };
      });
      this.filterViewArray.push(restriction);
    });
    this.isAllSelectedOrIndeterminate();
    this.applySelectedClassUIDs();
    if (this.selectedIDs.length)
    {
      this.initialize = false;
    }
  }

  selectAllCheckboxes(event: MatCheckboxChange, index: number): void
  {
    (this.filterViewArray.at(index).get('Items') as FormArray).controls.map(item =>
    {
      item.get('Selected').setValue(event.checked);
    });

    this.checkboxesState[index] = {
      checked: event.checked,
      indeterminate: false
    };
    this.applySelectedClassUIDs();
  }

  applySelectedClassUIDs(): void
  {
    this.filterViewArray.controls.map(filterView =>
    {
      const itemsArray = filterView.get('Items') as FormArray;
      itemsArray.controls.map(item =>
      {
        const ID = item.get('ID')?.value.toLowerCase();
        const isSelected = item.get('Selected')?.value;

        if (isSelected)
        {
          if (!this.selectedIDs.includes(ID))
          {
            this.selectedIDs.push(ID);
          }
        } else
        {
          this.selectedIDs = this.selectedIDs.filter(id => id !== ID);
        }
      });
    });
    if (this.isStandalone)
    {
      this.selectedIDsEmitter.emit(this.selectedIDs);
    } else
    {
      this.teeSheetFilterViewService.applyIds(this.selectedIDs);
    }
  }

  reset(): void
  {
    this.selectedIDs = [];
    this.createForm(this.filterViewData);
  }


  onSelectCheckbox(event: MatCheckboxChange, filterViewIndex: number, index: number): void
  {
    (this.filterViewArray.at(filterViewIndex).get('Items') as FormArray).at(index).get('Selected').setValue(event.checked);
    this.isAllSelectedOrIndeterminate();
    this.applySelectedClassUIDs();
  }

  isAllSelectedOrIndeterminate(): void
  {
    this.filterViewArray.controls.map((filterView, index) =>
    {
      const selectedCount = (filterView.get('Items') as FormArray).controls.filter(item => item.get('Selected').value).length;
      this.checkboxesState[index].indeterminate = 0 < selectedCount && selectedCount < filterView.get('Items').value.length;
      this.checkboxesState[index].checked = filterView.get('Items').value.length === selectedCount;
    });
  }

  removeRestrictions(filterView: string): void
  {
    this.selectedIDs = this.selectedIDs.filter(item => item !== filterView);
    (this.filterViewArray.at(this.filterViewInfo[filterView].ParentIndex).get('Items') as FormArray).at(this.filterViewInfo[filterView].ChildIndex).get('Selected').setValue(false);
    this.isAllSelectedOrIndeterminate();
    this.applySelectedClassUIDs();
  }

  get showBorder(): boolean
  {
    return this.filterViewData.length > 1;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
