import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { TeiCurrencyTranslateService } from '@tei/core-translate';
import { DonutChartConfigModel } from '@tei/material';
import moment from 'moment';
import { firstValueFrom, Subscription } from 'rxjs';
import { GetTeeSheetStatsRequest, GetTeeSheetStatsResponse } from '../../@intermediate/models/get-tee-sheet-stats.model';
import { ITeeSheetService } from '../../@intermediate/tee-sheet.interface';
import { AboutStatComponent } from './about-stat/about-stat.component';
@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-stats',
  templateUrl: './tee-sheet-stats.component.html',
  styleUrls: ['./tee-sheet-stats.component.scss']
})

export class TeeSheetStatsComponent implements OnInit, OnDestroy
{
  subscription = new Subscription();
  isMobile = false;
  teeSheetStats: GetTeeSheetStatsResponse;
  public chartRoundsOptions: DonutChartConfigModel;
  public chartRevenueOptions: DonutChartConfigModel;
  currency = '';
  constructor(private dialogRef: MatDialogRef<TeeSheetStatsComponent>,
    public responsiveService: ResponsiveService,
    @Inject(MAT_DIALOG_DATA) public data: GetTeeSheetStatsRequest,
    @Inject('ITeeSheetService') private teeSheetService: ITeeSheetService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private dialog: MatDialog,
    private currencyService: TeiCurrencyTranslateService
  )
  { }

  async ngOnInit(): Promise<void>
  {
    this.getMobileSize();
    await this.getTeeSheetStats();
  }

  async getTeeSheetStats(): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      const request: GetTeeSheetStatsRequest = {
        CourseID: this.data.CourseID,
        SheetDate: moment(this.data.SheetDate).format('YYYY-MM-DD'),
      };
      const response: GetTeeSheetStatsResponse = await firstValueFrom(this.teeSheetService.GetTeeSheetStats(request));
      this.fillChart(response);
      this.teeSheetStats = response;
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  fillChart(teeSheetStat: GetTeeSheetStatsResponse): void
  {
    const roundsSeries = [];
    roundsSeries.push(teeSheetStat.CheckedInRounds);
    roundsSeries.push(teeSheetStat.NoShowRounds);
    roundsSeries.push(teeSheetStat.AvailableRounds);
    roundsSeries.push(teeSheetStat.ExpiredRounds);
    roundsSeries.push(teeSheetStat.BlockedRounds);
    roundsSeries.push(Math.abs(teeSheetStat.BookedRounds));
    this.chartRoundsOptions = {
      Labels: ["CHECKED-IN", "NO SHOW", "AVAILABLE", "EXPIRED", "BLOCKED", "BOOKED"],
      Colors: ["#0AA874", "#F74231", "#BDBDBD", "#FFAB91", "#616161", "#028EB5"],
      Values: roundsSeries,
      Height: 280
    };

    const revenueSeries = [];
    revenueSeries.push(teeSheetStat.CheckedInRevenue);
    revenueSeries.push(teeSheetStat.ExpectedRevenue);
    const toFixedFn = (value: number) => value.toFixed(2);
    const currency = this.currencyService.getCurrencySymbol();
    this.chartRevenueOptions = {
      Labels: ["CHECKED-IN", "EXPECTED"],
      Colors: ["#0AA874", "#BDBDBD"],
      Values: revenueSeries,
      Height: 280,
      CustomTooltip: {
        custom: function ({ series, seriesIndex, dataPointIndex, w }: any)
        {
          const toFixed = toFixedFn(series[seriesIndex]);
          return `
            <div class="a-donut-chart-tooltip">
              ${ w.config.labels[seriesIndex] }: ${ currency }${ toFixed }
            </div>
          `;
        },
      }
    };
  }


  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  }

  openLearnMore(): void
  {
    this.dialog.open(AboutStatComponent, {
      panelClass: ['a-teesheet-stats-about', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen']
    });
  }

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
