import { ChangeDetectionStrategy, Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { FormControl, FormGroup, UntypedFormArray, UntypedFormBuilder } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacySelectChange as MatSelectChange } from '@angular/material/legacy-select';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import
{
  CartTypesID,
  Player
} from '../../../@intermediate/models/get-booking-details.model';
import { ManageTeeTimePlayersComponent } from '../../manage-tee-time/manage-tee-time-players/manage-tee-time-players.component';

import { ChangeDetectorRef } from '@angular/core';
import { MatExpansionPanel } from '@angular/material/expansion';
import { SelectedDisableOptionsCourse } from '../../../@intermediate/models/get-tee-sheet-main.model';
import
{
  PlayerStatus,
  TeeTimeHoles
} from '../../../@intermediate/models/get-tee-sheet.model';
import { GroupBookingService } from '../../../@services/group-booking.service';
import { ManageTeeTimeHelperService } from '../../../@services/manage-tee-time.helper.service';
import { ManageTeeTimeService } from '../../../@services/manage-tee-time.service';

@Component({
  selector: 'tei-pos-tee-sheet-group-players',
  templateUrl: './group-players.component.html',
  styleUrls: ['./group-players.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class GroupPlayersComponent extends ManageTeeTimePlayersComponent implements OnInit, OnDestroy
{
  @Input() SelectedDisableOptionsCourse: SelectedDisableOptionsCourse;
  @ViewChild('teeTimePanel') teeTimePanel!: MatExpansionPanel;

  @Output() onAllStatusChanged = new EventEmitter<PlayerStatus>();
  @Output() updataManageGroupPlayer = new EventEmitter();

  @Input() GroupBookingIndex: number;

  get hidden(): boolean
  {
    return this.config?.FilterPlayer &&
      ((this.config?.FilterPlayer?.HideCheckInPlayers && this.isCheckInAllForFilter())
        ||
        (this.config?.FilterPlayer?.ContactID
          && !this.playersFormRawValue?.playersList?.player?.
            some(a => a?.Contact?.ContactID === this.config?.FilterPlayer?.ContactID)));
  }

  panelOpenState = true;

  bookingPriceLevelDescription: string;

  headerBookingForm = new FormGroup({
    priceLevelID: new FormControl(''),
    priceLevelDescription: new FormControl(''),
    cartType: new FormControl(0),
    holes: new FormControl(TeeTimeHoles.Holes18),
    holesTitle: new FormControl(''),
  });

  constructor(
    manageTeeTimeHelper: ManageTeeTimeHelperService,
    @Inject('IManageTeeTimeService') manageTeeTimeService: ManageTeeTimeService,
    fb: UntypedFormBuilder,
    dialog: MatDialog,
    translate: TranslateService,
    changeDetectorRef: ChangeDetectorRef,
    responsiveService: ResponsiveService,
    groupBookingService: GroupBookingService,
    manageStandAloneLoadingService: ManageStandAloneLoadingService,
  )
  {
    super(
      manageTeeTimeHelper,
      manageTeeTimeService,
      fb,
      dialog,
      translate,
      changeDetectorRef,
      responsiveService,
      groupBookingService,
      manageStandAloneLoadingService
    );
  }

  /**
   * Component initialization
   * Calls parent ngOnInit and adds accordion-specific initialization
   */
  async ngOnInit(): Promise<void>
  {
    this.setPastDayStatus();
    // Call parent's ngOnInit after Booking is set up
    await super.ngOnInit();

    this.teeTimeInfo = { DisplayTime: this.Booking.bookingDetails.DisplayTime, HoleNumber: this.Booking.bookingDetails.HoleNumber };

    this.createHeaderBookingForm();

    if (this.screenSize < this.screenSizeEnum.Desktop)
    {
      this.panelOpenState = true;
    }
  }


  public override noShowAll(): void
  {
    super.noShowAll();
    this.onAllStatusChanged.emit();
  }

  public override checkInAll(): void
  {
    super.checkInAll();
    this.onAllStatusChanged.emit();
  }

  public override playerStatusChange(status): void
  {
    super.playerStatusChange(status);
    this.onAllStatusChanged.emit(status);
  }

  createHeaderBookingForm(): void
  {
    this.headerBookingForm = this.fb.group({
      priceLevelID: this.config?.GroupDefaultValues?.PriceLevelID || '',
      priceLevelDescription: this.config?.GroupDefaultValues?.PriceLevelDescription || '',
      cartType: this.config?.GroupDefaultValues?.CartType ?? CartTypesID.Walk,
      holes: this.config?.GroupDefaultValues?.NineHoles ?? TeeTimeHoles.Holes18,
      holesTitle: this.config?.GroupDefaultValues?.NineHoles == TeeTimeHoles.Holes9 ? '9' : '18',
    });

    this.bookingPriceLevelDescription = this.config?.GroupDefaultValues?.PriceLevelDescription;
  }

  setPastDayStatus(): void
  {
    const IsPastDate = this.SelectedDisableOptionsCourse?.IsPastDate;
    const IsCheckedInAndPaidAll = IsPastDate && this.Booking.bookingDetails?.Players.every(player => player.Status === PlayerStatus.CheckIn);
    const IsBookingNotCheckIn = IsPastDate && this.Booking.bookingDetails?.Players.some(player => player.Status === PlayerStatus.NoShow || player.Status === PlayerStatus.None);
    const IsPrePaidNotCheckIn = IsPastDate && this.Booking.bookingDetails?.Players.some(player => player.IsPrePaid);
    this.pastDayStatus = {
      IsCheckedInAndPaidAll,
      DisableCheckInBooking: !this.SelectedDisableOptionsCourse?.AllowPastCheckins && IsBookingNotCheckIn,
      IsPrePaidNotCheckIn,
      DisableCheckInPrePaid: !this.SelectedDisableOptionsCourse?.AllowPastCheckins && IsPrePaidNotCheckIn,
      IsBookingNotCheckIn
    };
  }

  ngOnDestroy(): void
  {
    super.ngOnDestroy();
  }

  onPriceLevelChange(event: MatSelectChange): void
  {
    const newPriceLevel = event.value as string;
    this.bookingPriceLevelDescription = this.priceLevels?.find(pl => pl.PriceLevelID === newPriceLevel)?.Description;
    // Update all players in the form with the new price level
    const playersFormArray = this.playersForm?.controls?.playersList?.get('player') as UntypedFormArray;

    if (playersFormArray?.length)
    {
      for (let i = 0; i < playersFormArray.length; i++)
      {
        const playerControl = playersFormArray.at(i);
        if (playerControl)
        {
          playerControl.patchValue({ PriceLevelID: newPriceLevel }, { emitEvent: true });
        }
      }
    }

  }

  //change holes value in header booking form
  onHolesChange(): void
  {
    this.headerBookingForm.get('holes').setValue(this.headerBookingForm.get('holes').value == TeeTimeHoles.Holes18 ? TeeTimeHoles.Holes9 : TeeTimeHoles.Holes18);
    this.headerBookingForm.get('holesTitle').setValue(this.headerBookingForm.get('holes').value === TeeTimeHoles.Holes18 ? '18' : '9');

    // Update all holes in the form with the new holes value
    const playersFormArray = this.playersForm?.controls?.playersList?.get('player') as UntypedFormArray;
    if (playersFormArray?.length)
    {
      for (let i = 0; i < playersFormArray.length; i++)
      {
        const playerControl = playersFormArray.at(i);
        if (playerControl)
        {
          playerControl.patchValue({ Holes: this.headerBookingForm.get('holes').value }, { emitEvent: true });
        }
      }
    }
  }

  /**
   * Handles cart type change in accordion header - applies to all players in this tee time
   */
  onCartTypeChange(event: MatSelectChange): void
  {
    const newCartType = event.value as CartTypesID;

    // Update all players in the form with the new cart type
    const playersFormArray = this.playersForm?.controls?.playersList?.get('player') as UntypedFormArray;

    if (playersFormArray?.length)
    {
      for (let i = 0; i < playersFormArray.length; i++)
      {
        const playerControl = playersFormArray.at(i);
        if (playerControl)
        {
          playerControl.patchValue({ Cart: newCartType }, { emitEvent: true });
        }
      }
    }

  }
}
