import { Injectable } from '@angular/core';
import { BehaviorSubject, firstValueFrom, Subject } from 'rxjs';
import { DisplayTeeSheetDialogData, GetDisplayDataRequest, GetDisplayDataResponse, TeeSheetDisplayApply, TeeSheetDisplayForm } from '../@intermediate/models/get-teesheet-display.model';
import { TeeSheetService } from './tee-sheet.service';
import { ManageStandAloneLoadingService } from '@tei/core';
import { SaveDisplayFormData } from '../@intermediate/models/post-display-tee-sheet.model';




@Injectable()
export class TeeSheetDisplayService {
  private Load = new BehaviorSubject<GetDisplayDataResponse | null>(null);
  load$ = this.Load.asObservable();

  private DisplayTeeSheetDialogData = new BehaviorSubject<DisplayTeeSheetDialogData>(new DisplayTeeSheetDialogData());
  displayTeeSheetDialogData$ = this.DisplayTeeSheetDialogData.asObservable();

  private Reset = new Subject<boolean>();
  reset$ = this.Reset.asObservable();

  private Apply = new Subject<TeeSheetDisplayApply>();
  apply$ = this.Apply.asObservable();

  public DisplayFormChanged = false;

  saveDisplayFormData= new SaveDisplayFormData();

  constructor(
    private teeSheetService: TeeSheetService,
    private manageStandAloneLoadingService: ManageStandAloneLoadingService
  ) {}

  apply(value: TeeSheetDisplayApply): void {
    this.Apply.next(value);
    this.displayFormChange(true);
  }

  reset(): void {
    this.Reset.next(true);
    this.displayFormChange(false);
  }

  setDisplayDialogData(value: DisplayTeeSheetDialogData): void {
    this.DisplayTeeSheetDialogData.next(value);
  }

  getDisplayDialogData(): DisplayTeeSheetDialogData {
    return this.DisplayTeeSheetDialogData.getValue();
  }

  displayFormChange(value: boolean): void {
    this.DisplayFormChanged = value;
  }

  /** Centralized method to call API and update BehaviorSubject */
  async load(request: GetDisplayDataRequest): Promise<void> {
    try {
      this.manageStandAloneLoadingService.startLoading();
      const response = await firstValueFrom(this.teeSheetService.GetDisplayData(request));
      this.setLoadData(response)
    } finally {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  getLoadData(): GetDisplayDataResponse {
   return this.Load.getValue();
  }

  setLoadData(data:GetDisplayDataResponse) {
    this.Load.next(data);
  }

  setSaveDisplayFormData(value: SaveDisplayFormData) {
    this.saveDisplayFormData = value;
  }

}
