import { ChangeDetectorRef, Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { ResponsiveService } from '@tei/core';
import { merge, Subscription } from 'rxjs';
import { debounceTime, finalize } from 'rxjs/operators';
import { IManageTeeTimeService } from '../../../@intermediate/manage-tee-time.interface';
import { AdvancedSearchDialogModel, AdvancedSearchTeeTime, PostTeeTimesRequest, PostTeeTimesResponse, SortType, TeeSheetSearchMetadata } from '../../../@intermediate/models/post-tee-time-advance-search.model';

@Component({
  selector: 'tei-pos-tee-sheet-tee-time-booking-advanced-search',
  templateUrl: './tee-time-booking-advanced-search.component.html',
  styleUrls: ['./tee-time-booking-advanced-search.component.scss']
})
export class TeeTimeBookingAdvancedSearchComponent implements OnInit, OnDestroy
{
  subscription: Subscription = new Subscription();
  name: string;
  model: PostTeeTimesResponse;
  message: string;
  dataSource = new Array<AdvancedSearchTeeTime>();
  metadata = new TeeSheetSearchMetadata();
  request = new PostTeeTimesRequest();
  selectedTeeTime = new AdvancedSearchTeeTime();
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  // paging & sorting
  totalCount = 100;
  pageIndex = 0;
  pageSize = 10;
  isSmallScreen = false;
  isLoadingResults = false;
  screenSize = '';

  get emptyFilterMessageState() { return (this.request.SearchText?.length ?? 0) === 0 && this.dataSource.length === 0; }

  form: UntypedFormGroup;

  constructor(
    @Inject('IManageTeeTimeService') public service: IManageTeeTimeService,
    @Inject(MAT_DIALOG_DATA) public data: AdvancedSearchDialogModel,
    private DialogRef: MatDialogRef<TeeTimeBookingAdvancedSearchComponent>,
    private responsiveService: ResponsiveService,
    public changeDetectorRef: ChangeDetectorRef,
    private formBuilder: UntypedFormBuilder)
  {
  }

  ngOnInit(): void
  {
    this.form = this.formBuilder.group({
      selectedTeeTime: []
    });

    this.handleScreenSizeChangeEvent();

    this.handleSortEvent();

    this.handlePaginationEvent();

    this.changeDetectorRef.detectChanges();
  }

  private handlePaginationEvent(): void
  {
    this.subscription.add(merge(this.sort.sortChange, this.paginator.page).subscribe(() =>
    {
      this.pageIndex = this.paginator.pageIndex;
      this.pageSize = this.paginator.pageSize;
      this.load();
    }));
  }

  private handleSortEvent(): void
  {
    this.subscription.add(this.sort.sortChange.subscribe(() =>
    {
      this.resetPaging();
    }));
  }

  private handleScreenSizeChangeEvent(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isSmallScreen = (screenSize === 'md' || screenSize === 'sm' || screenSize === 'xs');
      this.changeDetectorRef.detectChanges();
    }));
  }

  private resetPaging(): void
  {
    this.pageIndex = 0;
    this.paginator.pageIndex = 0;
  }

  public loadHandler(request: PostTeeTimesRequest): void
  {
    this.request = request;
    this.resetPaging();
    this.load();
  }

  public load(): void
  {
    this.updateRequest();

    if (!this.model)
    {
      this.model = new PostTeeTimesResponse();
    }

    if (!this.request.SearchText || this.request.SearchText?.length === 0)
    {
      this.handleLoadingResponse({ TeeTimes: [], TotalCount: 0 } as PostTeeTimesResponse);
      return;
    }
    this.isLoadingResults = true;
    this.subscription.add(this.service.PostTeeTimes(this.request).pipe(
      debounceTime(600),
      finalize(() => this.isLoadingResults = false))
      .subscribe({
        next: (response: PostTeeTimesResponse) =>
        {
          this.handleLoadingResponse(response);
        }
      }));
  }

  private handleLoadingResponse(response: PostTeeTimesResponse)
  {
    this.model = response;
    this.dataSource = this.model.TeeTimes;
    setTimeout(() =>
    {
      this.totalCount = this.model.TotalCount;
      this.paginator.pageIndex = this.pageIndex;
      this.paginator.pageSize = this.pageSize;
      this.paginator.length = this.totalCount;
    }, 300);
  }

  private updateRequest(): void
  {
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageCount = this.pageSize;

    if (this.sort.active && this.sort.direction)
    {
      this.request.SortOrder = this.sort.active;
      this.request.SortType = this.sort.direction === 'desc' ? SortType.Descending : SortType.Ascending;
    } else
    {
      delete this.request.SortOrder;
      delete this.request.SortType;
    }
  }

  public openTeeTime(): void
  {
    if (this.selectedTeeTime.BookingID)
    {
      this.DialogRef.close(this.selectedTeeTime);
    }
  }

  close(): void
  {
    this.DialogRef.close();
  }

  getPlayer(teeTime: AdvancedSearchTeeTime, index: number): string
  {
    return teeTime.Players.length >= index ? teeTime.Players[index - 1] : '';
  }


  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
