import { CartTypesID, Contact, Player } from './get-booking-details.model';
import { GetRainCheckSetupResponse, HolePricing } from './get-rain-check-setup.model';
import { Course } from './get-tee-sheet-main.model';
import { NumPlayers } from './post-booking-details.model';

export class RainCheckWizardDataModel
{
  BookingID: number;
  BookingDateTime: string;
  Course: Course;
  Players: Player[];
  RainCheckSetup?: GetRainCheckSetupResponse;
}

export class RainCheckWizardTransitionDataModel
{
  totalPrice: number;
  players: RainCheckSelectPlayersForm[];
}

export class RainCheckSelectPlayersForm
{
  contact: Contact;
  assignedContact: Contact;
  cartID: CartTypesID;
  cartPrice: number;
  holes: boolean | HolePricing;
  golfPrice: number;
  holesPrice: number;
  playerNumber: NumPlayers;
  isSelected: boolean;
  isDisabled: boolean;
  issued: boolean;
  holesTitle: string;

  teeTimeId: string;
  playerHoles: number;
  attachmentPrice: number;
}

export enum ConfirmationActionEnum
{
  Email = "email",
  Print = "print",
  AssignContact = 'AssignContact'
}

export enum HoleNumberEnum
{
  Hole9 = 9,
  Hole18 = 18
}
