import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable, forkJoin, of } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { IManageTeeTimeService } from '../@intermediate/manage-tee-time.interface';
import { CancelTeeTimeInProgressRequest, CancelTeeTimeInProgressResponse } from '../@intermediate/models/delete-cancel-tee-time-in-progress.model';
import { DeleteTeeTimeCheckInRequest, DeleteTeeTimeCheckInResponse } from '../@intermediate/models/delete-tee-time-check-in.model';
import { DeleteTeeTimeRequest, DeleteTeeTimeResponse } from '../@intermediate/models/delete-tee-time.model';
import { GetBookingCheckInDetailsRequest, GetBookingCheckInDetailsResponse } from '../@intermediate/models/get-booking-check-in-details.model';
import { GetBookingDetailsRequest, GetBookingDetailsResponse, Player } from '../@intermediate/models/get-booking-details.model';
import { GetContactHasNotificationRequest, GetContactHasNotificationResponse } from '../@intermediate/models/get-contact-notification.model';
import { GetGroupPriceRequest, GetGroupPriceResponse } from '../@intermediate/models/get-group-price';
import { GetPlayerEmailsRequest, GetPlayerEmailsResponse } from '../@intermediate/models/get-player-email.model';
import { GetPlayerPriceLevelsRequest, GetPlayerPriceLevelsResponse } from '../@intermediate/models/get-player-price-levels';
import { GetTeeTimeRequest, GetTeeTimeResponse } from '../@intermediate/models/get-tee-time.model';
import
{
  GetBookingNotesRequest, GetBookingNotesResponse,
  PostBookingNotesRequest, PostBookingNotesResponse
} from '../@intermediate/models/note-tee-time.model';
import { PostAddRemovePlayerRequest, PostAddRemovePlayerResponse } from '../@intermediate/models/post-add-remove-player.model';
import { PostBookingDetailsRequest, PostBookingDetailsResponse } from '../@intermediate/models/post-booking-details.model';
import { PostMoveTeeTimeRequest, PostMoveTeeTimeResponse } from '../@intermediate/models/post-move-teetime.model';
import { PostPlayerPriceRequest, PostPlayerPriceResponse } from '../@intermediate/models/post-player-price.model';
import { PostReactivateNoShowPlayerRequest, PostReactivateNoShowPlayerResponse } from '../@intermediate/models/post-reactivate-no-show-player.model';
import { PostRemoveBookingPlayerRequest, PostRemoveBookingPlayerResponse } from '../@intermediate/models/post-remove-booking-player.model';
import { PostRestrictionsRequest, PostRestrictionsResponse } from '../@intermediate/models/post-restrictions.model';
import
{
  StageTeeTimeCheckInRequest,
  StageTeeTimeCheckInResponse
} from '../@intermediate/models/post-stage-tee-time-check-in';
import { PostTeeTimesRequest, PostTeeTimesResponse } from '../@intermediate/models/post-tee-time-advance-search.model';
import { PostTeeTimeInProgressRequest, PostTeeTimeInProgressResponse } from '../@intermediate/models/post-tee-time-in-progress-model';
import { GetRemoveTeeTimeDialogDataFromContextMenuRequest, GetRemoveTeeTimeDialogDataRequest, GetRemoveTeeTimeDialogDataResponse, RemoveDialogData } from '../@intermediate/models/remove-tee-time.model';
import { HeaderService } from './header.service';

@Injectable()
export class ManageTeeTimeService implements IManageTeeTimeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient, private headerService: HeaderService)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/ManageTeeTime/';
  }

  GetBookingNotes(request: GetBookingNotesRequest): Observable<GetBookingNotesResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingNotes';
    return this.http.get<GetBookingNotesResponse>(apiUrl, { params: request as any });
  }

  PostBookingNotes(request: PostBookingNotesRequest): Observable<PostBookingNotesResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingNotes';
    return this.http.post<PostBookingNotesResponse>(apiUrl, request);
  }

  GetBookingDetails(request: GetBookingDetailsRequest): Observable<GetBookingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingDetails';
    return this.http.get<GetBookingDetailsResponse>(apiUrl, { params: request as any });
  }

  PostAddRemovePlayer(request: PostAddRemovePlayerRequest): Observable<PostAddRemovePlayerResponse>
  {
    const apiUrl = this.serviceUrl + 'AddRemovePlayer';
    return this.http.post<PostAddRemovePlayerResponse>(apiUrl, request);
  }

  PostTeeTimeInProgress(request: PostTeeTimeInProgressRequest): Observable<PostTeeTimeInProgressResponse>
  {
    const apiUrl = this.serviceUrl + 'TeeTimeInProgress';
    return this.http.post<PostTeeTimeInProgressResponse>(apiUrl, request);
  }

  DeleteCancelTeeTimeInProgress(request: CancelTeeTimeInProgressRequest): Observable<CancelTeeTimeInProgressResponse>
  {
    const apiUrl = this.serviceUrl + 'CancelTeeTimeInProgress';
    return this.http.delete<CancelTeeTimeInProgressResponse>(apiUrl, { params: request as any });
  }

  PostBookingDetails(request: PostBookingDetailsRequest): Observable<PostBookingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingDetails';
    return this.http.post<PostBookingDetailsResponse>(apiUrl, request);
  }

  GetTeeTime(request: GetTeeTimeRequest): Observable<GetTeeTimeResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + 'GetTeeTime';
    return this.http.get<GetTeeTimeResponse>(apiUrl, options);
  }

  PostPlayerPrice(request: PostPlayerPriceRequest): Observable<PostPlayerPriceResponse>
  {
    const apiUrl = this.serviceUrl + 'PlayerPrice';

    return this.http.post<PostPlayerPriceResponse>(apiUrl, request);
  }


  ReactivateNoShowPlayer(request: PostReactivateNoShowPlayerRequest): Observable<PostReactivateNoShowPlayerResponse>
  {
    const apiUrl = this.serviceUrl + 'ReactivateNoShowPlayer';

    return this.http.post<PostReactivateNoShowPlayerResponse>(apiUrl, request);
  }

  GetPlayerEmails(request: GetPlayerEmailsRequest): Observable<GetPlayerEmailsResponse>
  {
    const apiUrl = this.serviceUrl + 'GetPlayerEmails';
    return this.http.post<GetPlayerEmailsResponse>(apiUrl, request);
  }

  GetGroupPrice(request: GetGroupPriceRequest): Observable<GetGroupPriceResponse>
  {
    const apiUrl = this.serviceUrl + 'GetGroupPrice';
    return this.http.post<any>(apiUrl, request);
  }

  GetRemoveTeeTimeDialogData(request: GetRemoveTeeTimeDialogDataRequest): Observable<GetRemoveTeeTimeDialogDataResponse>
  {
    const requestPlayerEmails: GetPlayerEmailsRequest = new GetPlayerEmailsRequest();
    requestPlayerEmails.BookingID = request.BookingID;
    requestPlayerEmails.Players = request.players.filter(player => player.PlayerNumber && player.Contact).map((player: Player) => player.Contact.ContactID);

    const requestBookingPrice = new GetGroupPriceRequest();
    requestBookingPrice.bookingID = request.BookingID;
    requestBookingPrice.startTee = request.StartTee;
    requestBookingPrice.players = request.players.filter(player => player.PlayerNumber).map((player: Player) =>
    {
      return {
        playerNumber: player.PlayerNumber,
        holes: player.Holes,
        priceLevelID: player.PriceLevelID,
        cart: player.Cart
      };
    });

    const playerEmails$ = this.GetPlayerEmails(requestPlayerEmails);
    const user$ = this.headerService.getUser({});
    const bookingPrice$ = this.GetGroupPrice(requestBookingPrice);

    const forkJoinObject = {};
    forkJoinObject['playerEmails'] = playerEmails$;
    forkJoinObject['user'] = user$;
    forkJoinObject['groupPrice'] = bookingPrice$;

    return forkJoin(forkJoinObject);
  }

  GetRemoveTeeTimeDialogDataFromContextMenu(request: GetRemoveTeeTimeDialogDataFromContextMenuRequest): Observable<RemoveDialogData>
  {
    const getBookingDetailsRequest = new GetBookingDetailsRequest();
    getBookingDetailsRequest.BookingID = request.BookingID;
    getBookingDetailsRequest.CourseID = request.CourseID;
    getBookingDetailsRequest.IsFivePlayerTemplate = request.IsFivePlayerTemplate;

    const removeDialogData = new RemoveDialogData();
    return this.GetBookingDetails(getBookingDetailsRequest).pipe(
      tap((getBookingDetailsResponse: GetBookingDetailsResponse) =>
      {
        removeDialogData.allData = getBookingDetailsResponse;
        removeDialogData.bookingID = request.BookingID;
        removeDialogData.bookingDate = getBookingDetailsResponse.BookingDateTime;
        removeDialogData.numberOfPlayers = getBookingDetailsResponse.NumPlayers;
        removeDialogData.selectedCourseName = request.CourseName;
      }),
      switchMap((getBookingDetailsResponse: GetBookingDetailsResponse) =>
      {
        const getRemoveTeeTimeDialogDataRequest = new GetRemoveTeeTimeDialogDataRequest();
        getRemoveTeeTimeDialogDataRequest.BookingID = request.BookingID;
        getRemoveTeeTimeDialogDataRequest.StartTee = getBookingDetailsResponse.StartTee;
        getRemoveTeeTimeDialogDataRequest.players = getBookingDetailsResponse.Players;
        return this.GetRemoveTeeTimeDialogData(getRemoveTeeTimeDialogDataRequest).pipe(
          tap((getRemoveTeeTimeDialogDataResponse: GetRemoveTeeTimeDialogDataResponse) =>
          {
            removeDialogData.avatarCharacter = getRemoveTeeTimeDialogDataResponse.user.User.DisplayName[0];
            removeDialogData.userName = getRemoveTeeTimeDialogDataResponse.user.User.DisplayName;
            removeDialogData.totalPrice = getRemoveTeeTimeDialogDataResponse.groupPrice.TotalPrice;
            removeDialogData.playerEmails = getRemoveTeeTimeDialogDataResponse.playerEmails.PlayerEmails;
          }),
          switchMap(() => of(removeDialogData))
        );
      })
    );
  }

  GetPlayerPriceLevels(request: GetPlayerPriceLevelsRequest): Observable<GetPlayerPriceLevelsResponse>
  {
    const apiUrl = this.serviceUrl + 'GetPlayerPriceLevels';
    return this.http.post<GetPlayerPriceLevelsResponse>(apiUrl, request);
  }

  DeleteTeeTime(request: DeleteTeeTimeRequest): Observable<DeleteTeeTimeResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + 'DeleteTeeTime';
    return this.http.delete<DeleteTeeTimeResponse>(apiUrl, options);
  }

  StageTeeTimeCheckIn(request: StageTeeTimeCheckInRequest): Observable<StageTeeTimeCheckInResponse>
  {
    const apiUrl = this.serviceUrl + 'StageTeeTimeCheckIn';
    return this.http.post<StageTeeTimeCheckInResponse>(apiUrl, request);
  }

  DeleteTeeTimeCheckIn(request: DeleteTeeTimeCheckInRequest): Observable<DeleteTeeTimeCheckInResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + 'CheckIn';
    return this.http.delete<DeleteTeeTimeCheckInResponse>(apiUrl, options);
  }

  PostRestrictions(request: PostRestrictionsRequest): Observable<PostRestrictionsResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingDetails/restrictions';
    return this.http.post<PostRestrictionsResponse>(apiUrl, request);
  }

  PostTeeTimes(request: PostTeeTimesRequest): Observable<PostTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + 'TeeTimes';
    return this.http.post<PostTeeTimesResponse>(apiUrl, request);
  }

  RemoveBookingPlayer(request: PostRemoveBookingPlayerRequest): Observable<PostRemoveBookingPlayerResponse>
  {
    const apiUrl = this.serviceUrl + 'RemoveBookingPlayer';
    return this.http.post<PostRemoveBookingPlayerResponse>(apiUrl, request);
  }

  GetContactHasNotification(request: GetContactHasNotificationRequest): Observable<GetContactHasNotificationResponse>
  {
    const apiUrl = this.serviceUrl + 'GetAssignContact';
    return this.http.get<GetContactHasNotificationResponse>(apiUrl, { params: request as any });
  }

  MoveTeeTime(request: PostMoveTeeTimeRequest): Observable<PostMoveTeeTimeResponse>
  {
    const apiUrl = this.serviceUrl + 'MoveTeeTime';
    return this.http.post<PostMoveTeeTimeResponse>(apiUrl, request);
  }

  GetBookingCheckInDetails(request: GetBookingCheckInDetailsRequest): Observable<GetBookingCheckInDetailsResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingCheckinDetails';
    return this.http.get<GetBookingCheckInDetailsResponse>(apiUrl, { params: request as any });
  }
}
