import { Component, EventEmitter, Input, Output } from '@angular/core';
import { PriceBreakdownDialogDataModel } from '../../../@intermediate/models/price-breakdown.model';

@Component({
  selector: 'tei-pos-tee-sheet-price-breakdown-dialog',
  templateUrl: 'price-breakdown-dialog.component.html',
  styleUrls: ['price-breakdown-dialog.component.scss']
})
export class PriceBreakdownDialogComponent
{
  @Input() data: PriceBreakdownDialogDataModel;
  @Output() close: EventEmitter<null> = new EventEmitter();

  onCloseClick(): void
  {
    this.close.emit();
  }
}
