import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { IMainService } from "../@intermediate/main.interface";
import { GetTeeSheetMainCoursesRequest, GetTeeSheetMainCoursesResponse } from "../@intermediate/models/get-tee-sheet-main-courses.model";
import { GetTeeSheetMainRequest, GetTeeSheetMainResponse } from "../@intermediate/models/get-tee-sheet-main.model";

@Injectable()
export class MainService implements IMainService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/Main';
  }

  Get(request: GetTeeSheetMainRequest): Observable<GetTeeSheetMainResponse>
  {
    return this.http.get<GetTeeSheetMainResponse>(this.serviceUrl, { params: request as any });
  }

  Courses(request: GetTeeSheetMainCoursesRequest): Observable<GetTeeSheetMainCoursesResponse>
  {
    const apiUrl = this.serviceUrl + '/Courses';
    return this.http.get<GetTeeSheetMainCoursesResponse>(apiUrl, { params: request as any });
  }
}
