import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { DeleteUnBlockingTeeTimesRequest, DeleteUnBlockingTeeTimesResponse, GetTeeSheetBlockedTeeTimesRequest, GetTeeSheetBlockedTeeTimesResponse } from "../@intermediate/models/tee-sheet-unblock.model";
import { ITeeSheetUnBlockService } from "../@intermediate/tee-sheet-unblock-service.interface";

@Injectable({
  providedIn: 'root'
})

export class TeeSheetUnblockService implements ITeeSheetUnBlockService
{

  private readonly serviceUrl: string;
  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/ManageTeeTime';
  }

  TeeTimeBlocks(request: GetTeeSheetBlockedTeeTimesRequest): Observable<GetTeeSheetBlockedTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/TeeTimeBlocks';
    return this.http.get<GetTeeSheetBlockedTeeTimesResponse>(apiUrl, { params: request as any });
  }

  UnBlockingTeeTimes(request: DeleteUnBlockingTeeTimesRequest): Observable<DeleteUnBlockingTeeTimesResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + '/UnBlockingTeeTimes';
    return this.http.delete<DeleteUnBlockingTeeTimesResponse>(apiUrl, options);
  }

}
