import { Course, SelectedBlockCourse } from "./get-tee-sheet-main.model";
import { GroupDefaultValue } from "./group-booking-details.model";

export class GetTeeSheetRequest
{
  TeeSheetDate: string;
  CourseID: string;
  StartTeeHole?: Holes1or10;

}
export class GroupBookingSelectedTeeTime
{
  Time: string;
  Players: Array<number>;
  selectedRow: number;
  playerIndex: number | undefined;
  Course: Course;
  Bookings: Booking[] = [];
  SelectedDate?: Date;
  // Use in FE
  GroupUID?: string;
}
export class TeeSheet
{
  GetTeeSheet: GetTeeSheetResponse;
  IsEmptyTeeSheet: boolean;
  SelectedTeeSheetDate: Date;
  IsLoading: boolean;
  SelectedCourse: Course;
  StartTeeHole?: Holes1or10;
}
export class GetTeeSheetResponse
{
  PriceLevels: PriceLevel[];
  // Template
  TemplateID: string;
  FivePlayerTemplate: boolean;
  TemplateTimes: string[] = [];
  // Template

  TeeSheetTemplate: CourseTemplate;
  TeeTimes: TeeTime[] = [];
  StatisticIndicator: StatisticIndicator;
  Groups: GroupTeeSheet[] = [];
}
export class StatisticIndicator
{
  TotalAvailable: number;
  TotalBlocked: number;
  TotalBooked: number;
  TotalCheckedIn: number;
}
export class TeeTime
{
  TimeZone: TimeZone;
  TimeZoneName: string;
  TimeZoneColor: string;

  GolfPrice: number;
  CartPrice: number;

  Time: string;
  Bookings: Booking[] = [];

  SmallGroupBookings: SmallGroupBooking[] = [];
  GroupBookings: GroupBooking[] = [];
  EventBookings: EventBooking[] = [];
  Blockings: Blocking[] = [];
  teeTimeRow?: TeeTimeRow[] = [];
  firstOfEventBookings?: boolean;
  lastOfEventBookings?: boolean;
  IgnoreClassRestrictions: boolean;
  ClassRestrictions: ClassRestriction[];
  TeeTimeInfo: TeeTimeInfo;
  showInputTeeTimeText: boolean;
  inputValueDisplayTime?: string;
  showInputTeeTimeHoleNumber?: boolean;
  inputHoleNumberValue?: string;
  NineHoles: number;
  classRestrictionsInfo?: ClassRestrictionsInfo;
}
export class GroupTeeSheet
{
  GroupName: string;
  GroupUID: string;
}
export class TeeTimeInfo
{
  DisplayTime: string;
  HoleNumber: string;
}

export class Booking
{
  BookingID: number;
  BookingType: BookingType;
  BlockedTypeID: string;
  Contacts: Contact[] = [];
  CrossoverCourse: string;
  CrossoverStartTime: Date;
  contact?: Contact;
  H1Player1NineHoles: number;
  H1Player2NineHoles: number;
  H1Player3NineHoles: number;
  H1Player4NineHoles: number;
  H1Player5NineHoles: number;
  H10Player1NineHoles: number;
  H10Player2NineHoles: number;
  H10Player3NineHoles: number;
  H10Player4NineHoles: number;
  H10Player5NineHoles: number;
}

export class SmallGroupBooking
{
  BookingID: number;
  BookingType: BookingType;
  BlockedTypeID: string;
  Contacts: Contact[] = [];
  SmallGroupName?: string;
}
export class GroupBooking
{
  GroupName: string;
  TeeGroupUID: string;
}

export class EventBooking
{
  BookingID: number;
  BookingType: BookingType;
  BlockedTypeID: string;
  Contacts: Contact[] = [];
  EventID: string;
  EventName: string;
}

export class Blocking
{
  BlockingType: BlockingType;
  BlockedTypeID: string;
  DisplayName: string;
  AllowBookings: boolean;
  SlotsPerRow: number;
  Note: string;
  BlockColor: string;
  StartTeeHole: StartTeeHole;
  BookingID: number;
  CreatedDate: string;
  CrossoverCourse?: string;
  CrossoverStartTime: null;
  TeeTimeID: string;
}

export class TeeTimeRow extends Booking
{
  contact: Contact;
  ContactCount: number;
  blocking: Blocking;
  first: boolean;
  last: boolean;
  playerIndex: number;
  NotReservedContactCount?: number;
  HasReserved?: boolean;
  ReserveBlockColor?: string;
}

export class ClassRestriction
{
  ContactClassID: string;
  ContactClassDescription: string;
  ContactClassCode?: string;
  TeeSheetColour: string;
  SortOrder: number;
}

export class ClassRestrictionsInfo
{
  lockOpen: boolean;
  public: boolean;
  web: boolean;
  otherPermittedClasses: ClassRestriction[];
}

export enum ContactClassIDEnum
{
  Public = 'Public',
  Web = 'Web'
}

export class Contact
{
  TeeTimeID: string;
  ContactID: string;
  DisplayName: string;
  PlayerStatus: PlayerStatus;
  PlayerNumber: number;
  BookingNote: string;
  BookingID: number;
  WebDisplayName: string;
  Code: string;
  Colour: string;
  Description: string;
  CartSubTotal: number;
  GolfSubTotal: number;
  CartType: CartTypeEnum;
  HasPowerCart: boolean;
  DisplayNameInfo: string;
  IsWebBooking: boolean;
}
export class TeeTimeBooking
{
  Time: Date;
  TeeTimeID: string;
  PlayerNumber: number;
  DisplayName: string;
  BookingID: number;
  PlayerStatus: PlayerStatus;
  BookingNote: string;
  BookingType: BookingType;
  BlockedTypeID: string;
  ContactID: string;
}
export class CourseTemplate
{
  CourseType: CourseType;
}

export enum CourseType
{
  Holes18,
  Holes1and10,
  Holes9
}

export enum PlayerStatus
{
  None,
  PrePaid,
  Paid,
  CheckIn,
  NoShow,
  CheckInInprogress,
  NoShowFromAPI
}

export enum TimeZone
{
  PrimeTime,
  Zone1,
  Zone2,
  Zone3,
  Zone4,
  Zone5,
  Zone6,
  Zone7,
  Zone8
}

export enum BlockingType
{
  TeeTimeBlocking, // regular blocking
  SplitTee,
  Crossover,
  InProgress,
  Reserved,
  FlagCrossover
}

export enum BlockingTypeIDEnum
{
  FROST_DELAY = "FROST DELAY",
  Reserved = "Reserved"
}

export enum BookingType
{
  Retail_Item = -1,
  Regular = 0,
  Tournament = 1,
  SmallGroup = 2,
  Blocked = 3,
  Leagues = 4,
  GroupTeeTime = 5
}

export enum StartTeeHole
{
  Hole1,
  Hole10
}

export enum TeeTimeHoles
{
  Holes18,
  Holes9
}

export enum Transportation
{
  PowerCart,
  PullCart,
  Walk
}

export enum ContextMenuActionType
{
  Close,
  Booking,
  GroupBooking,
  Block,
  UnBlock,
  RainCheck,
  SqueezeTimeAbove,
  SqueezeTimeBelow,
  FlagReserved,
  FlagNineHoles,
  FlagCrossover,
  UnFlagReserved,
  UnFlagNineHoles,
  UnFlagCrossover,
  Admin,
  Cancel,
  Move,
  Copy,
  Notes,
  QuickCheckIn,
  DeleteInProgress,
  NewGroup
}

export enum Holes1or10
{
  Hole1,
  Hole10,
}

export class PriceLevel
{
  Description: string;
  Expire: boolean;
  ExpiryDate: string;
  IsPublic: boolean;
  PriceLevelID: string;
  IsHidden?: boolean;
  PlayerNumber?: number;
}

export enum CartTypeEnum
{
  Power,
  Pull,
  Walk
}

export interface TeeSheetDialogData
{
  SelectedTeeSheetDate: Date;
  Course: SelectedBlockCourse;
  PlayerIndex: number;
  RowIndex: number;
  TemplateTimes: string[];
  FivePlayerTemplate: boolean;
}

export class TeeTimeConfig
{
  ShowMoveButton = false;
  ShowDisplayContactIcon = true;
  ShowNotificationIcon = true;
  CallPlayerPriceApi = true;
  GroupDefaultValues: GroupDefaultValue;
  FilterPlayer?: TeeTimeConfigFilterPlayer;
}

export class TeeTimeConfigFilterPlayer
{
  HideCheckInPlayers?: boolean = false;
  ContactID?: string;
}
