import { AfterViewInit, ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, Output, ViewChild } from '@angular/core';
import { MatLegacyMenuTrigger as MatMenuTrigger } from '@angular/material/legacy-menu';
import { Subscription, fromEvent } from 'rxjs';
import { SelectedDisableOptionsCourse } from '../../../@intermediate/models/get-tee-sheet-main.model';
import { BlockingType, BlockingTypeIDEnum, BookingType, Contact, ContextMenuActionType, GroupTeeSheet, PlayerStatus } from '../../../@intermediate/models/get-tee-sheet.model';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-slot-context-menu',
  templateUrl: './tee-sheet-slot-context-menu.component.html',
  styleUrls: ['./tee-sheet-slot-context-menu.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TeeSheetSlotContextMenuComponent implements AfterViewInit, OnDestroy
{
  @Input() contextmenuX = 0;
  @Input() contextmenuY = 0;
  @Input() isRightClick: boolean;
  @Input() isNineHole: boolean;
  @Input() status: PlayerStatus;
  @Input() bookingType: BookingType;
  @Input() blockingType: BlockingType;
  @Input() teeTimeContacts: Contact[];
  @Input() teeTimeCurrentContact: Contact;
  @Input() allowBooking: boolean;
  @Input() PlayerIndex: number;
  @Input() showBlockIcon: boolean;
  @Input() showUnBlockIcon: boolean;
  @Input() showUnFlagReserved: boolean;
  @Input() showUnFlagCrossover: boolean;
  @Input() blockingTypeIDEnum: BlockingTypeIDEnum;
  @Input() groupsList: GroupTeeSheet[] = [];
  @Output() onCloseContextMenu = new EventEmitter();
  @Output() onSelectGroupTeeSheet: EventEmitter<GroupTeeSheet> = new EventEmitter<GroupTeeSheet>();
  @Input() selectedDisableOptionsCourse: SelectedDisableOptionsCourse;
  @Input() teeTimeGroupUID: string;

  @ViewChild('rightClickMenuTrigger') contextMenu: MatMenuTrigger;

  windowClickSubscription: Subscription;
  windowContextmenuSubscription: Subscription;
  windowTouchEndSubscription = new Subscription();
  matMenuIsOpen = false;
  playerStatus = PlayerStatus;
  contextMenuActionType = ContextMenuActionType;
  isAnyContactInTeeTimeCheckedIn: boolean = false;
  bookingTypeEnum = BookingType;
  showAdmin = false;
  showBlock = true;
  isQuickCheckIn: boolean;
  BlockingTypeIDEnum = BlockingTypeIDEnum;
  BlockingTypeEnum = BlockingType;
  noteNumbers = 1;
  ngOnInit(): void
  {
    this.isAnyContactInTeeTimeCheckedIn = this.teeTimeContacts?.some(contact => contact.PlayerStatus === PlayerStatus.CheckIn);
    this.checkQuickCheckIn();
  }

  checkQuickCheckIn(): void
  {
    if (this.teeTimeContacts?.length && (this.status === this.playerStatus.None || this.status === this.playerStatus.PrePaid) && (this.bookingType === BookingType.Regular || this.bookingType === BookingType.GroupTeeTime))
    {
      this.isQuickCheckIn = true;
    }
  }

  ngAfterViewInit(): void
  {
    if (this.isRightClick)
    {
      this.contextMenu.openMenu();
      // Close menu when tapping/clicking elsewhere

      this.windowClickSubscription = fromEvent(window, 'click').subscribe(() =>
      {
        if (this.contextMenu && this.contextMenu.menuOpened)
        {
          this.contextMenu.closeMenu();
        }
      });


      this.windowContextmenuSubscription = fromEvent(window, 'contextmenu').subscribe(() =>
      {
        if (this.contextMenu && this.matMenuIsOpen)
        {
          this.contextMenu.closeMenu();
        }
        this.matMenuIsOpen = true;
      });
    }
  }

  hasNote(): boolean
  {
    return this.teeTimeCurrentContact.BookingNote?.trim()?.length > 0;
  }
  onClickAction(contextMenuActionType: ContextMenuActionType): void
  {
    this.onCloseContextMenu.emit(contextMenuActionType);
  }

  onClickGroupAction(group: GroupTeeSheet): void
  {
    this.onSelectGroupTeeSheet.emit(group);
  }

  disableQuickCheckIn(status: PlayerStatus): boolean
  {
    return this.selectedDisableOptionsCourse.IsPastDate && !this.selectedDisableOptionsCourse.AllowPastCheckins && this.notCheckIn(status);
  }

  notCheckIn(status: PlayerStatus): boolean
  {
    return status === PlayerStatus.None || status === PlayerStatus.Paid || status === PlayerStatus.CheckIn || status === PlayerStatus.PrePaid;
  }

  checkInAndPaid(status: PlayerStatus): boolean
  {
    return status === PlayerStatus.Paid || status === PlayerStatus.CheckIn || status === PlayerStatus.PrePaid;
  }
  shouldShowBookingOptions(): boolean
  {
    return this.status === this.playerStatus.NoShow ||
      (this.bookingType === this.bookingTypeEnum.Blocked && this.allowBooking) ||
      this.blockingTypeIDEnum === BlockingTypeIDEnum.Reserved;
  }
  shouldShowAddToGroup(): boolean
  {
    const isBlockedWithBooking = this.bookingType === this.bookingTypeEnum.Blocked && this.allowBooking;
    return this.bookingType !== this.bookingTypeEnum.GroupTeeTime && !isBlockedWithBooking;
  }
  ngOnDestroy(): void
  {
    this.windowClickSubscription?.unsubscribe();
    this.windowContextmenuSubscription?.unsubscribe();
    this.windowTouchEndSubscription?.unsubscribe();
  }
}
