import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetGroupBookingDetailsRequest, GetGroupBookingDetailsResponse } from '../@intermediate/models/group-booking-details.model';
import { IGroupBookingDetailsService } from '../@intermediate/get-booking-details.interface';

@Injectable({
  providedIn: 'root'
})

export class GroupBookingDetailsService implements IGroupBookingDetailsService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient,)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/GroupTeeTimes/';
  }

  GetGroupBookingDetails(request: GetGroupBookingDetailsRequest): Observable<GetGroupBookingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingDetailsByGroupUID';
    return this.http.get<GetGroupBookingDetailsResponse>(apiUrl, { params: request as any });
  }

}
