export class GetTeeTimeAttachmentRequest
{
  CourseID: string;
  BookingID: number;
  PlayerNumber: number;
  Is9Holes: boolean;
}

export class GetTeeTimeAttachmentResponse
{
  AttachmentGroups: Array<AttachmentGroup>;
}

export class AttachmentGroup
{
  Attachments: Array<Attachment>;
  GroupName: string;
  GroupRequired: boolean;
  GroupSortIndex: number;
  GroupUID: string;
}

export class Attachment
{
  Abbreviation: string;
  AttachmentPrice: number;
  AttachmentUID: string;
  AutoApply: boolean;
  Holes: number;
  ItemDescription: string;
  ItemID: string;
  Selected: boolean;
  SortIndex: number;
  UofM: string;
}

export class AttachmentsDialogDataModel extends GetTeeTimeAttachmentRequest
{
  teeTimeAttachmentResponse: GetTeeTimeAttachmentResponse;
}

