import { Component } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TeeSheetDisplayService } from '../../../../@services/tee-sheet-display.service';
import { DisplayFormComponent } from '../display-form.component';
import { ManageLoadingService } from '@tei/core';
import { TeeSheetService } from '../../../../@services/tee-sheet.service';

@Component({
  selector: 'tei-pos-tee-sheet-display-form-mobile',
  templateUrl: './display-form-mobile.component.html',
  styleUrls: ['../display-form.component.scss']
})
export class DisplayFormMobileComponent extends DisplayFormComponent {
  expanded = true;

  constructor(
    public teeSheetDisplayService: TeeSheetDisplayService,
    public dialog: MatDialog,
    public teeSheetService: TeeSheetService,
    public manageLoadingService: ManageLoadingService
  ) {
    super(teeSheetDisplayService, dialog, teeSheetService, manageLoadingService);

  }

  getSelectedCourseName(): string {
    const courseId = this.form?.get('Course')?.value;
    if (!courseId) return '';

    const course = this.coursesList?.find(c => c.CourseID === courseId);
    return course?.CourseName || '';
  }

  getTimeDisplay(index: number): string {
    if (index === undefined || index === null) return '';
    return this.teeSheetTemplates?.find(t => t.Index === index)?.TeeTime || '';
  }

  onExpansionChange(): void {
    this.expanded = !this.expanded;
  }
}
