import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';

import { FormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from '@tei/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule, TreeModule } from '@tei/material';
import { TeeSheetUnFlagComponent } from './tee-sheet-un-flag.component';

const COMPONENTS = [TeeSheetUnFlagComponent];
const BASE_MODULES = [
  CommonModule,
  FormsModule
];
const MATERIAL_MODULES = [
  MatExpansionModule,
  MatCardModule,
  MatButtonModule,
  MatCheckboxModule,
  MatDividerModule,
  MatDialogModule
];
const TEI_MATERIAL_MODULES = [LoadingModule, TreeModule, BreadCrumbModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...COMPONENTS
  ],
  providers: [ManageStandAloneLoadingService]
})
export class TeeSheetUnFlagModule { }
