import { Component, Inject } from '@angular/core';
import { UntypedFormBuilder } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageLoadingService } from '@tei/core';
import { CounterEvents } from '../../../@intermediate/models/post-link-tee-time-to-group.model';

@Component({
  selector: 'tei-pos-tee-sheet-add-new-tee-time-dialog',
  templateUrl: './add-new-tee-time-dialog.component.html',
  styleUrls: ['./add-new-tee-time-dialog.component.scss']
})
export class AddNewTeeTimeDialogComponent
{
  counterEvents = CounterEvents;
  form = this.fb.group({
    teeTimes: 1
  });
  get formControls()
  {
    return this.form?.controls;
  }
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddNewTeeTimeDialogComponent>,
    public manageLoadingService: ManageLoadingService,
    private fb: UntypedFormBuilder) { }

  onCloseClick(): void
  {
    this.dialogRef.close();
  }

  onConfirmClick(): void
  {
    this.dialogRef.close(Number(this.formControls.teeTimes?.value));
  }
  changeNumberOfTeeTimes(event: number): void
  {
    let count = Number(this.formControls.teeTimes?.value);
    if (event === this.counterEvents.Minus)
    {
      if (count > 1)
      {
        count--;
        this.formControls.teeTimes.setValue(count);
      } else if (count === 1)
      {
        this.formControls.teeTimes.setValue(1);
      }
    }
    if (event === this.counterEvents.Plus)
    {
      if (count < 100)
      {
        count++;
        this.formControls.teeTimes.setValue(count);
      }
    }
  }
}

