import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { Subscription } from 'rxjs';
import { DisplayTeeSheetDialogData } from '../../@intermediate/models/get-teesheet-display.model';
import { TeeSheetDisplayService } from '../../@services/tee-sheet-display.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-display',
  templateUrl: './tee-sheet-display.component.html',
  styleUrls: ['./tee-sheet-display.component.scss']
})
export class TeeSheetDisplayComponent implements OnInit, OnDestroy {
  subscription = new Subscription();
  isMobile = false;
  courseId: string = '';
  sheetDate: string = '';
  dialogData = new DisplayTeeSheetDialogData();

  constructor(
    private dialogRef: MatDialogRef<TeeSheetDisplayComponent>,
    public responsiveService: ResponsiveService,
    public teeSheetDisplayService: TeeSheetDisplayService,
    public manageLoadingService: ManageLoadingService,

  ) { }

  ngOnInit(): void
  {

    this.getMobileSize();

    this.dialogData = this.teeSheetDisplayService.getDisplayDialogData()
    if (this.dialogData.selectedDate) {

      this.courseId = this.dialogData.selectedCourse?.CourseID;
      this.sheetDate = this.dialogData.selectedDate;
    }
  }



  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  onClose(): void
  {
    this.dialogRef.close();
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
  onCancel() {
    this.dialogRef.close(false);
  }

}
