import { BehaviorSubject } from 'rxjs';
import { TeeSheetTransfer } from '../@intermediate/models/tee-sheet-transfer-service.model';
import { ITeeSheetTransferService } from '../@intermediate/tee-sheet-transfer-service.interface';
import { Injectable } from '@angular/core';
@Injectable()
export class TeeSheetTransferService implements ITeeSheetTransferService
{
  private teeSheetTransfer = new BehaviorSubject<TeeSheetTransfer>(new TeeSheetTransfer());
  currentTeeSheetTransfer = this.teeSheetTransfer.asObservable();

  changeTeeSheet(subject: TeeSheetTransfer)
  {
    this.teeSheetTransfer.next(subject);
  }

  get(): TeeSheetTransfer
  {
    return this.teeSheetTransfer.getValue();
  }
}
