import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from '@tei/core';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { RebuildTeeSheetDialogComponent } from './rebuild-tee-sheet-dialog.component';

const MATERIAL_MODULES = [SafeHtmlPipeModule,MatDividerModule, MatIconModule, MatButtonModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [LoadingModule];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [RebuildTeeSheetDialogComponent];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [...COMMON_MODULES, ...MATERIAL_MODULES, ...TEI_MATERIAL_MODULES, ...TEI_CORE_MODULES],
  exports: [
    ...COMMON_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS,
    ...TEI_MATERIAL_MODULES,
  ],
  providers: [
    ManageStandAloneLoadingService
  ]
})

export class RebuildTeeSheetDialogModule { }