import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { AbstractControl, UntypedFormArray, UntypedFormControl, UntypedFormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { BehaviorSubject, Subscription, firstValueFrom } from 'rxjs';
import { CartTypes, CartTypesID, Player, PlayerStatus } from '../../../../@intermediate/models/get-booking-details.model';
import { GetRainCheckPriceOverrideAccessRequest } from '../../../../@intermediate/models/get-rain-check-price-override-access.model';
import { GetRainCheckPlayerPricesRequest, GetRainCheckPlayerPricesResponse } from '../../../../@intermediate/models/get-rain-check-setup-prices.model';
import { HolePricing, RainCheckTypeEnum } from '../../../../@intermediate/models/get-rain-check-setup.model';
import { PostVoidRainCheckRequest } from '../../../../@intermediate/models/post-void-rain-check.model';
import { HoleNumberEnum, RainCheckSelectPlayersForm, RainCheckWizardDataModel, RainCheckWizardTransitionDataModel } from '../../../../@intermediate/models/rain-check-helper.model';
import { ITeeSheetService } from '../../../../@intermediate/tee-sheet.interface';

@Component({
  selector: 'tei-pos-tee-sheet-rain-check-select-player',
  templateUrl: './rain-check-select-player.component.html',
  styleUrls: ['./rain-check-select-player.component.scss']
})
export class RainCheckSelectPlayerComponent implements OnInit, OnDestroy
{
  onSubmit()
  {
    throw new Error('Method not implemented.');
  }
  @Input() stepper: MatStepper;
  @Input() rainCheck: RainCheckWizardDataModel;
  @Input() reloadForm: BehaviorSubject<boolean>;
  @Output() setTransitionData: EventEmitter<RainCheckWizardTransitionDataModel> = new EventEmitter();
  @Output() removeRainCheckByPlayerNumber: EventEmitter<number> = new EventEmitter();

  players: Player[];
  transportationTypes: Array<CartTypes>;
  cartTypesID = CartTypesID;
  form: UntypedFormGroup;
  playersForm: UntypedFormArray = new UntypedFormArray([]);
  totalPrice = 0;
  subscriptions: Subscription = new Subscription();
  overridingPrice = false;
  isMobile: boolean;

  get isHolePricingSetup(): boolean
  {
    return this.rainCheck?.RainCheckSetup?.RainCheckType === RainCheckTypeEnum.HoleSetup;
  }

  get isPriceOverride(): boolean
  {
    return this.rainCheck?.RainCheckSetup?.RainCheckType === RainCheckTypeEnum.HoleSetup ||
      this.rainCheck?.RainCheckSetup?.RainCheckType === RainCheckTypeEnum.TemplatePriceWithOverride;
  }


  constructor(
    @Inject('ITeeSheetService') private teeSheetService: ITeeSheetService,
    public dialogRef: MatDialogRef<RainCheckSelectPlayerComponent>,
    public responsiveService: ResponsiveService,
    private snackBarService: SnackBarService,
    private dialog: MatDialog,
    private translate: TranslateService) { }

  ngOnInit(): void
  {
    this.initialData();
    this.subscribeToReloadForm();
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    this.subscriptions.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  }

  private subscribeToReloadForm()
  {
    this.subscriptions.add(this.reloadForm.subscribe(reloadForm =>
    {
      if (reloadForm)
      {
        this.initialData();
      }
    }));
  }

  initialData(): void
  {
    this.playersForm.clear();
    this.players = this.rainCheck?.Players;

    this.transportationTypes = [
      {
        CartID: this.cartTypesID.Power,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.POWER')
      },
      {
        CartID: this.cartTypesID.Pull,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.PULL')
      },
      {
        CartID: this.cartTypesID.Walk,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.WALK')
      }
    ];

    this.rainCheck?.Players.forEach((player, index) =>
    {
      if (player.Status === PlayerStatus.CheckIn)
      {
        const issuedPlayers = this.rainCheck?.RainCheckSetup.PlayerRainchecks.some(item => item.Issued && item.PlayerNumber === index + 1);
        this.playersForm.push(this.createPlayerForm(player, issuedPlayers));
      }
    });

    this.form = new UntypedFormGroup({
      players: this.playersForm
    }, { validators: this.checkPlayersSelectionValidator });

    this.updateTotalPrice(this.playersForm.getRawValue());

    this.handlePlayersFormValueChange();

    this.playersForm.controls.forEach((eachPlayerForm: UntypedFormGroup, index) =>
    {
      this.handleIsSelectedValueChange(eachPlayerForm);
      this.handleHolesValueChange(eachPlayerForm, index);
      this.handleCardIdValueChange(eachPlayerForm, index);
    });
  }

  private createPlayerForm(player: Player, issuedPlayers: boolean)
  {
    const formGroup = new UntypedFormGroup({
      contact: new UntypedFormControl(player.Contact),
      teeTimeId: new UntypedFormControl(player.TeeTimeID),
      playerHoles: new UntypedFormControl(player.Holes),
      cartID: new UntypedFormControl(player.Cart),
      playerNumber: new UntypedFormControl(player.PlayerNumber),
      holesPrice: new UntypedFormControl({ value: this.getPriceToFixed2(0), disabled: true }),
      cartPrice: new UntypedFormControl({ value: this.getPriceToFixed2(0), disabled: true }),
      attachmentPrice: new UntypedFormControl(player.AttachmentPrice),
      holes: new UntypedFormControl(),
      golfPrice: new UntypedFormControl(player.GolfPrice),
      isSelected: new UntypedFormControl(false),
      issued: new UntypedFormControl(issuedPlayers),
      holesTitle: new UntypedFormControl()
    });

    if (issuedPlayers)
    {
      formGroup.disable();
    }

    return formGroup;
  }

  private handleCardIdValueChange(eachPlayerForm: UntypedFormGroup, index: number): void
  {
    this.subscriptions.add(
      eachPlayerForm.controls.cartID.valueChanges.subscribe(async () =>
      {
        await this.updateCartPrice({ eachPlayerForm, holePricing: eachPlayerForm.controls.holes.value, index });
      })
    );
  }

  private handleHolesValueChange(eachPlayerForm: UntypedFormGroup, index: number): void
  {
    this.subscriptions.add(
      eachPlayerForm.controls.holes.valueChanges.subscribe(async (holePricing: HolePricing | boolean) =>
      {
        await this.updateHolesPrice({ eachPlayerForm, holePricing, index });
      })
    );
  }

  private handleIsSelectedValueChange(eachPlayerForm: UntypedFormGroup): void
  {
    this.subscriptions.add(
      eachPlayerForm.controls.isSelected.valueChanges.subscribe((isSelected: boolean) =>
      {
        if (isSelected)
        {
          eachPlayerForm.controls.holes.setValidators(Validators.required);
        } else
        {
          eachPlayerForm.controls.holes.clearValidators();
          eachPlayerForm.controls.holes.reset();
          eachPlayerForm.controls.holes.updateValueAndValidity();
        }
        this.playersForm.updateValueAndValidity();
      })
    );
  }

  private handlePlayersFormValueChange(): void
  {
    this.subscriptions.add(
      this.playersForm.valueChanges.subscribe(() =>
      {
        this.updateTotalPrice(this.playersForm.getRawValue());
      })
    );
  }


  async updateHolesPrice(
    { eachPlayerForm, holePricing, index }:
      {
        eachPlayerForm: UntypedFormGroup;
        holePricing: boolean | HolePricing;
        index: number;
      }
  )
  {
    const player: Player = this.players[index];

    let result: GetRainCheckPlayerPricesResponse;

    if (typeof holePricing !== 'boolean')
    {
      const totalGolfPrice = player.GolfPrice - player.GolfDiscount;

      eachPlayerForm.patchValue({
        holesPrice: this.getPriceToFixed2(totalGolfPrice * holePricing.GolfValue),
        holesTitle: this.getHolesTitle(holePricing)
      });

      await this.updateCartPrice({ eachPlayerForm, holePricing, index });
    }
    else
    {
      const request: GetRainCheckPlayerPricesRequest = {
        BookingID: this.rainCheck.BookingID,
        PlayerNumber: this.players[index].PlayerNumber,
        Is9Holes: holePricing
      };

      result = await firstValueFrom(this.teeSheetService.GetRainCheckSetupPrices(request));

      eachPlayerForm.patchValue({
        holesPrice: this.getPriceToFixed2(result.GolfPrice),
        holesTitle: this.getHolesTitle(holePricing)
      });

      await this.updateCartPrice({ eachPlayerForm, holePricing, index, result });
    }
  }

  getHolesTitle(holePricing: boolean | HolePricing): string
  {
    if (typeof holePricing === 'boolean')
    {
      const holeNumber = holePricing ? HoleNumberEnum.Hole9 : HoleNumberEnum.Hole18;
      return `${ holeNumber } ${ this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.HOLES') }`;
    }
    const holeText = this.translate.instant(holePricing.HoleNumber === 1 ? 'POS_TEE_SHEET.MANAGE_TEE_TIME.HOLE' : 'POS_TEE_SHEET.MANAGE_TEE_TIME.HOLES');
    return `${ holePricing.HoleNumber } ${ holeText }`;
  }

  async updateCartPrice(
    { eachPlayerForm, holePricing, index, result }:
      {
        eachPlayerForm: UntypedFormGroup;
        holePricing: boolean | HolePricing;
        index: number;
        result?: GetRainCheckPlayerPricesResponse;
      }
  ): Promise<void>
  {
    const player: Player = this.players[index];

    if (typeof holePricing !== 'boolean')
    {
      const totalCartPrice = player.CartPrice - player.CartDiscount;

      let cartPrice = '0.00';

      switch (eachPlayerForm.controls.cartID.value)
      {
        case CartTypesID.Power:
          cartPrice = this.getPriceToFixed2(totalCartPrice * holePricing.PowerCartValue);
          break;
        case CartTypesID.Pull:
          cartPrice = this.getPriceToFixed2(totalCartPrice * holePricing.PullCartValue);
          break;
        default:
          break;
      }
      eachPlayerForm.patchValue({
        cartPrice
      });
    }
    else
    {
      if (!result)
      {

        const request: GetRainCheckPlayerPricesRequest = {
          BookingID: this.rainCheck.BookingID,
          PlayerNumber: this.players[index].PlayerNumber,
          Is9Holes: holePricing
        };

        result = await firstValueFrom(this.teeSheetService.GetRainCheckSetupPrices(request));
      }

      let cartPrice = '0.00';

      switch (eachPlayerForm.controls.cartID.value)
      {
        case CartTypesID.Power:
          cartPrice = this.getPriceToFixed2(result.PowerCartPrice);
          break;
        case CartTypesID.Pull:
          cartPrice = this.getPriceToFixed2(result.PullCartPrice);
          break;
        default:
          break;
      }

      eachPlayerForm.patchValue({
        cartPrice
      });
    }
  }

  checkPlayersSelectionValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null =>
  {
    const players = control.get('players');
    return players.value.some((player) => player.isSelected) ? null : { noneSelect: true };
  };

  getPriceToFixed2(price: number): string
  {
    return price.toFixed(2);
  }

  updateTotalPrice(players: any[]): void
  {
    this.totalPrice = players.reduce((acc, player) =>
    {
      if (player.isSelected)
      {
        return acc + Number(player.cartPrice.replace('$', '')) + Number(player.holesPrice.replace('$', ''));
      } else
      {
        return acc;
      }
    }, 0);
  }

  toggleCheckPlayer(playerIndex: number, lastValue: boolean): void
  {
    this.playersForm.at(playerIndex).patchValue({ isSelected: !lastValue });
  }

  async overrideOrResetPrice(): Promise<void>
  {
    if (this.overridingPrice)
    {
      await this.resetPrice();
    } else
    {
      this.overridePrice();
    }
  }

  async resetPrice(): Promise<void>
  {
    const players = (this.form.get('players') as UntypedFormArray).controls;
    for (let index = 0; index < players.length; index++)
    {
      const eachPlayerForm = players[index] as UntypedFormGroup;

      if (eachPlayerForm.value.issued) { continue; }

      if (eachPlayerForm.controls.holes.value)
      {
        await this.updateHolesPrice({ eachPlayerForm, holePricing: eachPlayerForm.controls.holes.value, index });
      }
      else
      {
        eachPlayerForm.patchValue({
          cartPrice: this.getPriceToFixed2(0),
          holesPrice: this.getPriceToFixed2(0),
          holesTitle: null
        });
      }
    }

    this.overridingPrice = false;
  }

  overridePrice(): void
  {
    const request = new GetRainCheckPriceOverrideAccessRequest();
    this.subscriptions.add(this.teeSheetService.GetRainCheckPriceOverrideAccess(request).subscribe({
      next: (result) =>
      {
        if (result.Allowed)
        {
          (this.form.get('players') as UntypedFormArray).controls.forEach((player) =>
          {
            if (!player.value.issued)
            {
              player.get('cartPrice').enable();
              player.get('holesPrice').enable();
            }
          });

          this.overridingPrice = true;
        }
      }
    }));
  }

  voidRainCheck(playerNumber: number): void
  {

    const dialogData = {
      title: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.VOID_TITLE'),
      content: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.VOID_DESCRIPTION'),
      question: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.VOID_DESCRIPTION'),
      submitButtonLabel: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.PROCEED'),
      submitButtonColor: 'accent',
      cancelButtonLabel: 'POS_TEE_SHEET.MANAGE_TEE_TIME.CANCEL'
    };

    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: dialogData
      },
      width: '350px'
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.VOIDED_SUCCESSFULL'), '', null, 'left');
      if (result)
      {
        const request: PostVoidRainCheckRequest = {
          RainCheckID: this.rainCheck.RainCheckSetup.PlayerRainchecks.find(x => x.PlayerNumber == playerNumber).RainCheckID,
          PlayerNumber: playerNumber
        };
        this.subscriptions.add(this.teeSheetService.PostVoidRainCheck(request).subscribe(res =>
        {
          if (res)
          {
            this.removeRainCheckByPlayerNumber.emit(playerNumber);
          }
        }));
      }
    });
  }

  onCloseClick(): void
  {
    this.dialogRef.close();
  }

  newTab(): void
  {
    this.setTransitionData.emit(
      {
        totalPrice: this.totalPrice,
        players: (this.playersForm.getRawValue() as RainCheckSelectPlayersForm[]).filter(player => player.isSelected && !player.issued)
      }
    );

    this.stepper.next();
  }

  shortenNameIfButtonExists(name: string, buttonExists: boolean): string
  {
    if (buttonExists)
    {
      const maxLength = 25;
      return name.length > maxLength ? name.substring(0, maxLength - 3) + '...' : name;
    }
    return name;
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
