import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BookingRestriction } from '../../../@intermediate/models/post-restrictions.model';
import { BookingRestrictionPerPlayer } from '../../../@intermediate/models/restrictions-helper.model';

@Component({
  selector: 'tei-pos-tee-sheet-booking-restrictions-dialog',
  templateUrl: './booking-restrictions-dialog.component.html',
  styleUrls: ['./booking-restrictions-dialog.component.scss']
})
export class BookingRestrictionsDialogComponent implements OnInit
{

  restrictions: BookingRestrictionPerPlayer[] = [];
  constructor(private dialogRef: MatDialogRef<BookingRestrictionsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Partial<{ dialogData: BookingRestriction[]; }>)
  { }

  ngOnInit(): void
  {
    const restrictions: BookingRestrictionPerPlayer[] = [];

    this.data.dialogData.reduce((accumulator, currentValue) =>
    {
      const error = {
        errorCode: currentValue.ErrorCode,
        errorMsg: currentValue.ErrorMsg,
        restrictionMode: currentValue.RestrictionMode
      };

      if (!accumulator.includes(currentValue.PlayerID))
      {
        restrictions.push({
          name: currentValue.FirstName + " " + currentValue.LastName,
          playerNumber: currentValue.PlayerNumber,
          playerID: currentValue.PlayerID,
          errors: [error]
        });
        accumulator.push(currentValue.PlayerID);
      } else
      {
        restrictions.find(restriction => restriction.playerID === currentValue.PlayerID)
          .errors.push(error);
      }
      return accumulator;
    }, []);

    this.restrictions = restrictions.sort((a, b) => a.playerNumber - b.playerNumber);
  }

  close(): void { this.dialogRef.close(); }

  closeByOverride(): void { this.dialogRef.close(this.restrictions); }
}


