import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatBadgeModule } from '@angular/material/badge';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ExpansionPanelModule, LoadingModule, OverlayDialogModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ContactNotificationDialogModule, ContactNotificationsService } from '@tei/pos-shared';
import { ManageTeeTimeHelperService } from '../../../@services/manage-tee-time.helper.service';
import { ManageTeeTimeService } from '../../../@services/manage-tee-time.service';
import { PrepaidDepositBreakdownDialogModule } from '../prepaid-deposit-breakdown-dialog/prepaid-deposit-breakdown-dialog.module';
import { PriceBreakdownDialogModule } from '../price-breakdown-dialog/price-breakdown-dialog.module';
import { ManageTeeTimePlayersComponent } from './manage-tee-time-players.component';
import { PlayerRowComponent } from './player-row/player-row.component';
import { PlayersListComponent } from './players-list/players-list.component';
import { PrepaidECommerceTransactionsModule } from '../../prepaid-ecommerce-transaction/prepaid-ecommerce-transaction.module';



const MATERIAL_MODULES = [
  OverlayDialogModule,
  MatChipsModule,
  MatCheckboxModule,
  MatIconModule,
  MatSelectModule,
  MatButtonModule,
  MatBadgeModule,
  MatDividerModule,
  MatTooltipModule
];
const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule
];
const TEI_MATERIAL_MODULES = [
  PriceBreakdownDialogModule,
  MatButtonToggleModule,
  LoadingModule,
  ContactQuickSearchModule,
  ContactAvatarModule,
  ExpansionPanelModule
];
const TEI_MODULES = [
  TeiDateTranslateModule,
  TeiTextTranslatePipeModule,
  TeiCurrencyTranslateModule,
  TeiTextTranslatePipeModule,
  TeiTextEllipsisPipeModule
];
const TEI_POS_SHARED_MODULES = [ContactNotificationDialogModule];
const TEI_SHEET_MODULES = [PrepaidDepositBreakdownDialogModule, PrepaidECommerceTransactionsModule];
const COMPONENTS = [
  ManageTeeTimePlayersComponent,
  PlayerRowComponent,
  PlayersListComponent
];


@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_POS_SHARED_MODULES,
    ...TEI_SHEET_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IManageTeeTimeService', useClass: ManageTeeTimeService },
    { provide: 'IContactNotificationsService', useClass: ContactNotificationsService },
    ManageTeeTimeHelperService
  ]
})
export class ManageTeeTimePlayersModule { }
