import { FormControl } from "@angular/forms";
import { GetModifiedTeeSheetResponse } from "./get-modified-tee-sheet.model";

export class GetModifiedCourseCrossoversRequest
{
    CourseID: string;
    SheetDate: string;
    CourseFilter: string[];
}

export class GetModifiedCourseCrossoversResponse
{
    CourseID: string;
    SheetDate: string;
    CourseInfo: Array<CourseInfo>;
    SheetTimes: Array<CourseCrossoversSheetTime>;
    //Use in FE 
    SelectedCourseName: string;
    SelectedCourseID: string;
    SelectedTeeSheetDate: Date;
    TeeSheetTemplate: GetModifiedTeeSheetResponse;
}

export class CourseInfo
{
    CourseID: string;
    CourseName: string;
    CourseColor: string;
    // Use in FE
    Show: boolean;
}

export class CourseCrossoversSheetTime
{
    SheetTime: string;
    CourseCrossOvers: Array<CourseCrossover>;
    //Use in FE 
    SheetTimeTitle?: string;
    Index?: number;
}

export class CourseCrossover
{
    CourseID: string;
    Value: boolean;
}

export class TeeSheetCourseCrossoversForm
{
    TemplateID: FormControl<string>;
    CourseType: FormControl<number>;
    StartTime: FormControl<string>;
    Intervals: FormControl<string>;
    EndTime: FormControl<string>;
    Course: FormControl<string>;
    TimeRangeStartIndex: FormControl<number>;
    TimeRangeEndIndex: FormControl<number>;
}

export class TeeSheetCourseCrossoversApply
{
    TemplateID: string;
    CourseType: number;
    StartTime: string;
    Intervals: string;
    EndTime: string;
    Course: string;
    TimeRangeStartIndex: number;
    TimeRangeEndIndex: number;
}