import { Injectable } from "@angular/core";
import { MatLegacyDialog as MatDialog } from "@angular/material/legacy-dialog";
import { RainCheckWizardDataModel } from '../@intermediate/models/rain-check-helper.model';
import { RainCheckWizardComponent } from "../components/manage-tee-time/rain-check-wizard/rain-check-wizard.component";

@Injectable()
export class RainCheckHelperService
{

  constructor(
    private dialog: MatDialog
  ) { }

  openWizard(rainCheckWizardComponentData: RainCheckWizardDataModel)
  {

    rainCheckWizardComponentData.Players = rainCheckWizardComponentData.Players.map(player =>
    {
      const newPlayer = { ...player };

      if (newPlayer.Contact?.ContactID === '00000000-0000-0000-0000-000000000000'
        || newPlayer.Contact?.DisplayName === 'In progress'
        || newPlayer.Contact?.DisplayName.trim().length === 0)
      {
        newPlayer.Contact = null;
      }

      return newPlayer;
    });

    const dialogRef = this.dialog.open(RainCheckWizardComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen', 'ain-check-wizard-dialog-wrapper'],
      width: '400px',
      maxWidth: '95vw',
      data: rainCheckWizardComponentData
    });

    return dialogRef;
  }
}
