import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DateTimeUtilService
{

  createManualISODateTime(date: Date, time: string): string
  {
    const [timePart, period] = time.split(' ');
    const [hourPart, minutePart] = timePart.split(':').map(Number);
    const hour = period === 'PM' && hourPart < 12 ? hourPart + 12 : period === 'AM' && hourPart === 12 ? 0 : hourPart;
    const minute = minutePart;

    const year = date.getFullYear();
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const day = date.getDate().toString().padStart(2, '0');
    const hourString = hour.toString().padStart(2, '0');
    const minuteString = minute.toString().padStart(2, '0');

    return `${ year }-${ month }-${ day }T${ hourString }:${ minuteString }:00.000Z`;
  }

  buildTeeTimesArray(date: Date, teeTimeList: { TeeTime: string; }[]): { TeeTime: string; }[]
  {
    return teeTimeList.map(item => ({
      TeeTime: this.createManualISODateTime(date, item.TeeTime)
    }));
  }
}
