import { Injectable } from '@angular/core';
import { StorageService } from '@tei/core';
import { BehaviorSubject } from 'rxjs';
import { GroupBookingSelectedTeeTime } from '../@intermediate/models/get-tee-sheet.model';
import { SelectType } from '../@intermediate/models/tee-sheet-toolbar-model';

@Injectable({
  providedIn: 'root'
})
export class SmallGroupBookingHelperService
{
  constructor(private storageService: StorageService,)
  {
  }

  SmallGroupBookingSelectedTimes: GroupBookingSelectedTeeTime[] = [];
  SmallGroupBookingSelectedTimesList = new BehaviorSubject<GroupBookingSelectedTeeTime[]>([]);
  SmallGroupBookingRemoveTime = new BehaviorSubject<GroupBookingSelectedTeeTime>(new GroupBookingSelectedTeeTime());
  SelectTimeSlot = new BehaviorSubject<GroupBookingSelectedTeeTime>(new GroupBookingSelectedTeeTime());
  SmallGroupBookingSelectType = new BehaviorSubject<SelectType>(SelectType.Single);
  CourseChanged = new BehaviorSubject<boolean>(true);
  SelectedExistingGroupsUIDs: string[] = [];

  setSmallGroupBookingSelectedTimesList(value: GroupBookingSelectedTeeTime[]): void
  {
    this.SmallGroupBookingSelectedTimesList.next(value);
  }

  getSmallGroupBookingSelectedTimesList(): GroupBookingSelectedTeeTime[]
  {
    return this.SmallGroupBookingSelectedTimesList.getValue();
  }

  setSmallGroupBookingActive(selectType: SelectType): void
  {
    this.SmallGroupBookingSelectType.next(selectType);
  }

  setSmallGroupBookingSelectedTimes(newGroupBookingSelectedTeeTime: GroupBookingSelectedTeeTime): void
  {
    const index = this.SmallGroupBookingSelectedTimes.findIndex((item => item.Time === newGroupBookingSelectedTeeTime.Time));
    if (index >= 0)
      this.SmallGroupBookingSelectedTimes[index] = newGroupBookingSelectedTeeTime;
    else
    {
      this.SmallGroupBookingSelectedTimes.push(newGroupBookingSelectedTeeTime);
    }
    if (newGroupBookingSelectedTeeTime && newGroupBookingSelectedTeeTime?.Players?.length === 0)
    {
      this.SmallGroupBookingSelectedTimes = this.SmallGroupBookingSelectedTimes?.filter(item => item.Time !== newGroupBookingSelectedTeeTime.Time);
    }
    if (newGroupBookingSelectedTeeTime && newGroupBookingSelectedTeeTime.Time === '')
    {
      this.SmallGroupBookingSelectedTimes = [];
    }
    if (newGroupBookingSelectedTeeTime?.Players?.length === 0)
    {
      this.SmallGroupBookingSelectedTimes = this.SmallGroupBookingSelectedTimes.filter(item => item.Time !== newGroupBookingSelectedTeeTime.Time);
    }
    this.SmallGroupBookingSelectedTimesList.next(this.SmallGroupBookingSelectedTimes);
  }

  removeSmallGroupBookingSelectedTime(removedSelectedTeeTime: GroupBookingSelectedTeeTime): void
  {
    const index = this.SmallGroupBookingSelectedTimes.findIndex((item => item.Time === removedSelectedTeeTime.Time));
    if (index >= 0)
      this.SmallGroupBookingSelectedTimes.splice(index, 1);

    this.SmallGroupBookingRemoveTime.next(removedSelectedTeeTime);
  }

  checkedTeeSheetSlot(checkedTeeSheetSlot: GroupBookingSelectedTeeTime): void
  {
    if (this.SmallGroupBookingSelectedTimes?.length > 0)
    {
      const smallGroupPlayerNumbers = Number(this.storageService.Get('SmallGroupPlayerNumbers'));
      if (smallGroupPlayerNumbers)
      {
        let selectedPlayerCount = 0;
        const index = this.SmallGroupBookingSelectedTimes.findIndex((item => item.Time === checkedTeeSheetSlot.Time));
        if (index >= 0)
          this.SmallGroupBookingSelectedTimes.splice(index, 1);
        this.SmallGroupBookingSelectedTimes.forEach(item =>
        {
          selectedPlayerCount += item.Players.length;
        });
        if (smallGroupPlayerNumbers < selectedPlayerCount + checkedTeeSheetSlot.playerIndex + 1)
        {
          const remainingPlayers = smallGroupPlayerNumbers - selectedPlayerCount;
          checkedTeeSheetSlot.playerIndex = remainingPlayers - 1;
          if (checkedTeeSheetSlot.playerIndex != checkedTeeSheetSlot.Players.length)
            checkedTeeSheetSlot.Players.splice(checkedTeeSheetSlot.playerIndex + 1);
        }
      }

    }
    this.SelectTimeSlot.next(checkedTeeSheetSlot);
  }

  checkExistGroup(groupUID: string): boolean
  {
    return this.SelectedExistingGroupsUIDs.includes(groupUID);
  }

  hasBookingInGroup(groupUID: string): boolean
  {
    return this.getSmallGroupBookingSelectedTimesList().filter(item => (item.GroupUID === groupUID)).length > 0;
  }

}
