import { DatePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { finalize } from 'rxjs';
import { BlockedTeeTimes, DeleteUnBlockingTeeTimesRequest, DeleteUnBlockingTeeTimesResponse, GetTeeSheetBlockedTeeTimesRequest, GetTeeSheetBlockedTeeTimesResponse, ItemNode, TeeSheetUnblockDialogDataModel } from '../../@intermediate/models/tee-sheet-unblock.model';
import { TeeSheetUnblockService } from '../../@services/tee-sheet-unblock.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-unblock',
  templateUrl: './tee-sheet-unblock.component.html',
  styleUrls: ['./tee-sheet-unblock.component.scss']
})
export class TeeSheetUnblockComponent implements OnInit
{
  isMobile: boolean = false;
  BlockTypeList: BlockedTeeTimes[] = [];
  selectedBlock: string;
  treeData: Array<ItemNode> = [];
  selectedItemIds: string[] = [];
  constructor(private dialogRef: MatDialogRef<TeeSheetUnblockComponent>,
    @Inject(MAT_DIALOG_DATA) public data: TeeSheetUnblockDialogDataModel,
    private responsiveService: ResponsiveService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private teeSheetUnBlockService: TeeSheetUnblockService,
    private datePipe: DatePipe,
    private snackBarService: SnackBarService,
    private translate: TranslateService
  ) { }

  ngOnInit(): void
  {
    this.getMobileSize();
    if (this.data)
    {
      this.getBlockTypeList();
      this.selectedBlock = this.data.SheetDate;
      this.selectedItemIds.push(this.data.teeTimeID);
    }
  }

  getBlockTypeList(): void
  {
    const request: GetTeeSheetBlockedTeeTimesRequest = {
      CourseID: this.data.CourseID,
      SheetDate: this.data.SheetDate,
      StartTee: this.data.StartTee
    };
    this.manageStandAloneLoadingService.startLoading();
    this.teeSheetUnBlockService.TeeTimeBlocks(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: GetTeeSheetBlockedTeeTimesResponse) =>
      {
        this.BlockTypeList = response.BlockedTeeTimes;

        this.createTreeData(response.BlockedTeeTimes);
      }
    });
  }

  createTreeData(blockTypeList: BlockedTeeTimes[])
  {
    this.manageStandAloneLoadingService.startLoading();
    this.treeData = blockTypeList.map(blockedTeeTimes =>
    {
      const parentNode = new ItemNode();
      parentNode.Item = {
        Title: blockedTeeTimes.DisplayName,
        Id: '1',
        Key: '2',
        IsSelected: false
      };
      blockedTeeTimes.TeeTimes.forEach(teeTime =>
      {
        parentNode.Children.push({
          Item: {
            Title: this.datePipe.transform(teeTime.TeeTime, 'hh:mm a'),
            Id: teeTime.TeeTimeID,
            Key: '2',
            IsSelected: this.data.teeTimeID === teeTime.TeeTimeID
          }, Children: []
        });
      });
      return parentNode;
    });
    this.manageStandAloneLoadingService.finishLoading();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  onSelectedChildrenIdsChange(selectedIds: string[]): void
  {
    this.selectedItemIds = Array.from(new Set(selectedIds));
  }

  onSubmit(): void
  {
    const request: DeleteUnBlockingTeeTimesRequest = {
      TeeTimeIDs: this.selectedItemIds
    };
    this.manageStandAloneLoadingService.startLoading();
    this.teeSheetUnBlockService.UnBlockingTeeTimes(request).pipe(finalize(() => this.manageStandAloneLoadingService.finishLoading())).subscribe({
      next: (response: DeleteUnBlockingTeeTimesResponse) =>
      {
        if (response)
        {
          this.snackBarService.showSuccess(
            this.translate.instant(this.selectedItemIds.length > 1 ? "POS_TEE_SHEET.UNBLOCK_DIALOG.SAVE_SUCCESS_MULTIPLE" : "POS_TEE_SHEET.UNBLOCK_DIALOG.SAVE_SUCCESS_ONE"),
            '', null, 'left'
          );
          this.dialogRef.close(true);
        }
      }
    });
  }

  onClose(): void
  {
    this.dialogRef.close();
  }
}

