import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef, } from '@angular/material/legacy-dialog';
import { MatStepper } from '@angular/material/stepper';
import { ResponsiveService } from '@tei/core';
import { ECommerceTransaction } from '@tei/retail-admin';
import { PrepaidDepositBreakdownData } from '../../@intermediate/models/get-booking-details.model';
@Component({
  selector: 'tei-pos-tee-sheet-prepaid-ecommerce-transaction',
  templateUrl: './prepaid-ecommerce-transaction.component.html',
  styleUrls: ['./prepaid-ecommerce-transaction.component.scss']
})
export class PrepaidECommerceTransactionsComponent implements OnInit
{
  isMobile = false;
  selectedECommerceTransactionUIDs: string[] = [this.data.TransactionUID];
  selectedECommerceLineUIDs: string[] = this.data.eCommerceLineUIDs;
  @ViewChild('stepper') stepper: MatStepper;

  constructor(private responsiveService: ResponsiveService,
    private dialogRef: MatDialogRef<PrepaidECommerceTransactionsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: PrepaidDepositBreakdownData) { }

  ngOnInit(): void
  {
    this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    });
  }

  onECommerceTransaction(event: Array<ECommerceTransaction>): void
  {
    this.selectedECommerceTransactionUIDs = event?.map(t => t.TransactionUID);
    this.stepper.next();
  }

  onReturn(event: Array<any>): void
  {
    this.selectedECommerceLineUIDs = event?.map(t => t.LineUID);
    this.stepper.next();
  }

  close(): void
  {
    this.dialogRef.close(true);
  }

  cancel(): void
  {
    this.dialogRef.close();
  }
}
