import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { PostTeeSheetClassRestrictionsResponse, TeeSheetRestrictionsForm } from '../../../@intermediate/models/post-tee-sheet-class-restrictions.mode';
import { TeeSheetRestrictionsService } from '../../../@services/tee-sheet-restrictions.service';
import { TeeSheetFilterViewComponent } from '../../tee-sheet-filter-view/tee-sheet-filter-view.component';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-restrictions-apply',
  templateUrl: './tee-sheet-restrictions-apply.component.html',
  styleUrls: ['./tee-sheet-restrictions-apply.component.scss']
})
export class TeeSheetRestrictionsApplyComponent implements OnInit, OnDestroy
{
  @Input() public teeSheetRestrictionsData: PostTeeSheetClassRestrictionsResponse;
  @ViewChild('filterView') filterView: TeeSheetFilterViewComponent;
  public form: FormGroup<TeeSheetRestrictionsForm>;
  public courseTypeTitle = '';
  public restrictionsIDs: { [key: string]: string; } = {};
  subscription = new Subscription();
  constructor(public teeSheetRestrictionsService: TeeSheetRestrictionsService,
  ) { }

  public ngOnInit(): void
  {
    this.createForm();
  }

  public onSelectRestrictions(data: string[]): void
  {
    this.form.get('Restrictions').setValue(data);
  }

  public createForm(): void
  {
    this.form = new FormGroup<TeeSheetRestrictionsForm>({
      TemplateID: new FormControl<string>({ value: '', disabled: true }),
      CourseType: new FormControl<number>({ value: 0, disabled: true }),
      StartTime: new FormControl<string>({ value: '', disabled: true }),
      Intervals: new FormControl<number>({ value: 0, disabled: true }),
      EndTime: new FormControl<string>({ value: '', disabled: true }),
      IgnoreClass: new FormControl<boolean>(false),
      Apply: new FormControl<boolean>(null, Validators.required),
      Restrictions: new FormControl<string[]>([]),
      TimeRangeStartIndex: new FormControl<number>(null, Validators.required),
      TimeRangeEndIndex: new FormControl<number>(null, Validators.required)
    });
    this.fillForm(this.teeSheetRestrictionsData);
  }

  public fillForm(teeSheetRestrictionsData: PostTeeSheetClassRestrictionsResponse): void
  {
    this.form.patchValue({
      TemplateID: teeSheetRestrictionsData.Template?.TemplateID,
      CourseType: this.teeSheetRestrictionsData.CourseTypes.find(item => item.ID === teeSheetRestrictionsData.Template.CourseType).ID,
      StartTime: moment(teeSheetRestrictionsData?.Template.StartTime).format('LT'),
      Intervals: teeSheetRestrictionsData.Template?.Intervals,
      EndTime: moment(teeSheetRestrictionsData?.Template.EndTime).format('LT'),
      IgnoreClass: teeSheetRestrictionsData?.Template.IgnoreClassRestrictions
    });
    this.courseTypeTitle = this.teeSheetRestrictionsData.CourseTypes.find(item => item.ID === teeSheetRestrictionsData.Template.CourseType).Title;

    this.subscription.add(this.form.get('IgnoreClass').valueChanges.subscribe(value =>
    {
      this.teeSheetRestrictionsService.apply(this.form.getRawValue());
    }));

    this.teeSheetRestrictionsData.Groups.map(item =>
    {
      this.restrictionsIDs[item.ClassUID] = item.ClassID;
    });

    this.form.get('TimeRangeEndIndex').disable();
  }

  public onselectStartTime(): void
  {
    this.form.get('TimeRangeEndIndex').setValue(null);
    this.form.get('TimeRangeEndIndex').enable();
  }

  public removeRestrictions(restrictionID): void
  {
    const selected = this.form.get('Restrictions').value as string[];
    this.form.get('Restrictions').setValue(selected.filter(item => item !== restrictionID));
    if (this.form.get('Restrictions').value.length === 0 && !this.form.get('Timezone').value)
    {
      this.form.get('TimeRangeStartIndex').disable();
    }
  }

  public onApply(): void
  {
    this.teeSheetRestrictionsService.apply(this.form.getRawValue());
  }

  public onRest(): void
  {
    this.teeSheetRestrictionsService.reset();
  }

  public get disableForm(): boolean
  {
    return this.form.get('TimeRangeStartIndex').value === null || this.form.get('TimeRangeEndIndex').value === null || this.form.get('Apply').value === null || this.form.get('Restrictions').value?.length === 0;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
