import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetUserRequest, GetUserResponse } from '../@intermediate/models/get-user.model';


@Injectable()
export class HeaderService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Header/';
  }

  getUser(request: GetUserRequest): Observable<GetUserResponse>
  {
    const apiUrl = this.serviceUrl + 'GetCurrentUserInfo';
    return this.http.get<GetUserResponse>(apiUrl, request);
  }

}
