import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { DeleteGroupTeeTimesForm, TeeTimeActionEnum } from '../../../@intermediate/models/delete-group-tee-times-and-remove-order.model';

@Component({
  selector: 'tei-pos-tee-sheet-delete-group-tee-time-dialog',
  templateUrl: './delete-group-tee-time-dialog.component.html',
  styleUrls: ['./delete-group-tee-time-dialog.component.scss']
})
export class DeleteGroupTeeTimeDialogComponent implements OnInit
{
  form: FormGroup<DeleteGroupTeeTimesForm>;
  TeeTimeActionEnum = TeeTimeActionEnum;
  constructor(private dialogRef: MatDialogRef<DeleteGroupTeeTimeDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Partial<{ dialogData: any[]; }>)
  { }

  ngOnInit(): void
  {

    this.createForm();
  }
  ;

  createForm(): void
  {
    this.form = new FormGroup<DeleteGroupTeeTimesForm>({
      DeleteTypes: new FormControl<TeeTimeActionEnum>(TeeTimeActionEnum.RemoveGroup)
    });
  }

  close(): void { this.dialogRef.close(); }

  deleteGroup(): void { this.dialogRef.close(this.form.value.DeleteTypes); }
}


