import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ResponsiveService } from '@tei/core';
import { CheckInDialgDetailsAction, GetBookingCheckInDetailsResponse, PlayerCheckInDetail, ReturnCheckInForm } from '../../../@intermediate/models/get-booking-check-in-details.model';

@Component({
  selector: 'tei-pos-tee-sheet-return-check-in-dialog',
  templateUrl: './return-check-in-dialog.component.html',
  styleUrls: ['./return-check-in-dialog.component.scss']
})
export class ReturnCheckInDialogComponent implements OnInit
{
  isMobile = false;
  selectedPlayer: PlayerCheckInDetail;
  returnCheckInForm = new FormGroup<ReturnCheckInForm>({
    SelectedPlayer: new FormControl(this.data.BookingCheckinDetails[0], Validators.required),
  });
  constructor(public dialog: MatDialogRef<ReturnCheckInDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: GetBookingCheckInDetailsResponse,
    private responsiveService: ResponsiveService,
  )
  {

  }

  ngOnInit()
  {
    this.getMobileSize();
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  close(): void
  {
    this.dialog.close();
  }

  return(): void
  {
    this.dialog.close({
      action: CheckInDialgDetailsAction.Return,
      selectedPlayer: this.returnCheckInForm?.value?.SelectedPlayer
    });
  }


  print(): void
  {
    this.dialog.close({
      action: CheckInDialgDetailsAction.Print,
      selectedPlayer: this.returnCheckInForm?.value?.SelectedPlayer
    });
  }


}
