import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatStepperModule } from '@angular/material/stepper';
import { ManageTeeTimeService } from '../../../@services/manage-tee-time.service';
import { TeeSheetService } from '../../../@services/tee-sheet.service';
import { RainCheckConfirmationModule } from './rain-check-confirmation/rain-check-confirmation.module';
import { RainCheckSelectPlayerModule } from './rain-check-select-player/rain-check-select-player.module';
import { RainCheckWizardComponent } from './rain-check-wizard.component';

const COMPONENTS = [RainCheckWizardComponent];
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [
  MatStepperModule,
];
const POS_TEE_SHEET_MODULES = [
  RainCheckSelectPlayerModule,
  RainCheckConfirmationModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...POS_TEE_SHEET_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...POS_TEE_SHEET_MODULES,
    ...COMPONENTS,
  ],
  providers: [
    { provide: 'IManageTeeTimeService', useClass: ManageTeeTimeService },
    { provide: 'ITeeSheetService', useClass: TeeSheetService },
  ]
})
export class RainCheckWizardModule { }
