import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { TeiCurrencyTranslateModule } from '@tei/core-translate';
import { PriceBreakdownDialogComponent } from './price-breakdown-dialog.component';

const MATERIAL_MODULES = [
  MatButtonModule,
  MatIconModule,
  MatDividerModule
];
const BASE_MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule
];
const TEI_MODULES = [
  TeiCurrencyTranslateModule
];
const COMPONENTS = [
  PriceBreakdownDialogComponent
];


@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...COMPONENTS
  ]
})
export class PriceBreakdownDialogModule { }
