import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DatepickerModule, ExpansionPanelModule, LoadingModule } from '@tei/material';
import { TeeSheetDisplayComponent } from './tee-sheet-display.component';
import { DisplayFormModule } from './display-form/display-form.module';
import { DisplayGridModule } from './display-grid/display-grid.module';
import { TeeSheetService } from '../../@services/tee-sheet.service';
import { ManageLoadingService } from '@tei/core';

const COMPONENTS = [TeeSheetDisplayComponent];
const MATERIAL_MODULES = [MatButtonModule, MatDividerModule, MatDialogModule, MatInputModule, MatIconModule, MatFormFieldModule, MatRadioModule, MatTableModule, ExpansionPanelModule, MatChipsModule];
const BASE_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const TEI_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [DatepickerModule, LoadingModule];
const TEESHEET_DISPLAY_MODULES =[DisplayFormModule, DisplayGridModule]
@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEESHEET_DISPLAY_MODULES
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [
    {provide: TeeSheetService},
    {provide: ManageLoadingService}
  ]
})
export class TeeSheetDisplayModule { }
