import { NgModule } from '@angular/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ReturnCheckInDialogComponent } from './return-check-in-dialog.component';

import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ContactAvatarModule } from '@tei/office-contacts';
const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [ CommonModule, FormsModule,ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatDividerModule, MatRadioModule];
const TEI_MATERIAL_MODULES = [];
const TEI_MODULES = [ContactAvatarModule, TeiCurrencyTranslateModule];

const COMPONENTS = [ReturnCheckInDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES,
    ...TEI_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ]
})
export class ReturnCheckInDialogModule { }
