import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatDividerModule } from "@angular/material/divider";
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ContactQuickSearchModule } from '@tei/office-contacts';
import { GroupBookingDetailsService } from '../../@services/group-booking-details.service';
import { GroupPlayersModule } from './group-players/group-players.module';
import { ManagePlayersDialogComponent } from './manage-players-dialog.component';
import { MoveSwapPlayersModule } from './move-swap-players/move-swap-players.module';

const COMPONENTS = [
  ManagePlayersDialogComponent
];
const MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
];
const MATERIAL_MODULES = [
  MatButtonModule,
  MatDialogModule,
  MatFormFieldModule,
  MatIconModule,
  MatCheckboxModule,
  MatDividerModule,
  MatExpansionModule,
  MatMenuModule,
  ContactQuickSearchModule,
  QuickSearchFilterChipModule,
  FilterChipsModule
];

const TEI_MATERIAL_MODULES = [LoadingModule,
  MatButtonToggleModule,];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const POS_TEE_SHEET_MODULES = [GroupPlayersModule, MoveSwapPlayersModule];

@NgModule({
  declarations: [...COMPONENTS,],
  imports: [
    ...MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...POS_TEE_SHEET_MODULES],
  exports: [...COMPONENTS],
  providers: [
    {
      provide: 'IGroupBookingDetailsService',
      useClass: GroupBookingDetailsService
    }
  ]
})
export class ManagePlayersDialogModule { }
