import { FormControl, FormGroup, Validators } from '@angular/forms';

export class PostTeeSheetBlockForm extends FormGroup
{
  constructor(slotPerRowMax: number)
  {
    super({
      startTeeTime: new FormControl<string>('', Validators.required),
      endTeeTime: new FormControl<string>('', Validators.required),
      blockType: new FormControl<string>(''),
      numberOfRows: new FormControl<number>(0, Validators.required),
      slotsPerRow: new FormControl<number>(1, [Validators.required, Validators.min(1), Validators.max(slotPerRowMax)]),
      blockCost: new FormControl<string>('0.00', Validators.required),
      priceLevel: new FormControl<string>(''),
      displayName: new FormControl<string>('', Validators.required),
      note: new FormControl<string>('')
    });
  }

  get startTeeTime(): FormControl<string>
  {
    return this.get('startTeeTime') as FormControl<string>;
  }

  get endTeeTime(): FormControl<string>
  {
    return this.get('endTeeTime') as FormControl<string>;
  }

  get blockType(): FormControl<string>
  {
    return this.get('blockType') as FormControl<string>;
  }

  get numberOfRows(): FormControl<number>
  {
    return this.get('numberOfRows') as FormControl<number>;
  }

  get slotsPerRow(): FormControl<number>
  {
    return this.get('slotsPerRow') as FormControl<number>;
  }

  get blockCost(): FormControl<string>
  {
    return this.get('blockCost') as FormControl<string>;
  }

  get priceLevel(): FormControl<string>
  {
    return this.get('priceLevel') as FormControl<string>;
  }

  get displayName(): FormControl<string>
  {
    return this.get('displayName') as FormControl<string>;
  }

  get note(): FormControl<string>
  {
    return this.get('note') as FormControl<string>;
  }



}
