export class GetClassesAndGroupsRequest
{
    CourseID: string;
    SearchText: string;
}

export class GetClassesAndGroupsResponse
{
    Items: Array<RestrictionsItem>;
}


export class RestrictionsItem
{
    RestrictionType: RestrictionType;
    Items: Array<ClassesAndGroupsItem>;
}

export class ClassesAndGroupsItem
{
    ClassID: string;
    ClassUID: string;
    TeeSheetColour: string;
    Description: string;
}

export enum RestrictionType
{
    Public,
    Web,
    Classes,
    Groups
}
