import { Component, Inject, OnInit, QueryList, ViewChildren } from '@angular/core';
import { FormControl, FormGroup, UntypedFormBuilder, Validators } from '@angular/forms';
import { MatExpansionPanel } from '@angular/material/expansion';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { Player } from '../../../@intermediate/models/get-booking-details.model';
import { PlayerStatus } from '../../../@intermediate/models/get-tee-sheet.model';
import { MoveSwapActionEnum, MoveSwapBookingDetail, MoveSwapDialogData, MoveSwapGroupPlayersForm, PostSwapMoveGroupPlayersRequest, PostSwapMoveGroupPlayersResponse } from '../../../@intermediate/models/post-move-swap-group-players.model';
import { GroupTeeTimeService } from '../../../@services/group-tee-time.service';

@Component({
  selector: 'tei-pos-tee-sheet-move-swap-players',
  templateUrl: './move-swap-players.component.html',
  styleUrls: ['./move-swap-players.component.scss']
})
export class MoveSwapPlayersComponent implements OnInit
{
  form: FormGroup<MoveSwapGroupPlayersForm>;
  moveSwapActionEnum = MoveSwapActionEnum;
  radioButtonHeader: { [key: string]: boolean; } = {};
  subscription = new Subscription();
  isMobile = false;
  isFirstTeeTimePlayer = false;
  @ViewChildren(MatExpansionPanel) panels!: QueryList<MatExpansionPanel>;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: MoveSwapDialogData,
    private fb: UntypedFormBuilder,
    private snackBarService: SnackBarService,
    public dialog: MatDialogRef<MoveSwapPlayersComponent>,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject('IGroupTeeTimeService') private groupTeeTimeService: GroupTeeTimeService,
    private translate: TranslateService,
    private responsiveService: ResponsiveService,
  )
  {
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  closeAllPanels()
  {
    this.panels.forEach(panel => panel.close());
    this.form.get('ToTeeTimeID').setValue('');
    this.resetRadioButtonHeader();
  }

  ngOnInit(): void
  {
    this.createForm();
    this.getMobileSize();
  };

  createForm(): void
  {
    this.isFirstTeeTimePlayer = this.data.SelectedPlayer.TeeTimeID === this.data.BookingDetails[0].Players[0].TeeTimeID;
    this.form = new FormGroup<MoveSwapGroupPlayersForm>({
      FromTeeTimeID: new FormControl<string>(this.data.SelectedPlayer.TeeTimeID),
      Action: new FormControl<MoveSwapActionEnum>(MoveSwapActionEnum.Move),
      ToTeeTimeID: new FormControl<string>('', Validators.required),
    });
    this.resetRadioButtonHeader();
  };

  resetRadioButtonHeader(): void
  {
    this.data.BookingDetails.map(item =>
    {
      this.radioButtonHeader[item.BookingID] = false;
    });
  }

  resetAction(): void
  {
    this.form.get('ToTeeTimeIDHeader').reset();
  }

  onHeaderRadio(item: MoveSwapBookingDetail): void
  {
    if (this.checkHeaderRadio(item))
      return;
    this.resetRadioButtonHeader();
    this.radioButtonHeader[item.BookingID] = true;
    this.form.get('ToTeeTimeID').reset();
    const filterCondition = this.form.get('Action').value === MoveSwapActionEnum.Move
      ? player => !player.Contact && !this.disableRadioButton(player)
      : player => !this.disableRadioButton(player);
    const matchingPlayers = item.Players.filter(filterCondition);
    if (matchingPlayers.length)
    {
      this.form.get('ToTeeTimeID').setValue(matchingPlayers[0].TeeTimeID);
    }
  }

  checkHeaderRadio(item: MoveSwapBookingDetail): boolean
  {
    const action = this.form.get('Action').value as MoveSwapActionEnum;
    const isAllowedStatus = (player: Player) =>
      player.Status === PlayerStatus.NoShow ||
      player.Status === PlayerStatus.CheckIn ||
      player.Status === PlayerStatus.CheckInInprogress ||
      player.Status === PlayerStatus.NoShowFromAPI;

    if (action === MoveSwapActionEnum.Move)
    {
      return item.Players.every(player => player.Contact || isAllowedStatus(player)) || this.isFirstTeeTimePlayer;
    }

    if (action === MoveSwapActionEnum.Swap)
    {
      return item.Players.every(isAllowedStatus) || (item.Players.every(player => !player.Contact) && this.isFirstTeeTimePlayer);
    }
    return false;
  }

  disableRadioButton(item: Player): boolean
  {
    const action = this.form.get('Action').value;
    return (
      item.Status === PlayerStatus.NoShow ||
      item.Status === PlayerStatus.CheckIn ||
      item.Status === PlayerStatus.CheckInInprogress ||
      item.Status === PlayerStatus.NoShowFromAPI ||
      (action === MoveSwapActionEnum.Move && this.isFirstTeeTimePlayer) ||
      (action === MoveSwapActionEnum.Swap && this.isFirstTeeTimePlayer && !item.Contact)
    );
  }

  async moveSwap(): Promise<void>
  {
    try
    {
      const request: PostSwapMoveGroupPlayersRequest = this.form.getRawValue();
      this.manageStandAloneLoadingService.startLoading();
      const response: PostSwapMoveGroupPlayersResponse = await firstValueFrom(this.groupTeeTimeService.SwapMoveGroupPlayers(request));
      if (response.Success)
      {
        this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.SWAP_PLAYER.SAVE_SUCCESS'), '', null, 'left');
        this.dialog.close(true);
      }
    } catch (error)
    {
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  };

  close(): void
  {
    this.dialog.close();
  }
}
