import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { AllActionsOrderResponseWrapper } from '@tei/pos-shared';
import { Observable } from 'rxjs';
import { IManageOrderTeeTimeService } from '../@intermediate/manage-order-tee-time.interface';
import { DeleteGroupTeeTimesAndRemoveOrderRequest, DeleteGroupTeeTimesAndRemoveOrderResponse } from '../@intermediate/models/delete-group-tee-times-and-remove-order.model';
import { PostBulkTeeTimeToOrderRequest, PostBulkTeeTimeToOrderResponse, PostManageOrderTeeTimeRequest } from '../@intermediate/models/post-manage-order-tee-time.model';

@Injectable()
export class ManageOrderTeeTimeService implements IManageOrderTeeTimeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/Retail/ManageOrder/';
  }

  TeeTime(request: PostManageOrderTeeTimeRequest): Observable<AllActionsOrderResponseWrapper>
  {
    const apiUrl = this.serviceUrl + 'OrderItems/TeeTime';
    return this.http.post<AllActionsOrderResponseWrapper>(apiUrl, request);
  }

  BulkTeeTime(request: PostBulkTeeTimeToOrderRequest): Observable<PostBulkTeeTimeToOrderResponse>
  {
    const apiUrl = this.serviceUrl + 'OrderItems/BulkTeeTime';
    return this.http.post<PostBulkTeeTimeToOrderResponse>(apiUrl, request);
  }

  DeleteGroupTeeTimesAndRemoveOrder(request: DeleteGroupTeeTimesAndRemoveOrderRequest): Observable<DeleteGroupTeeTimesAndRemoveOrderResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + 'DeleteGroupTeeTimesAndRemoveOrder';
    return this.http.delete<DeleteGroupTeeTimesAndRemoveOrderResponse>(apiUrl, options);
  }
}
