import { Component, Inject, OnDestroy } from '@angular/core';
import { FormControl, FormGroup, UntypedFormBuilder, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import moment from 'moment';
import { finalize, Subscription } from 'rxjs';
import { ActionSqueeze, PostTeeTimeSqueezeRequest, PostTeeTimeSqueezeResponse } from '../../../@intermediate/models/post-tee-time-squeeze.model';
import { TeeSheetService } from '../../../@services/tee-sheet.service';

@Component({
  selector: 'tei-pos-tee-sheet-squeeze-dialog',
  templateUrl: './tee-sheet-squeeze-dialog.component.html',
  styleUrls: ['./tee-sheet-squeeze-dialog.component.css']
})

export class TeeSheetSqueezeDialogComponent implements OnDestroy
{
  startTime: Date = new Date(1995, 11, 17, 0, 0);
  endTime: Date = new Date(1995, 11, 17, 23, 45);
  form = new FormGroup({
    squeezeTime: new FormControl<Date>(this.data.SqueezeTime, [Validators.required])
  });
  subscriptions = new Subscription();
  constructor(@Inject(MAT_DIALOG_DATA) public data, private fb: UntypedFormBuilder,
    private snackBarService: SnackBarService, @Inject('ITeeSheetService') private teeSheetService: TeeSheetService,
    public dialog: MatDialogRef<TeeSheetSqueezeDialogComponent>, public manageStandAloneLoadingService: ManageStandAloneLoadingService,
  )
  {
  }

  close(): void
  {
    this.dialog.close();
  }

  getFormControlByName(controlName: string): FormControl
  {
    return this.form.get(controlName) as FormControl;
  }

  getActionSqueeze(): ActionSqueeze
  {
    const squeezeTime = new Date(this.form.get('squeezeTime').value);

    const isSqueezeTimeBelow = squeezeTime.getTime() === this.data.SheetTime.getTime() + 60000;
    const isSqueezeTimeAbove = squeezeTime.getTime() === this.data.SheetTime.getTime() - 60000;

    if (isSqueezeTimeBelow)
    {
      return ActionSqueeze.SqueezeTimeBelow;
    } else if (isSqueezeTimeAbove)
    {
      return ActionSqueeze.SqueezeTimeAbove;
    } else
    {
      return ActionSqueeze.SpecificSqueezeTime;
    }
  }

  addSqueezeTime(): void
  {
    const request: PostTeeTimeSqueezeRequest =
    {
      Action: this.getActionSqueeze(),
      SqueezeTime: moment(this.form.get('squeezeTime').value).format("YYYY-MM-DD[T]HH:mm:ss"),
      SheetTime: moment(this.data.SheetTime).format("YYYY-MM-DD[T]HH:mm:ss"),
      CourseID: this.data.CourseID
    };
    this.manageStandAloneLoadingService.startLoading();
    this.subscriptions.add(this.teeSheetService.squeezeTeeTime(request).pipe(finalize(() =>
    {
      this.manageStandAloneLoadingService.finishLoading();
    })).subscribe((response: PostTeeTimeSqueezeResponse) =>
    {
      if (response.Result)
      {
        this.dialog.close(true);
      } else
      {
        this.snackBarService.showError(response.Message, '', null, 'left');
      }
    }));

  }

  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }

}
