import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import moment from 'moment';
import { Subscription } from 'rxjs';
import { CourseInfo, GetModifiedCourseCrossoversResponse, TeeSheetCourseCrossoversForm } from '../../../@intermediate/models/get-modified-course-cross-overs.model';
import { TeeSheetFilterView } from '../../../@intermediate/models/tee-sheet-filter-view.model';
import { TeeSheetCourseCrossoversService } from '../../../@services/tee-sheet-course-crossovers.service';
import { TeeSheetFilterViewService } from '../../../@services/tee-sheet-filter-view.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-course-crossovers-apply',
  templateUrl: './tee-sheet-course-crossovers-apply.component.html',
  styleUrls: ['./tee-sheet-course-crossovers-apply.component.scss']
})
export class TeeSheetCourseCrossoversApplyComponent implements OnInit, OnDestroy
{
  @Input() public teeSheetCourseCrossoversData: GetModifiedCourseCrossoversResponse;
  subscription = new Subscription();
  public form: FormGroup<TeeSheetCourseCrossoversForm>;
  public courseTypeTitle = '';

  constructor(public TeeSheetFilterViewService: TeeSheetFilterViewService,
    public teeSheetCourseCrossoversService: TeeSheetCourseCrossoversService
  ) { }

  public createForm(): void
  {
    this.form = new FormGroup<TeeSheetCourseCrossoversForm>({
      TemplateID: new FormControl<string>({ value: '', disabled: true }),
      CourseType: new FormControl<number>({ value: 0, disabled: true }),
      StartTime: new FormControl<string>({ value: '', disabled: true }),
      Intervals: new FormControl<string>({ value: '', disabled: true }),
      EndTime: new FormControl<string>({ value: '', disabled: true }),
      Course: new FormControl<string>(null, Validators.required),
      TimeRangeStartIndex: new FormControl<number>(null, Validators.required),
      TimeRangeEndIndex: new FormControl<number>(null, Validators.required)
    });
    this.fillForm(this.teeSheetCourseCrossoversData);
  }

  public filterViewSubscription(): void
  {
    this.subscription.add(this.TeeSheetFilterViewService.filterSearch$.subscribe(value =>
    {
      this.filterCourses(value);
    }));
  }

  public filterCourses(searchText: string): void
  {
    let filteredCourses = [];
    if (searchText === '')
    {
      filteredCourses = this.teeSheetCourseCrossoversData.CourseInfo;
    } else
    {
      filteredCourses = this.teeSheetCourseCrossoversData.CourseInfo.filter(item => item.CourseName.toLowerCase().includes(searchText.toLowerCase()));
    }
    this.TeeSheetFilterViewService.load(this.convertToFilterView(filteredCourses));
  }

  public convertToFilterView(data: CourseInfo[]): TeeSheetFilterView[]
  {
    const convertedData: TeeSheetFilterView[] = [];
    if (data.length)
    {
      convertedData.push({
        Title: '',
        ID: null,
        Items: [],
        ShowChildren: true
      });
      data.map((course, index) =>
      {
        convertedData[0].Items.push({
          Title: course.CourseName,
          ID: course.CourseID,
          Selected: false
        });
      });
    }
    return convertedData;
  }

  ngOnInit(): void
  {
    this.createForm();
    this.filterViewSubscription();
  }

  fillForm(teeSheetCourseCrossoversData: GetModifiedCourseCrossoversResponse): void
  {
    this.form.patchValue({
      TemplateID: teeSheetCourseCrossoversData.TeeSheetTemplate?.TemplateID,
      CourseType: this.teeSheetCourseCrossoversData.TeeSheetTemplate?.CourseTypes.find(item => item.ID === teeSheetCourseCrossoversData.TeeSheetTemplate?.CourseType).ID,
      StartTime: moment(teeSheetCourseCrossoversData.TeeSheetTemplate?.StartTime).format('LT'),
      Intervals: teeSheetCourseCrossoversData.TeeSheetTemplate?.Intervals,
      EndTime: moment(teeSheetCourseCrossoversData.TeeSheetTemplate?.EndTime).format('LT'),
    });
    this.courseTypeTitle = this.teeSheetCourseCrossoversData.TeeSheetTemplate?.CourseTypes.find(item => item.ID === teeSheetCourseCrossoversData.TeeSheetTemplate?.CourseType).Title;
    this.form.get('TimeRangeEndIndex').disable();
  }

  public onApply(): void
  {
    this.teeSheetCourseCrossoversService.apply(this.form.getRawValue());
  }

  public onselectStartTime(): void
  {
    this.form.get('TimeRangeEndIndex').setValue(null);
    this.form.get('TimeRangeEndIndex').enable();
  }

  public onReset(): void
  {
    this.teeSheetCourseCrossoversService.reset();
  }

  public get disableApply(): boolean
  {
    return this.form.get('TimeRangeStartIndex').value === null || this.form.get('TimeRangeEndIndex').value === null || this.form.get('Course').value === null;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
