import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
// Material Modules
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySnackBarModule as MatSnackBarModule } from '@angular/material/legacy-snack-bar';
// TEI Modules
import { TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
// Components
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { ManageStandAloneLoadingService } from '@tei/core';
import { AvatarModule, FormTimePickerModule, LoadingModule, MultiChipModule } from '@tei/material';
import { MainService } from '../../@services/main.service';
import { TeeSheetTransferService } from '../../@services/tee-sheet-transfer.service';
import { TeeSheetService } from '../../@services/tee-sheet.service';
import { EmailPreviewDialogModule } from './email-preview-dialog/email-preview-dialog.module';
import { RebuildTeeSheetDialogModule } from './rebuild-tee-sheet-dialog/rebuild-tee-sheet-dialog.module';
import { TeeSheetMessageComponent } from './tee-sheet-message/tee-sheet-message.component';
import { TeeSheetToolbarComponent } from './tee-sheet-toolbar.component';

import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { ManageTeeTimeService } from '../../@services/manage-tee-time.service';
import { TeeSheetCourseCrossoversService } from '../../@services/tee-sheet-course-crossovers.service';
import { TeeSheetDisplayModule } from '../tee-sheet-display/tee-sheet-display.module';
import { TeeSheetRestrictionsModule } from '../tee-sheet-restrictions/tee-sheet-restrictions.module';
import { TeeSheetTemplateModule } from '../tee-sheet-template/tee-sheet-template.module';
import { TeeSheetWaitListModule } from '../tee-sheet-wait-list/tee-sheet-wait-list.module';
import { TeeTimeBookingAdvancedSearchModule } from './tee-time-booking-advanced-search/tee-time-booking-advanced-search.module';
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatSelectModule,
  MatFormFieldModule, MatRippleModule, MatTooltipModule, MatIconModule, MatInputModule,
  MatButtonModule, MatMenuModule, MatRadioModule, MatSlideToggleModule, MatCheckboxModule, MatSnackBarModule];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const TEI_MATERIAL_MODULES = [TeiTextTranslatePipeModule, FormTimePickerModule, LoadingModule, AvatarModule, MultiChipModule, TeiTextEllipsisPipeModule];
const COMPONENTS = [TeeSheetToolbarComponent, TeeSheetMessageComponent];
const TEE_TIME_MODULE = [TeeTimeBookingAdvancedSearchModule, EmailPreviewDialogModule, TeeSheetTemplateModule, TeeSheetDisplayModule, TeeSheetRestrictionsModule, RebuildTeeSheetDialogModule, TeeSheetWaitListModule];
@NgModule({
  declarations: [...COMPONENTS],
  exports: [...COMPONENTS, ...TEE_TIME_MODULE],
  imports: [...MATERIAL_MODULES, ...BASE_MODULES, ...TEI_MATERIAL_MODULES, ...TEE_TIME_MODULE],
  providers: [
    { provide: 'IMainService', useClass: MainService },
    { provide: 'ITeeSheetService', useClass: TeeSheetService },
    { provide: 'ITeeSheetTransferService', useClass: TeeSheetTransferService },
    { provide: 'IManageTeeTimeService', useClass: ManageTeeTimeService },
    ManageStandAloneLoadingService,
    TeeSheetCourseCrossoversService
  ]
})
export class TeeSheetToolbarModule { }
