import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { CartTypes, TeeTimeHolesType } from './get-booking-details.model';
import { BookingGroupTeeTime } from './get-booking-group.model';
import { PriceLevel } from './get-tee-sheet.model';

export class PostCreateTeeTimeGroupRequest
{
  BookingID: number;
  GroupName: string;
  GroupUID?: string;
  IsAny18Holes?: boolean;
}

export class PostCreateTeeTimeGroupResponse
{
  GroupName: string;
  Managers: any[];
  TeeTime: any;
  GroupUID: string;
}

export class TeeTimesSettingsDialogModel
{
  form: CreateTeeTimeGroupForm;
  selectedTeeTimes: BookingGroupTeeTime[];
  TeeTimeHolesTypes: Array<TeeTimeHolesType>;
  TransportationTypes: CartTypes[];
  PriceLevels: PriceLevel[];
}

export class CreateTeeTimeGroupForm extends UntypedFormGroup
{
  constructor()
  {
    super(
      {
        GroupName: new UntypedFormControl('', [Validators.required]),
        GroupManagers: new UntypedFormControl([]),
        PrimaryGroupManager: new UntypedFormControl({ value: '', disabled: true }),
        DefaultHoles: new UntypedFormControl({ value: null, disabled: true }),
        DefaultTransportation: new UntypedFormControl({ value: null, disabled: true }),
        DefaultPriceLevel: new UntypedFormControl({ value: null, disabled: true }),
        Shotgun: new UntypedFormControl({ value: false, disabled: true }),
        StartTime: new UntypedFormControl(null),
        HoleNumber: new UntypedFormControl(null),
        HoleType: new UntypedFormControl(null),
        CartType: new UntypedFormControl(null),
        Attachments: new UntypedFormControl(null),
        PriceLevel: new UntypedFormControl(null),
        headerSelectedTeeTimes: new UntypedFormControl(null),
        selectedTeeTimes: new UntypedFormControl([]),
      }
    );
  }
}
