import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Inject, Input, OnInit, Output, ViewChild } from '@angular/core';
import { AbstractControl, ValidationErrors } from '@angular/forms';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatLegacySelectChange as MatSelectChange } from '@angular/material/legacy-select';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { Contact, ContactGroupSearchComponent } from '@tei/office-contacts';
import { IOrderStoreService } from '@tei/pos-shared';
import { Subscription, firstValueFrom, of } from 'rxjs';
import { catchError, finalize } from 'rxjs/operators';
import { IGroupTeeTimeService } from '../../../@intermediate/group-tee-time-service.interface';
import { DeleteGroupTeeTimesAndRemoveOrderRequest, TeeTimeActionEnum } from '../../../@intermediate/models/delete-group-tee-times-and-remove-order.model';
import { DeleteTeeTimeFromGroupTeeTimesRequest } from '../../../@intermediate/models/delete-tee-time-from-group-tee-times.model';
import { Attachment, CartTypes, CartTypesID, TeeTimeHoles, TeeTimeHolesType } from '../../../@intermediate/models/get-booking-details.model';
import { BookingGroupManager, BookingGroupTeeTime, GetBookingGroupResponse } from '../../../@intermediate/models/get-booking-group.model';
import { Course, SelectedDisableOptionsCourse } from '../../../@intermediate/models/get-tee-sheet-main.model';
import { PriceLevel } from '../../../@intermediate/models/get-tee-sheet.model';
import { GroupBookingDetailsModalData } from '../../../@intermediate/models/group-booking-details.model';
import { ConfirmTeeTimeBookingDialogDataModel, ConfirmTeeTimeBookingForm, ManageTeeTimeConfirmMode } from '../../../@intermediate/models/manage-tee-time.model';
import { PostApplyShotgunRequest } from '../../../@intermediate/models/post-apply-shotgun.model';
import { CreateTeeTimeGroupForm, TeeTimesSettingsDialogModel } from '../../../@intermediate/models/post-create-tee-time-group';
import { PostFirstPlayersEmailsByGroupUIDRequest, PostFirstPlayersEmailsByGroupUIDResponse } from '../../../@intermediate/models/post-first-players-emails-group-uid.model';
import { PostLinkTeeTimeGroupResponse } from '../../../@intermediate/models/post-link-tee-time-to-group.model';
import { GroupTeeTimeManager, PostUpdateGroupTeeTimesRequest } from '../../../@intermediate/models/post-update-tee-time-group.model';
import { PostValidateAndLinkTeeTimeGroupRequest } from '../../../@intermediate/models/post-validate-and-link-tee-time-to-group.model';
import { TeeSheetTransfer } from '../../../@intermediate/models/tee-sheet-transfer-service.model';
import { GroupBookingService } from '../../../@services/group-booking.service';
import { HeaderService } from '../../../@services/header.service';
import { ManageOrderTeeTimeService } from '../../../@services/manage-order-tee-time.service';
import { ManageTeeTimeHelperService } from '../../../@services/manage-tee-time.helper.service';
import { ManagePlayersDialogComponent } from '../../manage-players-dialog/manage-players-dialog.component';
import { AddNewTeeTimeDialogComponent } from '../add-new-tee-time-dialog/add-new-tee-time-dialog.component';
import { ConfirmTeeTimeBookingDialogComponent } from '../confirm-tee-time-booking-dialog/confirm-tee-time-booking-dialog.component';
import { DeleteGroupTeeTimeDialogComponent } from '../delete-group-tee-time-dialog/delete-group-tee-time-dialog.component';
import { TeeTimesSettingsDialogComponent } from '../tee-times-settings-dialog/tee-times-settings-dialog.component';

@Component({
  selector: 'tei-pos-tee-sheet-group-tee-time',
  templateUrl: './group-tee-time.component.html',
  styleUrls: ['./group-tee-time.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GroupTeeTimeComponent implements OnInit
{
  @ViewChild('contactGroupSearch') contactGroupSearch: ContactGroupSearchComponent;
  @Input() BookingID: number;
  @Input() IsAny18Holes: boolean;
  @Input() set skipChanges(value: boolean)
  {
    if (value)
    {
      this.skipChangesGroup();
    }
  }
  @Input() set saveGroup(value: boolean)
  {
    if (value)
    {
      if (this.bookingGroup.GroupUID)
      {
        this.saveGroupTeeTime(true).then();
      } else
      {
        this.createGroupTeeTime(true).then();
      }
    }
  }
  @Input() priceLevels: PriceLevel[];
  @Input() selectedCourse: Course;
  @Input() BookingDateTime: string;
  @Input() currentTeeSheetTransfer: TeeSheetTransfer;
  @Input() selectedDisableOptionsCourse: SelectedDisableOptionsCourse;
  @Input() fivePlayer: boolean;
  @Output() reloadBooking: EventEmitter<number> = new EventEmitter();
  @Output() onSaveComplete: EventEmitter<boolean> = new EventEmitter();
  @Output() groupHasChanges: EventEmitter<boolean> = new EventEmitter();
  @Output() onGroupCreatedEvent: EventEmitter<boolean> = new EventEmitter();
  @Output() skipChangesOfGroup: EventEmitter<boolean> = new EventEmitter();
  @Output() closePanelWithoutSave = new EventEmitter();

  form: CreateTeeTimeGroupForm = new CreateTeeTimeGroupForm();

  contact: Contact;
  managersGroupUID: string[] = [];
  dialogRef: MatDialogRef<CardDialogComponent>;

  bookingGroup: GetBookingGroupResponse;
  attachments: Array<Attachment>;
  groupManagers: BookingGroupManager[] = [];
  transportationTypes: Array<CartTypes>;
  cartTypesID = CartTypesID;
  teeTimeHoles = TeeTimeHoles;
  TeeTimeHolesTypes: Array<TeeTimeHolesType>;
  isMobile = false;
  createGroupTeeTimeTriggered: boolean;
  shotgunApplied: boolean;
  subscription = new Subscription();
  selectedTeeTimes: BookingGroupTeeTime[] = [];

  get formControls()
  {
    return this.form.controls;
  }
  constructor(
    @Inject('IGroupTeeTimeService') private groupTeeTimeService: IGroupTeeTimeService,
    @Inject('IManageOrderTeeTimeService') private manageOrderTeeTimeService: ManageOrderTeeTimeService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    private headerService: HeaderService,
    private snackBarService: SnackBarService,
    private translate: TranslateService,
    private cdr: ChangeDetectorRef,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private dialog: MatDialog,
    private responsiveService: ResponsiveService,
    private manageTeeTimeHelper: ManageTeeTimeHelperService,
    public groupBookingService: GroupBookingService)
  {
  }

  ngOnInit(): void
  {
    this.form?.get('GroupName')?.addValidators(this.noOnlySpacesValidator.bind(this));
    if (this.BookingID)
    {
      this.getBookingGroup();
    }
    this.createTransportationAndHolesTypes();
    this.getMobileSize();
    this.shotgunChangeDetection();
    this.handleFormValueChange();
  }

  handleFormValueChange(): void
  {
    let lastFormState = { ...this.form.value };
    this.subscription.add(this.form.valueChanges.subscribe((newValues) =>
    {

      const oldValues = { ...lastFormState };
      lastFormState = { ...newValues };

      const key = this.manageTeeTimeHelper.findChangedRootKeys(newValues, oldValues);
      if (!key.length) return;
      if (key.includes('GroupName') || key.includes('GroupManagers') || key.includes('PrimaryGroupManager')
        || key.includes('DefaultHoles')
        || key.includes('DefaultTransportation')
        || key.includes('DefaultPriceLevel')
        || key.includes('Shotgun')
        || key.includes('StartTime')
        || key.includes('HoleNumber')
        || key.includes('DefaultHoles')
        || key.includes('DefaultPriceLevel'))
      {
        this.groupHasChanges.emit(true);
      }

      lastFormState = this.form.value;
    }));
  }

  noOnlySpacesValidator(control: AbstractControl): ValidationErrors | null
  {
    const value = control.value ?? '';
    const cleaned = value.replace(/\s|\u200B|\u200C|\u200D|\uFEFF/g, '');
    return cleaned.length ? null : { onlyInvisibleCharacters: true };
  }

  headerCheckboxChanged(checked: boolean): void
  {
    this.groupBookingService.bookingGroup.TeeTimes.forEach((teeTimes) =>
    {
      teeTimes.IsSelected = checked;
    });
    this.setSelectedTeeTimes();
    this.updateFlagStates();
  }

  teeTimeCheckboxChanged(selected: boolean, index?: number): void
  {
    this.groupBookingService.bookingGroup.TeeTimes[index].IsSelected = selected;
    this.setSelectedTeeTimes();
    this.updateFlagStates();
  }
  private updateFlagStates(): void
  {
    const allSelected = this.groupBookingService.bookingGroup.TeeTimes.every(teeTime => teeTime.IsSelected);
    const someSelected = this.groupBookingService.bookingGroup.TeeTimes.some(teeTime => teeTime.IsSelected);

    this.bookingGroup.IsSelected = allSelected;
    this.bookingGroup.Indeterminate = !allSelected && someSelected;
  }

  private setSelectedTeeTimes(): void
  {
    this.selectedTeeTimes = this.bookingGroup?.TeeTimes?.filter(teeTime => teeTime.IsSelected)?.map(teeTime => ({
      TeeTimeID: teeTime.TeeTimeID,
      PlayerCount: teeTime.PlayerCount ?? 0,
      Time: teeTime.Time ?? '',
      Contacts: teeTime.Contacts ?? [],
      BookingID: teeTime.BookingID ?? 0
    }));
  }

  async openTeeTimesSettingsDialog(): Promise<void>
  {
    const dialogData: TeeTimesSettingsDialogModel = {
      form: this.form,
      selectedTeeTimes: this.selectedTeeTimes,
      TeeTimeHolesTypes: this.TeeTimeHolesTypes,
      TransportationTypes: this.transportationTypes,
      PriceLevels: this.priceLevels
    };
    const dialogRef = this.dialog.open(TeeTimesSettingsDialogComponent, {
      panelClass: ['tee-times-settings-dialog', 'o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: dialogData,
      maxWidth: '80vh',
      width: '600px',
    });
    const result = await firstValueFrom(dialogRef.afterClosed());
    if (result.IsChanged)
    {
      this.applyChanges(result.result);
    }
  }

  async applyChanges(result?: CreateTeeTimeGroupForm): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();
      const formValue = result ? result.value : this.form.value;
      const teeTimeIDs: string[] = [];

      this.selectedTeeTimes.every(teeTime =>
      {
        teeTimeIDs.push(teeTime.TeeTimeID);
      });

      const request: PostValidateAndLinkTeeTimeGroupRequest = {
        GroupUID: this.bookingGroup.GroupUID,
        CourseID: this.selectedCourse.CourseID,
        StartTeeHole: this.selectedCourse.StartTeeHole,
        NineHoles: formValue.HoleType,
        CartType: formValue.CartType,
        PriceLevelID: formValue.PriceLevel
      };
      request.TeeTimesUIDs = teeTimeIDs.join(',');
      const response = await firstValueFrom(this.groupTeeTimeService.PostValidateAndLinkTeeTimeToGroup(request));
      if (response?.TeeTimes)
      {
        this.snackBarService.showSuccess(
          this.translate.instant('POS_TEE_SHEET.SWAP_PLAYER.SAVE_SUCCESS'),
          '', null, 'left'
        );
      }
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }

  }

  async applyGroupTeeTimeChanges(postUpdateRequest: PostUpdateGroupTeeTimesRequest)
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();

      await firstValueFrom(this.groupTeeTimeService.PostUpdateGroupTeeTimes(postUpdateRequest));

      this.manageStandAloneLoadingService.finishLoading();
      this.snackBarService.showSuccess(
        this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.APPLIED_CHANGES'),
        '', null, 'left'
      );
      this.getBookingGroup();
    } catch (err)
    {
      this.manageStandAloneLoadingService.finishLoading();
      this.snackBarService.showError(
        this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.FAILED'),
        '', null, 'left'
      );
    }
  }

  cancelChanges(): void
  {
    this.cancelBookingDialog(TeeTimeActionEnum.CancelBookingsOnly, false);
  }

  shotgunChangeDetection(): void
  {
    this.subscription.add(this.formControls.StartTime.valueChanges.subscribe(() =>
    {
      this.shotgunApplied = false;
    }));
    this.subscription.add(this.formControls.HoleNumber.valueChanges.subscribe(() =>
    {
      this.shotgunApplied = false;
    }));
  }

  getMobileSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    }));
  };

  createTransportationAndHolesTypes(): void
  {
    this.transportationTypes = [
      {
        CartID: this.cartTypesID.Power,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.POWER')
      },
      {
        CartID: this.cartTypesID.Pull,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.PULL')
      },
      {
        CartID: this.cartTypesID.Walk,
        CartName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.WALK')
      }
    ];

    this.TeeTimeHolesTypes = [
      {
        HoleID: this.teeTimeHoles.Holes18,
        HoleName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.18_HOLES')
      },
      {
        HoleID: this.teeTimeHoles.Holes9,
        HoleName: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.9_HOLES')
      }
    ];
  }

  get disabledShotgunApply(): boolean
  {
    return this.formControls.StartTime.value === '' || !this.formControls.HoleNumber;
  }

  async createGroupTeeTime(saveFromDialog?: boolean): Promise<void>
  {
    this.onGroupCreatedEvent.emit(true);

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      await firstValueFrom(this.groupTeeTimeService.PostGroupTeeTimes({
        BookingID: this.BookingID, GroupName: this.form.controls['GroupName'].value, IsAny18Holes: this.IsAny18Holes
      }));
      this.snackBarService.showSuccess(this.form.controls['GroupName'].value + ' ' + this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.CREATED'), '', null, 'left');
      this.getBookingGroup(true, saveFromDialog);
      this.createGroupTeeTimeTriggered = true;
    }
    catch (error)
    {
      console.error(error);
      if (saveFromDialog)
      {
        this.onSaveComplete.emit(false);
      }
    }
    finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }

  }

  async getBookingGroup(refresh?: boolean, saveFromDialog?: boolean): Promise<void>
  {
    try
    {
      this.manageStandAloneLoadingService.startLoading();

      if (!this.groupBookingService?.bookingGroup?.GroupName || refresh)
      {
        await this.groupBookingService.getBookingGroup(this.BookingID);
      }

      this.bookingGroup = this.groupBookingService?.bookingGroup;
      this.setSelectedTeeTimes();

      this.managersGroupUID = this.bookingGroup?.Managers?.map(item => item.ManagerUID);

      if (this.groupBookingService?.bookingGroup?.TeeTimes && this.groupBookingService?.bookingGroup?.TeeTimes?.length > 0)
      {
        if (this.bookingGroup.GroupName)
        {
          const primaryGroupManager = this.bookingGroup?.Managers?.filter(item => item.IsPrimary);
          this.form.controls['PrimaryGroupManager'].patchValue(primaryGroupManager.length ? primaryGroupManager[0].ContactID : '');
          this.form.controls['GroupManagers'].patchValue(this.groupContactToContact(this.bookingGroup.Managers));
          this.form.controls['GroupName'].patchValue(this.bookingGroup.GroupName);
          this.form.controls['DefaultHoles'].patchValue(this.bookingGroup.NineHoles);
          this.form.controls['DefaultTransportation'].patchValue(this.bookingGroup.CartType);
          this.form.controls['DefaultPriceLevel'].patchValue(this.bookingGroup.PriceLevelID);
          this.form.enable();
          this.updateGroupManagers(this.groupContactToContact(this.bookingGroup.Managers));

          this.form.controls['Shotgun'].patchValue(this.bookingGroup.IsShotgun);
          this.form.controls['StartTime'].patchValue(this.bookingGroup.ShotgunDisplayTime);
          this.form.controls['HoleNumber'].patchValue(this.bookingGroup.ShotgunHoleNumber);
        } else
        {
          if (this.contactGroupSearch)
          {
            this.contactGroupSearch.contacts = [];
          }
        }
      } else
      {
        this.resetFormControls();
      }

    } catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.groupHasChanges.emit(false);
      if (saveFromDialog)
      {
        this.onSaveComplete.emit(true);
      }
      this.manageStandAloneLoadingService.finishLoading();
      this.cdr.detectChanges();
    }

  }

  private resetFormControls(): void
  {
    this.form.reset({
      GroupName: '',
      PrimaryGroupManager: null,
      GroupManagers: null,
      DefaultHoles: null,
      DefaultTransportation: null,
      DefaultPriceLevel: null,
      Shotgun: false,
      StartTime: null,
      HoleNumber: null,
    });
    this.form.disable();
    this.form.get('GroupName').enable();
  }

  getContacts()
  {
    return this.form.controls['GroupManagers'].value;
  }

  updateGroupManagers(contacts: Contact[], formChanges?: boolean): void
  {
    this.groupManagers = [];
    contacts.forEach((item, index) =>
    {
      this.groupManagers.push({
        ManagerUID: this.findContactInGroupContact(item),
        ContactID: item.ContactID,
        IsPrimary: this.form.controls['PrimaryGroupManager'].value === item.ContactID,
        DisplayName: item.DisplayName
      });
    });
    if (formChanges)
    {
      this.groupHasChanges.emit(true);
    }

  }

  findContactInGroupContact(contact: Contact): string | undefined
  {
    const groupContact: Array<BookingGroupManager> = this.bookingGroup.Managers.filter(item => item.ContactID === contact.ContactID);
    if (groupContact.length)
    {
      return groupContact[0].ManagerUID;
    }
    return;
  }

  openAddNewDialog(): void
  {
    const dialogRef = this.dialog.open(AddNewTeeTimeDialogComponent, {
      panelClass: ['add-tee-time-dialog-wrapper', 'o-dialog-md-fullscreen'],
      width: '400px'
    });

    dialogRef.afterClosed().subscribe((result: number) =>
    {
      if (result)
      {
        this.createTeeTimes(result);
      }
    });
  }

  createTeeTimes(numberOfTeeTimes: number): void
  {
    this.manageStandAloneLoadingService.startLoading();
    this.groupTeeTimeService.PostValidateAndLinkTeeTimeToGroup({
      BookingID: this.BookingID,
      NumTeeTimes: numberOfTeeTimes,
      StartTeeHole: this.selectedCourse.StartTeeHole,
      GroupUID: this.bookingGroup.GroupUID,
      PriceLevelID: this.bookingGroup.PriceLevelID,
      NineHoles: this.bookingGroup.NineHoles,
      CartType: this.bookingGroup.CartType
    }).pipe(
      catchError((error) =>
      {
        if (error?.error?.ErrorCode?.toString() == '400067')
        {
          return this.groupTeeTimeService.PostLinkTeeTimeToGroup({
            BookingID: this.BookingID,
            NumTeeTimes: numberOfTeeTimes,
            GroupUID: this.bookingGroup.GroupUID
          });
        }
      }),
      finalize(() =>
      {
        this.manageStandAloneLoadingService.finishLoading();
        this.cdr.detectChanges();
      }))
      .subscribe((response: PostLinkTeeTimeGroupResponse) =>
      {
        let teeTimeText = numberOfTeeTimes > 1 ? this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.TEE_TIMES') : this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.TEE_TIME');
        this.snackBarService.showSuccess(numberOfTeeTimes + ' ' + teeTimeText + ' ' + this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.CREATED'), '', null, 'left');
        this.updateTeeTimesWithSelection(response.TeeTimes);
        this.updateFlagStates();
        this.cdr.detectChanges();
      });
  }

  contactToGroupContact(contactItem: Contact[]): BookingGroupManager[]
  {
    return contactItem.map((item, index) =>
    {
      return {
        ContactID: item.ContactID,
        ManagerUID: item.ContactID,
        IsPrimary: !index,
        FirstName: item.DisplayName,
        LastName: item.DisplayName
      };
    });
  }

  groupContactToContact(groupContact: BookingGroupManager[]): Contact[]
  {
    if (!groupContact) return [];
    return groupContact.map(item =>
    {
      return {
        ContactID: item.ContactID,
        IsPrimary: item.IsPrimary,
        Salutation: '',
        MemberID: item.ContactID,
        Email: '',
        Phone: '',
        ProfilePicture: '',
        DisplayName: `${ item.FirstName } ${ item.LastName }`,
      };
    });
  }

  groupManagerToGroupTeetimeManager(groupManagers: BookingGroupManager[]): GroupTeeTimeManager[]
  {
    return groupManagers.map(item =>
    {
      const managerUID = this.managersGroupUID.find(elem => elem === item.ContactID);
      const elem: GroupTeeTimeManager = {
        ManagerUID: managerUID,
        ContactID: item.ContactID,
        IsPrimary: item.IsPrimary
      };
      if (!elem.ManagerUID)
      {
        delete elem.ManagerUID;
      }
      return elem;
    }
    );
  }

  async saveGroupTeeTime(saveFromDialog?: boolean)
  {
    this.manageStandAloneLoadingService.startLoading();
    this.cdr.detectChanges();
    let request: PostUpdateGroupTeeTimesRequest = {
      GroupUID: this.bookingGroup.GroupUID,
      GroupName: this.form.controls['GroupName'].value,
      Managers: this.groupManagerToGroupTeetimeManager(this.groupManagers),
      PriceLevelID: this.formControls.DefaultPriceLevel.value,
      CartType: this.formControls.DefaultTransportation.value,
      NineHoles: this.formControls.DefaultHoles.value
    };
    try
    {
      await firstValueFrom(this.groupTeeTimeService.PostUpdateGroupTeeTimes(request));

      await this.getBookingGroup(true, saveFromDialog);
      this.snackBarService.showSuccess(this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.SAVED_SUCCESSFULLY'), '', null, 'left');

    } catch (error)
    {
      if (saveFromDialog)
      {
        this.onSaveComplete.emit(false);
      }
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  async skipChangesGroup(): Promise<void>
  {
    if (this.createGroupTeeTimeTriggered)
    {
      await this.deleteGroupTeeTimes(TeeTimeActionEnum.RemoveGroup);
      this.getBookingGroup(true);
    } else
    {
      this.skipChangesOfGroup.emit(true);
    }
  }


  openRemoveTeeTimeDialog(teeTime: BookingGroupTeeTime): void
  {
    this.dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_DIALOG_TITLE'),
          content: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_DIALOG_DESCRIPTION'),
          submitButtonLabel: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_DIALOG_REMOVE_LABEL'),
          closeButtonLabel: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_DIALOG_CANCEL_LABEL'),
          submitButtonColor: 'warn'
        }
      },
      width: '350px',
    });
    this.dialogRef.afterClosed().subscribe((result: boolean) =>
    {
      if (result)
      {
        this.removeTeeTime(teeTime);
      }
    });
  }

  changePrimaryManager(event: MatSelectChange): void
  {
    this.groupManagers.map(item =>
    {
      item.IsPrimary = item.ContactID === event.value;
      return item;
    });
    this.groupHasChanges.emit(true);

  }

  removeTeeTime(teeTime: BookingGroupTeeTime, skip?: boolean): void
  {
    const request: DeleteTeeTimeFromGroupTeeTimesRequest = {
      TeeTimeID: teeTime.TeeTimeID,
      GroupUID: this.bookingGroup.GroupUID
    };
    this.groupTeeTimeService.DeleteTeeTimeFromGroupTeeTimes(request).subscribe(response =>
    {
      this.updateTeeTimesWithSelection(response.TeeTimes);
      this.updateFlagStates();
      this.cdr.detectChanges();
      const message = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.BOOKING_REMOVED');
      this.snackBarService.showSuccess(message, '', null, 'left');
      if (!response.TeeTimes)
      {
        this.closePanelWithoutSave.emit();
      }
      if (skip)
      {
        this.skipChangesOfGroup.emit(true);
      }
    });
  }

  viewTeeTime(bookingID: number): void
  {
    this.reloadBooking.emit(bookingID);
  }

  async applyShotgun(isShotgun: boolean): Promise<void>
  {
    this.manageStandAloneLoadingService.startLoading();
    let request: PostApplyShotgunRequest = {
      GroupUID: this.bookingGroup.GroupUID,
      IsShotgun: isShotgun,
      ShotgunDisplayTime: this.formControls.StartTime.value,
      ShotgunHoleNumber: this.formControls.HoleNumber.value
    };
    try
    {
      const response = await firstValueFrom(this.groupTeeTimeService.PostApplyShotgun(request));
      this.updateTeeTimesWithSelection(response.TeeTimes);
      const message = isShotgun ? this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.SHOTGUN_APPLIED') : this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.SHOTGUN_REMOVED');
      this.snackBarService.showSuccess(message, '', null, 'left');
      this.shotgunApplied = isShotgun;
      this.cdr.detectChanges();
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  private updateTeeTimesWithSelection(newTeeTimes: BookingGroupTeeTime[]): void
  {
    this.groupBookingService.bookingGroup.TeeTimes = this.groupBookingService.bookingGroup.TeeTimes
      ?.filter(t => newTeeTimes?.some(nt => nt.TeeTimeID === t.TeeTimeID)) || [];

    newTeeTimes?.forEach(newItem =>
    {
      const existingItem = this.groupBookingService.bookingGroup.TeeTimes
        ?.find(t => t.TeeTimeID === newItem.TeeTimeID);

      if (existingItem)
      {
        Object.assign(existingItem, {
          ...newItem,
          IsSelected: existingItem.IsSelected
        });
      }
      else
      {
        this.groupBookingService.bookingGroup.TeeTimes.push({ ...newItem, IsSelected: false });
      }
    });

  }


  onShotgunToggle(event: MatCheckboxChange): void
  {
    const isChecked = event.checked;
    if (!isChecked && (this.shotgunApplied || this.bookingGroup.IsShotgun))
    {
      this.formControls.Shotgun.setValue(true, { emitEvent: false });
      const dialogRef = this.dialog.open(CardDialogComponent, {
        panelClass: ['o-confirm-dialog'],
        data: {
          component: ConfirmationDialogComponent,
          dialogData: {
            title: 'POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_SHOTGUN',
            content: 'POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_SHOTGUN_DESCRIPTION',
            submitButtonLabel: 'POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.REMOVE_SHOTGUN',
            cancelButtonLabel: 'POS_TEE_SHEET.GENERAL.CANCEL',
          }
        },
        width: '350px',
      });

      this.subscription.add(dialogRef.afterClosed().subscribe(async (result) =>
      {
        if (result)
        {
          await this.applyShotgun(false);
          this.formControls.Shotgun.setValue(false, { emitEvent: false });
          this.shotgunApplied = false;
        }
      }));
    } else
    {
      this.form.controls['StartTime'].patchValue(this.groupBookingService.bookingGroup.TeeTimes?.[0].Time);
      this.form.controls['HoleNumber'].patchValue(this.bookingGroup.ShotgunHoleNumberList[0]);
    }
  }

  async deleteGroup(): Promise<void>
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: ['o-confirm-dialog', 'o-dialog-sm-fullscreen'],
      data: {
        component: DeleteGroupTeeTimeDialogComponent,
        dialogData: {}
      },
      width: '350px'
    });
    const result = await firstValueFrom(dialogRef.afterClosed());

    if (result === TeeTimeActionEnum.RemoveGroup)
    {
      this.deleteGroupTeeTimes(result);
    } else if (result === TeeTimeActionEnum.RemoveGroupAndCancelBookings)
    {
      await this.cancelBookingDialog(result, true);
    }
  }

  async deleteGroupTeeTimes(result: TeeTimeActionEnum, confirmationForm?: ConfirmTeeTimeBookingForm): Promise<void>
  {
    const request: DeleteGroupTeeTimesAndRemoveOrderRequest = new DeleteGroupTeeTimesAndRemoveOrderRequest();
    request.Action = result;
    request.HeaderUID = this.bookingGroup.GroupUID;
    const productOrder = this.orderStoreService.Get();
    let message = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.GROUP_DELETED');
    if (productOrder?.RefID)
    {
      request.RefID = productOrder.RefID;
    }
    if (result === TeeTimeActionEnum.RemoveGroupAndCancelBookings)
    {
      request.BookingIDs = this.groupBookingService.bookingGroup.TeeTimes.map(teeTime => teeTime.BookingID);
      request.NotificationEmails = confirmationForm.to;
      request.CCEmails = confirmationForm.cc;
      request.SendNotificationEmailsToAllPlayers = confirmationForm.allPlayersChecked;
      request.Reason = confirmationForm.cancelationReason;
      message = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.GROUP_AND_BOOKINGS_DELETED');
    }
    if (result === TeeTimeActionEnum.CancelBookingsOnly)
    {
      request.BookingIDs = this.groupBookingService.bookingGroup.TeeTimes.filter(teeTime => teeTime.IsSelected).map(teeTime => teeTime.BookingID);
      request.NotificationEmails = confirmationForm.to;
      request.CCEmails = confirmationForm.cc;
      request.SendNotificationEmailsToAllPlayers = confirmationForm.allPlayersChecked;
      request.Reason = confirmationForm.cancelationReason;
      message = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.GROUP_TEE_TIMES.BOOKINGS_CANCELLED');
    }
    try
    {
      const response = await firstValueFrom(this.manageOrderTeeTimeService.DeleteGroupTeeTimesAndRemoveOrder(request));
      this.snackBarService.showSuccess(message, '', null, 'left');
      if (result === TeeTimeActionEnum.CancelBookingsOnly)
      {
        if (request?.BookingIDs?.length === this.bookingGroup?.TeeTimes?.length)
        {
          this.closePanelWithoutSave.emit();
        } else
        {
          this.getBookingGroup(true);
        }
      } else
      {
        this.closePanelWithoutSave.emit();
      }
    } catch (error)
    {

    }
  }
  async cancelBookingDialog(result: TeeTimeActionEnum, isDeleteGroup: boolean): Promise<void>
  {
    const user = await firstValueFrom(this.headerService.getUser({}));
    const teeTimes = result === TeeTimeActionEnum.CancelBookingsOnly ? this.groupBookingService.bookingGroup.TeeTimes.filter(teeTime => teeTime.IsSelected) : this.groupBookingService.bookingGroup.TeeTimes;
    const bookings = await this.getBookings(teeTimes, isDeleteGroup);
    const bookerEmails = new Set<string>();
    const playerEmails = new Set<string>();

    bookings.PlayerEmails.forEach(item =>
    {
      bookerEmails.add(item.PlayerEmails[0]?.Email);
      item.PlayerEmails?.forEach(a =>
      {
        playerEmails.add(a.Email);
      });
    });

    const confirmDialogData: ConfirmTeeTimeBookingDialogDataModel = {
      ConfirmMode: ManageTeeTimeConfirmMode.Cancel,
      HeaderTitle: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.CANCEL_TEE_TIME'),
      ButtonText: this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.CANCEL_TEE_TIME'),
      SubmitButtonColor: 'warn',
      BookingID: this.BookingID,
      SelectedCourseName: this.selectedCourse.CourseName,
      BookingDate: this.BookingDateTime,
      StartTee: this.form.value.DefaultHoles,
      Players: [],
      PlayerEmails: Array.from(playerEmails),
      BookerEmails: Array.from(bookerEmails),
      UserName: user.User.DisplayName,
      AvatarCharacter: user.User.DisplayName[0],
      IsMultiBookings: true,
      BookingDates: teeTimes.map(teeTime => teeTime.Time),
      BookingIDs: teeTimes.map(teeTime => teeTime.BookingID),
      TotalPrice: bookings.SumTotalPrice,
      NumberOfPlayers: bookings.PlayersCount
    };


    const dialogRef = this.dialog.open(ConfirmTeeTimeBookingDialogComponent, {
      panelClass: ['confirm-tee-time-booking-dialog', 'o-dialog-sm-fullscreen'],
      data: {
        component: ConfirmationDialogComponent,
        dialogData: confirmDialogData
      },
      maxWidth: '80vh',
      width: '450px',
    });
    const dialogRefSubscription = dialogRef.componentInstance.dialogSubmitted.subscribe((confirmationForm: ConfirmTeeTimeBookingForm) =>
    {
      this.deleteGroupTeeTimes(result, confirmationForm);
      dialogRef.close();
      dialogRefSubscription?.unsubscribe();
    });
  }

  async getBookings(teeTime: BookingGroupTeeTime[], isDeleteGroup: boolean): Promise<PostFirstPlayersEmailsByGroupUIDResponse>
  {
    const request: PostFirstPlayersEmailsByGroupUIDRequest = {
      GroupUID: isDeleteGroup ? this.bookingGroup.GroupUID : null,
      BookingIDs: !isDeleteGroup ? teeTime.map(item => item.BookingID) : null
    };

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response: PostFirstPlayersEmailsByGroupUIDResponse = await firstValueFrom(this.groupTeeTimeService.FirstPlayersEmailsByGroupUID(request));
      return response;
    } catch (error)
    {

    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  openManagePlayersDialog()
  {
    this.dialog.open(ManagePlayersDialogComponent, {
      panelClass: ['o-manage-players-dialog', 'o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: {
        GroupName: this.bookingGroup.GroupName,
        SelectedDisableOptionsCourse: this.selectedDisableOptionsCourse,
        CurrentTeeSheetTransfer: this.currentTeeSheetTransfer,
        GroupUID: this.bookingGroup.GroupUID,
        IsFivePlayerTemplate: this.fivePlayer
      } as GroupBookingDetailsModalData,
      width: '95vw',
      maxWidth: '95vw',

    });
  }
}
