import { FormControl } from "@angular/forms";
import { Course } from "./get-tee-sheet-main.model";

export enum DateSelectionEnum {
  Date = 'date',
  DateRange = 'dateRange'
}

export enum HoleNumberTypeEnum {
  OneTwoThree = '123',
  AB = 'AB',
  ABC = 'ABC',
  Custom = 'custom'
}

export class TeeSheetDisplayForm {
  Course: FormControl<string>;
  DateSelection: FormControl<DateSelectionEnum>;
  SelectedDate: FormControl<string>;
  StartDate: FormControl<string>;
  EndDate: FormControl<string>;
  TimeRangeStart: FormControl<string>;
  TimeRangeEnd: FormControl<string>;
  TimeRangeStartIndex: FormControl<number>;
  TimeRangeEndIndex: FormControl<number>;
  DisplayTime: FormControl<string>;
  HoleNumberType: FormControl<HoleNumberTypeEnum>;
  CustomHoleNumbers: FormControl<string>;
}

export class TeeSheetDisplayApply
{
    Course: string;
    DateSelection: DateSelectionEnum;
    SelectedDate: string;
    StartDate: string;
    EndDate: string;
    TimeRangeStart: string;
    TimeRangeEnd: string;
    TimeRangeStartIndex: number;
    TimeRangeEndIndex: number;
    DisplayTime: string;
    HoleNumberType: HoleNumberTypeEnum;
    CustomHoleNumbers: string;
}
export class GetDisplayDataRequest
{
    CourseID: string;
    SheetDate: string;
}

export class GetDisplayDataResponse
{
  TeeTimes: Array<TeeTime>;
}
export class TeeTime {
  TeeTime: string;
  HoleNumber: string;
  DisplayTime: string;
    //use in FE
  Index: number;
}

export class DisplayTeeSheetDialogData {
  courses: Course[];
  selectedCourse: Course;
  selectedDate: string
}
