import { Component, Inject, OnDestroy } from '@angular/core';
import { AbstractControl, FormControl, FormGroup, UntypedFormBuilder, ValidationErrors, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ManageStandAloneLoadingService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { Contact } from '@tei/office-contacts';
import { Subscription } from 'rxjs';
import { NewGroupDialogData, NewGroupForm } from '../../../@intermediate/models/post-update-tee-time-group.model';
import { GroupTeeTimeService } from '../../../@services/group-tee-time.service';

@Component({
  selector: 'tei-pos-tee-sheet-squeeze-new-group-dialog',
  templateUrl: './new-group-dialog.component.html',
  styleUrls: ['./new-group-dialog.component.scss']
})

export class NewGroupDialogComponent implements OnDestroy
{

  form: FormGroup<NewGroupForm> = new FormGroup<NewGroupForm>({
    ContactID: new FormControl('', Validators.required),
    GroupName: new FormControl('', [Validators.required, , this.noOnlySpacesValidator.bind(this)])
  });
  public contactId: Contact;
  subscriptions = new Subscription();
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: NewGroupDialogData,
    private fb: UntypedFormBuilder,
    private snackBarService: SnackBarService,
    @Inject('IGroupTeeTimeService') private groupTeeTimeService: GroupTeeTimeService,
    public dialog: MatDialogRef<NewGroupDialogComponent>,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
  )
  {
  }

  close(): void
  {
    this.dialog.close();
  }

  getFormControlByName(controlName: string): FormControl
  {
    return this.form.get(controlName) as FormControl;
  }
  onContactItemClickEvent(event: Contact): void
  {

  }

  removeContactItemClickEvent(event: Contact): void
  {

  }
  createGroup(): void
  {
    if (this.form.valid)
    {
      this.dialog.close({
        contactID: (this.form.value.ContactID as Contact).ContactID,
        groupName: this.form.value.GroupName
      });
    }
  }
  noOnlySpacesValidator(control: AbstractControl): ValidationErrors | null
  {
    const value = control.value;
    if (typeof value !== 'string') return null;

    const cleaned = value.replace(/[\s\u200B-\u200D\uFEFF]/g, '');

    if (cleaned.length === 0)
    {
      return { onlyInvisibleCharacters: true };
    }

    return null;
  }
  ngOnDestroy(): void
  {
    this.subscriptions?.unsubscribe();
  }
}
