import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiTextAbbreviationPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { PipesModule } from '../../@pipes/pipes.module';
import { GroupTeeTimeService } from '../../@services/group-tee-time.service';
import { RainCheckHelperService } from '../../@services/rain-check.helper.service';
import { RainCheckWizardModule } from '../manage-tee-time/rain-check-wizard/rain-check-wizard.module';
import { TeeSheetBlockModule } from '../tee-sheet-block/tee-sheet-block.module';
import { TeeSheetFlagModule } from '../tee-sheet-flag/tee-sheet-flag.module';
import { TeeSheetUnFlagModule } from '../tee-sheet-un-flag/tee-sheet-un-flag.module';
import { TeeSheetUnblockModule } from '../tee-sheet-unblock/tee-sheet-unblock.module';
import { NewGroupDialogModule } from './new-group-dialog/new-group-dialog.module';
import { TeeSheetSlotContextMenuComponent } from './tee-sheet-slot-context-menu/tee-sheet-slot-context-menu.component';
import { TeeSheetSlotComponent } from './tee-sheet-slot.component';
import { TeeSheetSqueezeDialogModule } from './tee-sheet-squeeze-dialog/tee-sheet-squeeze-dialog.module';

const MATERIAL_MODULES = [MatDividerModule, MatTableModule, MatIconModule, MatButtonModule, MatRippleModule, MatMenuModule, MatTooltipModule];
const BASE_MODULES = [CommonModule];
const TEI_MODULES = [PipesModule, TeiTextTranslatePipeModule, TeiTextAbbreviationPipeModule];
const MATERIAL_TEI_MODULES = [LoadingModule];
const COMPONENTS = [TeeSheetSlotComponent, TeeSheetSlotContextMenuComponent];
const PIPES = [];
const POS_TEE_SHEET_MODULES = [
  RainCheckWizardModule,
  TeeSheetBlockModule,
  TeeSheetUnblockModule,
  TeeSheetSqueezeDialogModule,
  NewGroupDialogModule,
  TeeSheetFlagModule,
  TeeSheetUnFlagModule
];

@NgModule({
  declarations: [...COMPONENTS, ...PIPES],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...MATERIAL_TEI_MODULES,
    ...POS_TEE_SHEET_MODULES
  ],
  exports: [
    ...COMPONENTS,
    ...PIPES,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...MATERIAL_TEI_MODULES,
    ...POS_TEE_SHEET_MODULES
  ],
  providers: [
    RainCheckHelperService,
    { provide: 'IGroupTeeTimeService', useClass: GroupTeeTimeService }
  ],
})
export class TeeSheetSlotModule { }
