const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, FilterChipsModule, CardDialogModule, LoadingModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [TeeTimeBookingAdvancedSearchComponent, TeeTimeBookingAdvancedSearchHeaderComponent];


@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    DefaultTabLayoutModule,
  ],
  providers: [{ provide: 'IManageTeeTimeService', useClass: ManageTeeTimeService }]
})
export class TeeTimeBookingAdvancedSearchModule { }
