import { SelectionModel } from '@angular/cdk/collections';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import
{
  AfterContentChecked,
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Inject,
  Input,
  OnDestroy,
  OnInit,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacySnackBarDismiss as MatSnackBarDismiss, MatLegacySnackBarRef as MatSnackBarRef } from '@angular/material/legacy-snack-bar';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService, StorageService } from '@tei/core';
import { ConfirmationDialogComponent, SnackBarComponent, SnackBarService } from '@tei/material';
import { ISecureNavigationService, PostNavigationRequest, PostNavigationResponse } from '@tei/material-smart-components';
import { teeSheetCacheName } from '@tei/pos-shared';
import moment from 'moment';
import { firstValueFrom, forkJoin, Subscription } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { Course, SelectedBlockCourse, SelectedDisableOptionsCourse } from '../../@intermediate/models/get-tee-sheet-main.model';
import
{
  BlockingType,
  BookingType,
  ContactClassIDEnum,
  CourseType,
  GetTeeSheetRequest,
  GetTeeSheetResponse,
  GroupBookingSelectedTeeTime,
  Holes1or10,
  PlayerStatus,
  StatisticIndicator,
  TeeSheet,
  TeeTime,
  TeeTimeRow,
  TimeZone
} from '../../@intermediate/models/get-tee-sheet.model';
import { GetTeeTimeTotalsRequest, GetTeeTimeTotalsResponse } from '../../@intermediate/models/get-tee-time-price-overlay.model';
import { GroupBookingDetailsModalData } from '../../@intermediate/models/group-booking-details.model';
import { GetBookingNotesRequest, GetBookingNotesResponse, PostBookingNotesRequest, PostBookingNotesResponse } from '../../@intermediate/models/note-tee-time.model';
import { AdvancedSearchTeeTime } from '../../@intermediate/models/post-tee-time-advance-search.model';
import
{
  PostDisplayTimeRequest,
  PostDisplayTimeResponse,
  TeeSheetTimes
} from "../../@intermediate/models/post-tee-time-display-times.model";
import { ColumnType, SelectType, TeeSheetSelectedView } from '../../@intermediate/models/tee-sheet-toolbar-model';
import
{
  BookingAction,
  BookingActionEntity,
  MultiTeeSheetTransfer,
  TeeSheetTransfer
} from '../../@intermediate/models/tee-sheet-transfer-service.model';
import { ManageTeeTimeService } from '../../@services/manage-tee-time.service';
import { SmallGroupBookingHelperService } from '../../@services/small-group-booking-helper.service';
import { TeeSheetActionService } from '../../@services/tee-sheet-action.service';
import { TeeSheetMainService } from '../../@services/tee-sheet-main.service';
import { TeeSheetTransferService } from '../../@services/tee-sheet-transfer.service';
import { TeeSheetService } from '../../@services/tee-sheet.service';
import { ManagePlayersDialogComponent } from '../manage-players-dialog/manage-players-dialog.component';
import
{
  ManageSmallGroupBookingComponent
} from '../manage-tee-time/manage-small-group-booking/manage-small-group-booking.component';
import { DialogNoteTeeTimeComponent } from '../manage-tee-time/note-tee-time-dialog/note-tee-time-dialog.component';
import { TeeSheetSlotComponent } from '../tee-sheet-slot/tee-sheet-slot.component';

@Component({
  selector: 'tei-pos-tee-sheet-tee-times',
  templateUrl: './tee-sheet-tee-times.component.html',
  styleUrls: ['./tee-sheet-tee-times.component.scss'],
  providers: [
    { provide: 'ITeeSheetTransferService', useClass: TeeSheetTransferService }
  ]
})
export class TeeSheetTeeTimesComponent implements OnInit, AfterViewInit, AfterContentChecked, OnDestroy
{
  static readonly TEE_SHEET_STATISTIC = 'tee-sheet-statistic';
  static readonly TEE_SHEET = 'tee-sheet';
  @Input() isCollapse = false;
  @Input() teeSheet = new TeeSheet();
  @Input() searchedTeeTime: AdvancedSearchTeeTime;
  @Input() courses = new Array<Course>(0);
  @Input() isReadOnly: boolean;
  @Input() teeSheetArrayIndex: number;
  lastSelectedType = SelectType.Single;
  @Input() set selectTypeInput(value: SelectType)
  {
    this.selectType = value;
    this.lastSelectedType = this.lastSelectedType;
    this.updateBookingsPerTeeSheet();
    this.prepareTeeTimeMultiSelect();
  }
  @Input() showContactClass: boolean;
  @Input() selectedDisableOptionsCourse: SelectedDisableOptionsCourse;
  @Input() teeSheetSelectedView: TeeSheetSelectedView;
  @ViewChild('teeTimeContainerPrint', { static: false }) teeTimesContent: ElementRef;

  @ViewChildren('teeTimeContainer') teeTimeContainer: QueryList<HTMLElement>;
  @ViewChild('teeSheetSlot') teeSheetSlot: TeeSheetSlotComponent;
  teeTimeContainerHTMLElement: QueryList<ElementRef>;
  CourseType = CourseType;
  columnType = ColumnType;
  teeSheetTransfer: TeeSheetTransfer = new TeeSheetTransfer();
  bookingActionTransfer: BookingAction;
  bookingActiveAction: BookingAction;
  actionSnackbarRef: MatSnackBarRef<SnackBarComponent>;
  showMessage = true;
  subscription: Subscription = new Subscription();
  form: UntypedFormGroup;
  selectType: SelectType;
  timeZone = TimeZone;
  isLoading = '';
  isTeeSheetPriceOverlayOpen: { [key: number]: boolean; } = {};
  teeSheetPriceOverlayTeeTime: TeeTime;
  teeSheetPriceOverlayData: GetTeeTimeTotalsResponse;
  SelectType = SelectType;
  selections: SelectionModel<number>[] = [];
  subscriptions: Subscription = new Subscription();
  isSmallScreen: boolean = false;
  isTabletExpanded: boolean = false;
  isTablet: boolean = false;
  screenSize: number;

  ContactLimit = 4;
  ContactCount = 0;
  playerStatus = PlayerStatus;
  bookingType = BookingType;
  blockingType = BlockingType;
  playerIndex: number;
  adjustedTeeSheetTimes: TeeSheetTimes[] = [];
  clickedTeeTime: TeeTime;
  selectedCourse: SelectedBlockCourse;
  startTee = Holes1or10;
  isLastDay: boolean = false;
  hasReserved: boolean;
  hasCrossOver: boolean;
  notReservedContactCount: number;
  reserveBlockColor: string;
  statisticIndicator: StatisticIndicator;

  constructor(
    private changeDetector: ChangeDetectorRef,
    private storageService: StorageService,
    @Inject('ITeeSheetTransferService') private teeSheetTransferService: TeeSheetTransferService,
    @Inject('ITeeSheetActionService') private teeSheetActionService: TeeSheetActionService,
    private snackbarService: SnackBarService,
    private responsiveService: ResponsiveService,
    private teeSheetMainService: TeeSheetMainService,
    private translate: TranslateService,
    @Inject('ITeeSheetService') private teeSheetService: TeeSheetService,
    @Inject('IManageTeeTimeService') private manageTeeTimeService: ManageTeeTimeService,
    private bottomSheet: MatBottomSheet,
    public manageLoadingService: ManageLoadingService,
    private fb: UntypedFormBuilder,
    private dialog: MatDialog,
    private smallGroupBookingHelperService: SmallGroupBookingHelperService,
    private snackBarService: SnackBarService,
    @Inject('ISecureNavigationService') private secureNavigationService: ISecureNavigationService
  ) { }

  ngOnInit(): void
  {
    this.detectMobileScreen();
    this.changeTeeSheet();
    this.handleChangeMultiTeeSheet();
    this.getBookingAction();
    this.createForm();
    if (this.teeSheet.SelectedCourse)
    {
      this.selectedCourse = {
        CourseID: this.teeSheet.SelectedCourse.CourseID,
        StartTeeHole: this.teeSheet.StartTeeHole == this.startTee.Hole10 ? this.startTee.Hole10.toString() : this.startTee.Hole1.toString(),
      };
      this.selectedDisableOptionsCourse.AllowFutureCheckins = this.teeSheet.SelectedCourse.AllowFutureCheckins;
      this.selectedDisableOptionsCourse.AllowPastCheckins = this.teeSheet.SelectedCourse.AllowPastCheckins;
    }

  }

  private createForm(): void
  {
    this.form = this.fb.group({
      teeTime: false,
    });
  }
  printTeeTimes()
  {
    const selectedDateElement = this.teeTimesContent.nativeElement.querySelector('tei-material-datepicker input');
    if (selectedDateElement)
    {
      const selectedDate = moment(this.teeSheet.SelectedTeeSheetDate).format('DD MMM,YYYY');
      const staticDateElement = document.createElement('span');
      staticDateElement.style.marginTop = '10px';
      staticDateElement.style.marginLeft = '25px';
      staticDateElement.innerText = selectedDate.toString();
      selectedDateElement.parentNode.replaceChild(staticDateElement, selectedDateElement);
    }
    const printContent = this.teeTimesContent.nativeElement.innerHTML;
    const originalContent = document.body.innerHTML;
    document.body.innerHTML = printContent;
    window.print();
    document.body.innerHTML = originalContent;
    window.location.reload();
  }

  private getBookingAction(): void
  {
    this.subscription.add(this.teeSheetActionService.getBookingAction().subscribe((bookingAction: BookingAction) =>
    {
      this.bookingActionTransfer = bookingAction;
      if ((bookingAction.status === BookingActionEntity.MOVE || bookingAction.status === BookingActionEntity.COPY) && !bookingAction?.postponed?.status)
      {
        this.bookingActiveAction = Object.assign({}, this.bookingActionTransfer);
        this.openActionSnackBar(bookingAction);

      }
      else if (bookingAction.status === BookingActionEntity.NOTES)
      {
        this.openTeeTimeNoteDialog(bookingAction);
      }
      else if (bookingAction.status === BookingActionEntity.NONE)
      {
        this.closeActionSnackbar();
      }

      if (bookingAction?.postponed?.status)
      {
        if (this.showMessage)
        {
          this.snackbarService.dismiss();
          if (!bookingAction?.postponed?.messageFromAPI)
          {
            this.snackbarService.showError(this.translate.instant(bookingAction.postponed.message), '', null, 'left');
          }
          setTimeout(() =>
          {
            this.showMessage = true;
            this.openActionSnackBar(this.bookingActiveAction);
          }, 5000);
        }
        this.showMessage = false;
      }
    }));
  }

  openTeeTimeNoteDialog(bookingAction: BookingAction): void
  {
    const request = new GetBookingNotesRequest();
    request.BookingID = bookingAction.Book.BookingID;
    request.CourseID = this.teeSheet?.SelectedCourse.CourseID;
    this.subscription.add(this.manageTeeTimeService.GetBookingNotes(request).subscribe({
      next: (response: GetBookingNotesResponse) =>
      {
        const dialogRef = this.dialog.open(DialogNoteTeeTimeComponent, {
          panelClass: 'remove-tee-time-dialog',
          data: {
            component: ConfirmationDialogComponent,
            dialogData: {
              BookingNote: response.BookingNote,
              OnlineNote: response.OnlineNote
            }
          },
          maxWidth: '80vh',
          width: '450px',
        });
        this.subscription.add(dialogRef?.afterClosed().subscribe((result: GetBookingNotesResponse) =>
        {
          if (result?.hasOwnProperty('BookingNote'))
          {
            const postBookingNotesRequest = new PostBookingNotesRequest();
            postBookingNotesRequest.bookingID = bookingAction.Book.BookingID;
            postBookingNotesRequest.bookingNote = result.BookingNote;
            this.subscription.add(this.manageTeeTimeService.PostBookingNotes(postBookingNotesRequest).subscribe({
              next: (postResult: PostBookingNotesResponse) =>
              {
                if (postResult?.Result)
                {
                  const successMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.BOOKING_NOTE_SAVED_SUCCESSFUL');
                  this.snackBarService.showSuccess(successMessage, '', null, 'left');
                }
                this.teeSheet.IsLoading = false;
                this.loadTeeSheet(this.teeSheet);
              }
            }));
          }
          this.teeSheetActionService.setBookingAction({ status: BookingActionEntity.NONE, Book: bookingAction.Book, postponed: { status: false, message: null } });
          this.teeSheetActionService.setTeeSheetActionStatus(true);
        }));
      },
      error: () => this.teeSheet.IsLoading = false,
      complete: () => this.teeSheet.IsLoading = false
    },
    ));
  }

  private getTeeTimeContainerHTMLElement(): void
  {
    this.subscription.add(this.teeTimeContainer?.changes?.subscribe((elements: QueryList<ElementRef>) =>
    {
      this.teeTimeContainerHTMLElement = elements;
    }));
  }

  private detectMobileScreen(): void
  {
    this.subscriptions.add(
      this.responsiveService.screenWidth.subscribe(screenSize =>
      {
        this.screenSize = screenSize;
      }));
  }

  prepareTeeTimes(): void
  {
    this.prepareTeeTimeMultiSelect();

    this.subscriptions.add(
      this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
      {
        this.isSmallScreen = screenSize === 'xs' || screenSize === 'sm';
        this.isTabletExpanded = !this.isCollapse && (screenSize === 'md');
        this.isTablet = screenSize === 'md';

        this.changeDetector.detectChanges();
      })
    );
    this.selectionModelChecked();
    this.selectionModelUnChecked();
    this.selectedCourseChanged();
  }

  closeActionSnackbar(): void
  {
    this.snackbarService.dismiss();
  }

  openActionSnackBar(bookingAction: BookingAction): void
  {
    const actionMessage = `POS_TEE_SHEET.MANAGE_TEE_TIME.${ bookingAction.status === BookingActionEntity.MOVE ? 'MOVING' : 'COPYING' }`;

    this.actionSnackbarRef = this.snackbarService.showSuccess(this.translate.instant(actionMessage, {
      BookingID: this.bookingActionTransfer.Book.BookingID
    }), this.translate.instant('POS_TEE_SHEET.GENERAL.CANCEL'), null, 'left', null, 0);

    this.actionSnackbarRef.afterDismissed().subscribe((matSnackBarDismiss: MatSnackBarDismiss) =>
    {
      if (matSnackBarDismiss.dismissedByAction)
      {
        this.teeSheetActionService.setBookingAction({ status: BookingActionEntity.NONE, Book: bookingAction.Book, postponed: { status: false, message: null } });
      }
    });
  }

  getSelectedDate(date: Date): void
  {
    this.teeSheet.SelectedTeeSheetDate = date;
    this.loadTeeSheet(this.teeSheet);
    this.isPastDay(this.teeSheet.SelectedTeeSheetDate);
  }

  isPastDay(date: Date)
  {
    const currentDate = this.normalizeToMidnight(new Date());
    const selectedDate = this.normalizeToMidnight(date);
    this.isLastDay = selectedDate < currentDate;
  }

  normalizeToMidnight(date: Date): Date
  {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate());
  }

  onChangeSelectedCourse(selectedCourse: Course): void
  {
    this.teeSheet.SelectedCourse = this.courses.find(course => course.CourseName === selectedCourse.CourseName);
    this.selectedCourse = {
      CourseID: selectedCourse.CourseID,
      StartTeeHole: selectedCourse.StartTeeHole == this.startTee.Hole10 ? this.startTee.Hole10.toString() : this.startTee.Hole1.toString(),
    };
    this.selectedDisableOptionsCourse.AllowFutureCheckins = selectedCourse.AllowFutureCheckins;
    this.selectedDisableOptionsCourse.AllowPastCheckins = selectedCourse.AllowPastCheckins;
    this.loadTeeSheet(this.teeSheet);
  }

  private updateCache(date: Date, courseName: string)
  {
    let teeSheetSelectedView = new TeeSheetSelectedView();

    if (this.storageService.HasKey(teeSheetCacheName))
    {
      teeSheetSelectedView = Object.assign(new TeeSheetSelectedView(), this.storageService.Get<TeeSheetSelectedView>(teeSheetCacheName));
    }
    if (teeSheetSelectedView.ColumnType == this.columnType.Single)
    {
      teeSheetSelectedView.TeeSheetViewSettings = [{ CourseName: courseName, LastDate: date }];
    } else if (teeSheetSelectedView.ColumnType == this.columnType.Multi)
    {
      teeSheetSelectedView.TeeSheetViewSettings[this.teeSheetArrayIndex] = { CourseName: courseName, LastDate: date };
    }

    this.storageService.Set(teeSheetCacheName, teeSheetSelectedView);
  }

  private loadTeeSheet(currentTeeSheet: TeeSheet): void
  {
    this.updateCache(this.teeSheet.SelectedTeeSheetDate, this.teeSheet?.SelectedCourse?.CourseName);
    this.teeSheet.GetTeeSheet = {
      TeeTimes: [],
      TemplateTimes: [],
    } as GetTeeSheetResponse;

    const request = new GetTeeSheetRequest();
    if (currentTeeSheet.SelectedCourse.CourseID)
    {
      request.CourseID = currentTeeSheet.SelectedCourse.CourseID;
    }

    if (currentTeeSheet?.SelectedCourse?.StartTeeHole)
    {
      request.StartTeeHole = currentTeeSheet.SelectedCourse.StartTeeHole;
    }
    if (currentTeeSheet.SelectedTeeSheetDate)
    {
      request.TeeSheetDate = moment(currentTeeSheet.SelectedTeeSheetDate).format('YYYY-MM-DD');
    }

    this.teeSheet.IsLoading = true;
    this.manageLoadingService.startLoading();
    this.changeDetector.detectChanges();

    const navigationRequest: PostNavigationRequest = {
      WebItemGroupIdentifier: TeeSheetTeeTimesComponent.TEE_SHEET,
      ForbiddenAccessOnEmptyWebItems: false,
      WebItemIdentifiers: [{ Identifier: TeeSheetTeeTimesComponent.TEE_SHEET_STATISTIC }]
    };

    const teeSheet$ = this.teeSheetService.GetTeeSheet(request);
    const teeSheetPermissions$ = this.secureNavigationService.Post(navigationRequest);

    this.subscription.add(forkJoin({
      teeSheetResponse: teeSheet$,
      teeSheetPermissionsResponse: teeSheetPermissions$
    }).pipe(
      finalize(() =>
      {
        this.teeSheet.IsLoading = false;
        this.manageLoadingService.finishLoading();
        this.changeDetector.detectChanges();
      })
    ).subscribe(
      {
        next: ({ teeSheetResponse, teeSheetPermissionsResponse }) =>
        {
          this.updateStatistics(teeSheetPermissionsResponse, request).then();

          this.teeSheet.IsLoading = false;
          this.manageLoadingService.finishLoading();
          this.changeDetector.detectChanges();
          this.teeSheetActionService.setUpdateTeeSheetConfigsSubject(request.TeeSheetDate);

          if (teeSheetResponse)
          {
            this.getTeeSheetResponseHandler(teeSheetResponse, currentTeeSheet);
            this.scrollToNearestTeeTimes(currentTeeSheet.SelectedTeeSheetDate);
          }
        },
        error: () =>
        {
          this.teeSheet.IsLoading = false;
          this.manageLoadingService.finishLoading();
          this.changeDetector.detectChanges();
        },
      }));
  }

  async updateStatistics(teeSheetPermissionsResponse: PostNavigationResponse, request: GetTeeSheetRequest)
  {
    try
    {
      if (!teeSheetPermissionsResponse?.WebItemIdentifiers?.find(a => a.Identifier === TeeSheetTeeTimesComponent.TEE_SHEET_STATISTIC))
      {
        return;
      }
      const statisticsResponse = await firstValueFrom(this.teeSheetService.GetStatistics(request));
      this.statisticIndicator = statisticsResponse?.StatisticIndicator;
      this.changeDetector.detectChanges();
    } catch (err)
    {
      console.error(err);
    }
  }

  getTeeSheetResponseHandler(response: GetTeeSheetResponse, currentTeeSheet: TeeSheet): void
  {
    this.teeSheet.GetTeeSheet = response;
    this.ContactLimit = this.teeSheet.GetTeeSheet.FivePlayerTemplate ? 5 : 4;

    this.updateTeeTimesPerTeeSheet(response);

    this.updateBookingsPerTeeSheet();

    this.teeSheet.StartTeeHole = currentTeeSheet.SelectedCourse.StartTeeHole;
    this.teeSheetTransfer.SelectedCourse = currentTeeSheet.SelectedCourse;
    this.teeSheetTransfer.SelectedTeeSheetDate = currentTeeSheet.SelectedTeeSheetDate;
    this.teeSheetTransfer.CourseType = currentTeeSheet.GetTeeSheet.TeeSheetTemplate.CourseType;
    this.teeSheetTransfer.HasAttachments = currentTeeSheet.SelectedCourse.HasAttachments;
    this.changeTeeSheetTransferService(this.teeSheetTransfer);
    this.prepareTeeTimes();

    this.changeDetector.detectChanges();
    this.openBookingForSelectedTeeTime();
  }

  private scrollToNearestTeeTimes(currentTeeSheetDate: Date): void
  {
    if (!this.isToday(currentTeeSheetDate))
    {
      return;
    }

    const currentDate = Date.now();

    const teeTimes = this.teeSheet.GetTeeSheet.TeeTimes;

    const scrollToTeeTimeElements = (teeTimeToScroll: TeeTime) =>
    {
      const listOfElements = document.getElementsByClassName(this.getTeeTimeClassName(teeTimeToScroll, currentTeeSheetDate));
      Array.from(listOfElements).forEach((element: any) =>
      {
        let container = element.parentNode;

        while (!container.className.includes('o-teesheet-container'))
        {
          container = container.parentNode;
        }
        container.scrollTop = element.offsetParent.offsetTop - element.offsetHeight;
      });
    };

    const findTeeTimeToScroll = () =>
    {
      return teeTimes.find((currentTee: TeeTime, index: number) =>
      {

        if (index + 1 >= teeTimes.length)
        {
          return false;
        }

        const prevTeeTime = getTimestamp(currentTee.Time);
        const currentTeeTime = getTimestamp(teeTimes[index + 1].Time);

        return currentDate < currentTeeTime && currentDate > prevTeeTime;
      });

    };

    const getTimestamp = (time: string) => Number(moment(time, 'hh:mm:ss A').format('x'));

    const firstTeeTime = teeTimes[0];
    const lastTeeTime = teeTimes[teeTimes.length - 1];

    const firstTeeTimeTimestamp = getTimestamp(firstTeeTime.Time);
    const lastTeeTimeTimestamp = getTimestamp(lastTeeTime.Time);

    if (currentDate < firstTeeTimeTimestamp)
    {
      scrollToTeeTimeElements(firstTeeTime);
    } else if (currentDate > lastTeeTimeTimestamp)
    {
      scrollToTeeTimeElements(lastTeeTime);
    } else
    {
      const teeTimeToScroll = findTeeTimeToScroll();

      if (teeTimeToScroll)
      {
        scrollToTeeTimeElements(teeTimeToScroll);
      }
    }
  }

  getTeeTimeClassName(teeTime: TeeTime, currentTeeSheetDate: Date): string
  {
    return teeTime.Time.split(' ').join('') + moment(currentTeeSheetDate).format('YYYYMMDD');
  }

  private isToday(date: Date): boolean
  {
    return date.setHours(0, 0, 0, 0) === new Date().setHours(0, 0, 0, 0);
  }

  private openBookingForSelectedTeeTime(): void
  {
    if (!this.searchedTeeTime?.BookingDateTime)
    {
      this.searchedTeeTime = null;
      return;
    }

    if (this.teeSheet?.SelectedCourse.CourseID === this.searchedTeeTime.CourseID)
    {
      const selectedTeeTime = new Date(this.searchedTeeTime.BookingDateTime)?.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true });
      const teeTimeToScroll = this.teeTimeContainerHTMLElement?.find(p => p?.nativeElement?.id === `teeTime_${ selectedTeeTime }_${ this.teeSheet?.SelectedCourse.CourseID }`);
      teeTimeToScroll?.nativeElement?.scrollIntoView({
        behavior: 'smooth',
        block: 'center',
        inline: 'center'
      });
      setTimeout(() =>
      {
        this.teeSheetActionService.setBookingSelectedTeeTime(this.searchedTeeTime.BookingID);
        this.searchedTeeTime = null;
      }, 1000);
    }
  }

  private updateBookingsPerTeeSheet(): void
  {
    this.teeSheet?.GetTeeSheet?.TeeTimes.forEach((teeTime: TeeTime, teeTimeIndex: number, teeTimes: TeeTime[]) =>
    {
      this.ContactCount = 0;
      this.hasReserved = false;
      this.hasCrossOver = false;
      this.reserveBlockColor = '';
      this.notReservedContactCount = 0;
      teeTime.teeTimeRow = Array.from(new Array(this.ContactLimit), () => { return new TeeTimeRow; });
      if (teeTime.Bookings.length)
      {
        teeTime.Bookings.forEach(booking =>
        {
          if (booking.BookingType === this.bookingType.Blocked && !booking.BlockedTypeID)
          {
            booking.BlockedTypeID = 'BlockTypeIDEmpty';
          }
          if (booking.BlockedTypeID.toUpperCase() !== '*CROSSOVER')
          {
            this.ContactCount = booking.Contacts.length + this.ContactCount;
          }
          if (booking.BlockedTypeID.toUpperCase() !== 'RESERVED')
          {
            this.notReservedContactCount = booking.Contacts.length + this.notReservedContactCount;
          }
        });
      }
      else
      {
        teeTime.Blockings = [];
      }
      if (teeTime.Blockings.length !== 0)
      {
        teeTime.Blockings.forEach(blocking =>
        {
          if (blocking.BlockingType === this.blockingType.Crossover)
          {
            this.ContactCount = blocking.SlotsPerRow + this.ContactCount;

          }
          else if (blocking.BlockingType === this.blockingType.FlagCrossover)
          {
            this.hasCrossOver = true;
          }
          else if (blocking.BlockingType === this.blockingType.Reserved)
          {
            this.hasReserved = true;
            this.reserveBlockColor = blocking.BlockColor;
          }
        });
      }
      teeTime.Bookings?.forEach(booking =>
      {
        booking?.Contacts?.forEach((contact, index, contacts) =>
        {
          teeTime.teeTimeRow[contact.PlayerNumber - 1] = {
            ...booking, ...{ contact: contact },
            ...{ blocking: teeTime.Blockings.find(blocking => blocking.BookingID === booking.BookingID) },
            ...{ first: index === 0 },
            ...{ last: index === contacts.length - 1 },
            ...{ playerIndex: index },
            ...{ ContactCount: this.ContactCount },
            ...{ NotReservedContactCount: this.notReservedContactCount },
            ...{ HasReserved: this.hasReserved },
            ...{ HasCrossOver: this.hasCrossOver },
            ...{ ReserveBlockColor: this.reserveBlockColor },
          };
        });
      });
      this.playerIndex = 0;
      teeTime.teeTimeRow.forEach(array =>
      {
        if (!array?.Contacts.length || this.selectType === SelectType.Multi)
        {
          array.playerIndex = this.playerIndex;
          this.playerIndex = ++this.playerIndex;
        }
      });
      if (teeTime.EventBookings.length)
      {
        if (teeTimes[teeTimeIndex - 1]?.EventBookings?.[0]?.EventID !== teeTime.EventBookings[0].EventID)
        {
          teeTime.firstOfEventBookings = true;
        }
        if (teeTimes[teeTimeIndex + 1]?.EventBookings?.[0]?.EventID !== teeTime.EventBookings[0].EventID
          && this.isNotNextTeeTimeGroupBooking(teeTimes[teeTimeIndex + 1]))
        {
          teeTime.lastOfEventBookings = true;
        }
      }

      if (teeTime.SmallGroupBookings.length)
      {
        if (teeTimes[teeTimeIndex - 1]?.SmallGroupBookings?.[0]?.BookingID !== teeTime.SmallGroupBookings[0].BookingID)
        {
          teeTime.firstOfEventBookings = true;
        }
        if (teeTimes[teeTimeIndex + 1]?.SmallGroupBookings?.[0]?.BookingID !== teeTime.SmallGroupBookings[0].BookingID
          && this.isNotNextTeeTimeGroupBooking(teeTimes[teeTimeIndex + 1]))
        {
          teeTime.lastOfEventBookings = true;
        }
      }

      if (teeTime.GroupBookings.length)
      {
        if (teeTimes[teeTimeIndex - 1]?.GroupBookings?.[0]?.TeeGroupUID !== teeTime.GroupBookings[0].TeeGroupUID)
        {
          teeTime.firstOfEventBookings = true;
        }
        if (teeTimes[teeTimeIndex + 1]?.GroupBookings?.[0]?.TeeGroupUID !== teeTime.GroupBookings[0].TeeGroupUID
          && this.isNotNextTeeTimeGroupBooking(teeTimes[teeTimeIndex + 1]))
        {
          teeTime.lastOfEventBookings = true;
        }
      }

      if (teeTime.ClassRestrictions?.length)
      {
        teeTime.classRestrictionsInfo = {
          lockOpen: false,
          public: teeTime.ClassRestrictions.some(classRestriction => classRestriction.ContactClassID === ContactClassIDEnum.Public),
          web: teeTime.ClassRestrictions.some(classRestriction => classRestriction.ContactClassID === ContactClassIDEnum.Web),
          otherPermittedClasses: []
        };

        teeTime.classRestrictionsInfo.lockOpen = teeTime.classRestrictionsInfo.public && teeTime.classRestrictionsInfo.web;

        teeTime.classRestrictionsInfo.otherPermittedClasses = teeTime.ClassRestrictions.filter(
          classRestriction => classRestriction.ContactClassID !== ContactClassIDEnum.Web &&
            classRestriction.ContactClassID !== ContactClassIDEnum.Public
        );
      }
    });
  }

  isNotNextTeeTimeGroupBooking(nextTeetime: TeeTime): boolean
  {
    return (!nextTeetime?.EventBookings?.length &&
      !nextTeetime?.SmallGroupBookings?.length &&
      !nextTeetime?.GroupBookings?.length);
  }

  private updateTeeTimesPerTeeSheet(response: GetTeeSheetResponse): void
  {
    for (let i; i <= response.TemplateTimes.length; i++)
    {
      let hasValue = -1;
      for (let y; y <= this.teeSheet.GetTeeSheet.TeeTimes.length; y++)
      {
        if (response.TemplateTimes[i] === this.teeSheet.GetTeeSheet.TeeTimes[y].Time)
        {
          hasValue = y;
          break;
        }
      }
      const time = {
        Time: response.TemplateTimes[i],
        Bookings: [],
      } as TeeTime;

      if (hasValue !== -1)
      {
        time.Bookings = this.teeSheet.GetTeeSheet.TeeTimes[hasValue].Bookings;
      }

      this.teeSheet.GetTeeSheet.TeeTimes[i] = time;
    }
  }

  changeTeeSheet(): void
  {
    this.subscription.add(this.teeSheetTransferService.currentTeeSheetTransfer.subscribe((teeSheetTransfer: TeeSheetTransfer) =>
    {
      if (teeSheetTransfer.ReloadTeeSheet)
      {
        this.loadTeeSheet(this.teeSheet);
      }
    }));
  }

  handleChangeMultiTeeSheet(): void
  {
    this.subscription.add(this.teeSheetMainService.currentMultiTeeSheetTransfer.subscribe((multiTeeSheetTransfer: MultiTeeSheetTransfer) =>
    {
      if (multiTeeSheetTransfer && multiTeeSheetTransfer.ReloadTeeSheet
        && multiTeeSheetTransfer?.SelectedCourse?.CourseID === this.teeSheet?.SelectedCourse?.CourseID
      )
      {
        this.loadTeeSheet(this.teeSheet);
      }
    }));
  }

  changeTeeSheetTransferService(teeSheetTransfer: TeeSheetTransfer): void
  {
    this.teeSheetTransferService.changeTeeSheet({
      SelectedCourse: teeSheetTransfer.SelectedCourse,
      SelectedTeeSheetDate: teeSheetTransfer.SelectedTeeSheetDate,
      CourseType: teeSheetTransfer.CourseType,
      HasAttachments: teeSheetTransfer.HasAttachments
    });
  }

  selectionModelChecked(): void
  {
    this.subscriptions.add(this.smallGroupBookingHelperService.SelectTimeSlot.subscribe(SelectTimeSlot =>
    {
      if (Object.keys(SelectTimeSlot).length && (this.teeSheet.SelectedCourse.CourseID === SelectTimeSlot.Course?.CourseID && this.teeSheet.SelectedCourse.StartTeeHole === SelectTimeSlot.Course?.StartTeeHole))
      {
        if (SelectTimeSlot.Players.length == 1)
        {
          const players = [];
          if (SelectTimeSlot.Players[0] != SelectTimeSlot.playerIndex)
          {
            players.push(SelectTimeSlot.Players[0]);
          }
          else
          {
            players.push(SelectTimeSlot.playerIndex);
          }
          for (let i = 0; i < SelectTimeSlot.playerIndex; i++)
          {
            players.push(i);
          }
          players.sort();
          SelectTimeSlot.Players = players;
        }
        this.selections[SelectTimeSlot.selectedRow] = new SelectionModel<number>(true, SelectTimeSlot.Players);
        this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimes(SelectTimeSlot);
        this.changeDetector.detectChanges();
      }
    }));
  }

  selectionModelUnChecked(): void
  {
    this.subscriptions.add(this.smallGroupBookingHelperService.SmallGroupBookingRemoveTime.subscribe(removedTime =>
    {
      this.selections[removedTime.selectedRow] = new SelectionModel<number>(true, []);
    }));
  }

  prepareTeeTimeMultiSelect(): void
  {
    this.teeSheet.GetTeeSheet?.TeeTimes.forEach((teeTime: TeeTime, row: number) =>
    {
      this.selections[row] = new SelectionModel<number>(true, []);
    });
  }

  selectedCourseChanged(): void
  {
    this.subscriptions.add(this.smallGroupBookingHelperService.CourseChanged.subscribe(() =>
    {
      if (this.smallGroupBookingHelperService.SmallGroupBookingSelectedTimes.length > 0)
      {
        {
          this.smallGroupBookingHelperService.SmallGroupBookingSelectedTimes.forEach(selectTime =>
          {

            this.snackBarService.dismiss();
            this.smallGroupBookingHelperService.removeSmallGroupBookingSelectedTime(selectTime);

            this.selections[selectTime.selectedRow] = new SelectionModel<number>(true, []);
            this.storageService.Remove('SmallGroupPlayerNumbers');
            this.smallGroupBookingHelperService.setSmallGroupBookingActive(SelectType.Single);
          });
          this.changeDetector.detectChanges();
        }
      }
    }));
  }

  openTeeSheetPriceOverlay(teeTime: TeeTime, index: number): void
  {
    const request = new GetTeeTimeTotalsRequest();
    request.CourseID = this.teeSheetTransferService.get().SelectedCourse.CourseID;
    request.TeeTime = moment(this.teeSheetTransferService.get().SelectedTeeSheetDate).format('YYYY-MM-DD') + ' ' + moment(teeTime.Time, 'hh:mm:ss A').format('HH:mm:ss');
    this.teeSheet.IsLoading = true;

    if (this.teeSheet?.SelectedCourse?.StartTeeHole)
    {
      request.StartTeeHole = this.teeSheet.SelectedCourse.StartTeeHole;
    }

    this.teeSheetService.TeeTimeTotals(request).subscribe({
      next: (res: GetTeeTimeTotalsResponse) =>
      {
        this.isTeeSheetPriceOverlayOpen = {};
        this.teeSheetPriceOverlayData = res;
        this.teeSheetPriceOverlayTeeTime = teeTime;
        this.isTeeSheetPriceOverlayOpen[index] = true;
        this.teeSheet.IsLoading = false;
        this.changeDetector.detectChanges();
      },
      error: () =>
      {
        this.teeSheet.IsLoading = false;
        this.changeDetector.detectChanges();
      },
      complete: () => { }
    });
  }

  closeTeeSheetPriceOverlay(index: number): void
  {
    this.isTeeSheetPriceOverlayOpen[index] = false;
  }

  isAllSelected(row: number): boolean
  {
    const numSelected = this.selections[row].selected.length;
    const numRows = this.teeSheet.GetTeeSheet.FivePlayerTemplate ? 5 : 4;
    return numSelected === numRows;
  }

  masterToggle(row: number, teeTime: TeeTime): void
  {
    const groupBookingSelectedTeeTime = new GroupBookingSelectedTeeTime();

    if (teeTime.GroupBookings[0]?.TeeGroupUID)
    {
      groupBookingSelectedTeeTime.GroupUID = teeTime.GroupBookings[0].TeeGroupUID;
    }

    if (this.isAllSelected(row))
    {
      this.selections[row] = new SelectionModel<number>(true, []);
      groupBookingSelectedTeeTime.Time = teeTime.Time;
      groupBookingSelectedTeeTime.selectedRow = row;
      groupBookingSelectedTeeTime.Players = this.selections[row].selected;
      groupBookingSelectedTeeTime.Course = this.teeSheet.SelectedCourse;
      groupBookingSelectedTeeTime.Bookings = teeTime.Bookings;
      this.smallGroupBookingHelperService.setSmallGroupBookingSelectedTimes(groupBookingSelectedTeeTime);
    }
    else
    {
      const allSelected = this.teeSheet.GetTeeSheet.FivePlayerTemplate ? [0, 1, 2, 3, 4] : [0, 1, 2, 3];
      groupBookingSelectedTeeTime.Time = teeTime.Time;
      groupBookingSelectedTeeTime.Players = [];
      groupBookingSelectedTeeTime.Course = this.teeSheet.SelectedCourse;
      groupBookingSelectedTeeTime.playerIndex = allSelected.length - 1;
      for (let i = 0; i <= groupBookingSelectedTeeTime.playerIndex; i++)
        groupBookingSelectedTeeTime.Players.push(i);
      groupBookingSelectedTeeTime.selectedRow = row;
      groupBookingSelectedTeeTime.Bookings = teeTime.Bookings;
      this.smallGroupBookingHelperService.checkedTeeSheetSlot(groupBookingSelectedTeeTime);
    }
    if (teeTime.GroupBookings[0]?.TeeGroupUID && !this.smallGroupBookingHelperService.checkExistGroup(teeTime.GroupBookings[0].TeeGroupUID))
    {
      this.smallGroupBookingHelperService.SelectedExistingGroupsUIDs.push(teeTime.GroupBookings[0].TeeGroupUID);
    } else if (!this.smallGroupBookingHelperService.hasBookingInGroup(teeTime.GroupBookings[0]?.TeeGroupUID))
    {
      this.smallGroupBookingHelperService.SelectedExistingGroupsUIDs = this.smallGroupBookingHelperService.SelectedExistingGroupsUIDs.filter(item => item !== teeTime.GroupBookings[0]?.TeeGroupUID);
    }
    this.changeDetector.detectChanges();
  }

  openGroupBooking(): void
  {
    this.bottomSheet._openedBottomSheetRef = this.bottomSheet.open(ManageSmallGroupBookingComponent, {
      backdropClass: 'manage-tee-time-back-drop',
      panelClass: ['manage-tee-time-panel', 'manage-small-group-panel'],
      disableClose: true,
      scrollStrategy: new NoopScrollStrategy(),
      data: {}
    });
  }

  loadSplitTeeSheet(): void
  {
    if (this.teeSheet?.SelectedCourse.CourseType === CourseType.Holes1and10)
    {
      this.teeSheetMainService.changeMultiTeeSheet({
        ReloadTeeSheet: true,
        SelectedCourse: this.teeSheet?.SelectedCourse
      });
    }
  }

  onTimeClick(teeTime: TeeTime, isHoleNumber: boolean): void
  {
    if(isHoleNumber) {
      this.selectType = SelectType.AdjustTime;
      teeTime.showInputTeeTimeHoleNumber = !teeTime.showInputTeeTimeHoleNumber;
      if (teeTime.showInputTeeTimeHoleNumber)
      {
        // Set the input value to the current time when showing input
        this.setInputValueForHoleNumber(teeTime);
      }
      if (teeTime?.TeeTimeInfo?.HoleNumber)
      {
        teeTime.TeeTimeInfo.HoleNumber = "";
      }
    }
    else {
      this.selectType = SelectType.AdjustTime;
      teeTime.showInputTeeTimeText = !teeTime.showInputTeeTimeText;
      if (teeTime.showInputTeeTimeText)
      {
        // Set the input value to the current time when showing input
        this.setInputValueForDisplayTime(teeTime);
      }
      if (teeTime?.TeeTimeInfo?.DisplayTime)
      {
        teeTime.TeeTimeInfo.DisplayTime = "";
      }
    }

  }

  setInputValueForDisplayTime(teeTime)
  {
    const displayTime = teeTime?.TeeTimeInfo?.DisplayTime || teeTime?.Time;
    teeTime.inputValueDisplayTime = displayTime;
  }

  setInputValueForHoleNumber(teeTime)
  {

    const holeNumber = teeTime?.TeeTimeInfo?.HoleNumber ? teeTime?.TeeTimeInfo?.HoleNumber : '';
    teeTime.inputHoleNumberValue =  holeNumber;
  }

  onAdjustTimeChange($event, teeTime: TeeTime, isHoleNumber: boolean): void
  {
    if (isHoleNumber) {
      teeTime.inputHoleNumberValue = $event.target.value;
    }
    else {
      teeTime.inputValueDisplayTime = $event.target.value;
    }

    if ($event?.key === 'Enter')
    {
      this.adjustTime();
    }
  }

  adjustTime(): void
  {

    this.teeSheet.GetTeeSheet?.TeeTimes.forEach(teeTime =>
      {
      const change = { TeeTime: moment(this.teeSheet.SelectedTeeSheetDate).format('YYYY-MM-DD') + 'T' + moment(teeTime.Time, 'hh:mm A').format('HH:mm'), DisplayTime: teeTime?.inputValueDisplayTime ? teeTime.inputValueDisplayTime : teeTime?.TeeTimeInfo?.DisplayTime, HoleNumber: teeTime?.inputHoleNumberValue ? teeTime.inputHoleNumberValue : teeTime?.TeeTimeInfo?.HoleNumber };
      this.adjustedTeeSheetTimes.push(change);
      });

      if (!this.adjustedTeeSheetTimes?.length)
      {
       this.selectType = this.lastSelectedType;
       return;
      }



    const request: PostDisplayTimeRequest =
    {
      CourseID: this.teeSheetTransfer?.SelectedCourse?.CourseID,
      TeeSheetTimes: this.adjustedTeeSheetTimes
    };

    this.teeSheetService.adjustTime(request).subscribe({
      next: (response: PostDisplayTimeResponse) =>
      {
        this.selectType = this.lastSelectedType;
        if (response.Result)
        {
          this.loadSplitTeeSheet();
          this.loadTeeSheet(this.teeSheet);
          this.adjustedTeeSheetTimes = [];
        }
      }
    });
  }

  openManagePlayersDialog(teeTime: TeeTime): void
  {

    this.dialog.open(ManagePlayersDialogComponent, {
      panelClass: ['o-manage-players-dialog', 'o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: {
        GroupName: teeTime.GroupBookings?.[0]?.GroupName,
        CurrentTeeSheetTransfer: this.teeSheetTransferService.get(),
        GroupUID: teeTime.GroupBookings?.[0]?.TeeGroupUID,
        IsFivePlayerTemplate: this.teeSheet.GetTeeSheet.FivePlayerTemplate,
        SelectedDisableOptionsCourse: this.selectedDisableOptionsCourse,
      } as GroupBookingDetailsModalData,
      width: '95vw',
      maxWidth: '95vw',

    });
  }

  onChangeBlockedStatus(isSaved: boolean): void
  {
    if (isSaved)
    {
      this.loadTeeSheet(this.teeSheet);
    }
  }

  clickedTeeTimeClassRestriction(clickedTeeTime: TeeTime): void
  {
    this.clickedTeeTime = clickedTeeTime;
  }

  ngAfterViewInit(): void
  {
    this.getTeeTimeContainerHTMLElement();
  }

  ngAfterContentChecked()
  {
    this.changeDetector.detectChanges();
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
