import { Component, Inject, OnInit } from '@angular/core';
import { UntypedFormBuilder } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { NoteDialogData } from '../../../@intermediate/models/note-tee-time.model';

@Component({
  selector: 'tei-pos-tee-sheet--dialog',
  templateUrl: 'note-tee-time-dialog.component.html',
  styleUrls: ['note-tee-time-dialog.component.scss']
})
export class DialogNoteTeeTimeComponent implements OnInit
{
  dialogData: NoteDialogData = new NoteDialogData();
  noteTeeTimeForm = this.fb.group({
    BookingNote: '',
    OnlineNote: '',
  });
  constructor(@Inject(MAT_DIALOG_DATA) public noteDialogData, private fb: UntypedFormBuilder) { }

  ngOnInit()
  {
    this.dialogData = this.noteDialogData.dialogData;
    this.noteTeeTimeForm.controls['BookingNote'].setValue(this.dialogData.BookingNote);
    this.noteTeeTimeForm.controls['OnlineNote'].setValue(this.dialogData.OnlineNote);
  }
}
