import { ITeeSheetSelectedView, TeeSheetViewSetting } from "@tei/pos-shared";
import { Holes1or10 } from "./get-tee-sheet.model";

export enum ColumnType
{
  Single,
  Multi
}
export class ColumnViewType
{
  ColumnNumber: number;
  Disabled: boolean;
}

export class TeeSheetSelectedView implements ITeeSheetSelectedView
{
  TeeSheetViewSettings: TeeSheetViewSetting[] = [];
  ColumnType: ColumnType = ColumnType.Single;
  ColumnNumber: number = 1;
  SelectedGroupID: string;
  StartTeeHole?: Holes1or10;
  CurrentTeeSheetPage? = 1;
  ContactClass?: boolean = false;
  firstSetting(): TeeSheetViewSetting { return this.TeeSheetViewSettings?.length ? this.TeeSheetViewSettings[0] : null; }
}

export class Layers
{
  type: LayersType;
  contactClass: boolean;
}

export enum StartTeeHole
{
  Hole1,
  Hole10
}

export enum CourseType
{
  Holes18,
  Holes1and10,
  Holes9
}

export enum SelectType
{
  Single,
  Multi,
  Drag,
  AdjustTime
}

export enum LayersType
{
  CheckInStatus
}
