import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacySnackBarModule as MatSnackBarModule } from '@angular/material/legacy-snack-bar';
import { TranslateModule } from '@ngx-translate/core';
import { ManageStandAloneLoadingService } from '@tei/core';
import { OrderStoreService, SidenavService } from '@tei/pos-shared';
import { GroupTeeTimeService } from '../../../@services/group-tee-time.service';
import { ManageOrderTeeTimeService } from '../../../@services/manage-order-tee-time.service';
import { ManageTeeTimeHelperService } from '../../../@services/manage-tee-time.helper.service';
import { ManageTeeTimeService } from '../../../@services/manage-tee-time.service';
import { ConfirmTeeTimeBookingDialogModule } from '../../manage-tee-time/confirm-tee-time-booking-dialog/confirm-tee-time-booking-dialog.module';
import { ManageTeeTimePlayersModule } from '../../manage-tee-time/manage-tee-time-players/manage-tee-time-players.module';
import { GroupPlayersComponent } from './group-players.component';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatChipsModule } from '@angular/material/chips';

const MODULES = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
  MatDialogModule,
  MatSnackBarModule,
  TranslateModule
];
const COMPONENTS = [GroupPlayersComponent];
const POS_TEE_SHEET_MODULES = [
  ManageTeeTimePlayersModule,
  ConfirmTeeTimeBookingDialogModule
];
const MATERIAL_MODULES = [
  MatSelectModule,
  MatButtonModule,
  MatIconModule,
  MatButtonToggleModule,
  MatChipsModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MODULES,
    ...POS_TEE_SHEET_MODULES,
    ...MATERIAL_MODULES
  ],
  exports: [...COMPONENTS],
  providers: [
    { provide: 'IManageTeeTimeService', useClass: ManageTeeTimeService },
    { provide: 'IOrderStoreService', useClass: OrderStoreService },
    { provide: 'IManageOrderTeeTimeService', useClass: ManageOrderTeeTimeService },
    { provide: 'IGroupTeeTimeService', useClass: GroupTeeTimeService },
    ManageTeeTimeHelperService,
    ManageStandAloneLoadingService,
    SidenavService
  ]
})
export class GroupPlayersModule { }
