import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatStepperModule } from '@angular/material/stepper';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule } from '@tei/core-translate';
import { AvatarModule, FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ECommerceTransactionsLookupModule } from '@tei/retail-admin';
import { PrepaidECommerceTransactionsComponent } from './prepaid-ecommerce-transaction.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const COMPONENT = [PrepaidECommerceTransactionsComponent];
const MATERIAL_MODULES = [MatTableModule, MatIconModule, MatDividerModule, MatCardModule, MatRippleModule, MatRadioModule, MatStepperModule, MatCheckboxModule, MatRippleModule, MatButtonToggleModule];
const TEI_CORE_MODULES = [TeiDateTranslateModule, TeiCurrencyTranslateModule];
const TEI_CONTACT_MODULES = [ContactAvatarModule, AvatarModule];
const TEI_MATERIAL_MODULES = [FilterChipsModule, LoadingModule, QuickSearchFilterChipModule, ContactQuickSearchModule, LoadingModule];
const TEI_RETAIL_ADMIN_MODULES = [ECommerceTransactionsLookupModule];
@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CONTACT_MODULES,
    ...TEI_RETAIL_ADMIN_MODULES
  ],
  exports: [
    ...COMPONENT,
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CONTACT_MODULES,
    ...TEI_RETAIL_ADMIN_MODULES
  ],
  providers: [
    ]
})
export class PrepaidECommerceTransactionsModule { }
