import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { GetTeeSheetBlockPriceLevelResponse, GetTeeSheetBlockTypeResponse, PostTeeTimeBlockRequest, PostTeeTimeBlockResponse } from "../@intermediate/models/tee-sheet-block.model";
import { ITeeSheetBlockService } from "../@intermediate/tee-sheet-block-service.interface";

@Injectable({
  providedIn: 'root'
})

export class TeeSheetBlockService implements ITeeSheetBlockService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet';
  }

  PriceLevels(): Observable<GetTeeSheetBlockPriceLevelResponse>
  {
    const apiUrl = this.serviceUrl + '/TeeSheet/PriceLevels';
    return this.http.get<GetTeeSheetBlockPriceLevelResponse>(apiUrl);
  }

  BlockTypes(): Observable<GetTeeSheetBlockTypeResponse>
  {
    const apiUrl = this.serviceUrl + '/TeeSheet/BlockingTypes';
    return this.http.get<GetTeeSheetBlockTypeResponse>(apiUrl);
  }

  PostTeeTimeBlocks(request: PostTeeTimeBlockRequest): Observable<PostTeeTimeBlockResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageTeeTime/TeeTimeBlocks';
    return this.http.post<PostTeeTimeBlockResponse>(apiUrl, request);
  }

}
