import { Component, Inject } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { WaitListPlayersDialogData } from '../../../@intermediate/models/post-tee-sheet-wait-list.model';

@Component({
  selector: 'tei-pos-tee-sheet-players-wait-list',
  templateUrl: './players-wait-list.component.html',
  styleUrls: ['./players-wait-list.component.scss']
})
export class PlayersWaitListComponent
{

  constructor(private dialogRef: MatDialogRef<PlayersWaitListComponent>,
    @Inject(MAT_LEGACY_DIALOG_DATA) public data: WaitListPlayersDialogData) { }


  onClose(): void
  {
    this.dialogRef.close();
  }
}
