import { FormControl } from "@angular/forms";
import { GetClassesAndGroupsResponse } from "./get-tee-sheet-classes-groups.model";

export class PostTeeSheetClassRestrictionsRequest
{
    CourseID: string;
    SheetDate: string;
    ClassAndGroupUIDs: Array<string>;
}
export class PostTeeSheetClassRestrictionsResponse
{
    CourseID: string;
    SheetDate: Date;
    SheetTimes: Array<ClassRestrictionsSheetTime>;
    Groups: Array<GroupRestrictions>;
    Template: Template;
    CourseTypes: Array<CourseType>;
    // Use in FE
    SelectedCourseName: string;
    SelectedCourseID: string;
    SelectedTeeSheetDate: Date;
    IgnoreClassRestrictions: boolean;
}

export class Template
{
    CourseType: number;
    EndTime: string;
    Intervals: number;
    StartTime: string;
    TemplateID: string;
    IgnoreClassRestrictions: boolean;
}


export class CourseType
{
    ID: number;
    Title: string;
}

export class ClassRestrictionsSheetTime
{
    SheetTime: string;
    Groups: Array<ClassRestrictionsGroup>;
    //Use in FE
    Index: number;
    SheetTimeTitle: string;
}

export class ClassRestrictionsGroup
{
    ClassUID: string;
    Value: boolean;
}


export class GroupRestrictions
{
    ClassUID: string;
    ClassID: string;
    TeeSheetColoure: string;
    RestrictionType: number;
    Description: string;
    // Use in FE
    Show: boolean;
}


export class TeeSheetRestrictionsDialogData
{
    Restrictions: PostTeeSheetClassRestrictionsResponse;
    FilterView: GetClassesAndGroupsResponse;
}


export class TeeSheetRestrictionsForm
{
    TemplateID: FormControl<string>;
    CourseType: FormControl<number>;
    StartTime: FormControl<string>;
    Intervals: FormControl<number>;
    EndTime: FormControl<string>;
    IgnoreClass: FormControl<boolean>;
    Apply: FormControl<boolean>;
    Restrictions: FormControl<string[]>;
    TimeRangeStartIndex: FormControl<number>;
    TimeRangeEndIndex: FormControl<number>;
}

export class TeeSheetRestrictionsApply
{
    TemplateID: string;
    CourseType: number;
    StartTime: string;
    Intervals: number;
    EndTime: string;
    IgnoreClass: boolean;
    Apply: boolean;
    Restrictions: string[];
    TimeRangeStartIndex: number;
    TimeRangeEndIndex: number;
}