import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyProgressSpinnerModule as MatProgressSpinnerModule } from '@angular/material/legacy-progress-spinner';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatTreeModule } from '@angular/material/tree';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { TeeSheetFilterViewComponent } from './tee-sheet-filter-view.component';





const MATERIAL_MODULES = [MatDividerModule, MatCheckboxModule, MatInputModule, MatIconModule, MatFormFieldModule, MatSelectModule, MatTreeModule, MatChipsModule, MatMenuModule, MatProgressSpinnerModule];
const BASE_MODULES = [CommonModule, ReactiveFormsModule, FormsModule];
const TEI_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [LoadingModule];
const COMPONENTS = [TeeSheetFilterViewComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...COMPONENTS,
  ]
})

export class TeeSheetFilterViewModule { }
