import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material/bottom-sheet';
import { StorageService } from '@tei/core';
import { Subscription } from 'rxjs';
import { SelectType } from '../../../@intermediate/models/tee-sheet-toolbar-model';
import { SmallGroupBookingHelperService } from '../../../@services/small-group-booking-helper.service';
import { GroupBookingSelectedTeeTime } from '../../../@intermediate/models/get-tee-sheet.model';
import { ManageSmallGroupDataModel } from '../../../@intermediate/models/manage-tee-time.model';
import { PutSmallGroupTeeTimesRequest } from '../../../@intermediate/models/put-small-group-tee-time.model';
import { ISmallGroupBookingService } from '../../../@intermediate/small-group-booking.interface';

@Component({
  selector: 'tei-pos-tee-sheet-manage-small-group-booking',
  templateUrl: './manage-small-group-booking.component.html',
  styleUrls: ['./manage-small-group-booking.component.scss']
})
export class ManageSmallGroupBookingComponent implements OnInit, OnDestroy
{
  SmallGroupBookingSubscription = new Subscription();
  defaultPriceLevel = [
    {
      value: 1,
      title: '20 Dollar Off',

    },
    {
      value: 2,
      title: '50 Dollar Off',

    },
    {
      value: 1,
      title: 'Junior Rates',

    },
  ];

  defaultTransportation = [
    {
      value: 1,
      title: 'Power Cart',
    },
    {
      value: 2,
      title: 'Pull Cart',
    },
    {
      value: 1,
      title: 'Walk',
    },
  ];

  form: UntypedFormGroup;
  selectedTimesCount = 0;
  smallGroupBookingSelected: GroupBookingSelectedTeeTime[] = [];
  get formControls()
  {
    return this.form?.controls;
  }

  constructor(private fb: UntypedFormBuilder,
    @Inject('ISmallGroupBookingService') private smallGroupBookingService: ISmallGroupBookingService,
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: ManageSmallGroupDataModel,
    private bottomSheetRef: MatBottomSheetRef<ManageSmallGroupBookingComponent>,
    private storageService: StorageService,
    private smallGroupBookingHelperService: SmallGroupBookingHelperService)
  {

  }

  ngOnInit(): void
  {
    this.createForm();
    this.handleSelectedTimes();
    this.setSmallGroupPlayerNumbers();
    this.getSmallGroupPlayerNumbers();

  }

  private handleSelectedTimes(): void
  {
    this.SmallGroupBookingSubscription = this.smallGroupBookingHelperService.SmallGroupBookingSelectedTimesList.subscribe(response =>
    {
      this.smallGroupBookingSelected = response;
      this.calculateNumberPlayers(response);
    });
  }

  private createForm(): void
  {
    this.form = this.fb.group({
      GroupName: ['', Validators.required],
      Booker: ['', Validators.required],
      Players: [null, Validators.required],
      Holes: null,
      DefaultPriceLevel: null,
      DefaultTransportation: null,
      Shotgun: false,
      StartTime: ''
    });
  }

  getSmallGroupPlayerNumbers(): void
  {
    this.form.get("Players").valueChanges.subscribe(() =>
    {
      if (this.form?.controls.Players.value)
        this.storageService.Set('SmallGroupPlayerNumbers', this.form?.controls.Players.value.toString());
    });
  }

  getPlayerNumbers(groupBookingSelectedTeeTime: GroupBookingSelectedTeeTime): number
  {
    return groupBookingSelectedTeeTime.Players?.length;
  }

  calculateNumberPlayers(selectedTime: GroupBookingSelectedTeeTime[]): void
  {
    this.selectedTimesCount = 0;
    selectedTime.forEach((item: GroupBookingSelectedTeeTime) =>
    {
      this.selectedTimesCount += this.getPlayerNumbers(item);
    });
  }

  setSmallGroupPlayerNumbers(): void
  {
    const smallGroupPlayerNumbers = this.storageService.Get('SmallGroupPlayerNumbers');
    if (smallGroupPlayerNumbers)
    {
      this.form?.controls.Players.setValue(smallGroupPlayerNumbers);
    }
  }

  onlyNumber(event): boolean
  {
    return (event.charCode == 8 || event.charCode == 0) ? null : event.charCode >= 48 && event.charCode <= 57;
  }

  removeSelectedTime(removedSelectedTeeTime): void
  {
    this.smallGroupBookingHelperService.removeSmallGroupBookingSelectedTime(removedSelectedTeeTime);
    this.calculateNumberPlayers(this.smallGroupBookingSelected);
  }

  selectTime(): void
  {
    this.smallGroupBookingHelperService.setSmallGroupBookingActive(SelectType.Multi);
    this.SmallGroupBookingSubscription?.unsubscribe();
    this.bottomSheetRef.dismiss({
      selectTime: true,
      data: {
        selectedTimesCount: this.selectedTimesCount,
        numberOfPlayers: this.formControls.Players.value ? Number(this.formControls.Players.value) : 0
      }
    });
  }

  closeBottomSheet(): void
  {
    this.storageService.Remove('SmallGroupPlayerNumbers');
    this.SmallGroupBookingSubscription?.unsubscribe();
    this.bottomSheetRef.dismiss({
      selectTime: false,
      data: null,
    });
  }

  onContactItemClickEvent(event): void
  {
    this.formControls.Booker.setValue(event);
  }

  removeContactItemClickEvent(): void
  {
    this.formControls.Booker.setValue('');
  }

  saveChanges(): void
  {
    const request: PutSmallGroupTeeTimesRequest = {
      BookingID: this.data.SmallGroupBookingId,
      ContactID: this.form.get("Booker")?.value?.ContactID,
      DefaultCartType: this.form.get("DefaultTransportation")?.value,
      DefaultHoles: this.form.get("Holes")?.value,
      DefaultPriceLevel: this.form.get("DefaultPriceLevel")?.value,
      DisplayName: this.form.get("GroupName")?.value,
      SGNote: null,
      Shotgun: 0,
      Players: this.form.get("Players")?.value,
      ShotgunStartTime: null,
      SmallGroupPlayer: [],
      UDSourceUID: null,
      UpdatePlayerPriceLevel: true
    };

    this.smallGroupBookingService.PutSmallGroupBooking(request).subscribe();
  }

  ngOnDestroy(): void
  {
    this.SmallGroupBookingSubscription?.unsubscribe();
  }
}
