import { FormControl } from "@angular/forms";

export class GetBookingCheckInDetailsRequest
{
  BookingID: string;
}

export class GetBookingCheckInDetailsResponse
{
  BookingCheckinDetails: PlayerCheckInDetail[];
}

export class PlayerCheckInDetail
{
  PlayerNumber: number;
  TeeTimeID: string;
  ContactID: string;
  FirstName: string;
  LastName: string;
  TeeTimeDisplayName: string;
  DisplayName: string;
  MemberID: string;
  ProfilePicture: string;
  TransactionID: number;
  TransactionUID: string;
  POSID: string;
  LinkGUID: string;
  ItemTotal: number;
  LineUIDs: string[];
}

export class CheckInDialgDetailsModel
{
  action: CheckInDialgDetailsAction;
  selectedPlayer: PlayerCheckInDetail;
}

export enum CheckInDialgDetailsAction
{
  Return,
  Print
}

export interface ReturnCheckInForm
{
  SelectedPlayer: FormControl<PlayerCheckInDetail>;
}
