import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, Inject, OnDestroy, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { FormArray, UntypedFormGroup } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService, ResponsiveService } from '@tei/core';
import { ChipItemModel, SnackBarService } from '@tei/material';
import { IOrderStoreService, SidenavService } from '@tei/pos-shared';
import moment from 'moment';
import { Subscription, firstValueFrom, take } from 'rxjs';
import { IGroupBookingDetailsService } from '../../@intermediate/get-booking-details.interface';
import { IGroupTeeTimeService } from '../../@intermediate/group-tee-time-service.interface';
import { ConfirmRestrictionsDialog } from '../../@intermediate/models/confirm-restrictions-dialog.model';
import { ManageTeeTimePlayerBookingInputBase } from '../../@intermediate/models/get-booking-details.model';
import { PlayerStatus, PriceLevel, TeeTimeConfig, TeeTimeConfigFilterPlayer } from '../../@intermediate/models/get-tee-sheet.model';
import { GetGroupBookingDetailsRequest, GetGroupBookingDetailsResponse, GroupBookingDetailsModalData, GroupDefaultValue } from '../../@intermediate/models/group-booking-details.model';
import { ConfirmTeeTimeBookingDialogDataModel, ConfirmTeeTimeBookingForm, ManageTeeTimeConfirmMode, ManageTeeTimeScreenSizeEnum } from '../../@intermediate/models/manage-tee-time.model';
import { Contact, Player, PlayerModel, PostBookingDetailsRequest, PostBookingDetailsRequestForForm } from '../../@intermediate/models/post-booking-details.model';
import { BookingPlayer } from '../../@intermediate/models/post-booking-players-import.model';
import { PostFirstPlayersEmailsByGroupUIDRequest, PostFirstPlayersEmailsByGroupUIDResponse } from '../../@intermediate/models/post-first-players-emails-group-uid.model';
import { PostBulkTeeTimeToOrderRequest } from '../../@intermediate/models/post-manage-order-tee-time.model';
import { MoveSwapBookingDetail, MoveSwapDialogData } from '../../@intermediate/models/post-move-swap-group-players.model';
import { GroupBookingService } from '../../@services/group-booking.service';
import { HeaderService } from '../../@services/header.service';
import { ManageOrderTeeTimeService } from '../../@services/manage-order-tee-time.service';
import { ManageTeeTimeHelperService } from '../../@services/manage-tee-time.helper.service';
import { TeeSheetMainService } from '../../@services/tee-sheet-main.service';
import { ConfirmTeeTimeBookingDialogComponent } from '../manage-tee-time/confirm-tee-time-booking-dialog/confirm-tee-time-booking-dialog.component';
import { GroupPlayersComponent } from './group-players/group-players.component';
import { MoveSwapPlayersComponent } from './move-swap-players/move-swap-players.component';

@Component({
  selector: 'tei-pos-tee-sheet-manage-players-dialog',
  templateUrl: './manage-players-dialog.component.html',
  styleUrls: ['./manage-players-dialog.component.scss']
})
export class ManagePlayersDialogComponent implements OnInit, OnDestroy, AfterViewInit
{
  @ViewChild('container', { static: true }) container!: ElementRef<HTMLDivElement>;
  @ViewChildren('row', { read: ElementRef }) rows!: QueryList<ElementRef<HTMLElement>>;

  @ViewChildren(GroupPlayersComponent) groupPlayersComponents: QueryList<GroupPlayersComponent>;

  subscription = new Subscription();

  config = {
    ShowMoveButton: true,
    ShowDisplayContactIcon: true,
    ShowNotificationIcon: false,
    CallPlayerPriceApi: false,
    GroupDefaultValues: null,
    FilterPlayer: {
      HideCheckInPlayers: false,
      ContactID: ''
    } as TeeTimeConfigFilterPlayer
  } as TeeTimeConfig;

  GroupName = '';
  GroupBookingDetails: GetGroupBookingDetailsResponse;
  teeTimeBookings: ManageTeeTimePlayerBookingInputBase[] = [];

  panelOpenState = false;
  screenSizeEnum = ManageTeeTimeScreenSizeEnum;
  selectedTeeTimeIndex = 0;
  hasUnsavedChanges = false;
  PlayerStatus = PlayerStatus;
  globalAllStatus: PlayerStatus = null;
  // Price levels
  priceLevels: PriceLevel[] = [];
  screenSize: number;
  minTermLength = 3;
  DisplayName: ChipItemModel[] = [
    {
      chipName: 'ContactName',
      displayName: this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.CONTACT'),
      bindValue: 'ContactID'
    } as ChipItemModel,
  ];

  selectedContact: Contact;
  moveSwapDialogData = new MoveSwapDialogData();
  showEmptyMessage = false;
  hasCheckIn = false;

  constructor(private manageTeeTimeHelper: ManageTeeTimeHelperService,
    private teeSheetMainService: TeeSheetMainService,
    @Inject('IOrderStoreService') private orderStoreService: IOrderStoreService,
    private sidenavService: SidenavService,
    @Inject('IManageOrderTeeTimeService') private manageOrderTeeTimeService: ManageOrderTeeTimeService,
    @Inject('IGroupTeeTimeService') private groupTeeTimeService: IGroupTeeTimeService,
    private translate: TranslateService,
    private headerService: HeaderService,
    private snackBarService: SnackBarService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    @Inject(MAT_DIALOG_DATA) public data: GroupBookingDetailsModalData,
    private dialog: MatDialog,
    private dialogRef: MatDialogRef<ManagePlayersDialogComponent>,
    private responsiveService: ResponsiveService,
    private changeDetectorRef: ChangeDetectorRef,
    @Inject('IGroupBookingDetailsService') private groupBookingDetailsService: IGroupBookingDetailsService,
    private cdr: ChangeDetectorRef,
    public groupBookingService: GroupBookingService,
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.detectMobileScreen();
    this.GroupName = this.data?.GroupName;

    await this.getGroupBookingDetails();

    this.onAllStatusChanged(null);

    this.onMoveSwapPlayer();
  }

  onMoveSwapPlayer(): void
  {
    this.subscription.add(this.groupBookingService.moveSwapPlayer$.subscribe(player =>
    {
      this.openMoveSwapPlayerDialog(player);
    }));
  }

  openMoveSwapPlayerDialog(player): void
  {
    this.moveSwapDialogData.SelectedPlayer = player;
    const bookingDetails: MoveSwapBookingDetail[] = [];
    this.groupPlayersComponents.forEach((component, index) =>
    {
      const playerFormData = component.playersForm.getRawValue();
      if (playerFormData.playersList.player.find(playerForm => playerForm.TeeTimeID === player.TeeTimeID))
      {
        this.moveSwapDialogData.SelectedBooking = playerFormData;
      }
      bookingDetails.push({
        DisplayTime: playerFormData.DisplayTime,
        BookingID: playerFormData.BookingID,
        Players: playerFormData.playersList.player,
        HoleNumber: playerFormData.HoleNumber
      });
    });

    this.moveSwapDialogData.BookingDetails = bookingDetails;

    const dialog = this.dialog.open(MoveSwapPlayersComponent, {
      panelClass: ['a-teesheet-move-swap-container', 'o-dialog-md-fullscreen', 'o-dialog-sm-fullscreen'],
      width: '400px',
      data: this.moveSwapDialogData
    });

    this.subscription.add(dialog.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.getGroupBookingDetails();
      }
    }));
  }

  private detectMobileScreen(): void
  {
    this.subscription.add(this.responsiveService.screenWidth.subscribe(screenSize =>
    {
      this.screenSize = screenSize;
      this.changeDetectorRef.detectChanges();
    }));
  }

  ngAfterViewInit(): void
  {
    this.onAllStatusChanged(null);
  }

  async getGroupBookingDetails(): Promise<void>
  {
    this.teeTimeBookings = [];
    try
    {
      this.manageStandAloneLoadingService.startLoading();

      this.GroupBookingDetails = await firstValueFrom(
        this.groupBookingDetailsService.GetGroupBookingDetails({
          CourseID: this.data.CurrentTeeSheetTransfer.SelectedCourse.CourseID,
          GroupUID: this.data.GroupUID,
          IsFivePlayerTemplate: this.data.IsFivePlayerTemplate
        } as GetGroupBookingDetailsRequest)
      );


      this.config.GroupDefaultValues = {
        CartType: this.GroupBookingDetails?.GroupDefaultValues?.CartType,
        NineHoles: this.GroupBookingDetails?.GroupDefaultValues?.NineHoles,
        PriceLevelID: this.GroupBookingDetails?.GroupDefaultValues?.PriceLevelID,
        PriceLevelDescription: this.GroupBookingDetails?.GroupDefaultValues?.PriceLevelDescription,
      } as GroupDefaultValue;

      this.config = { ...this.config };

      this.priceLevels = this.GroupBookingDetails?.CoursePriceLevels;
      if (this.config.GroupDefaultValues.PriceLevelID && this.priceLevels.findIndex(a => a.PriceLevelID === this.config.GroupDefaultValues.PriceLevelID) === -1)
      {
        this.priceLevels.push({
          Description: this.config.GroupDefaultValues.PriceLevelDescription, PriceLevelID: this.config.GroupDefaultValues.PriceLevelID,
          Expire: null, ExpiryDate: null, IsPublic: false
        });
      }

      this.priceLevels = [...this.priceLevels];

      this.groupBookingService.groupBookingDetails = this.GroupBookingDetails;

      for (let item of this.GroupBookingDetails.BookingDetails)
      {
        item.IsNewBooking = (!(item?.Players?.some(a =>
          a.Status !== PlayerStatus.None
          && a.Status !== PlayerStatus.NoShow
          && a.Status !== PlayerStatus.CheckInInprogress) ?? true));

        this.teeTimeBookings.push({
          bookingDetails: item,
          isFivePlayer: this.data.IsFivePlayerTemplate,
        } as ManageTeeTimePlayerBookingInputBase);
      }


    } catch (error)
    {
      console.error('Error getting group booking details:', error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  updateChildrenAllStatus()
  {
    this.groupPlayersComponents.forEach((component) =>
    {
      component.updateAllStatusState();
      const a = component.hidden;
    });

    this.onAllStatusChanged();
  }

  // ===== GLOBAL ACTION METHODS =====
  onAllStatusChanged(value?: PlayerStatus): void
  {
    if (!this.groupPlayersComponents || this.groupPlayersComponents.length === 0)
    {
      this.globalAllStatus = PlayerStatus.None;
      return;
    }

    // Get all the allStatus values from each group component
    const allStatusValues = this.groupPlayersComponents.map(component =>
      component.playersForm?.get('allStatus')?.value
    ).filter(status => status != undefined && status != null);
    if (allStatusValues.length === 0)
    {
      this.globalAllStatus = PlayerStatus.None;
      return;
    }
    const firstStatus = allStatusValues[0];
    const allSameStatus = allStatusValues.every(status => status === firstStatus);
    if (allSameStatus)
    {
      this.globalAllStatus = firstStatus;
    } else
    {
      this.globalAllStatus = PlayerStatus.None;
    }
    // Force change detection to update the UI
    this.cdr.detectChanges();
  }
  /**
   * Apply No Show status to all bookings
   */
  applyNoShowToAll(): void
  {
    if (!this.groupPlayersComponents || this.groupPlayersComponents.length === 0)
    {
      return;
    }

    const newState = this.globalAllStatus === PlayerStatus.NoShow ? PlayerStatus.None : PlayerStatus.NoShow;

    this.groupPlayersComponents.forEach((component, index) =>
    {
      const currentStatus = component.playersForm?.get('allStatus')?.value;

      if (currentStatus !== newState &&
        !(component.pastDayStatus.IsCheckedInAndPaidAll || component.pastDayStatus.DisableCheckInBooking || component.pastDayStatus.DisableCheckInPrePaid))
      {
        component.noShowAll();
      }
    });

    // Update global state
    this.globalAllStatus = newState;
    this.cdr.detectChanges();
  }

  /**
   * Apply Check-In status to all bookings
   */
  applyCheckInToAll(): void
  {
    if (!this.groupPlayersComponents || this.groupPlayersComponents.length === 0)
    {
      return;
    }
    // Determine the new state based on current global state
    const newState = this.globalAllStatus === PlayerStatus.CheckIn ? PlayerStatus.None : PlayerStatus.CheckIn;
    // Apply to all child components
    this.groupPlayersComponents.forEach((component, index) =>
    {
      const currentStatus = component.playersForm?.get('allStatus')?.value;

      if (currentStatus !== newState &&
        !(component.pastDayStatus.IsCheckedInAndPaidAll || component.pastDayStatus.DisableCheckInBooking || component.pastDayStatus.DisableCheckInPrePaid))
      {
        component.checkInAll();
      }
    });

    // Update global state
    this.globalAllStatus = newState;
    //this.cdr.detectChanges();
  }

  /**
   * Confirm and save all changes
   */
  async confirm(noValidate = false): Promise<void>
  {
    this.hasCheckIn = false;

    const request = new PostBulkTeeTimeToOrderRequest();
    request.BookingDetails = [];
    let playerIDs: PlayerModel[] = [];
    let dialogData: ConfirmRestrictionsDialog;
    const validate = this.groupPlayersComponents.toArray().every((playerControl, index) =>
    {
      playerControl.playersFormSubmitted = false;
      if (!noValidate && !dialogData && playerControl.isAnyPlayerCheckedIn)
      {
        const differenceOfBookingDateWithTodayInDays = moment(playerControl.Booking.bookingDetails.BookingDateTime).startOf('day')
          .diff(moment().startOf('day'), 'days');
        if (differenceOfBookingDateWithTodayInDays !== 0)
        {
          dialogData = {
            title: differenceOfBookingDateWithTodayInDays > 0 ?
              this.translate.instant('POS_TEE_SHEET.RESTRICTIONS.CHECK_IN.FUTURE_TITLE') :
              this.translate.instant('POS_TEE_SHEET.RESTRICTIONS.CHECK_IN.PAST_TITLE'),
            content: differenceOfBookingDateWithTodayInDays > 0 ?
              this.translate.instant('POS_TEE_SHEET.RESTRICTIONS.CHECK_IN.FUTURE_CONTENT') :
              this.translate.instant('POS_TEE_SHEET.RESTRICTIONS.CHECK_IN.PAST_CONTENT'),
            question: this.translate.instant('POS_TEE_SHEET.RESTRICTIONS.CHECK_IN.QUESTION'),
            submitButtonLabel: this.translate.instant('POS_TEE_SHEET.RESTRICTIONS.CHECK_IN.PROCEED'),
          };
        }
      }

      const isValid = noValidate || this.validatePlayerForm(playerControl.playersForm, index);
      if (isValid)
      {
        const detailsRequest = this.CreateBookingDetailRequest(playerControl);
        if (!detailsRequest)
        {
          const el = this.rows?.toArray()[index]?.nativeElement;
          el?.scrollIntoView({ behavior: 'smooth', block: 'start' });
          playerControl.teeTimePanel?.open();
          return false;
        }
        request.BookingDetails.push(detailsRequest);
      } else
      {
        playerControl.playersFormSubmitted = true;
        playerControl.playersForm.updateValueAndValidity();
        playerControl.teeTimePanel?.open();
        if (this.container?.nativeElement)
        {
          this.container.nativeElement.scrollTop = 0;
        }
        this.changeDetectorRef.detectChanges();

      }

      return isValid; // Stops when false is returned
    });

    if (!validate) { return; }

    this.groupPlayersComponents.forEach((playerControl, index) =>
    {
      if (!noValidate && !this.hasCheckIn)
      {
        this.fillPlayerIDsFromPlayerControl(playerControl, playerIDs);
      }
    });

    if (dialogData)
    {
      const result = await firstValueFrom(this.manageTeeTimeHelper.openRestrictionsDialog(dialogData));

      if (!result)
      {
        return;
      }
    }

    if (playerIDs?.length && !noValidate)
    {
      await this.ConfirmTeeTimeBookingDialog(request, playerIDs, noValidate);
    }
    else
    {
      await this.saveBookings(request, null, null, noValidate);
    }
  }

  private fillPlayerIDsFromPlayerControl(playerControl: GroupPlayersComponent, playerIDs: PlayerModel[])
  {
    const players = playerControl?.playersForm?.getRawValue()?.playersList?.player;
    let playerAdded = false;
    let player1Added = false;
    for (let playerIndex = 0; playerIndex < players.length; playerIndex++)
    {
      const player = players[playerIndex];
      const bookingPlayer = playerControl?.Booking?.bookingDetails?.Players?.[playerIndex];

      if (player?.Contact?.ContactID
        && player?.Contact?.ContactID !== '00000000-0000-0000-0000-000000000000'
        && player?.Contact?.ContactID !== '88888888-8888-8888-8888-888888888888'
      )
      {
        if (bookingPlayer?.Status !== PlayerStatus.CheckIn
          && bookingPlayer?.Status !== PlayerStatus.CheckInInprogress
          && bookingPlayer?.Status !== PlayerStatus.Paid
          // && (bookingPlayer?.Cart !== player.Cart
          //   || bookingPlayer?.Holes !== player.Holes
          //   || bookingPlayer?.PriceLevelID !== player.PriceLevelID
          //   || bookingPlayer?.Contact?.ContactID !== player?.Contact?.ContactID
          // )
        )
        {
          playerIDs.push({ ContactID: player.Contact.ContactID, IsBooker: playerIndex === 0, BookingID: playerControl?.Booking?.bookingDetails?.BookingID });
          playerAdded = true;
          if (playerIndex === 0)
          {
            player1Added = true;
          }
        }
      }
    }

    const player1 = players[0]?.Contact;
    if (playerAdded && !player1Added && player1?.ContactID)
    {
      playerIDs.push({ ContactID: player1.ContactID, IsBooker: true, BookingID: playerControl?.Booking?.bookingDetails?.BookingID });
    }
  }

  async getPlayersEmailsByGroupUID(playerIDs: string[]): Promise<PostFirstPlayersEmailsByGroupUIDResponse>
  {
    const request: PostFirstPlayersEmailsByGroupUIDRequest = {
      GroupUID: this.data.GroupUID,
      PlayerUIDs: playerIDs
    };

    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const response: PostFirstPlayersEmailsByGroupUIDResponse = await firstValueFrom(this.groupTeeTimeService.FirstPlayersEmailsByGroupUID(request));
      return response;
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  async ConfirmTeeTimeBookingDialog(request: PostBulkTeeTimeToOrderRequest, playerIDs: PlayerModel[], noValidate: boolean): Promise<void>
  {
    const user = await firstValueFrom(this.headerService.getUser({}));
    const bookings = await this.getPlayersEmailsByGroupUID(playerIDs.map(a => a.ContactID));

    const bookerEmails = new Set<string>();
    const playerEmails = new Set<string>();

    bookings.PlayerEmails.forEach(item =>
    {
      item.PlayerEmails.forEach(e =>
      {
        const player = playerIDs.filter(a => a.ContactID === e.ContactID);
        player?.forEach(c =>
        {
          c.Email = e.Email;
          if (c.IsBooker)
          {
            bookerEmails.add(e.Email);
          }
          playerEmails.add(e.Email);
        });
      });
    });

    const booking = this.GroupBookingDetails.BookingDetails[0];

    const confirmDialogData: ConfirmTeeTimeBookingDialogDataModel = {
      ConfirmMode: ManageTeeTimeConfirmMode.Save,
      HeaderTitle: this.translate.instant('POS_TEE_SHEET.CONFIRMATION_DIALOG.CONFIRMATION'),
      ButtonText: this.translate.instant('POS_TEE_SHEET.CONFIRMATION_DIALOG.SAVE_AND_CONFIRM'),
      SubmitButtonColor: 'accent',
      BookingID: null,
      SelectedCourseName: this.data.CurrentTeeSheetTransfer.SelectedCourse.CourseName,
      BookingDate: booking.BookingDateTime,
      StartTee: booking.StartTee,
      Players: [],
      PlayerEmails: Array.from(playerEmails),
      BookerEmails: Array.from(bookerEmails),
      UserName: user.User.DisplayName,
      AvatarCharacter: user.User.DisplayName[0],
      IsMultiBookings: true,
      BookingDates: this.GroupBookingDetails.BookingDetails.map(b => b.DisplayTime),
      BookingIDs: this.GroupBookingDetails.BookingDetails.map(b => b.BookingID),
      TotalPrice: bookings.SumTotalPrice,
      NumberOfPlayers: bookings.PlayersCount
    };


    const dialogRef = this.dialog.open(ConfirmTeeTimeBookingDialogComponent, {
      panelClass: ['confirm-tee-time-booking-dialog', 'o-dialog-sm-fullscreen'],
      data: {
        dialogData: confirmDialogData
      },
      maxWidth: '80vh',
      width: '450px',
    });

    const confirmationForm: ConfirmTeeTimeBookingForm = await firstValueFrom(dialogRef.afterClosed());

    if (confirmationForm)
    {
      await this.saveBookings(request, confirmationForm, playerIDs, noValidate);
    }
  }

  async saveBookings(request: PostBulkTeeTimeToOrderRequest, confirmationForm?: ConfirmTeeTimeBookingForm, playerIDs?: PlayerModel[], noValidate?: boolean): Promise<void>
  {
    request.SendNotificationEmailsToAllPlayers = confirmationForm?.allPlayersChecked ?? false;
    if (!noValidate && !request.SendNotificationEmailsToAllPlayers && playerIDs?.length)
    {
      request.BookingDetails.forEach(booking =>
      {
        const playerEmails = playerIDs.filter(a => a.BookingID === booking.BookingID);
        booking.SendNotificationEmailsToAllPlayers = false;
        if (playerEmails?.length)
        {
          booking.CCEmails = playerEmails.filter(a => confirmationForm?.cc.includes(a.Email)).map(a => a.Email) ?? [];
          booking.NotificationEmails = playerEmails.filter(a => confirmationForm?.to.includes(a.Email)).map(a => a.Email) ?? [];
        }
      });
    }

    const productOrder = this.orderStoreService.Get();
    if (productOrder)
    {
      request.RefID = productOrder.RefID;
    }

    try
    {
      this.manageStandAloneLoadingService.startLoading();

      const result = await firstValueFrom(this.manageOrderTeeTimeService.BulkTeeTime(request));

      const successMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.SAVE_SUCCESSFUL', { ItemName: request.BookingDetails[0].BookingID });
      const showSuccessMessage = () => this.snackBarService.showSuccess(successMessage, '', null, 'left');
      const errMessage = result.BookingDetailsResults?.find(a => a.EmailErrorMessage);

      if (!noValidate)
      {
        if (errMessage)
        {
          this.snackBarService.showError(errMessage?.EmailErrorMessage, '', null, 'left').afterDismissed().subscribe(showSuccessMessage);
        } else
        {
          showSuccessMessage();
        }


        this.dialogRef.afterClosed().pipe(take(1)).subscribe(() =>
        {
          this.reloadTeeSheet();
          this.changeDetectorRef.detectChanges();
          this.orderStoreService.changeProductOrderValue(result.Order);
          if (this.hasCheckIn)
          {
            this.sidenavService.setSidenavStatus(true);
          }
        });

        this.dialogRef.close();
      } else
      {
        this.reloadTeeSheet();
        this.changeDetectorRef.detectChanges();
        this.orderStoreService.changeProductOrderValue(result.Order);
      }
    } catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
    return null;
  }

  validatePlayerForm(playersForm: UntypedFormGroup, index: number): boolean
  {
    if (index === 0 && playersForm.invalid)
    {
      const requiredMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.PLAYER_ONE_REQUIRED', { playerNum: ((index + 1) * 4) - 3 });
      this.snackBarService.showError(requiredMessage, '', null, 'left');
      return false;
    }
    return true;
  }

  CreateBookingDetailRequest(playerControl: GroupPlayersComponent): PostBookingDetailsRequest
  {
    const playersForm = playerControl.playersForm;
    const form: PostBookingDetailsRequestForForm = playersForm.getRawValue();
    const request: PostBookingDetailsRequest = {
      BookingID: playersForm.value.BookingID,
      BookingDateTime: playerControl.Booking.bookingDetails.BookingDateTime,
      CourseID: this.data.CurrentTeeSheetTransfer.SelectedCourse.CourseID,
      NumPlayers: form.NumPlayers,
      StartTee: form.StartTee,
      Players: [],
      Notes: form.Notes,
      NotificationEmails: [],
      CCEmails: [],
      SendNotificationEmailsToAllPlayers: false,
      HasManagerAuthorizeRestriction: form.HasManagerAuthorizeRestriction
    };

    let checkedInPlayerContactID = '';
    const contacts = [];
    const duplicateMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_TEE_TIME.SAVE_FAILED_DUPLICATE_CONTACTS');
    for (let playerIndex = 0; playerIndex < form.NumPlayers; playerIndex++)
    {
      const playerItemForm = form.playersList.player[playerIndex];
      const playerItem: Player = new Player();
      playerItem.PlayerNumber = playerIndex + 1;
      playerItem.Contact = new Contact();

      if (playerItemForm.Contact)
      {
        playerItem.Contact.ContactID = playerItemForm.Contact.ContactID;
        playerItem.Contact.DisplayName = playerItemForm.DisplayName ?
          playerItemForm.DisplayName : playerItemForm.Contact.DisplayName;
        playerItem.Contact.ProfilePicture = playerItemForm.Contact.ProfilePicture;
      }
      else if (playerItemForm.DisplayName)
      {
        playerItem.Contact.ContactID = '00000000-0000-0000-0000-000000000000';
        playerItem.Contact.DisplayName = playerItemForm.DisplayName;
      } else if (playerItem.PlayerNumber === 1)
      {
        playerItem.Contact.ContactID = '00000000-0000-0000-0000-000000000000';
        playerItem.Contact.DisplayName = this.GroupName;
      }

      if (playerItem.Contact.ContactID !== '00000000-0000-0000-0000-000000000000'
        && playerItem.Contact.ContactID !== '88888888-8888-8888-8888-888888888888'
        && contacts.find(contact => contact === playerItem.Contact.ContactID))
      {
        this.snackBarService.showError(duplicateMessage, '', null, 'left');
        this.manageStandAloneLoadingService.finishLoading();
        return;
      } else
      {
        contacts.push(playerItem.Contact.ContactID);
      }

      if (!request.HasManagerAuthorizeRestriction)
      {
        request.HasManagerAuthorizeRestriction = playerItemForm.Contact?.HasRestriction;
      }

      playerItem.Holes = playerItemForm.Holes ? 1 : 0;
      playerItem.PriceLevelID = playerItemForm.PriceLevelID;
      playerItem.Cart = playerItemForm.Cart;
      playerItem.Attachments = playerItemForm.Attachments.map(attachment => attachment.AttachmentUID);
      playerItem.NoShow = !!(PlayerStatus.NoShow === playerItemForm?.Status || PlayerStatus.NoShowFromAPI === playerItemForm?.Status);

      if (playerControl.Booking.bookingDetails.Players[playerIndex]?.Status !== PlayerStatus.CheckIn && playerItemForm.Status && playerItemForm.Status === PlayerStatus.CheckIn)
      {
        if (!checkedInPlayerContactID)
        {
          checkedInPlayerContactID = playerItem?.Contact?.ContactID;
        }
        playerItem.CheckIn = true;
        this.hasCheckIn = true;
      }

      request.Players[playerIndex] = playerItem;
    }

    return request;
  }

  /**
   * Cancel dialog
   */
  cancel(): void
  {
    this.dialogRef.close({ cancelled: true });
  }

  /**
   * Close dialog
   */
  close(): void
  {
    this.cancel(); // Use same logic as cancel
  }

  // ===== ACTION METHODS (existing) =====

  async importPlayers(): Promise<void>
  {
    let errorMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.CAN_NOT_IMPORT_WHEN_HAS_CHECKEDIN');

    const bookingHasCheckedInPlayers = this.GroupBookingDetails.BookingDetails
      ?.some(item =>
        item.Players?.some(player => player.Status === PlayerStatus.CheckIn || player.Status === PlayerStatus.Paid || player.Status === PlayerStatus.CheckInInprogress)
      ) ?? false;

    if (bookingHasCheckedInPlayers)
    {
      this.snackBarService.showError(errorMessage, '', null, 'left');
      return;
    }

    errorMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.IMPORT_PLAYERS_FAILED');

    try
    {
      const input = document.createElement('input');
      input.type = 'file';
      input.accept = '.csv,.xlsx';
      input.style.display = 'none';

      const file = await this.waitForFileSelection(input);
      if (!file) return;

      const allowedTypes = [
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'text/csv'
      ];

      if (!allowedTypes.includes(file.type))
      {
        this.snackBarService.showError(errorMessage, '', null, 'left');
        return;
      }
      this.manageStandAloneLoadingService.startLoading();
      await this.uploadFile(file, errorMessage);
    } catch (error)
    {
      console.error(error);
      if (!error?.message)
      {
        this.snackBarService.showError(errorMessage, '', null, 'left');
      }
    } finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }
  private waitForFileSelection(input: HTMLInputElement): Promise<File | null>
  {
    return new Promise(resolve =>
    {
      input.onchange = (event: Event) =>
      {
        const selectedFile = (event.target as HTMLInputElement).files?.[0] || null;
        resolve(selectedFile);
      };
      document.body.appendChild(input);
      input.click();
      document.body.removeChild(input);
    });
  }
  private async uploadFile(file: File, errorMessage): Promise<void>
  {
    const formData = new FormData();
    formData.append('ImportedFile', file);
    formData.append('GroupBookingUID', this.data.GroupUID);
    const result = await firstValueFrom(
      this.groupTeeTimeService.PostBookingPlayersImport(formData)
    );

    if (result?.Players)
    {
      await this.assigningContactToTeeTimes(result.Players);
      const successMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.IMPORT_PLAYERS_SUCCESS');
      this.snackBarService.showSuccess(successMessage, '', null, 'left');
    } else
    {
      this.snackBarService.showError(errorMessage, '', null, 'left');
    }
  }

  async assigningContactToTeeTimes(bookingPlayer: Array<BookingPlayer>): Promise<void>
  {
    let globalPlayerIndex = 0;

    this.groupPlayersComponents.toArray().forEach((playerControl) =>
    {
      const players = (playerControl.playersForm.controls.playersList.get('player') as FormArray).controls;

      for (const playerForm of players)
      {
        if (globalPlayerIndex > bookingPlayer.length)
        {
          break;
        }

        const bookingPlayerFounded = bookingPlayer?.[globalPlayerIndex++];

        if (!bookingPlayerFounded) return;

        const contact = new Contact();
        contact.ContactID = bookingPlayerFounded.ContactID;

        const firstName = bookingPlayerFounded.FirstName;
        const lastName = bookingPlayerFounded.LastName;
        const memberID = bookingPlayerFounded.MemberID;
        const email = bookingPlayerFounded.Email;

        const isGuest = firstName?.toLowerCase() === 'guest';
        const hasLastName = !!lastName;
        const hasEmail = !!email;
        if (bookingPlayerFounded.ContactID)
        {
          playerForm.get('EditDisplayName')?.setValue(true);
          playerForm.get('Contact')?.setValue(contact);
          setTimeout(() =>
          {
            playerForm.get('EditDisplayName')?.setValue(false);
          });
        } else
        {
          playerForm.get('Contact')?.reset();
        }
        if (hasLastName)
        {
          let displayName = `${ firstName } ${ lastName }`;
          if (memberID)
          {
            displayName += ` - ${ memberID }`;
          }
          contact.DisplayName = displayName;
          playerForm.get('DisplayName')?.setValue(displayName);
        } else if (hasEmail)
        {
          let displayName = firstName;
          if (memberID)
          {
            displayName += ` - ${ memberID }`;
          }
          contact.DisplayName = displayName;
          playerForm.get('DisplayName')?.setValue(displayName);
        } else if (isGuest)
        {
          playerForm.get('DisplayName')?.reset();
        }
        playerForm.updateValueAndValidity();

        const firstContact = players[0].get('Contact').value;
        if (firstContact?.ContactID && !playerControl.config.GroupDefaultValues.PriceLevelID)
        {
          playerControl.determineDefaultsOfPlayerPriceLevelsAndSelectFirstDefault({
            contact: firstContact,
            hasRestrictionAuthorize: false,
            playerIndex: 0,
          });
        }
      }
    });
    this.changeDetectorRef.detectChanges();
  }

  async exportPlayers(): Promise<void>
  {
    await this.confirm(true);
    this.manageStandAloneLoadingService.startLoading();
    try
    {
      const result = await firstValueFrom(this.groupTeeTimeService.GetBookingPlayersExport({ GroupUID: this.data.GroupUID }));

      await this.DownloadFile(result?.FileUrl);

    } catch (error)
    {
      console.error(error);
      const errorMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.FILE_DOWNLOAD_FAILED');
      this.snackBarService.showError(errorMessage, '', null, 'left');
    }
    finally
    {
      this.manageStandAloneLoadingService.finishLoading();
    }
  }

  async DownloadFile(url: string): Promise<void>
  {
    try
    {
      window.open(url, '_blank');
      const successMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.FILE_DOWNLOADED');
      this.snackBarService.showSuccess(successMessage, '', null, 'left');
    } catch (error)
    {
      console.error(error);
      const errorMessage = this.translate.instant('POS_TEE_SHEET.MANAGE_PLAYERS.FILE_DOWNLOAD_FAILED');
      this.snackBarService.showError(errorMessage, '', null, 'left');
    }
  }

  onContactItemClickEvent(contact: Contact): void
  {
    this.config.FilterPlayer.ContactID = contact.ContactID;
    this.selectedContact = contact;
    this.config = { ...this.config };
    this.updateChildrenAllStatus();
    this.updateShowEmptyMessageState();
  }

  removeContactItemClickEvent(event): void
  {
    event.stopPropagation();
    this.config.FilterPlayer.ContactID = null;
    this.selectedContact = null;
    this.config = { ...this.config };
    this.updateChildrenAllStatus();
    this.updateShowEmptyMessageState();
  }

  hideCheckedIns(): void
  {
    this.config.FilterPlayer.HideCheckInPlayers = !this.config.FilterPlayer.HideCheckInPlayers;
    this.config = { ...this.config };
    this.updateChildrenAllStatus();
    this.updateShowEmptyMessageState();
  }

  updateShowEmptyMessageState()
  {
    this.showEmptyMessage = true;
    this.groupPlayersComponents.forEach(a =>
    {
      if (!a.hidden)
      {
        this.showEmptyMessage = false;
      }
    });
  }

  playersFormChanged()
  {
    this.updateShowEmptyMessageState();
  }

  reloadTeeSheet(): void
  {
    this.teeSheetMainService.changeMultiTeeSheet({
      ReloadTeeSheet: true,
      SelectedCourse: this.data.CurrentTeeSheetTransfer?.SelectedCourse
    });

  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
    this.groupBookingService.groupBookingDetails = {} as GetGroupBookingDetailsResponse;
  }
}
