import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { GetCrossoverTeeTimesRequest, GetCrossoverTeeTimesResponse, GetNineHolesTeeTimeResponse, GetNineHolesTeeTimesRequest, GetReservedTeeTimesRequest, GetReservedTeeTimesResponse } from "../@intermediate/models/get-flagged-tee-times.model";
import { PostFlagNineHoleTeeTimesRequest, PostFlagNineHoleTeeTimesResponse, PostFlagReservedTeeTimesRequest, PostFlagReservedTeeTimesResponse, PostFlagTeeTimesRequest, PostFlagTeeTimesResponse } from "../@intermediate/models/post-flag-reserved-tee-time.model";
import { PostUnFlagTeeTimesRequest, PostUnFlagTeeTimesResponse } from "../@intermediate/models/post-unflag-tee-time.model";
import { ITeeSheetFlagService } from "../@intermediate/tee-sheet-flag-service.interface";

@Injectable()

export class TeeSheetFlagService implements ITeeSheetFlagService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet';
  }
  
  PostCrossoverTeeTimes(request: PostUnFlagTeeTimesRequest): Observable<PostUnFlagTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageTeeTime/UnflagCrossoverTeeTimes';
    return this.http.post<PostUnFlagTeeTimesResponse>(apiUrl, request);
  }

  GetCrossoverTeeTimes(request: GetCrossoverTeeTimesRequest): Observable<GetCrossoverTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/TeeSheet/TeeSheetCrossover';
    return this.http.get<GetCrossoverTeeTimesResponse>(apiUrl, { params: request as any });
  }

  PostFlagCrossover(request: PostFlagTeeTimesRequest): Observable<PostFlagTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageTeeTime/CrossoverTeeTimes';
    return this.http.post<PostFlagReservedTeeTimesResponse>(apiUrl, request);
  }

  PostFlagReserved(request: PostFlagReservedTeeTimesRequest): Observable<PostFlagReservedTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageTeeTime/ReserveTeeTimes';
    return this.http.post<PostFlagReservedTeeTimesResponse>(apiUrl, request);
  }

  PostFlagNineHole(request: PostFlagNineHoleTeeTimesRequest): Observable<PostFlagNineHoleTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/TeeSheet/TeeTimeNineHole';
    return this.http.post<PostFlagNineHoleTeeTimesResponse>(apiUrl, request);
  }

  PostUnflagNineHole(request: PostUnFlagTeeTimesRequest): Observable<PostUnFlagTeeTimesResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + '/TeeSheet/TeeTimeNineHole';;
    return this.http.delete<PostUnFlagTeeTimesResponse>(apiUrl, options);
  }
  GetTeeTimeNineHoles(request: GetNineHolesTeeTimesRequest): Observable<GetNineHolesTeeTimeResponse>
  {
    const apiUrl = this.serviceUrl + '/TeeSheet/GetTeeTimeNineHole';
    return this.http.get<GetNineHolesTeeTimeResponse>(apiUrl, { params: request as any });
  }

  ReservedTeeTimes(request: GetReservedTeeTimesRequest): Observable<GetReservedTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageTeeTime/ReserveTeeTimes';
    return this.http.get<GetReservedTeeTimesResponse>(apiUrl, { params: request as any });
  }

  UnreserveTeeTimes(request: PostUnFlagTeeTimesRequest): Observable<PostUnFlagTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '/ManageTeeTime/UnreserveTeeTimes';
    return this.http.post<PostUnFlagTeeTimesResponse>(apiUrl, request);
  }

}
