import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { Subscription } from 'rxjs';
import { ClassRestrictionsSheetTime, PostTeeSheetClassRestrictionsResponse, TeeSheetRestrictionsApply } from '../../../@intermediate/models/post-tee-sheet-class-restrictions.mode';
import { TeeSheetFilterViewService } from '../../../@services/tee-sheet-filter-view.service';
import { TeeSheetRestrictionsService } from '../../../@services/tee-sheet-restrictions.service';

@Component({
  selector: 'tei-pos-tee-sheet-tee-sheet-restrictions-tee-time',
  templateUrl: './tee-sheet-restrictions-tee-time.component.html',
  styleUrls: ['./tee-sheet-restrictions-tee-time.component.scss']
})
export class TeeSheetRestrictionsTeeTimeComponent implements OnInit, OnDestroy
{
  @Output() public onCancel = new EventEmitter();
  teeSheetRestrictionsData: PostTeeSheetClassRestrictionsResponse;
  subscription = new Subscription();
  public isCheckboxChanged = false;
  dataSource = new MatTableDataSource<ClassRestrictionsSheetTime>();
  groupColumns: string[] = [];
  displayedColumns: string[] = [];
  initialize = true;
  emptyTable = false;
  @Input() public set teeSheetRestrictionsDataInput(value: PostTeeSheetClassRestrictionsResponse)
  {
    this.dataSource.data = value.SheetTimes;
    if (value.SheetTimes?.length && value.SheetTimes[0].Groups)
    {
      this.groupColumns = value.SheetTimes[0].Groups.map(group => group.ClassUID);
      this.displayedColumns = ['teeTime', ...this.groupColumns];
    }
    this.teeSheetRestrictionsData = value;
    this.teeSheetRestrictionsData.IgnoreClassRestrictions = false;
    this.updateShowingRestrictions(this.teeSheetFilterViewService.getApplyIds());
  };
  sheetTimesForm: FormGroup;
  constructor(public teeSheetRestrictionsService: TeeSheetRestrictionsService,
    public teeSheetFilterViewService: TeeSheetFilterViewService,
  ) { }

  ngOnInit(): void
  {
    this.loadData();
  }

  getGroupValue(element: ClassRestrictionsSheetTime, classUID: string): boolean
  {
    const group = element.Groups.find(g => g.ClassUID === classUID);
    return group.Value;
  }

  setGroupValue(element: ClassRestrictionsSheetTime, classUID: string, value: boolean): void
  {
    const group = element.Groups.find(g => g.ClassUID === classUID);
    if (group)
    {
      group.Value = value;
    }

    this.isCheckboxChanged = true;
    this.teeSheetRestrictionsService.SheetTimeFormChanged = true;
  }

  isAllGroupValuesChecked(groupCol: string): boolean
  {
    return this.dataSource.data.every(element => this.getGroupValue(element, groupCol));
  }

  isSomeGroupValuesChecked(groupCol: string): boolean
  {
    const values = this.dataSource.data.map(element => this.getGroupValue(element, groupCol));
    return values.includes(true) && values.includes(false);
  }

  toggleAllGroupValues(groupCol: string, checked: boolean): void
  {
    this.dataSource.data.map(element =>
    {
      this.setGroupValue(element, groupCol, checked);
    });
  }

  public loadData(): void
  {
    this.subscription.add(this.teeSheetFilterViewService.applyIds$.subscribe(value =>
    {
      if (value)
        this.updateShowingRestrictions(value);
    }));

    this.subscription.add(this.teeSheetRestrictionsService.apply$.subscribe((value: TeeSheetRestrictionsApply) =>
    {
      this.updateTable(value);
    }));

    this.subscription.add(this.teeSheetRestrictionsService.reset$.subscribe(value =>
    {
      this.isCheckboxChanged = !value;
    }));
  }

  updateShowingRestrictions(restrictions: string[]): void
  {
    if (restrictions.length)
      this.initialize = false;

    this.teeSheetRestrictionsData.Groups.map((item, index) =>
    {
      if (!restrictions?.length && !this.initialize)
      {
        item.Show = false;
        this.emptyTable = true;
        return;
      }
      item.Show = !!restrictions.find(restrict => restrict === item.ClassUID);
      this.emptyTable = false;
    });
  }

  public updateTable(teeSheetRestrictionsApply: TeeSheetRestrictionsApply): void
  {
    const {
      TimeRangeStartIndex,
      TimeRangeEndIndex,
      Apply,
      Restrictions,
      IgnoreClass
    } = teeSheetRestrictionsApply;

    if (TimeRangeStartIndex !== null && TimeRangeEndIndex !== null)
    {
      for (let i = TimeRangeStartIndex; i <= TimeRangeEndIndex; i++)
      {
        const sheetTime = this.teeSheetRestrictionsData.SheetTimes.find(item => item.Index === i);
        Restrictions.map(item =>
        {
          const group = sheetTime.Groups.find(group => group.ClassUID === item);
          group.Value = Apply;
        });
      }
    }
    this.dataSource.data = this.teeSheetRestrictionsData.SheetTimes;
    this.teeSheetRestrictionsData.IgnoreClassRestrictions = IgnoreClass;
    this.isCheckboxChanged = true;
  }

  public onSave(): void
  {
    this.teeSheetRestrictionsService.saveRestrictions({ ...this.teeSheetRestrictionsData, ModifiedTSItems: this.dataSource.data });
    this.teeSheetRestrictionsService.setSheetTimeFormChange(false);
    this.isCheckboxChanged = false;
  }

  public cancel(): void
  {
    this.onCancel.emit();
  };

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
}
