import { Course } from "./get-tee-sheet-main.model";

export class PostTeeSheetWaitListRequest
{
    StartDateTime: string = '';
    EndDateTime: string = '';
    BookerID: string = '';
    ContactID: string = '';
    Players: string = '';
    Courses: Array<WaitListCourse> = [];
    CreatedDateStart: string = '';
    CreatedDateEnd: string = '';
    CreatedBy: string = '';
    AvailableOnly: boolean = false;
    Status: WaitListStatus = WaitListStatus.Pending;
    PageNumber: number = 1;
    PageSize: number = 10;
}

export class WaitListCourse
{
    CourseID: string;
    StartTeeHole: number;
}

export class PostTeeSheetWaitListResponse
{
    BookedCount: number;
    CancelledCount: number;
    ExpiredCount: number;
    PendingCount: number;
    WaitLists: Array<WaitList>;
    TotalCount: number;
}

export enum WaitListStatus
{
    Pending = 1,
    Available = 2,
    Booked = 3,
    Cancelled = 4,
    Expired = 5
}

export enum CreatorEnum
{
    Booker,
    Contact
}

export class WaitList
{
    TeeTime: string;
    EndTeeTime: string;
    Status: WaitListStatus;
    NineHoles: number;
    StartTee: number;
    Player1ID: string;
    Player2ID: string;
    Player3ID: string;
    Player4ID: string;
    Player5ID: string;
    Player1CT: number;
    Player2CT: number;
    Player3CT: number;
    Player4CT: number;
    Player5CT: number;
    Player1: string;
    Player2: string;
    Player3: string;
    Player4: string;
    Player5: string;
    Players: number;
    ModifiedDate: string;
    ModifiedBy: string;
    CreatedDate: string;
    CreatedBy: string;
    Notes: string;
    GroupUID: string;
    GTeeTimesHeaderUID: string;
    BookedDateTime: string;
    BookedCourseID: string;
    CourseIDs: string;
    CourseNames: string;
    Player1ProfilePicture: string;
    PlayerDetails: Array<PlayerDetail>;
}

export class PlayerDetail
{
    PlayerNo: number;
    ContactID: string;
    FirstName: string;
    LastName: string;
    Email: string;
    MemberID: string;
    Phone: string;
    ProfilePicture: string;
}

export class WaitListDialogData
{
    Courses: Array<Course>;
    SelectedDateAndTime: string;
    SelectedCourse: Course;
}

export class WaitListPlayersDialogData
{
    Players: Array<PlayerDetail>;
    PlayersCount: number;
    IsMobile: boolean;
}