import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { PostTeeSheetWaitListRequest, PostTeeSheetWaitListResponse } from '../@intermediate/models/post-tee-sheet-wait-list.model';
import { ITeeSheetWaitListService } from '../@intermediate/tee-sheet-wait-list.interface';

@Injectable({
  providedIn: 'root'
})
export class TeeSheetWaitListService implements ITeeSheetWaitListService
{

  private readonly serviceUrl: string;


  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/';
  }

  PostWaitListSearch(request: PostTeeSheetWaitListRequest): Observable<PostTeeSheetWaitListResponse>
  {
    const apiUrl = this.serviceUrl + 'WaitList/Search';
    return this.http.post<PostTeeSheetWaitListResponse>(apiUrl, request);
  }
}
