import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { GetModifiedTeeSheetResponse } from '../@intermediate/models/get-modified-tee-sheet.model';
import { TeeSheetTemplateApply } from '../@intermediate/models/get-teesheet-template.model';
import { PostModifiedTeeSheetRequest } from '../@intermediate/models/post-modified-tee-sheet.model';

@Injectable()
export class TeeSheetTemplateService
{
  constructor() { }
  private Load = new BehaviorSubject<GetModifiedTeeSheetResponse>(new GetModifiedTeeSheetResponse());
  load$ = this.Load.asObservable();

  private Apply = new Subject<TeeSheetTemplateApply>();
  apply$ = this.Apply.asObservable();

  private Reset = new Subject<boolean>();
  reset$ = this.Reset.asObservable();

  private Save = new Subject<PostModifiedTeeSheetRequest>();
  save$ = this.Save.asObservable();

  public TeeTimeFormChanged = false;

  load(value: GetModifiedTeeSheetResponse): void
  {
    this.Load.next(value);
  }

  apply(value: TeeSheetTemplateApply): void
  {
    this.Apply.next(value);
    this.teeTimeFormChange(true);
  }

  reset(): void
  {
    this.Reset.next(true);
    this.teeTimeFormChange(false);
  }

  teeTimeFormChange(value: boolean): void
  {
    this.TeeTimeFormChanged = value;
  }

  saveTemplate(value): void
  {
    this.Save.next(value);
  }
}
