import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatDialogModule } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { ManageStandAloneLoadingService } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DonutChartModule, LoadingModule } from '@tei/material';
import { TeeSheetService } from '../../@services/tee-sheet.service';
import { AboutStatComponent } from './about-stat/about-stat.component';
import { TeeSheetStatsComponent } from './tee-sheet-stats.component';


const MATERIAL_MODULES = [MatButtonModule, MatDialogModule, MatIconModule, MatTooltipModule];
const BASE_MODULES = [CommonModule];
const TEI_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule];
const TEI_MATERIAL_MODULES = [LoadingModule, DonutChartModule];
const COMPONENTS = [TeeSheetStatsComponent];

@NgModule({
  declarations: [...COMPONENTS, AboutStatComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [
    { provide: 'ITeeSheetService', useClass: TeeSheetService },
    ManageStandAloneLoadingService
  ]
})
export class TeeSheetStatsModule { }
