import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextEllipsisPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';
import { ContactAvatarModule, ContactQuickSearchModule } from '@tei/office-contacts';
import { ManageTeeTimeHelperService } from '../../../../@services/manage-tee-time.helper.service';
import { TeeSheetService } from '../../../../@services/tee-sheet.service';
import { RainCheckConfirmationComponent } from './rain-check-confirmation.component';

const MATERIAL_MODULES = [
  MatTooltipModule,
  MatInputModule,
  MatButtonToggleModule,
  MatDividerModule,
  FormsModule,
  MatButtonModule,
  MatRippleModule,
  MatIconModule,
  MatListModule
];
const COMMON_MODULES = [CommonModule];
const COMPONENTS = [RainCheckConfirmationComponent];
const TEI_MATERIAL_MODULES = [LoadingModule];
const TEI_CORE_MODULES = [
  TeiTextEllipsisPipeModule,
  TeiDateTranslateModule,
  TeiTextTranslatePipeModule,
  TeiCurrencyTranslateModule
];
const TEI_OFFICE_CONTACT = [
  ContactAvatarModule,
  ContactQuickSearchModule
];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_OFFICE_CONTACT
  ],
  exports: [
    ...MATERIAL_MODULES,
    ...COMMON_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_OFFICE_CONTACT,
    ...COMPONENTS
  ],
  providers: [
    ManageTeeTimeHelperService,
    { provide: 'ITeeSheetService', useClass: TeeSheetService }
  ]
})
export class RainCheckConfirmationModule
{ }
