import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IGroupTeeTimeService } from '../@intermediate/group-tee-time-service.interface';
import { DeleteGroupTeeTimesRequest, DeleteGroupTeeTimesResponse } from '../@intermediate/models/delete-group-tee-times.model';
import { DeleteTeeTimeFromGroupTeeTimesRequest, DeleteTeeTimeFromGroupTeeTimesResponse } from '../@intermediate/models/delete-tee-time-from-group-tee-times.model';
import { GetBookingGroupRequest, GetBookingGroupResponse } from '../@intermediate/models/get-booking-group.model';
import { GetBookingPlayersExportRequest, GetBookingPlayersExportResponse } from '../@intermediate/models/get-booking-players-export.model';
import { GetGroupTeeTimesRequest, GetGroupTeeTimesResponse } from '../@intermediate/models/get-group-tee-times.model';
import { PostApplyShotgunRequest, PostApplyShotgunResponse } from '../@intermediate/models/post-apply-shotgun.model';
import { PostBookingPlayersImportResponse } from '../@intermediate/models/post-booking-players-import.model';
import { PostCreateTeeTimeGroupRequest, PostCreateTeeTimeGroupResponse } from '../@intermediate/models/post-create-tee-time-group';
import { PostFirstPlayersEmailsByGroupUIDRequest, PostFirstPlayersEmailsByGroupUIDResponse } from '../@intermediate/models/post-first-players-emails-group-uid.model';
import { PostLinkTeeTimeGroupRequest, PostLinkTeeTimeGroupResponse } from '../@intermediate/models/post-link-tee-time-to-group.model';
import { PostSwapMoveGroupPlayersRequest, PostSwapMoveGroupPlayersResponse } from '../@intermediate/models/post-move-swap-group-players.model';
import { PostUpdateGroupTeeTimesRequest, PostUpdateGroupTeeTimesResponse } from '../@intermediate/models/post-update-tee-time-group.model';
import { PostValidateAndLinkTeeTimeGroupRequest, PostValidateAndLinkTeeTimeGroupResponse } from '../@intermediate/models/post-validate-and-link-tee-time-to-group.model';

@Injectable()

export class GroupTeeTimeService implements IGroupTeeTimeService
{

  serviceUrl: string;
  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient,
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/POS/TeeSheet/GroupTeeTimes/';
  }

  GetGroupTeeTimes(request: GetGroupTeeTimesRequest): Observable<GetGroupTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetGroupTeeTimesResponse>(apiUrl, { params: request as any });
  }

  PostGroupTeeTimes(request: PostCreateTeeTimeGroupRequest): Observable<PostCreateTeeTimeGroupResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateTeeTimeGroupResponse>(apiUrl, request);
  }

  DeleteGroupTeeTimes(request: DeleteGroupTeeTimesRequest): Observable<DeleteGroupTeeTimesResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + '';
    return this.http.delete<DeleteGroupTeeTimesResponse>(apiUrl, options);
  }

  GetBookingGroup(request: GetBookingGroupRequest): Observable<GetBookingGroupResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingGroup';
    return this.http.get<GetBookingGroupResponse>(apiUrl, { params: request as any });
  }

  PostLinkTeeTimeToGroup(request: PostLinkTeeTimeGroupRequest): Observable<PostLinkTeeTimeGroupResponse>
  {
    const apiUrl = this.serviceUrl + 'LinkTeeTimeToGroup';
    return this.http.post<PostLinkTeeTimeGroupResponse>(apiUrl, request);
  }

  PostValidateAndLinkTeeTimeToGroup(request: PostValidateAndLinkTeeTimeGroupRequest): Observable<PostValidateAndLinkTeeTimeGroupResponse>
  {
    const apiUrl = this.serviceUrl + 'ValidateAndLinkTeeTimeGroup';
    return this.http.post<PostValidateAndLinkTeeTimeGroupResponse>(apiUrl, request);
  }

  PostApplyShotgun(request: PostApplyShotgunRequest): Observable<PostApplyShotgunResponse>
  {
    const apiUrl = this.serviceUrl + 'ApplyShotgun';
    return this.http.post<PostApplyShotgunResponse>(apiUrl, request);
  }

  DeleteTeeTimeFromGroupTeeTimes(request: DeleteTeeTimeFromGroupTeeTimesRequest): Observable<DeleteTeeTimeFromGroupTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + 'TeeTime';
    return this.http.delete<DeleteTeeTimeFromGroupTeeTimesResponse>(apiUrl, { params: request as any });
  }

  PostUpdateGroupTeeTimes(request: PostUpdateGroupTeeTimesRequest): Observable<PostUpdateGroupTeeTimesResponse>
  {
    const apiUrl = this.serviceUrl + 'Update';
    return this.http.post<PostUpdateGroupTeeTimesResponse>(apiUrl, request);
  }

  FirstPlayersEmailsByGroupUID(request: PostFirstPlayersEmailsByGroupUIDRequest): Observable<PostFirstPlayersEmailsByGroupUIDResponse>
  {
    const apiUrl = this.serviceUrl + 'FirstPlayersEmailsByGroupUID';
    return this.http.post<PostFirstPlayersEmailsByGroupUIDResponse>(apiUrl, request);
  }

  SwapMoveGroupPlayers(request: PostSwapMoveGroupPlayersRequest): Observable<PostSwapMoveGroupPlayersResponse>
  {
    const apiUrl = this.serviceUrl + 'SwapMoveGroupPlayers';
    return this.http.post<PostSwapMoveGroupPlayersResponse>(apiUrl, request);
  }

  GetBookingPlayersExport(request: GetBookingPlayersExportRequest): Observable<GetBookingPlayersExportResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingPlayersExport';
    return this.http.get<GetBookingPlayersExportResponse>(apiUrl, { params: request as any });
  }

  PostBookingPlayersImport(request: FormData): Observable<PostBookingPlayersImportResponse>
  {
    const apiUrl = this.serviceUrl + 'BookingPlayersImport';
    return this.http.post<PostBookingPlayersImportResponse>(apiUrl, request);
  }
}
