import { Inject, Injectable } from '@angular/core';
import { firstValueFrom, Subject } from 'rxjs';
import { IGroupTeeTimeService } from '../@intermediate/group-tee-time-service.interface';
import { Player } from '../@intermediate/models/get-booking-details.model';
import { GetBookingGroupResponse } from '../@intermediate/models/get-booking-group.model';
import { GetGroupBookingDetailsResponse } from '../@intermediate/models/group-booking-details.model';


@Injectable({ providedIn: 'root' })
export class GroupBookingService
{
  bookingGroup: GetBookingGroupResponse;
  groupBookingDetails: GetGroupBookingDetailsResponse;
  private moveSwapPlayer = new Subject<Player>();
  moveSwapPlayer$ = this.moveSwapPlayer.asObservable();

  constructor(
    @Inject('IGroupTeeTimeService') private groupTeeTimeService: IGroupTeeTimeService,
  )
  {
  }
  async getBookingGroup(BookingID: number): Promise<void>
  {
    try
    {
      this.bookingGroup = await firstValueFrom(this.groupTeeTimeService.GetBookingGroup({
        BookingID: BookingID
      }));
    } catch (error)
    {
      console.error(error);
    }
  }

  setMoveSwapPlayer(value: Player): void
  {
    this.moveSwapPlayer.next(value);
  }
}
